import debounce from 'lodash.debounce';

import constants, { publicConstants } from '../constants';
import { dispatch } from '../store/state/state-manager';
import * as RulesStore from '../rules/rules-store';
import AnnotationProcessor from '../annotation/annotation-processor';
import ContextProcessor from '../context/context-processor';
import * as CaseUtils from '../utils/case-utils';
import { ActionManagerInstance } from '../actions/manager';
import Utils from '../utils/utils';

import { refreshData, fillFormWithAI } from './action-creator';

// Keeps track of the views with refresh targets.
export const refreshTargetViewsList = {};

/**
 * Description : Removes stale container item from refreshList
 * @param {string} context container name
 */
export const removeViewsFromTargetList = (context) => {
  if (context && refreshTargetViewsList[context]) {
    delete refreshTargetViewsList[context];
  }
};

/**
 * [dispatchRefreshData]
 * Description : Dispatches the REFRESH_DATA action
 * @param   {string}    ID              CaseID or AssignmentID
 * @param   {string}    actionID        Action ID either a casewide action or assignement action
 * @param   {string}    context         Data context
 * @param   {boolean}   caseWideAction  Whether the action action is a case wide action or not.
 * @param   {object}    options         options object for actions
 */
export const dispatchRefreshData = (ID, actionID, context, caseWideAction, options) => {
  if (!(ID && context && actionID)) {
    return;
  }

  const actionMgrID = options.actionMgrID || ActionManagerInstance.getNextActionMgrId();

  // payload to the REFRESH_DATA action
  const payload = {
    ID,
    actionID,
    context,
    caseWideAction,
    options: {
      ...options,
      actionMgrID
    }
  };
  if (options.isFillFormWithAI) {
    dispatch(fillFormWithAI(payload));
  } else {
    // Dispatching REFRESH_DATA
    dispatch(refreshData(payload));
  }
};

const debouncedRefreshDispatch = debounce(dispatchRefreshData, 100);
/**
 * [dispatchCaseViewRefresh]
 * Description : Dispatches the REFRESH_DATA action
 * @param   {string}    ID              CaseID or AssignmentID
 * @param   {string}    viewID          View ID
 * @param   {string}    context         Data context
 * @param   {string}    pageReference   page reference
 * @param   {object}    options         Options object for action
 */
export const dispatchCaseViewRefresh = (ID, viewID, context, pageReference, options) => {
  if (!(ID && context && viewID)) {
    return;
  }

  if (pageReference) {
    pageReference = Utils.convertPageRefToClipboardPageNotation(pageReference);
  }

  // payload to the REFRESH_DATA action
  const payload = {
    ID,
    viewID,
    context,
    options,
    caseViewAction: true,
    interestPage: pageReference
  };
  // Dispatching REFRESH_DATA
  dispatch(refreshData(payload));
};

/**
 * [recordViewForRefresh]
 * Description : Pushes the view to a list. This list holds all the views which have refresh targets.
 * @param   {string}    viewName        view name from which the refresh is triggered
 * @param   {string}    context         Data context
 * @param   {string}    pageReference   Page reference inside the content
 */
export const recordViewForRefresh = (viewName, context, pageReference) => {
  if (!refreshTargetViewsList[context]) {
    refreshTargetViewsList[context] = {};
  }
  if (!refreshTargetViewsList[context][viewName]) {
    refreshTargetViewsList[context][viewName] = { pageReference };
  }
};

/**
 * [shouldViewRefresh]
 * Description : Returns true if context has any views present in it,else returns false.
 *               Refresh will happen for all the views present in that context.
 * @param   {string}    context     Data context
 * @returns {boolean}   boolean
 */
export const shouldViewRefresh = (context) => {
  return (
    refreshTargetViewsList[context] &&
    Object.keys(refreshTargetViewsList[context]).length > 0 &&
    CaseUtils.isPerform(context)
  );
};

/**
 * This returns the leaf page name and property name of the embedded property
 * @param {string} property with annotation
 * @returns {object} containing pageName and propertyName
 * @example if we pass '@P .Address.Street' it will return { pageName : ".Address", propertyName : "Street"}
 */
const getPropertyReferenceInfo = (property) => {
  const pageName = property.substring(0, property.lastIndexOf('.'));
  const propertyName = property.substring(property.lastIndexOf('.') + 1);

  return { pageName, propertyName };
};

/**
 *This takes the property reference value as input and returns the isDeclarativeTarget value from property metadata
 * @param {object} component C11nEnv object
 * @param {string} paramValue data source parameter value
 * @returns {boolean}  isDeclarativeTarget
 *
 */
const extractDeclarativeTargetFromParam = (component, paramValue) => {
  const propertyReferenceObj = getPropertyReferenceInfo(paramValue.substring(paramValue.lastIndexOf(' ') + 1));
  const paramMetadata = RulesStore.getPropertyMetadata(
    propertyReferenceObj.propertyName,
    component.getValue(constants.CLASS_ID, propertyReferenceObj.pageName)
  );
  return !!paramMetadata?.isDeclarativeTarget;
};

/**
 * This function takes c11Env object and datasource parameters and returns whether parameters are
 * declarative expression or not
 * @param {object} component C11nEnv object
 * @param {object | Array} parameters datasource parameters can be object/array
 * @returns {boolean} isDeclarativeTarget boolean value
 */
const checkDataSourceParamForDeclarativeTargets = (component, parameters) => {
  let viewShouldRefresh = false;
  if (parameters && Array.isArray(parameters)) {
    const checkForDeclarativeTargets = (param) => {
      const paramValue = param.value;
      if (paramValue.startsWith('@P')) {
        viewShouldRefresh = extractDeclarativeTargetFromParam(component, paramValue);
      }
      return viewShouldRefresh;
    };
    parameters.some(checkForDeclarativeTargets);
  } else if (parameters && typeof parameters === 'object') {
    for (const key in parameters) {
      if (parameters[key].startsWith('@P')) {
        viewShouldRefresh = extractDeclarativeTargetFromParam(component, parameters[key]);
        if (viewShouldRefresh) break;
      }
    }
  }
  return viewShouldRefresh;
};

/**
 * [inspectComponentForRefreshTargets]
 * Description : Inspects the component, checks if there are any refresh targets in it.
 * If any refresh targets, calls a method which tracks the view for further processing.
 * @param   {object}    component   C11nEnv object
 */
export const inspectComponentForRefreshTargets = (component) => {
  let viewShouldRefresh = false;
  // view data should be refreshed if any component in the view has a when authored on it.
  if (component.isWhenExist()) {
    viewShouldRefresh = true;
  } else {
    const prop = component.getRawMetadata().config?.value;
    if (prop) {
      const propMetadata = RulesStore.getPropertyMetadata(
        AnnotationProcessor.getLeafPropertyName(prop),
        ContextProcessor.resolveClassID(prop, component.getPageReference(), component.getContextName())
      );
      // view data should be refreshed if any component in the view is a declare target.
      viewShouldRefresh = !!propMetadata?.isDeclarativeTarget;

      if (!viewShouldRefresh && propMetadata?.datasource?.parameters) {
        viewShouldRefresh = checkDataSourceParamForDeclarativeTargets(component, propMetadata?.datasource?.parameters);
      }
    }
  }
  // View should be refreshed only if any if its components has when authored or is a declare target
  if (viewShouldRefresh) {
    recordViewForRefresh(component.getCurrentView(), component.getContextName(), component.getPageReference());
  }
};

export const refreshAssignment = (context, pageReference, options) => {
  const caseWideAction = CaseUtils.isCaseWideAction(context, pageReference);
  // Get actionID
  const actionID = ContextProcessor.getProperyValue(publicConstants.CASE_INFO.ACTIVE_ACTION_ID, pageReference, context)
    ? ContextProcessor.getProperyValue(publicConstants.CASE_INFO.ACTIVE_ACTION_ID, pageReference, context)
    : ContextProcessor.getProperyValue(publicConstants.CASE_INFO.ASSIGNMENTACTION_ID, pageReference, context);
  const ID = caseWideAction
    ? ContextProcessor.getProperyValue(publicConstants.CASE_INFO.CASE_INFO_ID, pageReference, context)
    : ContextProcessor.getProperyValue(publicConstants.CASE_INFO.ASSIGNMENT_ID, pageReference, context);
  debouncedRefreshDispatch(ID, actionID, context, caseWideAction, options);
};

/**
 * [refreshOnPropertyChange]
 * Description : This function gets the action type, action/assignment ID and gives a call to dispatch REFRESH_DATA
 * @param   {object}    payload
 *                      payload.context - Data context
 *                      payload.pageReference - Page reference in the content
 */
export const refreshOnPropertyChange = (payload) => {
  const { context, pageReference } = payload;

  const viewsTobeRefreshed = shouldViewRefresh(context);
  // Dispatch action to refresh the data only if any views are tracked for refresh.
  if (!viewsTobeRefreshed) {
    return;
  }
  const isMultiRecordData = ContextProcessor.getProperyValue(
    constants.CONTEXT_DATA_MODAL_VIEW.IS_MULTI_RECORD_DATA,
    pageReference,
    context
  );

  if (isMultiRecordData) {
    const caseID = ContextProcessor.getProperyValue(publicConstants.CASE_INFO.CASE_INFO_ID, pageReference, context);
    const viewID = ContextProcessor.getProperyValue(
      constants.CONTEXT_DATA_MODAL_VIEW.VIEW_NAME,
      pageReference,
      context
    );
    const pageRef = pageReference.replace(constants.CASE_INFO.CASE_INFO_CONTENT, '');
    dispatchCaseViewRefresh(caseID, viewID, context, pageRef, {});
  } else {
    refreshAssignment(context, pageReference, {});
  }
};

export const appendQueryParamsForRefreshAPI = (payload, restApi) => {
  if (payload?.options?.refreshFor) {
    restApi += `?refreshFor=${payload?.options?.refreshFor}`;
  }
  if (payload?.options?.isFillFormWithAI) {
    restApi += `?fillFormWithAI=true`;
  }
  return restApi;
};
