import { processHeaders, SCOPE_TYPES } from '../store/state/epics/util';
import Constants from '../constants';
import HeaderProcessor from '../service_broker/header-processor';

import * as RestClient from './index';

// const { RESOURCE_TYPES } = publicConstants;

class CaseAPIs {
  /**
     * Create the work object associated with the given caseTypeID
     * @param {string} caseTypeID The work object to be created<br>
     * Example: "OPB1HW-SpaceTra-Work-RequestApproval"<br><br>

     * @param {object} options - Optional, object containing extra infomation like startingFields and pageName
     * @param {string} options.context The current context where to render
     * @param {string} options.viewType     Flow type
     * @param {object} options.startingFields  startingFields for creating the case
     * @param {string} options.channelName  - Optional, Channel Name
     * @param {object}
     * @returns {Promise<object>} response as promise

     */
  static createCase(caseTypeID, options) {
    const {
      caseInfo,
      startingFields,
      flowType,
      viewType = SCOPE_TYPES.PAGE,
      pageName,
      context = Constants.APP.APP,
      debugInfo,
      headers
    } = options;

    // Support use case (Mashup) to allow the specification of some
    // initialContent used when creating the work.
    // For this, we are including this initial content in caseInfo.content
    // For standard OOTB behavior, it's ok for this to be empty/undefined
    // This initial content is then sent along in the body's "content"
    let initialContent = {};
    if (caseInfo && caseInfo.content) {
      initialContent = caseInfo.content;
    }
    const requestPayload = {
      caseTypeID,
      content: {
        ...initialContent,
        ...startingFields
      },
      processID: flowType
    };

    return RestClient.invokeRestApi(
      'createWork',
      {
        body: requestPayload,
        queryPayload: {
          caseClassName: caseTypeID,
          viewType,
          params: {
            pageName
          }
        },
        headers: {
          ...headers,
          ...(debugInfo && { ...HeaderProcessor.addDebugHeaders(debugInfo) })
        }
      },
      context
    ).then((response) => ({
      response: response.data,
      requestPayload,
      headers: processHeaders(response.headers, caseTypeID, headers)
    }));
  }
}

export default CaseAPIs;
