import * as StateManager from '../../../store/state/state-manager';
import { listAction } from '../../../store/state/actions/action-creators';
import Utils from '../../../utils/utils';

class Update {
  static execute(contextName, target, index, payload, pageReference, skipStateUpdate) {
    const actionPayload = {
      instruction: 'UPDATE',
      content: {
        ...Utils.unflatten(payload)
      }
    };
    const instruction = {
      content: { ...actionPayload.content },
      target: Utils.buildInstructionPageReference(pageReference, target),
      listIndex: index,
      instruction: 'UPDATE'
    };
    if (!skipStateUpdate) {
      StateManager.dispatch(
        listAction({
          ...actionPayload,
          listIndex: index - 1,
          context: contextName,
          referenceList: `${pageReference}${target}`
        })
      );
    }
    return instruction;
  }
}

export default Update;
