/**
 * API to return proper semantic url by removing global parameters like portal and noPortal
 * @param {string} url Input url which should be sanitized by removing global query params
 * @returns {string} Proper semantic url by removing global parameters like portal and noPortal
 */
export const getSemanticURLWithRelatedQueryParams = (url) => {
  if (url.indexOf('?') > 0) {
    const [pathName, queryParams] = url.split('?');
    const urlSearchParams = new URLSearchParams(queryParams);
    const paramsMap = new Map();
    for (const [key, value] of urlSearchParams) {
      if (key !== 'portal' && key !== 'noPortal') {
        paramsMap.set(key, value);
      }
    }

    const searchParamsString = new URLSearchParams(paramsMap).toString();
    return searchParamsString ? `${pathName}?${searchParamsString}` : pathName;
  }
  return url;
};
