/*
  sample flow and usage of the below APIs
  const uid = startXRay();
  startXRayRestApi(uid, "D_employeesList");
  startXRayRestApi(uid, "count");
  stopXRayRestApi(uid, "D_employeesList");
  stopXRayRestApi(uid, "count");
  updateXRay(uid, { extraInfo: values })
  stopXRay(uid, pConnect);
  */

class XRayRuntime {
  constructor(xRayDebugger) {
    this.xrayInfo = {};
    this.xRayDebugger = xRayDebugger;
  }

  startXRay() {
    if (!this.xRayDebugger.xray) return null;
    const uid = `${Date.now() + Math.random()}`;

    this.xrayInfo[uid] = {
      startTime: Date.now() / 1000
    };

    return uid;
  }

  startXRayRestApi(uid, apiName) {
    if (!this.xRayDebugger.xray) return;
    this.xrayInfo[uid] = {
      ...this.xrayInfo[uid],
      restApisTime: {
        ...(this.xrayInfo[uid] ? this.xrayInfo[uid].restApisTime : {}),
        [apiName]: Date.now()
      }
    };
  }

  stopXRayRestApi(uid, apiName) {
    if (!this.xRayDebugger.xray) return;

    this.xrayInfo[uid] = {
      ...this.xrayInfo[uid],
      restApisTime: {
        ...this.xrayInfo[uid]?.restApisTime,
        [apiName]: (Date.now() - this.xrayInfo[uid]?.restApisTime[apiName]) / 1000
      }
    };
  }

  updateXRay(uid, payload) {
    if (!this.xRayDebugger.xray) return;

    this.xrayInfo[uid] = {
      ...this.xrayInfo[uid],
      ...payload
    };
  }

  stopXRay(uid, pConnect) {
    if (!this.xRayDebugger.xray) return;

    this.xrayInfo[uid] = {
      ...this.xrayInfo[uid],
      totalTime: Date.now() / 1000 - this.xrayInfo[uid].startTime
    };

    pConnect().setXRayInfo(this.xrayInfo[uid]);
    delete this.xrayInfo[uid];
  }
}

export default XRayRuntime;
