import ContextProcessor from '../context/context-processor';
import Constants from '../constants';

const REMOTE_SYSTEM_ID = '.RemoteSystemID';

class RemoteCase {
  static remoteCaseMapping = {};

  /**
   * * [setRemoteCaseMapping]
   * Description : Sets the remote case mapping object.
   * @param {object} mapping captures the case remote relationship object
   * eq.,
   * {
   *   Work-Host:{
   *     RemoteClassName: "Work-Remote",
   *     RemoteSystemID: "SystemName"
   *   }
   * }
   */
  static setRemoteCaseMapping = (mapping) => {
    this.remoteCaseMapping = mapping;
  };

  /**
   * * [setRemoteCaseMapping]
   * Description : returns the captured remote case mapping.
   * @returns {object} returns the captured mapping
   * eq.,
   * {
   *   Work-Host:{
   *     RemoteClassName: "Work-Remote",
   *     RemoteSystemID: "SystemName"
   *   }
   * }
   */
  static getRemoteCaseMapping = () => this.remoteCaseMapping;

  /**
   * * [getRemoteCase]
   * Description : Sets the remote case mapping object.
   * @param {string} hostCaseClass host case class
   * @returns {object} Returns the mapped object
   * eq.,
   * {
   *     RemoteClassName: "Work-Remote",
   *     RemoteSystemID: "SystemName"
   *   }
   */
  static getRemoteCase = (hostCaseClass) => this.remoteCaseMapping[hostCaseClass];

  /**
   * * [getRemoteCase]
   * Description : Sets the remote case mapping object.
   * @param {string} hostCaseClass host case class
   * @returns {object} Returns the mapped object
   * eq.,
   * {
   *     RemoteClassName: "Work-Remote",
   *     RemoteSystemID: "SystemName"
   *   }
   */
  static isRemoteCase = (hostCaseClass) =>
    !!(hostCaseClass && this.remoteCaseMapping && this.remoteCaseMapping[hostCaseClass]);

  /**
   * * [getRemoteHeaderByContext]
   * Description : Sets the remote case mapping object.
   * @param {string} context context name
   * @returns {string} Returns mapped header
   */
  static getRemoteHeaderByContext = (context) => {
    return ContextProcessor.getProperty(REMOTE_SYSTEM_ID, Constants.CASE_INFO.HEADERS, context);
  };

  /**
   * * [getRemoteHeader]
   * Description : Sets the remote case mapping object.
   * @param {string} caseClass hostclass name
   * @returns {string} Returns mapped header
   */
  static getRemoteHeader = (caseClass) => {
    return this.remoteCaseMapping[caseClass]?.RemoteSystemID;
  };

  static injectRemoteCaseHeader = (headers, payload, context) => {
    if (headers && headers.RemoteSystemID) return;

    const RemoteSystemID = this.getRemoteHeaderByContext(context);
    if (RemoteSystemID) {
      headers.RemoteSystemID = RemoteSystemID;
    } else if (payload && this.isRemoteCase(payload.caseTypeID)) {
      headers.RemoteSystemID = this.getRemoteHeader(payload.caseTypeID);
    }
  };
}

export default RemoteCase;
