const renderApp = (translatedDXAPIResponse, domContainerID) => {
  const options = { context: Constants.APP.APP };
  const rootData = Utils.getIn(translatedDXAPIResponse, Constants.ROOT);
  const root = { ...rootData, options };

  configureStore(translatedDXAPIResponse);
  StreamObservable.publishResponse(translatedDXAPIResponse, options, true);
  const props = createC11nEnv({
    meta: root,
    isRoot: true
  });
  /* pub sub */
  PubSubUtils.publish(publicConstants.PUB_SUB_EVENTS.EVENT_RENDER_APP, { props, domContainerID });
};

/** updates store with apiResponse
 * @private
 * @param {*} dxAPIResponse - uiResources response
 * @param {string} containerName - container name
 * @param {boolean} isPortal - is portal
 */
export const updateStoreWithUiRoot = async (dxAPIResponse, containerName, isPortal) => {
  const translatedDXAPIResponse = getTranslatedDXAPIResponse(dxAPIResponse);
  const { data, uiResources = {} } = dxAPIResponse;
  const contextData = Utils.getIn(uiResources, Constants.CONTEXT_DATA);
  await processViewResponse(translatedDXAPIResponse, {}, {}, false, true);

  const appData = { ...data, [Constants.CONTEXT_DATA]: contextData };
  const { root } = uiResources;
  PCoreInstance.getContainerUtils().addRootContainerItem(root, containerName, appData, isPortal);
};

const fetchEndPointResource = (endPoint, containerName, isPortal) => {
  fetch(endPoint, { method: 'GET' }, Constants.APP.ROOT).then(async (response) => {
    const { data: dxAPIResponse, status } = response;
    if (status === 200) {
      updateStoreWithUiRoot(dxAPIResponse, containerName, isPortal);
    }
  });
};

export const loadComponent = async (componentConfig, domContainerID, additionalComponents) => {
  return loadComponents(additionalComponents).then(() => {
    const props = createC11nEnv({
      meta: componentConfig,
      isRoot: true
    });

    if (!StateManager.getStore()) {
      configureStore({});
    }

    /* pub sub */
    PubSubUtils.publish(publicConstants.PUB_SUB_EVENTS.EVENT_RENDER_APP, { props, domContainerID });
  });
};

export const setStaticServerUrl = (staticUrl, appStaticUrl, b2sJWT) => {
  AssetLoader.initServer(staticUrl, appStaticUrl, b2sJWT);
};

export const loadView = async (dxAPIResponse, domContainerID, additionalComponents = []) => {
  services.init();
  const translatedDXAPIResponse = getTranslatedDXAPIResponse(dxAPIResponse);
  const views = Utils.getIn(translatedDXAPIResponse, Constants.VIEWS);
  const navigations = Utils.getIn(translatedDXAPIResponse, Constants.NAVIGATIONS);
  const components = Utils.getIn(translatedDXAPIResponse, Constants.COMPONENTS) || [];
  components.push(...additionalComponents);

  const staticServer = Utils.getIn(translatedDXAPIResponse, Constants.STATIC_SERVER_URL);
  const applicationData = Utils.getIn(translatedDXAPIResponse, Constants.APPLICATION_DATA);

  if (views) updateViewStore(views);
  if (navigations) updateNavigationStore(navigations);

  initializeApplicationLabel(applicationData);

  if (staticServer) AssetLoader.initServer(staticServer);

  await loadComponents(components, staticServer).then(() => renderApp(translatedDXAPIResponse, domContainerID));
};

/**
 * This is common method to initialize app shell and it gets invoked from pxBootstrapShell
 * @param {*} appRoutingTable - appRoutingTable
 * @param {*} actionsTargetModel -  actionsTargetModel
 * @param {*} servicesObj - servicesObj
 */
export const initAppShell = (appRoutingTable, actionsTargetModel, servicesObj) => {
  init(appRoutingTable);
  TargetMap.buildActionTargetMap(actionsTargetModel);
  services.init(servicesObj);
};

/**
 * This method on the bootstrap is used to initialize just the routing table - this is used in scenarios such as the constellation
 * bridge wherein actions targeting and service objects are not useful
 * @param {*} appRoutingTable - appRoutingTable
 *  @param {*} servicesObj - servicesObj
 */
export const initRoutingTable = (appRoutingTable, servicesObj) => {
  init(appRoutingTable, true);
  services.init(servicesObj);
};

export const setAppAlias = (appAlias) => {
  AssetLoader.setAppAlias(appAlias);
};

export const getHomeRestApi = (portalName) => {
  const { restApi } = getRestEndPoint('home', { portalName });
  return services.getServerURL() + restApi;
};

/**
 * Returns the full path to the view endpoint
 * @param   {string} viewID - View Name passed in the queryparam
 * @param   {string} viewClass - View Name passed in the queryparam
 * @returns {string} Full path to the view endpoint
 */
export const getViewRestApi = (viewID, viewClass) => {
  const { restApi } = getViewRestEndPoint(viewID, viewClass);
  return services.getServerURL() + restApi;
};

/**
 * Renders RootContainer initially
 * @param {*} domContainerID - Container under which view must be loaded
 * @param {*} componentMetaData - componentMetaData of RootContainer
 * @param {*} additionalComponents - Additional components
 * @param {*} portalTarget - Optional, DOM element into which view will be loaded and used as cosmos portal target(usually a shadowdom element)
 * @param {*} styleSheetTarget - Optional, DOM element into which cosmos styles will be injected
 * @param {boolean} removeContainer - check to remove the container
 */
export const loadRootComponent = async (
  domContainerID,
  componentMetaData,
  additionalComponents = [],
  portalTarget,
  styleSheetTarget,
  removeContainer
) => {
  const { config: { name: rootContainerName } = {} } = componentMetaData;
  if (!StateManager.getStore()) {
    configureStore({});
  }
  if (removeContainer) {
    PCoreInstance.getContainerUtils().removeContainer(`${Constants.APP.ROOT}/${rootContainerName}`);
    PCoreInstance.getContainerUtils()
      .getContainerAPI()
      .deleteContainerItem(`${Constants.APP.ROOT}/${rootContainerName}`);
  }
  if (
    PCoreInstance.getContainerUtils().getContainerAPI().getContainerItems()?.length > 1 &&
    !rootContainerName?.startsWith('hybrid_') &&
    rootContainerName !== 'authoring-preview'
  ) {
    PCoreInstance.resetStore();
  }
  StateManager.dispatch(
    ActionCreators.mergeData({
      data: {},
      context: Constants.APP.ROOT
    })
  );
  const props = createC11nEnv({
    meta: componentMetaData,
    options: {
      context: Constants.APP.ROOT,
      pageReference: '',
      target: `${Constants.APP.ROOT}/${rootContainerName}`
    }
  });
  const pConn = props.getPConnect();
  const baseContext = pConn.getContextName();
  const containerName = pConn.getContainerName();
  const containerMgr = pConn.getContainerManager();

  const rootContainerTarget = `${baseContext}/${containerName}`;
  const isContainerItemAvailable = PCoreInstance.getContainerUtils().getActiveContainerItemName(rootContainerTarget);

  if (!isContainerItemAvailable && componentMetaData.config.renderingMode !== 'noPortal') {
    containerMgr.initializeContainers({
      type: 'single'
    });
  }
  const _components = [
    'RootContainer',
    'Region',
    'ModalViewContainer',
    'PreviewViewContainer',
    'reference',
    ...additionalComponents
  ];
  return loadComponents(_components).then(() => {
    /* pub sub */
    PubSubUtils.publish(publicConstants.PUB_SUB_EVENTS.EVENT_RENDER_APP, {
      props,
      domContainerID,
      componentName: 'RootContainer',
      portalTarget,
      styleSheetTarget
    });
  });
};

/**
 * Loads the view under the given container after hitting the view endpoint and getting the view response.
 * @param {string}  viewName - View Name passed in the queryparam
 * @param {string}  portalName - Default portal, used for a fallback when the viewName can not be resolved
 * @param {string}  viewClass - View Name passed in the queryparam
 * @param {string}  containerName - container name
 */
export const loadViewByName = (viewName, portalName, viewClass, containerName) => {
  let endPoint = getViewRestApi(viewName, viewClass);
  // endpoint is same as ServerURL when the view is requested in a wrong context or if there is any error in fetching the loadView endpoint
  // In that case load the Home for the portal
  if (endPoint === services.getServerURL()) {
    endPoint = getHomeRestApi(portalName);
  }
  fetchEndPointResource(endPoint, containerName);
};

export const loadPortalView = (portalName, containerName) => {
  const isPortal = true;
  const portalShellEndPoint = getHomeRestApi(portalName);
  fetchEndPointResource(portalShellEndPoint, containerName, isPortal);
};

export const configureForBrowserBookmark = (payload) => {
  publishForBrowserBookmark(payload);
};

export const checkIfSemanticURL = () => {
  return isSemanticURL();
};

/**
 * onPCoreReady API ensures that PCore is loaded and ready to use. This accepts a callback function,
 * that will be called once the application (PCore) infrastructure has established and is ready to perform its initial render.
 *
 * This API registers a callback that will be called once the application infrastructure has established the store and is ready to perform its initial render.
 *
 * @param {Function} callback callback function
 * @example <caption>Usage of onPCoreReady</caption>
 * Usage:
 * const render = function(){
 *  // logic to render and bootstrap your application.
 * };
 * onPCoreReady usage: onPCoreReady((args) => render(args));
 *
 * the callback function with render is called once the infrastructure is ready.
 */
export const onPCoreReady = (callback) => {
  PubSubUtils.subscribe(publicConstants.PUB_SUB_EVENTS.EVENT_RENDER_APP, callback, 'renderAppCallback');
};

