export const processHeaders = (responseHeaders, caseClassName, optionalHeaders = {}) => {
  if (!responseHeaders) return {};
  const headers = {
    etag: responseHeaders.etag
  };
  const { RemoteSystemID } = optionalHeaders;
  if (
    responseHeaders[publicConstants.REMOTE_SYSTEM_ID] ||
    responseHeaders[publicConstants.REMOTE_SYSTEM_ID.toLowerCase()] ||
    RemoteSystemID
  ) {
    headers.RemoteSystemID =
      responseHeaders[publicConstants.REMOTE_SYSTEM_ID] ||
      responseHeaders[publicConstants.REMOTE_SYSTEM_ID.toLowerCase()] ||
      RemoteSystemID;
  } else if (RemoteCase.isRemoteCase(caseClassName)) {
    headers.RemoteSystemID = RemoteCase.getRemoteHeader(caseClassName);
  }
  return headers;
};

/**
 * This function converts Object string in path to array path for lodash unset
 * @param {string} string path to be converted
 * @returns {string} converted string
 */
/*
'content.Property.AdditionalAttributes[0].abb.Nested[2]' -->
"['content']['AdditionalAttributes[0]']"

*/
export const convertLiteralKey = (str) => {
  if (!str) return '';
  const arr = str.split('.');
  let removerReference = '';
  for (const token of arr) {
    if (token) {
      if (token.indexOf('[') !== -1 && token.indexOf(']') !== -1) {
        removerReference += `['${token}']`;
        return removerReference;
      }
      removerReference += `['${token}']`;
    }
  }

  return removerReference;
};

/**
 * This function return form data for a given context
 * @param {string} context context of form
 * @param {object} options optional details like onlyChangedData
 * @param {boolean} options.onlyChangedData if true, getSubmitData returns only edited fields data from the form
 * @param {boolean} options.nonEmptyAndChangedData if true, getSubmitData returns pre-populated nonEmpty data and data edited by the user
 * @returns {*} form data
 */
export const getSubmitData = (context, options = {}) => {
  const { onlyChangedData = false, nonEmptyAndChangedData = false } = options;
  let formContent = {};
  formContent = getFormContent(context);
  if (onlyChangedData) {
    delete formContent?.caseInfo?.content;
  }
  const changes = getChanges(context, false);

  if (nonEmptyAndChangedData) {
    Object.entries(formContent?.caseInfo?.content).forEach(([key, value]) => {
      const contextData = StateManager.getDataObject(context);
      const classID = get(contextData, publicConstants.CASE_INFO.CASE_INFO_CLASSID);
      const propertyMeta = getPropertyMetadata(key, classID);
      if ((propertyMeta?.type === 'True-False' && !value) || value === '') {
        delete formContent.caseInfo.content[key];
      }
    });
  }

  const submitData = merge(changes, formContent);
  let content = {};
  let attachmentsExist = false;

  if (submitData && submitData.caseInfo) {
    content = { ...submitData.caseInfo };
    if (submitData.caseInfo.attachments) {
      attachmentsExist = true;
    }
  }

  const instructionsForCurrentContext = getInstructions(context);
  if (instructionsForCurrentContext) {
    content.pageInstructions = instructionsForCurrentContext;
  }

  const references = getPageInstructionReferences(context);
  references.forEach((ref) => {
    if (content && content.content) {
      Utils.deleteIn(content.content, convertLiteralKey(ref));
    }
  });
  if (submitData && submitData.dataInfo) {
    return { ...submitData.dataInfo, pageInstructions: content.pageInstructions };
  }
  return { content, attachmentsExist };
};

export const getActionPayload = (translatedDXAPIResponse, commonInfo) => {
  let contextData;
  let root;
  let actionButtons = {};
  const uiResources = Utils.getIn(translatedDXAPIResponse, constants.UI_RESOURCERS);
  const confirmationNote = Utils.getIn(translatedDXAPIResponse, 'confirmationNote');
  if (uiResources) {
    contextData = Utils.getIn(translatedDXAPIResponse, constants.UI_RESOURCERS_CONTEXT_DATA);
    root = Utils.getIn(translatedDXAPIResponse, constants.ROOT);
    actionButtons = Utils.getIn(translatedDXAPIResponse, constants.ACTION_BUTTONS);
  } else if (confirmationNote) {
    root = {};
  }

  const { data = {}, Errors: errors, semanticURL, key, flowName } = translatedDXAPIResponse;

  const nextAssignmentInfo = Utils.getIn(translatedDXAPIResponse, 'nextAssignmentInfo', null);
  const assignmentContext = Utils.getIn(translatedDXAPIResponse, 'nextAssignmentInfo.context', '');
  const confirmationLinks = Utils.getIn(translatedDXAPIResponse, 'confirmationLinks', null);

  const { isInCreateStage, openCaseViewAfterCreate, debugInfo } = commonInfo;
  return {
    data: {
      ...data,
      key,
      flowName,
      coreHeaders: debugInfo && {
        debugInfo: {
          [constants.COREHEADER_APP_DEBUG_ID]: debugInfo.debugId
        }
      },
      caseInfo: {
        ...data.caseInfo,
        actionButtons,
        headers: commonInfo.headers
      },
      [constants.CONTEXT_DATA]: isInCreateStage
        ? { ...data[constants.CONTEXT_DATA], ...contextData, openCaseViewAfterCreate, errors }
        : { ...data[constants.CONTEXT_DATA], ...contextData, errors }
    },
    semanticURL,
    nextAssignmentInfo,
    assignmentContext,
    confirmationLinks,
    key,
    root,
    ...commonInfo
  };
};

export const processViewResponse = async (
  translatedDXAPIResponse,
  actionConfig,
  options,
  isSemanticURLAssociatedWithActivateContainer = false,
  skipParseResponse = false
) => {
  // get locale jsons
  const localeRefs = Utils.getIn(translatedDXAPIResponse, constants.LOCALE_REFERENCES);
  await localeUtils.loadLocaleResources(localeRefs);
  const components = Utils.getIn(translatedDXAPIResponse, constants.COMPONENTS);
  if (components) {
    try {
      await loadComponents(components);
    } catch (error) {
      console.error(error);
    }
  }
  const optionsData = { actionConfig, ...options };
  if (isSemanticURLAssociatedWithActivateContainer) {
    parseResponse({ ...translatedDXAPIResponse, options: optionsData, directUrlUpdation: true });
  } else {
    publishResponse(translatedDXAPIResponse, optionsData, skipParseResponse);
  }
};

/**
 * this function returns the work id from the insKey
 * @private
 * @function getIdFromInsKey
 * @param {string} insKey
 * @returns {string}
 * @static
 *
 * @example  <caption>Example for getIdFromInsKey.</caption>
 * getIdFromInsKey('ASSIGN-WORKLIST ON8TTL-C11NGALL-WORK C-5028!DETAILS_FLOW');
 * example will return C-5028
 */

export const getIdFromInsKey = (insKey) => {
  const result = insKey && insKey.match(/\S+(?=!)/);
  return result ? result[0] : '';
};

/**
 * this function returns the work id from the insKey
 * @private
 * @function getWorkIDFromInsKey
 * @param {string} insKey inskey
 * @returns {string}    case id
 * @static
 *
 * @example  <caption>Example for getIdFromInsKey.</caption>
 * getIdFromInsKey('ASSIGN-WORKLIST ON8TTL-C11NGALL-WORK C-5028!DETAILS_FLOW');
 * example will return v
 */
export const getWorkIDFromInsKey = (insKey = '') => {
  const startIndex = insKey.indexOf(' ');
  const endIndex = insKey.indexOf('!');
  return insKey.substring(startIndex + 1, endIndex);
};

/**
 * this function returns the assignment key and flow name from insKey
 * @private
 * @function getKeyAndFlowName
 * @param {string} insKey inskey
 * @returns {object} key and flow name of case
 * @static
 *
 * @example  <caption>Example for getKeyAndFlowName.</caption>
 * getKeyAndFlowName('ASSIGN-WORKLIST ON8TTL-C11NGALL-WORK C-5028!DETAILS_FLOW');
 * example will return
 * {
 *  key: 'ASSIGN-WORKLIST ON8TTL-C11NGALL-WORK C-5028',
 *  flowName: DETAILS_FLOW
 * }
 */
export const getKeyAndFlowName = (insKey) => {
  if (insKey) {
    const [, flowName] = insKey.split('!');
    const key = getWorkIDFromInsKey(insKey);
    return { key, flowName };
  }
  return {};
};

/**
 * this function returns if the container item needs to be replaced
 * @private
 * @function shouldReplaceActiveItem
 * @param {string} currentKey current item key
 * @param {string} target target container
 * @returns {boolean} returns true if container item needs to be replaced, otherwise returns false
 * @static
 *
 * @example  <caption>Example for getKeyAndFlowName.</caption>
 * shouldReplaceActiveItem('ON8TTL-C11NGALL-WORK C-5028', 'app/primary_1/workarea');
 */
export const shouldReplaceActiveItem = (currentKey, target) => {
  const activeContainerItemName = getActiveContainerItemName(target);
  let activeContainerItemInfo = null;
  if (activeContainerItemName) {
    activeContainerItemInfo = ContainerUtils.getContainerItemData(target, activeContainerItemName);
  }
  return activeContainerItemInfo?.key === currentKey && isContainerMultipleMode(target);
};

const processErrors = (err, formFields, pageErrors, fieldErrors) => {
  switch (true) {
    // Embedded page errors
    case Boolean(err[constants.ERROR_IN_PAGE_IDENTIFIER] && err[constants.ERROR_FIELD_IN_PAGE]): {
      const embeddedErrorField = `${err[constants.ERROR_FIELD_IN_PAGE]}`.replace(/(\(\d\))/g, (str) => {
        return `[${parseInt(str.substring(1, str.length - 1), 10) - 1}]`;
      });
      const indexedEmbeddedErrorField = `${embeddedErrorField}${err[constants.ERROR_IN_PAGE_IDENTIFIER]}`;
      const cloneErr = Utils.deepClone(err);
      cloneErr[constants.ERROR_FIELD_IN_PAGE] = embeddedErrorField;
      formFields.includes(indexedEmbeddedErrorField.slice(1)) ? fieldErrors.push(cloneErr) : pageErrors.push(err);
      break;
    }
    // Field level errors
    case Boolean(err[constants.ERROR_IN_PAGE_IDENTIFIER] && !err[constants.ERROR_FIELD_IN_PAGE]): {
      const errorField = err[constants.ERROR_IN_PAGE_IDENTIFIER].slice(1);
      formFields.includes(errorField) ? fieldErrors.push(err) : pageErrors.push(err);
      break;
    }
    // Page level errors
    default:
      pageErrors.push(err);
      break;
  }
  /* istanbul ignore else */
};

/**
 * Process the Error response and separate out field level errors, page level errors & (@TODO)app level errors,
 * @param {Array} error error content
 * @param {Array} formFields
 * ErroneousInputOutputIdentifier --> field which has error
 * ErroneousInputOutputFieldInPage --> Decides whether it is page level or field level error.
 */
export const processErrorResponse = (error, formFields) => {
  const pageErrors = [];
  const fieldErrors = [];

  // Separating out the app (@TODO), thread, field level errors
  error.forEach((err) => {
    processErrors(err, formFields, pageErrors, fieldErrors);
  });

  return {
    fieldErrors,
    pageErrors
  };
};

export const processDataToDispatchErrorAction = (data) => {
  const { payload } = data;
  const { err, context, actionMgrID } = payload;
  const error = err && err.response ? err.response.data : '';
  const formFields = getVisibleFormFields(context);
  // Since an error has occured, try to get the actionMgrID from the payload
  //  and reject that entry's promise before returning...
  if (actionMgrID) {
    ActionManagerInstance.rejectActionByID(actionMgrID);
  }

  // payload with all errors (app, page, field)
  return actions.postProcess(actions.error, 'Error action triggered', error, context, formFields);
};

export const getActionBasedOnContainerMode = (target, payload) => {
  if (isContainerSingleMode(target) && hasContainerItems(target)) {
    return actions.postProcess(actions.replaceContainerItem, payload);
  }
  return actions.postProcess(actions.addContainerItem, payload);
};

export const getContext = (target, containerName, context = constants.APP.APP, options = {}) => {
  let _context = null;
  const { buildNew = true } = options;
  if (isContainerSingleMode(target)) {
    if (hasContainerItems(target)) {
      _context = getActiveContainerItemName(target);
    } else {
      _context = buildContextName(containerName, context);
    }
  } else if (isContainerMultipleMode(target)) {
    if (hasContainerItems(target) && !buildNew) {
      _context = getActiveContainerItemContext(target);
    } else if (!hasContainerItems(target) || buildNew) {
      _context = buildContextName(containerName, context);
    }
  }
  return _context;
};

export const isAddContainerAction = (action) => {
  return action && action.type === ADD_CONTAINER_ITEM;
};

export const areChildContextsInDirtyState = (parentContainerItemID) => {
  let isDirty = false;
  const childContainersContexts = ContainerUtils.getChildContainersContexts(parentContainerItemID);
  for (let index = 0; index < childContainersContexts.length; index += 1) {
    const childContext = childContainersContexts[index];
    if (childContext && isContextDirty(childContext)) {
      isDirty = true;
      break;
    }
  }
  return isDirty;
};

/**
* This function will help to get dirty details of target passed and checks for dirty based on container type
* -- if container type is single - it checks whether active container is in dirty state or not. If dirty it prompts a window confirmation
*    based on user action, continueExecution will be set.
* -- if container type is multiple - it checks whether container limit has reached or not.
*        a) if container limit is not reached then continueExecution will be true.
*        b) if container limit is reached and found non dirty container then continueExecution will be true and removeActionPayload contains
                the containerItem details which need to be rmeoved.
*        c) if container limit is reached and all are dirty containers then continueExecution will be false and
*              window alert will be shown on UI.
* @param {string} target - target container e.g. app/primary
* @param {object} options - additional information for proccessing logic. e.g. showConfirmDialogForMaxDocumentLimitReached
* @returns {object} - object contains below details
                        1) continueExecution (boolean type) - a) true indicates to continue execution
                                                            - b) false indicates to stop further execution
                        2) removeActionPayload (object) - will have details related to container which need to be removed.

  @private
*/
export const getDirtyDetails = (target, options = {}) => {
  let showDirtyDialog = false;
  let showMaxDocumentLimitReachedDialog = false;
  let removeActionPayload = {};
  let continueExecution = true;
  let dirtyDialogStatus = false;
  let maxDocumentLimitReachedDialogStatus = false;
  /* we will be not using this flag for now, as we are going with infinity behavior as default */
  const { showConfirmDialogForMaxDocumentLimitReached = false, dirtyMessage, skipChildDirtyState = false } = options;

  if (ContainerUtils.isContainerSingleMode(target)) {
    const activeContainerName = ContainerUtils.getActiveContainerItemName(target);
    const dataContextName = ContainerUtils.getDataContextName(activeContainerName);
    if (isContextDirty(dataContextName)) {
      showDirtyDialog = true;
    }

    if (!skipChildDirtyState && !showDirtyDialog) {
      showDirtyDialog = areChildContextsInDirtyState(activeContainerName);
    }
  } else if (ContainerUtils.isContainerMultipleMode(target) && ContainerUtils.isContainerLimitReached(target)) {
    let leastRecentlyUsedContainerItemID = ContainerUtils.getLeastRecentlyUsedNonDirtyContainerItemID(target);
    if (!leastRecentlyUsedContainerItemID) {
      leastRecentlyUsedContainerItemID = ContainerUtils.getActiveContainerItemName(target);
      showMaxDocumentLimitReachedDialog = true;
    }
    removeActionPayload.containerItemID = leastRecentlyUsedContainerItemID;
    removeActionPayload.context = ContainerUtils.getDataContextName(leastRecentlyUsedContainerItemID);
  }

  /* Show Dirty Dialog in case of Single Mode Container Type */
  if (showDirtyDialog) {
    const msg = dirtyMessage || FORM_DIRTY_MESSAGE;
    dirtyDialogStatus = window.confirm(localeUtils.getLocaleValue(msg, 'Messages'));
  }

  if (!dirtyDialogStatus && showDirtyDialog) {
    removeActionPayload = {};
    continueExecution = false;
  }

  /* Show Max Document Reached Limit Dialog in case of Multiple Mode Container Type */
  if (showMaxDocumentLimitReachedDialog) {
    const msg = localeUtils.getLocaleValue(MAX_DOCUMENT_LIMIT_REACHED, 'Messages');
    if (showConfirmDialogForMaxDocumentLimitReached) {
      maxDocumentLimitReachedDialogStatus = window.confirm(msg);
    } else {
      window.alert(msg);
      continueExecution = false;
    }
  }

  if (!maxDocumentLimitReachedDialogStatus && showMaxDocumentLimitReachedDialog) {
    removeActionPayload = {};
    continueExecution = false;
  }

  return { continueExecution, removeActionPayload };
};

/**
* This function will help to get activation details of ASSIGNMENT type ITEM based on config details
* @param {string} target - target container e.g. app/primary
* @param {object} config - contains information related to ASSIGNMENT e.g. semanticURL, inskey
* @returns {object} - object contains below details
                        1) activate (boolean type) - a) true indicates if ITEM is already present
                                                   - b) false indicates if ITEM is not present
                        2) activateContainerActionPayload (object) - will have details related to container in which ITEM is present

  @private
*/
const getAssignmentActivationDetails = (target, config) => {
  let activate = false;
  const isCoexistenceUIKITApp = EnvironmentInfo.isCoexistenceAppOfType(constants.APP_TYPE.UIKIT);
  const { insKey, isParallelCall, skipFlowNameCheck } = config;

  if (!insKey) return {};
  const [, flowName] = insKey.split('!') || '';
  const key = skipFlowNameCheck ? insKey : getWorkIDFromInsKey(insKey);
  const containerItemName = ContainerUtils.getContainerItemName(target, key);

  let containerItemData = {};
  let isParallelAssignment = false;

  if (containerItemName && !isCoexistenceUIKITApp) {
    activate = true;
    containerItemData = ContainerUtils.getContainerItemData(target, containerItemName);

    if (!skipFlowNameCheck) {
      let existingFlowName = containerItemData?.flowName;
      const containerName = getContainerNameFromContainerItemID(containerItemName);
      const childContainers = ContainerUtils.getChildContainerItems(containerItemName);
      if (childContainers.length) {
        const childTarget = getResolvedTargetName(containerName, childContainers[0]);
        existingFlowName = ContainerUtils.getContainerItemData(childTarget, childContainers[0])?.flowName;
      } else {
        activate = existingFlowName === flowName;
      }
      if (existingFlowName !== flowName || isParallelCall) {
        isParallelAssignment = true;
      }
    }
  }

  return {
    activate,
    activateContainerActionPayload: {
      target,
      isParallelAssignment,
      containerItemID: containerItemName,
      semanticURL: containerItemData.semanticURL,
      insKey
    }
  };
};

/**
* This function will help to get activation details of PAGE type ITEM based on config details
* @param {string} target - target container e.g. app/primary
* @param {object} config - contains information related to PAGE e.g. semanticURL, inskey
* @returns {object} - object contains below details
                        1) activate (boolean type) - a) true indicates if ITEM is already present
                                                   - b) false indicates if ITEM is not present
                        2) activateContainerActionPayload (object) - will have details related to container in which ITEM is present

  @private
*/
const getPageActivationDetails = (target, config = {}) => {
  let activate = false;
  const { semanticURL } = config;
  const containerItemName = ContainerUtils.getContainerItemName(target, semanticURL);

  if (containerItemName) {
    activate = true;
  }

  return {
    activate,
    activateContainerActionPayload: {
      target,
      containerItemID: containerItemName,
      semanticURL
    }
  };
};

/**
* This function will help to get activation details of item based on config details (PAGE or ASSIGNMENT)
* @param {string} type - PAGE or ASSIGNMENT
* @param {string} target - target container e.g. app/primary
* @param {object} config - contains information related to PAGE or ASSIGNMENT e.g. semanticURL, inskey
* @returns {object} - object contains below details
                        1) activate (boolean type) - a) true indicates if ITEM is already present
                                                   - b) false indicates if ITEM is not present
                        2) activateContainerActionPayload (object) - will have details related to container in which ITEM is present

  @private
*/
export const getActivationDetails = (type, target, config) => {
  if (type === RESOURCE_TYPES.PAGE) {
    return getPageActivationDetails(target, config);
  }
  if (type === RESOURCE_TYPES.ASSIGNMENT) {
    return getAssignmentActivationDetails(target, config);
  }
  if (type === RESOURCE_TYPES.CASE) {
    return getAssignmentActivationDetails(target, { ...config, skipFlowNameCheck: true });
  }
  return {};
};

/**
* This function is used to perform resetContainers as a batch action
* @returns {object} - returns a action object with type "RESET_CONTAINER"
  @private
*/
export const performResetContainer = () => {
  return actions.postProcess(
    actions.resetContainers,
    ContainerUtils.resetContainersPayload({
      context: publicConstants.APP.APP,
      name: constants.PREVIEW
    })
  );
};

/**
 * This function is used to perform merge content of current container to target inside taregt.data.caseinfo
 * @param {object} target - target object
 * @param {object} containerInfo - target object to be merged
 * @returns {object} - returns merged object
 * @private
 */

export const mergeCurrentModalContentToTarget = (target, containerInfo) => {
  if (target?.data?.caseInfo && containerInfo?.caseInfo?.content) {
    target.data.caseInfo.content = containerInfo?.caseInfo?.content;
  }
  return target;
};

/**
 * This function will return the resetContainer Action if we have any Preview Containers Exist else return null
 * @param {Array} theReturn - An array which contains all the actions which needs to be dispatched.
 * @private
 */

export const addPreviewResetAction = (theReturn = []) => {
  if (hasContainerItems(`${constants.APP.APP}/${constants.PREVIEW}`)) {
    theReturn.push(performResetContainer());
  }
};

/**
 * This function will return the resourcetype (i.e PAGE or CASE or ASSIGNMENT) based on the routeKey of the url
 * @param {string} routeKey - routeKey associated with the url.
 * @returns {string} - type of resource i.e PAGE or CASE or ASSIGNMENT
 * @private
 */
export const getResourceType = (routeKey) => {
  let actionCategory;
  for (const key of Object.keys(RESOURCE_TYPE_ACTIONS_MAP)) {
    if (RESOURCE_TYPE_ACTIONS_MAP[key].includes(routeKey)) {
      actionCategory = key;
      break;
    }
  }
  return actionCategory;
};

export const addActionsToParentContext = (childPayload, reduxActions) => {
  const { data = {}, context, caseViewMode } = childPayload;
  const parentContext = getParentContextName(context);
  const parentPayload = {
    data: { ...data },
    caseViewMode,
    context: parentContext
  };
  /* remove shared node, as the shared data pages are not being requested by parent here */
  Utils.deleteIn(parentPayload.data, 'shared');
  Utils.setIn(parentPayload.data, ['context_data', 'caseViewMode'], caseViewMode);
  reduxActions.push({
    type: actionType.DELETE_CASE_SUMMARY,
    payload: {
      context: parentPayload.context
    }
  });
  reduxActions.push(actions.postProcess(actions.mergeData, parentPayload));
};
const getActionInfo = (childOrRemoteKey, data, assignmentContext) => {
  return {
    assignmentID: childOrRemoteKey,
    vcName: constants.PRIMARY,
    targetContainerName: Utils.getIn(data, ['payload', 'target']),
    context: Utils.getIn(data, ['payload', 'context']),
    ...(assignmentContext === 'other' && { isChild: true }),
    isProcessAction: data?.payload?.isProcessAction,
    ...(assignmentContext === 'remote' && { isRemote: true }),
    ...(assignmentContext === 'remote' && { remoteClassName: data.payload.nextAssignmentInfo.className })
  };
};
const getChildOrRemoteContextAction = (theReturn) => {
  const data = theReturn[0];
  for (const item of theReturn) {
    if (
      item?.payload?.nextAssignmentInfo?.context === 'other' ||
      item?.payload?.nextAssignmentInfo?.context === 'remote'
    ) {
      return item;
    }
  }
  return data;
};
export const dispatchCorrectActionIfChildOrRemote = (theReturn) => {
  if (Array.isArray(theReturn) && theReturn.length > 0) {
    const data = getChildOrRemoteContextAction(theReturn);
    const assignmentContext = Utils.getIn(data, ['payload', 'assignmentContext']);
    if (assignmentContext === 'other' || assignmentContext === 'remote') {
      const childOrRemoteKey = Utils.getIn(data, ['payload', 'nextAssignmentInfo', 'ID']);
      if (data?.payload?.isProcessAction) {
        const cancelActionInfo = {
          acName: Utils.getIn(data, ['payload', 'target']),
          caseID: data.payload.data.caseInfo.ID,
          containerItemID: Utils.getIn(data, ['payload', 'context']),
          context: Utils.getIn(data, ['payload', 'context'])
        };
        const cancelAssignment = {
          type: actionType.CANCEL_ASSIGNMENT,
          payload: { ...cancelActionInfo }
        };

        theReturn.push(cancelAssignment);

        // This filter should be removed when BUG-663687 is resolved
        theReturn = theReturn.filter((action) => delete action?.payload?.data?.caseMessages);

        StateManager.dispatch(theReturn);
        theReturn = theReturn.filter((action) => action.type !== actionType.CANCEL_ASSIGNMENT);
      }
      // Dispatch all the previous actions as well as along with child case open assignment action.
      /* Other actions are required to make sure the previous rendered assignment is not more visible
               and ToDo list is shown in case if the child case is create stage based case
            */
      const actionInfo = getActionInfo(childOrRemoteKey, data, assignmentContext);
      const isSingleDoc = ContainerUtils.isContainerSingleMode(actionInfo.targetContainerName);
      if (!isSingleDoc && data?.payload?.containerName === constants.WORKAREA) {
        actionInfo.isMultiDoc = true;
        theReturn = [];
      }
      const childOrRemoteCaseOpenAssignmentAction = {
        type: actionType.OPEN_ASSIGNMENT,
        payload: { ...actionInfo }
      };
      theReturn.push(childOrRemoteCaseOpenAssignmentAction);
      StateManager.dispatch(theReturn);
    } else {
      StateManager.dispatch(theReturn);
    }
  }
};

export const getUpdatedPayloadBasedOnResourceType = (resourceType, data, payload) => {
  if (resourceType === RESOURCE_TYPES.CASE) {
    if (data?.caseInfo?.assignments) {
      const { key, flowName } = getKeyAndFlowName(data.caseInfo.assignments[0].ID);
      payload.key = key;
      payload.flowName = flowName;
    } else {
      payload.flowName = '';
      payload.key = data?.caseInfo?.ID || data.semanticURL;
    }
    payload.resourceType = resourceType;
    payload.semanticURL = data.semanticURL;
    payload.caseViewMode = constants.REVIEW_MODE;
  } else {
    payload.resourceType = resourceType;
    payload.semanticURL = data.semanticURL;
  }
  return payload;
};

/**
 * This function merges Objects and concats Arrays
 * @example
 * let object = { a: [{ b: 2 }], d: { x: 2 } };
 * let other = { a: [{ c: 3 }], d: { y: 3 } };
 * mergeObjectsAndConcatArrays(object, other) = { a: [{ b: 2 }, { c: 3 }], d: { x: 2, y: 3 } };
 */

export const mergeObjectsAndConcatArrays = (originalObj, newObj) => {
  function concatIfArray(objValue, srcValue) {
    if (Array.isArray(objValue)) {
      return objValue.concat(srcValue);
    }
  }
  return merge(originalObj, newObj, concatIfArray);
};

export const getParentContextChangeSet = (context) => {
  let result = {};
  let parentContext = ContainerUtils.getContainerParentContext(context);
  /** ex: app/primary_1/workarea_1 <-parent- app/primary_1/workarea_1/modal_1 <-parent- app/primary_1/workarea_1/modal_1/modal_1 */
  while (parentContext) {
    const parentContextChangeSet = getSubmitData(parentContext);
    result = mergeObjectsAndConcatArrays(parentContextChangeSet.content, result);
    parentContext = ContainerUtils.getContainerParentContext(parentContext);
  }

  return result;
};
