import Parser from './parser';

const LITERAL = 'literal';
const IDENTIFIER = 'identifier';

const andGrammar = {
  operatorType: 'binaryOperator',
  precedence: 1,
  evaluate: (left, right) => left && right
};

const orGrammar = {
  operatorType: 'binaryOperator',
  precedence: 1,
  evaluate: (left, right) => left || right
};

/**
 * A map of all expression elements to their properties. Note that
 * changes here may require changes in the Lexer and Parser.
 */
export const supportedGrammar = {
  '>': {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left, right) => left > right
  },
  '>=': {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left, right) => left >= right
  },
  '<': {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left, right) => left < right
  },
  '<=': {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left, right) => left <= right
  },
  '==': {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left, right) => {
      return left === right;
    }
  },
  '!=': {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left, right) => {
      return left !== right;
    }
  },
  '!': {
    operatorType: 'unaryOperator',
    precedence: 999,
    evaluate: (right) => {
      let { value } = right;
      if (value === 'true' || value === 'false') {
        value = value === 'true';
      }
      return !value;
    }
  },
  '(': { operatorType: 'openParenthesis' },
  ')': { operatorType: 'closeParenthesis' },
  '&&': andGrammar,
  AND: andGrammar,
  and: andGrammar,
  '||': orGrammar,
  OR: orGrammar,
  or: orGrammar,
  CONTAINS: {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left, right) => {
      return left.indexOf(right) > -1;
    }
  },
  NOT_CONTAINS: {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left, right) => {
      return left.indexOf(right) === -1;
    }
  },
  STARTS_WITH: {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left, right) => {
      return left.startsWith(right);
    }
  },
  NOT_STARTS_WITH: {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left, right) => {
      return !left.startsWith(right);
    }
  },
  ENDS_WITH: {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left, right) => {
      return left.endsWith(right);
    }
  },
  NOT_ENDS_WITH: {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left, right) => {
      return !left.endsWith(right);
    }
  },
  IS_NULL: {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (value) => {
      return !value;
    }
  },
  IS_NOT_NULL: {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (value) => {
      return !!value;
    }
  },
  IS_IN_LIST: {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left, right) => {
      const list = right.split(',');
      return list.includes(left);
    }
  },
  IS_NOT_IN_LIST: {
    operatorType: 'binaryOperator',
    precedence: 2,
    evaluate: (left, right) => {
      const list = right.split(',');
      return !list.includes(left);
    }
  }
};

/**
 * Expression handlers. Handles the different types of expression.
 */
const expressionHandlers = {};
/**
 * [BinaryExpression]
 * Description : Evaluates a binary expression.
 * @param       {object}    ast     Abstract systax tree
 * @returns     {boolean}   true if the expression evaluates to true else false.
 */
expressionHandlers.BinaryExpression = function BinaryExpression(ast) {
  const result = [];
  result[0] = this.evaluateAST(ast.left);
  result[1] = ast.right ? this.evaluateAST(ast.right) : '';
  return this._grammar[ast.operator].evaluate(result[0], result[1]);
};

/**
 * [identifier]
 * Description : Returns the identifier value from the context.
 * @param       {object}    ast     Abstract systax tree
 * @returns     {*}         identifier value from the context
 */
expressionHandlers.identifier = function identifier(ast) {
  if (this._localDataObject) {
    return this._localDataObject[ast.value.substring(1)];
  }
  return this._context.getValue(ast.value);
};

/**
 * [literal]
 * Description : Returns the literal value.
 * @param       {object}    ast     Abstract systax tree
 * @returns     {boolean}   Literal value.
 */
expressionHandlers.literal = function literal(ast) {
  if (ast.value === 'true' || ast.value === 'false') {
    return ast.value === 'true';
  }
  return ast.value;
};

/**
 * [UnaryExpression]
 * Description : Evaluates a unary expression.
 * @param       {object}    ast     Abstract systax tree
 * @returns     {boolean}   true if the expression evaluates to true else false.
 */
expressionHandlers.UnaryExpression = function UnaryExpression(ast) {
  return this._grammar[ast.operator].evaluate(ast.right);
};

/**
 * Handlers for all the types of operators that are supported.
 */
const OperatorHandlers = {};
/**
 * [unaryOperator]
 * Description : Handles the unary operator.
 * @param       {object}    token       Current token
 */
OperatorHandlers.unaryOperator = function unaryOperator(token) {
  this.setPositionInAST({
    operationType: 'UnaryExpression',
    operator: token.value
  });
};

/**
 * [binaryOperator]
 * Description : Handles the binary operator token.
 * @param       {object}    token       Current token
 */
OperatorHandlers.binaryOperator = function binaryOperator(token) {
  const { precedence } = this._grammar[token.value];
  let parent = this._position._parent;
  while (parent && parent.operator && this._grammar[parent.operator].precedence >= precedence) {
    this._position = parent;
    parent = parent._parent;
  }
  const node = {
    operationType: 'BinaryExpression',
    operator: token.value,
    left: this._position
  };

  Parser._setParent(this._position, node);
  this._position = parent;
  this.setPositionInAST(node);
};

/**
 * [identifier]
 * Description : Handles the identifier token.
 * @param       {object}    token       Current token
 */
OperatorHandlers.identifier = function identifier(token) {
  const node = {
    operationType: IDENTIFIER,
    value: token.value
  };
  this.setPositionInAST(node);
};

/**
 * [literal]
 * Description : Handles the literal token.
 * @param       {object}    token       Current token
 */
OperatorHandlers.literal = function literal(token) {
  this.setPositionInAST({
    operationType: LITERAL,
    value: token.value
  });
};

/**
 * [subExpression]
 * Description : Handles the subExpression token.
 * @param       {object}    ast       Abstract syntax tree
 */
OperatorHandlers.subExpression = function subExpression(ast) {
  this.setPositionInAST(ast);
};

/**
 * States in the evaluation of an expression.
 */
export const states = {
  expectOperand: {
    operatorTypes: {
      literal: { toState: 'expectBinOp' },
      identifier: { toState: IDENTIFIER },
      unaryOperator: {},
      openParenthesis: { toState: 'subExpression' }
    }
  },
  expectBinOp: {
    operatorTypes: {
      binaryOperator: { toState: 'expectOperand' }
    }
  },
  identifier: {
    operatorTypes: {
      binaryOperator: { toState: 'expectOperand' }
    }
  },
  subExpression: {
    subHandler: OperatorHandlers.subExpression,
    endStates: {
      closeParenthesis: 'expectBinOp'
    }
  }
};

export { OperatorHandlers, expressionHandlers };
