import { getSharedDataObject, getValue } from '../../store/state/state-manager';
import AnnotationsHandler from '../annotations-handler';
import Constants from '../../constants';
import Utils from '../../utils/utils';
import AnnotationProcessor from '../annotation-processor';
import DatapageDependencyMap from '../../cascade/datapage-dependency-handler';

function getResultsFromDataPage(c11nEnv, parameterValueArray, dataPageName, isSharedDataPage) {
  let hashDigest = '';
  let dataPageWithHash = dataPageName;
  /* istanbul ignore else */
  if (parameterValueArray.join(Constants.HASH_DELIMITER)) {
    hashDigest = Utils.getHashedString(parameterValueArray.join(Constants.HASH_DELIMITER));
    dataPageWithHash = dataPageName + Constants.UNDERSCORE_STRING + hashDigest;
  }
  if (isSharedDataPage) {
    const sharedDataObject = getSharedDataObject();
    const sharedData = sharedDataObject ? sharedDataObject[dataPageName]?.[dataPageWithHash] : {};
    return Array.isArray(sharedData?.pxResults) ? sharedData.pxResults : [];
  }
  const dataInContext = c11nEnv.getDataObject();
  const dataPagesObject = dataInContext[dataPageName];
  const dataPage = dataPagesObject[dataPageWithHash];
  return Array.isArray(dataPage?.pxResults) ? dataPage.pxResults : [];
}

/**
 * This handler will be invoked if the property is configured as associated property.
 * If the property is configured with data page will resolve the value from the data object else
 * we resolve it from context_data.
 */

AnnotationsHandler.registerHandler('@ASSOCIATED', (value, c11nEnv) => {
  let metaData = {};
  const parameterValueArray = [];
  let propertyName = '';
  let parentPropertyName = '';
  const pageReference = c11nEnv.getPageReference();
  if (value.startsWith('.')) {
    propertyName = `${pageReference}.${value.substring(1)}`;
    const parentPropertySplitArray = value ? value.split('.').filter((val) => !!val) : [];
    if (parentPropertySplitArray.length > 1) {
      parentPropertySplitArray.pop();
      parentPropertyName = parentPropertySplitArray.join('.');
    }
    metaData = c11nEnv.getFieldMetadata(value.substring(1));
  } else {
    metaData = c11nEnv.getFieldMetadata(value);
    propertyName = value;
  }
  const tableType = metaData?.datasource?.tableType;
  const isSharedDataPage = metaData?.datasource?.mode === Constants.READONLY;

  const dataPageName = metaData?.datasource?.name || '';
  const componentConfig = c11nEnv.getComponentConfig();
  if (componentConfig && componentConfig.deferDatasource) {
    componentConfig.datasourceMetadata = `@FIELD_META ${value}`;
    c11nEnv.setStateProps({
      datasourceMetadata: componentConfig.datasourceMetadata
    });
    return tableType !== Constants.DATA_PAGE ? metaData?.datasource?.records : null;
  }
  if (tableType !== Constants.DATA_PAGE) {
    return metaData?.datasource?.records;
  }
  const propertyForDisplayText = AnnotationProcessor.getPropertyName(metaData?.datasource?.propertyForDisplayText);
  const propertyForValue = AnnotationProcessor.getPropertyName(metaData?.datasource?.propertyForValue);
  const parameters = Array.isArray(metaData?.datasource?.parameters) ? metaData?.datasource?.parameters : [];
  /**
   * This contains all the dependent
   */
  const currentContextName = c11nEnv.getContextName();
  const parameterValues = [];
  parameters.forEach((parameter) => {
    if (AnnotationProcessor.isProperty(parameter?.value)) {
      let resolvedValue = getValue(
        `.${Utils.getFullPropertyReference([
          parentPropertyName,
          AnnotationProcessor.getPropertyName(parameter.value)
        ])}`,
        pageReference,
        currentContextName
      );
      parameterValues.push(
        Utils.getFullPropertyReference([
          pageReference,
          parentPropertyName,
          AnnotationProcessor.getPropertyName(parameter?.value)
        ])
      );

      if (resolvedValue === undefined || resolvedValue === null || resolvedValue === '') {
        resolvedValue = Constants.NULL_STRING;
      }
      parameterValueArray.push(resolvedValue);
    } else {
      // Add constant value as it is to get proper pxResults generated with correct hash.
      parameterValueArray.push(parameter?.value || Constants.NULL_STRING);
    }
  });

  /* istanbul ignore else */
  if (currentContextName) {
    const currentContextObject = DatapageDependencyMap.getContextObjectByName(currentContextName) || {};
    parameterValues.forEach((val) => {
      if (val in currentContextObject) {
        const existingDependentProperties = currentContextObject[val];
        currentContextObject[val] = [...new Set([...existingDependentProperties, propertyName])];
      } else {
        currentContextObject[val] = [propertyName];
      }
    });
    DatapageDependencyMap.addContextName(currentContextName, currentContextObject);
  }
  const pxResults = getResultsFromDataPage(c11nEnv, parameterValueArray, dataPageName, isSharedDataPage);
  return pxResults.map((val) => {
    return {
      key: val[propertyForValue] || val[`.${propertyForValue}`],
      value: val[propertyForDisplayText] || val[`.${propertyForDisplayText}`]
    };
  });
});
