import constants, { publicConstants } from '../../constants';
import {
  processViewResponse,
  SCOPE_TYPES,
  processHeaders,
  shouldReplaceActiveItem
} from '../../store/state/epics/util';
import * as actionType from '../../store/state/actions';
import * as actions from '../../store/state/actions/action-creators';
import { isAssignmentInCreateStage, isAssignmentInManualStage } from '../../utils/case-utils';
import {
  getCaseInfo,
  getCaseInfoID,
  getContainerName,
  getDisplayCaseID,
  isWorkAreaContainer,
  addActionToRemoveContainerItem
} from '../common-helper';
import {
  getDataContextName,
  getActiveContainerItemContext,
  getTargetFromContainerItemID,
  getContainerItemName,
  isContainerSingleMode,
  hasContainerItems
} from '../../store/state/container-utils';
import { getSemanticURL } from '../../router/app-router';
import EnvironmentInfo from '../../environment-info';

const { RESOURCE_TYPES } = publicConstants;

export const getViewType = (actionInPayload, dxApiResponse = []) => {
  const { viewType, containerName, targetContainerName, isChild } = actionInPayload;
  const caseInfo = getCaseInfo(dxApiResponse);
  if (isAssignmentInManualStage(caseInfo) && actionInPayload?.stageID === caseInfo.stageID) {
    return SCOPE_TYPES.PAGE;
  }

  if (!viewType) {
    return (isWorkAreaContainer(containerName) || isWorkAreaContainer(targetContainerName)) && !isChild
      ? SCOPE_TYPES.FORM
      : SCOPE_TYPES.PAGE;
  }

  return viewType;
};

export const getContainerNameForCaseStage = (dxApiResponse, actionInPayload) => {
  const caseInfo = getCaseInfo(dxApiResponse);
  let isInCreateStage;
  if (EnvironmentInfo.getDefaultCasePage()) {
    isInCreateStage = false;
  } else {
    isInCreateStage = isAssignmentInCreateStage(caseInfo);
  }
  const { isChild, containerName } = actionInPayload;
  /*
    Scenario: Case opened using openWorkByHandle opens case details page with Case Summary, ToDo gadget and other gadgets rendered.
    Opening assignment from here by clicking on Go should render assignment inline even in case of create stage if the case is not a child case.
    If the case is child case then the case should be opened in modal. So having a check if the openAssignment action is from ToDo list gadget.
  */
  const isChildCaseInCreateStage = isChild && isInCreateStage;
  const isCreateStageOpenAssignmentFromWorklistToDoList = isInCreateStage && !actionInPayload.isActionFromToDoList;

  return isChildCaseInCreateStage || isCreateStageOpenAssignmentFromWorklistToDoList
    ? constants.MODAL
    : containerName || getContainerName(actionType.ACTION_OPENASSIGNMENT, actionInPayload);
};

export const addActionToActivateContainerItem = async (actionsOut, containerItemData, options) => {
  const { semanticURL, containerItemID, isParallelAssignment, insKey } = containerItemData;
  const { target, actionInPayload } = options;
  const { skipBrowserSemanticUrlUpdate = false } = actionInPayload;
  const context = getDataContextName(containerItemID);
  /* [TODO]: we shouldn't use "acName" anywhere, setting this value..since app-router uses this logic */
  await processViewResponse(
    {
      directUrlUpdation: !skipBrowserSemanticUrlUpdate,
      semanticURL
    },
    actionInPayload,
    {
      acName: target,
      target,
      context
    }
  );

  actionsOut.push(
    actions.postProcess(actions.activateContainerItem, {
      containerItemID,
      target,
      isParallelAssignment,
      insKey,
      resourceType: RESOURCE_TYPES.CASE,
      context,
      doDeleteCaseSummary: actionInPayload.doDeleteCaseSummary
    })
  );
};

export const setDirectUrlUpdation = (dxApiResponse, actionInPayload) => {
  const caseInfo = getCaseInfo(dxApiResponse);
  const caseInfoID = getCaseInfoID(dxApiResponse);
  const isInCreateStage = isAssignmentInCreateStage(caseInfo);
  const { skipBrowserSemanticUrlUpdate = false, viewType, isTraditionalUICase } = actionInPayload;
  // URL and title updation should be done only for response content which is replacing entire case view.
  const updateUrl =
    viewType === SCOPE_TYPES.PAGE && (!isInCreateStage || isTraditionalUICase) && !skipBrowserSemanticUrlUpdate;

  const { caseTypeID } = caseInfo;
  if (updateUrl) {
    const semanticURL = getSemanticURL(actionType.ACTION_OPENWORKBYHANDLE, {
      caseClassName: caseTypeID
    }).replace(/{.+?}/, getDisplayCaseID(caseInfoID));
    dxApiResponse.semanticURL = semanticURL;
    dxApiResponse.directUrlUpdation = true;
  }
};
export const addRemoteHeadersIfRequired = (actionInPayload) => {
  let requestHeaders = actionInPayload?.headers ?? {};
  if (actionInPayload?.isRemote) {
    requestHeaders = processHeaders(requestHeaders, actionInPayload.remoteClassName);
  }
  return requestHeaders;
};

const processContainerItemInSingleMode = (actionInPayload, response, payload, actionInCaseViewMode, actionsOut) => {
  if (getViewType(actionInPayload, response) === SCOPE_TYPES.FORM) {
    // for viewType=form, usecases ==> caseViewMode is generally perform mode but consumer can override sending through payload.
    payload.caseViewMode = actionInCaseViewMode || constants.PERFORM_MODE;
    actionsOut.push(actions.postProcess(actions.updateContainerItem, payload));
  } else {
    actionsOut.push(actions.postProcess(actions.replaceContainerItem, payload));
  }
};

const processContainerItemsInMultipleMode = (actionsOut, removeActionPayload, target, payload, actionInPayload) => {
  /*
    - container in multiple mode (e.g. primary, modal).
    - container in single mode with no items present in it.
  */
  addActionToRemoveContainerItem(actionsOut, {
    removeActionPayload,
    target
  });
  if (shouldReplaceActiveItem(payload.key, target)) {
    payload.context = getActiveContainerItemContext(target);
    payload.containerItemID = payload.context;
    actionsOut.push(actions.postProcess(actions.replaceContainerItem, payload));
  } else if ((actionInPayload?.isChild || actionInPayload?.isRemote) && actionInPayload?.isMultiDoc) {
    const containerTarget = getTargetFromContainerItemID(actionInPayload.context);
    payload.target = containerTarget;
    payload.context = actionInPayload.context;
    payload.containerItemID = actionInPayload.context;
    actionsOut.push(actions.postProcess(actions.replaceContainerItem, payload));
  } else {
    const containerTarget = getTargetFromContainerItemID(payload.context);
    const containerItemName = getContainerItemName(containerTarget, payload.key);
    if (containerItemName) {
      payload.target = containerTarget;
      payload.context = containerItemName;
      payload.containerItemID = containerItemName;
      actionsOut.push(actions.postProcess(actions.replaceContainerItem, payload));
    } else {
      actionsOut.push(actions.postProcess(actions.addContainerItem, payload));
    }
  }
};

export const processAppropriateContainerItem = (
  payload,
  target,
  actionsOut,
  actionInPayload,
  response,
  actionInCaseViewMode,
  removeActionPayload
) => {
  if (isContainerSingleMode(target) && hasContainerItems(target)) {
    processContainerItemInSingleMode(actionInPayload, response, payload, actionInCaseViewMode, actionsOut);
  } else {
    processContainerItemsInMultipleMode(actionsOut, removeActionPayload, target, payload, actionInPayload);
  }
};
