import { getChildContainers, getActiveContainerItemName } from '../store/state/container-utils';
import * as StateManager from '../store/state/state-manager';
import { CLEAR_MESSAGES, ADD_MESSAGES } from '../store/state/actions';
import Utils from '../utils/utils';
import constants, { publicConstants } from '../constants';

/**
 * Provides APIs for accessing and manipulating the messages from Redux store.
 * These APIs can be used for different purposes e.g. validation messages, nextwork failure messages etc.
 * @module MessageManager
 */

export const MESSAGES_PATH = {
  CONTENT_FIELDS_PATH: [constants.CONTEXT_DATA, constants.CASE_INFO.CASE_INFO, constants.CONTENT],
  FIELD_MESSAGES_PATH: [
    constants.CONTEXT_DATA,
    constants.CASE_INFO.CASE_INFO,
    constants.CONTENT,
    constants.SUMMARY_OF_MESSAGES,
    constants.PROPERTY_MESSAGES
  ],
  PAGE_MESSAGES_PATH: [constants.CONTEXT_DATA, constants.SUMMARY_OF_MESSAGES, constants.PAGE_MESSAGES],
  HTTP_MESSAGES_PATH: [constants.CONTEXT_DATA, constants.SUMMARY_OF_MESSAGES, constants.HTTP_MESSAGES],
  CASE_MESSAGE_PATH: [constants.CASEMESSAGES]
};

/**
 * @typedef {object} MessagesConfigObject
 * @property {string} type - (optional) type of the message e.g. error or info
 * @property {string} property - (required) property name e.g. firstName
 * @property {string} pageReference - (required) page reference of the property
 * @property {string} category - (required) category type of the messages e.g. property, PAGE or HTTP
 * @property {string} context - (required) context where messages gets displayed e.g. app or app/primary_2 or app/primary_2/workarea_1
 * @function
 */

/**
 * this function helps to get corresponding message path based on property and category params
 * @function getMessagesPath
 * @private
 * @param {string} property - property name
 * @param {string} pageReference - page reference of the property
 * @param {string} category - category of message
 * @param {boolean} isListEntry - is list entry
 * @returns {string} path
 */
export const getMessagesPath = (property = '', pageReference = '', category, isListEntry) => {
  let path = [];
  let propertyRef;
  if (property !== null)
    propertyRef = property && property[0] === '.' ? property.substr(1).split('.') : property.split('.');
  const pageRef = pageReference && pageReference[0] === '.' ? pageReference.substr(1) : pageReference;
  if ((category === constants.PROPERTY && property) || property) {
    path = [constants.CONTEXT_DATA, ...pageRef.split('.')];
    for (let i = 0; i < propertyRef.length - 1; i += 1) {
      // to add all the embedded object name
      path.push(propertyRef[i]);
    }
    path.push(constants.SUMMARY_OF_MESSAGES, constants.PROPERTY_MESSAGES, propertyRef[propertyRef.length - 1]);
  } else if (category === publicConstants.PAGE) {
    path = [...MESSAGES_PATH.PAGE_MESSAGES_PATH];
  } else if (category === constants.HTTP) {
    path = [...MESSAGES_PATH.HTTP_MESSAGES_PATH];
  } else if (category === constants.CASEMESSAGES) {
    path = [...MESSAGES_PATH.CASE_MESSAGE_PATH];
  } else if (isListEntry) {
    path = [constants.CONTEXT_DATA, ...pageRef.split('.')];
  }
  return path.filter(Boolean);
};

/**
 * this function helps to retrieve all error messages from context object
 * @function getValidationErrorMessages
 * @public
 * @property {string} context - (required) context where messages are stored
 * @returns {Array} - error messages
 *
 * @example <caption>Example for retrieving error messages.</caption>
 * context - required
 * getValidationErrorMessages('app/primary_2');
 */
export const getValidationErrorMessages = (context) => {
  const storeDataObj = StateManager.getDataObject(context);
  const errors = [];

  const propertyErrors = Utils.getIn(storeDataObj, [...MESSAGES_PATH.FIELD_MESSAGES_PATH]) || {};

  Object.keys(propertyErrors).forEach((property) => {
    if (
      propertyErrors[property].length &&
      propertyErrors[property][0].type === constants.MESSAGES.MESSAGES_TYPE_ERROR
    ) {
      if (propertyErrors[property][0].fieldLabel) {
        errors.push({
          label: `${propertyErrors[property][0].fieldLabel}:`,
          description: propertyErrors[property][0].message
        });
      } else {
        errors.push(propertyErrors[property][0].message);
      }
    }
  });

  const contentProperties = Utils.getIn(storeDataObj, [...MESSAGES_PATH.CONTENT_FIELDS_PATH]) || {};

  Object.keys(contentProperties).forEach((contentProperty) => {
    if (
      contentProperty !== constants.SUMMARY_OF_MESSAGES &&
      contentProperties[contentProperty][constants.SUMMARY_OF_MESSAGES] &&
      contentProperties[contentProperty][constants.SUMMARY_OF_MESSAGES][constants.PROPERTY_MESSAGES]
    ) {
      const contentPropertyErrors =
        contentProperties[contentProperty][constants.SUMMARY_OF_MESSAGES][constants.PROPERTY_MESSAGES];
      Object.keys(contentPropertyErrors).forEach((property) => {
        if (
          contentPropertyErrors[property].length &&
          contentPropertyErrors[property][0].type === constants.MESSAGES.MESSAGES_TYPE_ERROR
        ) {
          if (contentPropertyErrors[property][0].fieldLabel) {
            errors.push({
              label: `${contentPropertyErrors[property][0].fieldLabel}:`,
              description: contentPropertyErrors[property][0].message
            });
          } else {
            errors.push(contentPropertyErrors[property][0].message);
          }
        }
      });
    }
  });

  return errors;
};

/**
 * this function helps to retrieve messages from context object
 * @function getMessages
 * @public
 * @param {MessagesConfigObject} config {@link MessagesConfigObject} object which has information to process the messages information
 * @returns {object} - messages object
 *
 * @example <caption>Example for retrieving property messages.</caption>
 * property - required
 * context - required
 * type - optional
 * category - not required
 * getMessages({
 *    type: 'error',
 *    property: '.firstName',
 *    context: 'app/primary_2'
 * });
 *
 * @example <caption>Example for retrieving http messages.</caption>
 * category - required
 * context - required
 * type - optional
 * getMessages({
 *    type: 'error',
 *    category: 'HTTP'
 *    context: 'app/primary_2'
 * });
 *
 * @example <caption>Example for retrieving page messages.</caption>
 * category - required
 * context - required
 * type - optional
 * getMessages({
 *    type: 'error',
 *    category: 'PAGE'
 *    context: 'app/primary_2'
 * });
 */
export const getMessages = (config) => {
  const { type, property, category, context, pageReference } = config;
  let propertyWithoutDot;
  if (property) {
    propertyWithoutDot = property.slice(1);
  }

  const storeDataObj = StateManager.getDataObject(context);
  let messages = [];
  const allMessages = Utils.getIn(storeDataObj, getMessagesPath(propertyWithoutDot, pageReference, category));

  if (!type || type === 'all') {
    return allMessages;
  }

  if (allMessages && Array.isArray(allMessages) && allMessages.length > 0) {
    messages = allMessages.filter((msg) => msg.type === type);
  }

  return messages;
};

/**
 * this function helps to clear validation messages on property,
 * http messages on context/page.
 *
 * @function clearMessages
 * @public
 * @param {MessagesConfigObject} config {@link MessagesConfigObject} object which has information to process the messages information
 * @example <caption>Example for clearing property messages.</caption>
 * clearMessages({
 *    type: 'error',
 *    property: '.firstName',
 *    context: 'app/primary_2'
 * });
 *
 * @example <caption>Example for clearing http messages.</caption>
 * clearMessages({
 *    type: 'error',
 *    category: 'HTTP'
 *    context: 'app/primary_2'
 * });
 *
 * @example <caption>Example for clearing page messages.</caption>
 * clearMessages({
 *    type: 'info',
 *    category: 'PAGE'
 *    context: 'app/primary_2'
 * });
 *
 *
 * @returns {void}
 */
export const clearMessages = (config) => {
  const { property, pageReference, category, context, type, isListEntry, clearContextMessages } = config;
  const payload = { context, type };
  if (property && !isListEntry) {
    payload.property = property;
    payload.pageReference = pageReference;
  } else if (isListEntry) {
    payload.pageReference = pageReference;
    payload.isListEntry = isListEntry;
  } else payload.category = category;
  if (clearContextMessages) {
    StateManager.dispatch({
      type: CLEAR_MESSAGES,
      payload: {
        stateObj: config.stateObj,
        context
      }
    });
  } else {
    StateManager.dispatch({
      type: CLEAR_MESSAGES,
      payload
    });
  }
};

/**
 * this function helps to add validation messages on property,
 * http messages on context/page.
 * @function addMessages
 * @public
 * @param {MessagesConfigObject} config {@link MessagesConfigObject} object which has information to process the messages information
 * @example <caption>Example for add http messages.</caption>
 * addMessages({
 *    messages: [
 *      {
 *        type: 'error',
 *        message: 'Validation Error'
 *      }
 *    ],
 *    category: 'HTTP'
 *    context: 'app/primary_2'
 * });
 *
 * @example <caption>Example for add page messages.</caption>
 * addMessages({
 *    messages: [
 *      {
 *        type: 'info',
 *        message: 'Info Message'
 *      }
 *    ],
 *    category: 'PAGE'
 *    context: 'app/primary_2'
 * });
 *
 * @example <caption>Example for add property messages.</caption>
 * addMessages({
 *    messages: [
 *      {
 *        type: 'info',
 *        message: 'Info Message'
 *      }
 *    ],
 *    property: '.lastName'
 *    context: 'app/primary_2'
 * });
 *
 *
 * @returns {void}
 */
export const addMessages = (config) => {
  const { messages, property, pageReference, category, context } = config;
  const payload = { messages, context };

  if (property) {
    payload.property = property;
    payload.pageReference = pageReference;
  } else payload.category = category;

  StateManager.dispatch({
    type: ADD_MESSAGES,
    payload
  });
};

export const getAllMessagesPaths = (validationMessages, currentPath, allPaths) => {
  for (const key of Object.keys(validationMessages)) {
    currentPath.push(key);
    if (key === constants.SUMMARY_OF_MESSAGES) {
      allPaths.push([...currentPath]);
    } else {
      const typeOfKey = typeof validationMessages[key];
      /* istanbul ignore else */
      if (typeOfKey === 'object') {
        getAllMessagesPaths(validationMessages[key], currentPath, allPaths);
      }
    }
    currentPath.pop();
  }
  return allPaths;
};

/**
 * this function helps to retrieve messages for Rootcontainer from root context or active container context
 * @function getRootContainerHttpMessages
 * @private
 * @param {boolean} isNonPortalRootContainer - if renderMode is other than portal then true else false
 * @param {string} activeContainerItemName - (optional) active container item name
 * @returns {object} - messages object
 *
 * @example <caption>Example for retrieving http messages.</caption>
 * isNonPortalRootContainer - required
 * activeContainerItemName - optional
 * getRootContainerHttpMessages(true,'app/primary_1');
 */
export const getRootContainerHttpMessages = (isNonPortalRootContainer, activeContainerItemName) => {
  let httpMessages = getMessages({
    context: constants.APP.ROOT,
    category: 'HTTP'
  });
  if (isNonPortalRootContainer) {
    const activeContainerHttpMessages = getMessages({
      context: activeContainerItemName,
      category: 'HTTP'
    });
    if (httpMessages) {
      if (activeContainerHttpMessages) {
        httpMessages = httpMessages.concat(
          getMessages({
            context: activeContainerItemName,
            category: 'HTTP'
          })
        );
      }
    } else {
      httpMessages = getMessages({
        context: activeContainerItemName,
        category: 'HTTP'
      });
    }
  }
  return httpMessages;
};

export const clearContextMessagesInActiveWorkArea = (context) => {
  let activeWorkArea = null;
  if (getChildContainers(context)[0]) {
    activeWorkArea = getActiveContainerItemName(getChildContainers(context)[0]);
  } else {
    activeWorkArea = context;
  }
  if (activeWorkArea) {
    clearMessages({
      context: activeWorkArea,
      stateObj: StateManager.getDataObject(activeWorkArea),
      clearContextMessages: true
    });
  }
};

const MessageManager = {
  addMessages,
  getValidationErrorMessages,
  getMessages,
  clearMessages,
  getRootContainerHttpMessages,
  clearContextMessagesInActiveWorkArea
};

export default MessageManager;
