import PubSubUtils from '../../utils/pubsub-utils';
import APP_CONSTANTS from '../../constants';

export const publishDisplayCreatedCaseToasterEvent = (data) => {
  /* publish event for displaying a toaster message when new case is created */
  PubSubUtils.publish(APP_CONSTANTS.PUB_SUB_EVENTS.DISPLAY_CASE_CREATED_TOASTER, data);
};
export const publishCreateStageDoneEvent = (data) => {
  /* publish create stage info to infinity for case tracking */
  PubSubUtils.publish(APP_CONSTANTS.PUB_SUB_EVENTS.CASE_EVENTS.CREATE_STAGE_DONE, data);
};

export default { publishDisplayCreatedCaseToasterEvent, publishCreateStageDoneEvent };
