import * as StateManager from '../store/state/state-manager';
import * as RuleStore from '../rules/rules-store';
import Utils from '../utils/utils';
import AnnotationProcessor from '../annotation/annotation-processor';
import DeferLoadManager from '../deferload/manager';
import constants from '../constants';

// This class provide api which can be called explicitly for use like resgistring fields for subscription callback
// and registering resetting dependency.
/**
 * @description
 * Exposes APIs to handle Datapage parameters for callback subscriptions.
 */

class CascadeManager {
  constructor() {
    this.name = 'CascadeManager';
    /*
    {
      "<contextName>":{
        "<propertyReference>": {
          "uniqueid" : "callbackFn"
        }
      }
    }

    callbackFns --> callback(propertyReference, propertyValue)
    */
    /*
   {
     "app/primary":{
       "caseInfo.content.FirstName" : {
         "subscriptions": {
         "firstNameChange" : ()=>{ updateName();}
         },
         "previousValue": "test"
       }
     }
   }
   */
    this.cascadeSubscribers = {};
    /*
    {
      'app/primary_1': {
        'caseInfo.content.productId': ['caseInfo.content.backlogId','caseInfo.content.release']
      }
    }
    */
    this.cascadeResetSubscribers = {};
    this.listChangeSubscribers = [];
  }

  /**
   * Registers all the dependencies for the target field when the source field changes.
   * @description Registers all the dependencies of the target field to perform cascade resetting. 
   * Cascade resetting is the process of resetting the value of the target field when the value of the source field changes.
   *
   * @example <caption>In this example, the API registers the dependencies of the backlogId target field.</caption>
   * Example usage - CascadeManager.registerResetDependencies('app/primary_1', 'caseInfo.content','.backlogId', ['.productId','release'], 'Text', 'singleRecord')
   *
   * @param {string} contextName The name of the context containing the fields to be registered.
   * @param {string} pageReference The reference to the page that contains the field to be registered.
   * @param {string} target The field whose dependencies needs to be registered.
   * @param {Array} dependentProperties Array of fields on which target field is dependent.
   * @param {string} fieldType The type of the target field which needs to be registered. eg. Text
   * @param {string} mode The selection mode of the target field, the value of mode can singleRecord or multiRecord.
   * @public
   * @function
   */
  registerResetDependencies(contextName, pageReference, target, dependentProperties, fieldType, mode = 'singleRecord') {
    if (!contextName || !pageReference || !target || !dependentProperties?.length > 0) {
      // TODO : We should be Introducing a logger to capture all the Console logs, since logging to console is not a recommended practice.
      return;
    }
    const propertyName = target.startsWith('.') ? `${pageReference}.${target.substring(1)}` : target;
    const currentContextObject = this.cascadeResetSubscribers[contextName] || {};
    const updatedPageReference = this._handlePageReference(pageReference, target, fieldType);
    dependentProperties.forEach((parameter) => {
      if (!parameter) {
        return;
      }
      let fullPropertyReference = parameter.startsWith('.') ? parameter.substring(1) : parameter;
      fullPropertyReference = Utils.getFullPropertyReference([updatedPageReference, fullPropertyReference]);
      if (fullPropertyReference in currentContextObject) {
        const existingDependentPropertiesSet = currentContextObject[fullPropertyReference];
        const isPropertyFound = existingDependentPropertiesSet.find((prop) => prop.propertyName === propertyName);
        if (!isPropertyFound) {
          existingDependentPropertiesSet.push({ propertyName, mode });
        }
      } else {
        currentContextObject[fullPropertyReference] = [{ propertyName, mode }];
      }
    });
    if (Object.keys(currentContextObject).length > 0) {
      this.cascadeResetSubscribers[contextName] = currentContextObject;
    }
  }

  /**
   * De-Registers all the dependencies for the target field when the source field changes.
   * @description De-registers all the registered dependencies of the target field that have undergone cascade resetting.
   * Cascade resetting is the process of resetting the value of the target field when the value of the source field changes.
   *
   * @example <caption>In this example, the API de-registers the registered dependencies of the backlogId target field..</caption>
   * Example usage - CascadeManager.deregisterResetDependencies('app/primary_1', 'caseInfo.content','.backlogId', ['.productId','release'], 'Text')
   *
   * @param {string} contextName The name of the context containing the fields to be de-registered.
   * @param {string} pageReference The reference to the page that contains the field to be de-registered.
   * @param {string} target The field whose dependencies needs to be de-registered.
   * @param {Array} dependentProperties Array of fields on which target field is dependent.
   * @param {string} fieldType The type of the target field which needs to be de-registered. eg. Text
   * @public
   * @function
   */

  deregisterResetDependencies(contextName, pageReference, target, dependentProperties, fieldType) {
    if (!contextName || !pageReference || !target || !dependentProperties?.length > 0) {
      // TODO : We should be Introducing a logger to capture all the Console logs, since logging to console is not a recommended practice.
      return;
    }
    /**
     * {
     *  "productID":["productVersionID"]
     *  "EpicID":[]
     *  "CategoryID":["productVersionID"]
     * }
     */
    const propertyName = target.startsWith('.') ? `${pageReference}.${target.substring(1)}` : target;
    const currentContextObject = this.cascadeResetSubscribers[contextName] || {};
    const updatedPageReference = this._handlePageReference(pageReference, target, fieldType);
    dependentProperties.forEach((parameter) => {
      if (parameter) {
        let fullPropertyReference = parameter.startsWith('.') ? parameter.substring(1) : parameter;
        fullPropertyReference = Utils.getFullPropertyReference([updatedPageReference, fullPropertyReference]);
        if (fullPropertyReference in currentContextObject) {
          const existingDependentPropertiesSet = currentContextObject[fullPropertyReference];
          Utils.removeExistingDependentProperty(existingDependentPropertiesSet, propertyName);
        }
      }
    });
    if (Object.keys(currentContextObject).length > 0) {
      this.cascadeResetSubscribers[contextName] = currentContextObject;
    }
  }

  /**
   * get all the dependencies associated with the property
   * @param {string} contextName name of the context
   * @param {string} propertyName name of dependent property inside the context
   * @returns {Array} dependent properties array which needs to be resetted.
   * @example <caption>Example for getResetDependencies() depicting how to get resetting dependencies for change in productId.</caption>
   * Example usage - CascadeManager.getResetDependencies('app/primary_1', 'caseInfo.content.productId')
   * @private
   */
  getResetDependencies(contextName, propertyName) {
    return this.cascadeResetSubscribers[contextName]?.[propertyName] || [];
  }

  /**
   * purge dependency map on given contextName
   * @param {string} contextName name of the context
   * @example <caption>Example for purgeResetDependencies() depicting how to purge dependencies on a context.</caption>
   * Example usage - CascadeManager.purgeResetDependencies('app/primary_1')
   * @private
   */
  purgeResetDependencies(contextName) {
    if (contextName && this.cascadeResetSubscribers[contextName]) delete this.cascadeResetSubscribers[contextName];
  }

  /**
   * Registers the fields to the CascadeManager Class.
   * @description Register the fields to the CascadeManager Class.
   *
   * @example <caption>Example for registerFields() depicting how to register the firstName and lastName
   * fields to trigger callback.</caption>
   * Example usage - CascadeManager.registerFields('app/primary_1', 'caseInfo.content', ['firstName','lastName'], () => { console.log("field changed")}, '001-002-003')
   *
   * @param {string} context The name of the context containing the fields to be registered.
   * @param {string} pageReference The reference to the page that contains the field to be registered.
   * @param {Array} fields Array of fields to be registered.
   * @param {Function} callback function to be called when the registered field is updated.
   * @param {string} subscriptionId uniqueId for registering the fields. The same subscriptionId should be provided for de-registering th fields.
   *
   * @function
   * @public
   */
  registerFields(context, pageReference, fields = [], callback, subscriptionId) {
    this.cascadeSubscribers[context] = this.cascadeSubscribers[context] || {};
    fields.forEach((field) => {
      this.cascadeSubscribers[context][`${pageReference}${field}`] =
        this.cascadeSubscribers[context][`${pageReference}${field}`] || {};

      const fullPathRef = this.cascadeSubscribers[context][`${pageReference}${field}`];
      if (!fullPathRef[subscriptionId]) {
        fullPathRef[subscriptionId] = callback;
        const val = StateManager.getValue(field, pageReference, context);
        fullPathRef.previousValue = typeof value === 'object' ? null : val;
      }
    });
  }

  /**
   * Registers the fields of type PageList to the CascadeManager Class.
   * @description Registers the field of type PageList to the CascadeManager Class.
   *
   * @example <caption>Example for registerListField() depicting how to register the phoneNumber
   * field to trigger callback. </caption>
   * Example usage - CascadeManager.registerListField('app/primary_1', 'caseInfo.content', phoneNumber, () => { console.log("field changed")}, '002-002-004')
   *
   * @param {string} context The name of the context containing the field to be registered.
   * @param {string} pageReference The reference to the page that contains the field to be registered.
   * @param {string} listField field to be registered.
   * @param {Function} callback function to be called when the registered field is updated.
   * @param {string} subscriptionId uniqueId for registering the field. The same subscriptionId should be provided for de-registering th field.
   *
   * @function
   * @public
   */
  registerListField(context, pageReference, listField, callback, subscriptionId) {
    const fieldArray = CascadeManager._getFieldsForList(context, pageReference, listField);
    if (fieldArray.length > 0) {
      this.registerFields(context, pageReference, fieldArray, callback, subscriptionId);
      DeferLoadManager.enableCascade(context);
    }
  }

  /**
   * De-Registers the field of type PageList from the CascadeManager Class.
   * @description De-Registers the field of type PageList from the CascadeManager Class.
   *
   * @example <caption>Example for unRegisterListField() depicting how to de-register
   * field.</caption>
   * Example usage - CascadeManager.unRegisterListField('app/primary_1', 'caseInfo.content', phoneNumber, '002-002-004')
   *
   * @param {string} context The name of the context containing the field to be de-registered.
   * @param {string} pageReference The reference to the page that contains the field to be de-registered.
   * @param {string} listField field to be de-registered.
   * @param {string} subscriptionId uniqueId for de-registering the field.
   *
   * @function
   * @public
   */
  unRegisterListField(context, pageReference, listField, subscriptionId) {
    const fieldArray = CascadeManager._getFieldsForList(context, pageReference, listField);
    if (fieldArray.length > 0) {
      this.unRegisterFields(context, pageReference, fieldArray, subscriptionId);
    }
  }

  static _getFieldsForList(context, pageReference, listField) {
    const fieldArray = [];
    const fieldMetadata = RuleStore.getPropertyMetadata(
      listField,
      StateManager.getValue('classID', pageReference, context)
    );
    if (fieldMetadata && fieldMetadata.datasource && fieldMetadata.datasource.parameters) {
      Object.values(fieldMetadata.datasource.parameters).forEach((param) => {
        if (AnnotationProcessor.isProperty(param)) {
          const trimParam = AnnotationProcessor.trimAnnotation(param);
          fieldArray.push(trimParam);
        }
      });
    }
    return fieldArray;
  }

  /**
   * De-registers the fields from the CascadeManager Class.
   * @description De-register fields from the CascadeManager Class.
   *
   * @example <caption>Example for unRegisterFields() depicting how to unregister the firstName and lastName fields.</caption>
   * Example usage - CascadeManager.unRegisterFields('app/primary_1', 'caseInfo.content', ['firstName','lastName'], '001-002-003')
   *
   * @param {string} context The name of the context containing the fields to be de-registered.
   * @param {string} pageReference The reference to the page that contains the field to be de-registered.
   * @param {Array} fields Array of fields to be de-registered.
   * @param {string} subscriptionId uniqueId for de-registering the fields.
   *
   * @function
   * @public
   */

  unRegisterFields(context, pageReference, fields = [], subscriptionId) {
    if (!this.cascadeSubscribers[context]) {
      return;
    }

    fields.forEach((field) => {
      if (!this.cascadeSubscribers[context][`${pageReference}${field}`]) {
        return;
      }

      const fullPathRef = this.cascadeSubscribers[context][`${pageReference}${field}`];
      if (fullPathRef[subscriptionId]) {
        delete fullPathRef[subscriptionId];
      }
    });
  }

  /**
   * Invokes subscribers registered to the fields.
   * @description invokes subscribers registered to the fields.
   *
   * @example <caption>Example for triggerCascade() depicting how to triggerCascade for subscribers </caption>
   * Example usage - CascadeManager.triggerCascade('app/Primary', 'pgRef0', 'value 0')
   *
   * @param {string} context name of the context
   * @param {string} propertyReference reference path
   * @param {string} propertyValue property value
   *
   * @private
   */

  triggerCascade(context, propertyReference, propertyValue, changeSet) {
    let isUpdatePrevValue = false;
    if (!this.cascadeSubscribers[context]) {
      return;
    }

    if (!this.cascadeSubscribers[context][propertyReference]) {
      return;
    }
    Object.keys(this.cascadeSubscribers[context][propertyReference]).forEach((id) => {
      const refObj = this.cascadeSubscribers[context][propertyReference];
      if (id !== 'previousValue' && refObj.previousValue !== propertyValue) {
        isUpdatePrevValue = true;
        if (changeSet) {
          changeSet.add(refObj[id]);
        } else {
          refObj[id](propertyReference, propertyValue);
        }
      }
    });
    if (isUpdatePrevValue) {
      this.cascadeSubscribers[context][propertyReference].previousValue = propertyValue;
    }
  }

  /* @private */
  purgeCascadeFields(context) {
    delete this.cascadeSubscribers[context];
  }

  /**
   * {
   *  caseInfo: {
   *    content: {
   *      ..fields
   *   }
   * }
   * }
   * @param {object} serverData dxApi reponse
   * @param {string} context app context
   * @param {string} path reference path for the key
   * @private
   */
  handleServerChanges(serverData, context, path = constants.CASE_INFO.CASE_INFO_CONTENT) {
    if (!serverData || !context) {
      return;
    }
    const changeSet = new Set();
    const serverFields = Utils.getIn(serverData, path);
    if (serverFields && Object.keys(serverFields).length > 0) {
      this._handleCascadeObject(serverFields, context, path, changeSet);
      changeSet.forEach((callbackItem) => {
        callbackItem();
      });
    }
  }

  /*
  @private
  */
  _handleCascadeObject(serverFields, context, pathPrefix, changeSet) {
    if (typeof serverFields === 'object') {
      Object.keys(serverFields).forEach((key) => {
        const tempPrefix = `${pathPrefix}.${key}`;
        this._handleCascadeObject(serverFields[key], context, tempPrefix, changeSet);
      });
    } else {
      this.triggerCascade(context, pathPrefix, serverFields, changeSet);
      this._triggerListChanges(context, pathPrefix, serverFields);
    }
  }

  /**
  @private
  */
  _handlePageReference(pageReference, target, fieldType) {
    let pageRef = pageReference;

    /*
      target -> .EmbeddedPage.NestedPage.Property1
      pageReference -> caseInfo.content.EmbeddedPage.NestedPage
    */

    if (fieldType === 'Page' && target.startsWith('.') && target.match(/[.]/g).length > 1) {
      const refpath = target.substring(0, target.lastIndexOf('.'));
      pageRef = `${pageReference}${refpath}`;
    }
    return pageRef;
  }

  /**
   * registers the provided function on every redux update which came from server.
   * @param {*} callback a callback function which needs to be called when the redux changes
   * @private
   */
  registerForListChanges(callback = () => {}) {
    this.listChangeSubscribers.push(callback);
  }

  /**
   * invokes subscribers registered to the field
   * @param {*} context name of the context in which the filed present eg. 'app/primary_1/workarea_1'
   * @param {*} propertyReference complete path of the property eg. 'caseInfo.content.pageList(0).page.anotherPageList(1).name
   * @param {*} value value of filed updated in the redux which came from server
   * @private
   */
  _triggerListChanges(context, propertyReference, value) {
    if (this.listChangeSubscribers.length === 0) {
      return;
    }
    // regex which tests the strings 'caseInfo.content.pageList.0.page
    const regexForPageList = /[a-zA-Z]+\.(\d+)\./g;
    if (regexForPageList.test(propertyReference)) {
      /*
      regex which matches the string contains dot and followed by any number of digits with minimum one digit
      and replace the digit enclosed with square brackets
      eg..   .0.pageList  ----> [0].pagelist
      */
      propertyReference = propertyReference.replace(/\.(\d+)/g, '[$1]');
      this.listChangeSubscribers.forEach(subscriber => {
        subscriber(context, propertyReference, value);
      });
    }
  }

  /**
   * This will unRegister all the existing subscribers
   * @private
   */
  unRegisterForListChanges() {
    this.listChangeSubscribers = [];
  }
}

export default new CascadeManager();
