/**
 *  @description
 *  Exposes APIs to log the message in different format.
 */
class LogHandler {
  /**
   * This method is used to log error message.
   * @param {string} errorMessage - error message which needs to be logged
   */
  error = (errorMessage) => {
    console.error(errorMessage);
  };
}

export default new LogHandler();
