import PubSubUtils from '../utils/pubsub-utils';
import constants from '../constants';

export const publishAssignmentSubmissionDoneEvent = (data) => {
  /* publishing event to notify, the assignment is submitted */
  PubSubUtils.publish(constants.PUB_SUB_EVENTS.CASE_EVENTS.ASSIGNMENT_SUBMISSION, data);
};

export const publishEndOfAssignmentProcessingEvent = (data) => {
  /* publishing event to notify, case reached confirmation and routed appropriately */
  PubSubUtils.publish(constants.PUB_SUB_EVENTS.CASE_EVENTS.END_OF_ASSIGNMENT_PROCESSING, data);
};

export const publishCaseOpenedEvent = (data) => {
  /* publishing event to notify, case is opened in review mode (openWorkByHandle) */
  PubSubUtils.publish(constants.PUB_SUB_EVENTS.CASE_EVENTS.CASE_OPENED, data);
};

export const publishAssignmentOpenedEvent = (data) => {
  /* publishing event to notify, assignment is opened (openAssignment) */
  PubSubUtils.publish(constants.PUB_SUB_EVENTS.CASE_EVENTS.ASSIGNMENT_OPENED, data);
};

export const publishCasePreviewEvent = (data) => {
  /* publishing event to notify, case preview  */
  PubSubUtils.publish(constants.PUB_SUB_EVENTS.CASE_EVENTS.CASE_PREVIEW, data);
};

export const publishCaseAssignmentIDUpdateEvent = data => {
  /* publishing event to notify, case assignmentId for coexistence  */
  PubSubUtils.publish(constants.PUB_SUB_EVENTS.CASE_EVENTS.CURRENT_ASSIGNMENT_UPDATED, data);
};
