import { invokeRestApi } from '../rest-client';

/**
 * The RelatedCasesApi module contains APIs that handle the related cases of a case.
 * The following APIs are part of the RelatedCasesApi module:
 * @module RelatedCasesApi
 */

const ADD_RELATED_CASES = 'addRelatedCases';
const REMOVE_RELATED_CASE = 'removeRelatedCase';
const GET_RELATED_CASES = 'getRelatedCases';

/**
 * This API relates several cases to a specific case.
 * @function
 * @static
 * @example <caption>In this example, the API relates two cases whose IDs are T-200 and T-201 to the case whose ID is W-102.</caption>
 * const casesArray =  [{ ID : 'ORG-MYAPP-WORK T-200'}, {ID : 'ORG-MYAPP-WORK T-201'}]
 * PCore.getRelatedCasesApi().addRelatedCases('ORG-MYAPP-WORK W-102', casesArray, 'app/primary_1')
 * .then(() => {
 *   // success
 * }).catch(err => {
 *    // Error handling
 * });
 *
 * @param {string} caseID         The ID of the case pointing to pzInsKey value to which several cases can be related.
 * @param {object[]} relatedCases The list of case IDs that must be related to a case.
 * @param {string} context        The name of the context from where the API is being called.
 * @returns {Promise<any>}        A Promise that returns the list of related cases added to the case.
 */

const addRelatedCases = (caseID, relatedCases, context) => {
  const queryPayload = {
    caseID
  };
  const reqBody = JSON.stringify({
    cases: relatedCases
  });
  return invokeRestApi(ADD_RELATED_CASES, { body: reqBody, queryPayload }, context);
};

/**
 * This API removes a related case from its relationship with a specific case.
 * @function
 * @static
 * @example <caption>In this example, the API removes the case whose ID is T-200 from its relationship with the case whose ID is W-102.</caption>
 * PCore.getRelatedCasesApi().removeRelatedCase('ORG-MYAPP-WORK W-102', 'ORG-MYAPP-WORK T-200', 'app/primary_1')
 * .then(() => {
 *    // success
 * }).catch(err => {
 *   // errors
 * });
 *
 * @param {string} caseID         The ID of the case pointing to pzInsKey value from whose relationship the related case must be removed.
 * @param {string} relatedCaseID  The ID of the case that should be removed from the relationship.
 * @param {string} context        The name of the context from where the API is being called.
 */

const removeRelatedCase = (caseID, relatedCaseID, context) => {
  const queryPayload = {
    caseID,
    relatedCaseID
  };
  return invokeRestApi(REMOVE_RELATED_CASE, { queryPayload }, context);
};

/**
 * This API obtains the related cases of a specific case.
 * @function
 * @static
 * @example <caption>In this example, the API obtains the related cases of a case whose ID is W-102.</caption>
 * PCore.getRelatedCasesApi().getRelatedCases('ORG-MYAPP-WORK W-102', 'app/primary_1')
 * .then(relatedCases => {
 *    // relatedCases array
 * }).catch(err => {
 *   // errors
 * });
 *
 * @param {string} caseID     The ID of the case pointing to pzInsKey value whose related cases must be obtained.
 * @param {string} context    The name of the context from where the API is being called.
 * @returns {Promise<any>}    A Promise that returns the list of related cases linked to the specified case.
 */

const getRelatedCases = (caseID, context) => {
  const queryPayload = { caseID };
  return invokeRestApi(GET_RELATED_CASES, { queryPayload }, context).then((response) => response.data.relatedCases);
};

export default {
  addRelatedCases,
  removeRelatedCase,
  getRelatedCases
};
