/**
 * Provides the basic APIs related to target map
 */
class TargetMap {
  /**
   * Returns the view container name associated with action
   * @param {*} action name of the action
   */

  static actionsTargetMap = new Map();

  static getTargetViewContainerName = (action) => {
    if (action) {
      const normalizedAction = action.toLowerCase();
      let vcName = 'primary';
      if (this.actionsTargetMap.has(normalizedAction)) {
        vcName = this.actionsTargetMap.get(normalizedAction);
      }
      return vcName;
    }
    return '';
  };

  /**
   * builds the action target map by using the action target model
   * @param {*} actionTargetModel is action target model
   */
  static buildActionTargetMap = (actionTargetModel) => {
    if (actionTargetModel) {
      Object.keys(actionTargetModel).forEach((vcName) => {
        const actionsObj = actionTargetModel[vcName];
        if (actionsObj && actionsObj.actions) {
          actionsObj.actions.forEach((action) => {
            this.actionsTargetMap.set(action.toLowerCase(), vcName);
          });
        }
      });
    }
  };
}

export default TargetMap;
