import { ofType } from 'redux-observable';
import { of } from 'rxjs';
import { map, mergeMap } from 'rxjs/operators';

import * as StateManager from '../../store/state/state-manager';
import { processDataToDispatchErrorAction } from '../../store/state/epics/util';
import { REMOVE_CONTAINER_ITEM, REPLACE_CONTAINER_ITEM } from '../../store/state/actions';
import { isErrorActionType, promiseHandler } from '../common-helper';

import { executeReleaseLockRequest } from './async-request';

export const releaseLock = (action$) => {
  let actionMgrID = 0;
  let actionsOut = [];

  return action$.pipe(
    ofType(REMOVE_CONTAINER_ITEM, REPLACE_CONTAINER_ITEM),
    map((action) => {
      ({ actionMgrID } = action.payload);
      return action.payload;
    }),
    mergeMap((actionInPayload) => {
      const { containerItemID, caseID, context, caseDeleted, skipReleaseLockRequest } = actionInPayload;

      // For deleted cases don't trigger release lock. Request will fail as resource is not available.
      if (containerItemID === context && caseID && !caseDeleted && !skipReleaseLockRequest) {
        return executeReleaseLockRequest(actionInPayload);
      }
      return of([]);
    }),
    mergeMap((chainedData) => {
      if (chainedData && isErrorActionType(chainedData.type)) {
        actionsOut.push(processDataToDispatchErrorAction(chainedData));
      }
      return actionsOut;
    }),
    map(() => {
      StateManager.dispatch(actionsOut);
      promiseHandler(actionsOut, actionMgrID);
      actionsOut = [];
      return actionsOut;
    })
  );
};

export default releaseLock;
