import { clearInstructions, clearPageInstructionReferences } from '../../../page-instructions';
import { ADD_CONTAINER_ITEM, UPDATE_CONTAINER_ITEM, REPLACE_CONTAINER_ITEM, REMOVE_CONTAINER_ITEM } from '../actions';

/**
 * Redux middleware to track the redux changes
 * and manipulate the data reference data
 * @param {*} store the store that will be affected
 */

const DataReferenceMiddleware = (store) => (next) => (action) => {
  const { payload: { context } = {}, type } = action;

  if (
    context &&
    (type === ADD_CONTAINER_ITEM ||
      type === UPDATE_CONTAINER_ITEM ||
      type === REPLACE_CONTAINER_ITEM ||
      type === REMOVE_CONTAINER_ITEM)
  ) {
    clearInstructions(context);
    clearPageInstructionReferences(context);
  }

  next(action);
};

export default DataReferenceMiddleware;
