import * as StateManager from '../../../store/state/state-manager';
import { listAction } from '../../../store/state/actions/action-creators';
import Utils from '../../../utils/utils';

class Insert {
  static execute(contextName, target, index, payload, pageReference, skipStateUpdate) {
    const displayPropertPayload = {};
    // nonFormProperties holds displayProperty if it is sent thorugh payload
    // Ex- Multiselect sends display property so that it can be maintained in redux along with unique key
    if (Object.prototype.hasOwnProperty.call(payload, 'nonFormProperties')) {
      payload.nonFormProperties.forEach((displayProperty) => {
        displayPropertPayload[displayProperty] = payload[displayProperty];
        delete payload[displayProperty];
      });
      delete payload.nonFormProperties;
    }
    const actionPayload = {
      instruction: 'INSERT',
      content: {
        ...Utils.unflatten(payload)
      }
    };
    const formSubmitContent = JSON.parse(JSON.stringify(actionPayload)); // cloning the JSON
    delete formSubmitContent.content.classID;
    const instruction = {
      target: Utils.buildInstructionPageReference(pageReference, target),
      content: { ...formSubmitContent.content },
      listIndex: index,
      instruction: 'INSERT'
    };
    if (!skipStateUpdate) {
      actionPayload.content = { ...actionPayload.content, ...displayPropertPayload };
      StateManager.dispatch(
        listAction({
          ...actionPayload,
          listIndex: index - 1,
          context: contextName,
          referenceList: `${pageReference}${target}`
        })
      );
    }

    return instruction;
  }
}
export default Insert;
