import { invokeRestApi, getCancelTokenSource, isRequestCanceled } from '../rest-client';
import { updateViewResources } from '../utils/view-resources';

const POST_FEED_MESSAGES = 'postFeedMessages';
const DELETE_FEED_MESSAGE = 'deleteFeedMessage';
const GET_FEED_MESSAGES = 'getFeedMessages';
const LIKE_FEED_MESSAGES = 'likeFeedMessages';
const UNLIKE_FEED_MESSAGES = 'unlikeFeedMessages';
const GET_MENTION_SUGGESTIONS = 'getMentionSuggestions';
const GET_TAG_SUGGESTIONS = 'getTagSuggestions';
const GET_FEED_MESSAGE_LIKES = 'getFeedMessageLikes';
const EDIT_FEED_MESSAGE = 'editFeedMessage';
const GET_MENTION_TYPES = 'getMentionTypes';

const headers = { 'Content-Type': 'application/json' };

/**
 * @description Use the APIs in the FeedUtils class to handle the feeds of a case.
 */
class FeedUtils {
  constructor() {
    this.mentionsTagsCancelTokenSource = [];
    this.mentions = [];
  }

  /**
   * Obtains the feeds for a given context.
   * @function
   * @static
   * @example <caption>In this example, the API obtains the feed of a case whose ID is pyDashboardFeed.</caption>
   * PCore.getFeedUtils().getFeeds('DATA-PORTAL', 'pyDashboardFeed','class','[{id: 'All', label: 'All', on: false, disabled: false}]',[],'app/primary_1',true)
   * .then(feedResponse => {
   *    // feedResponse array
   * }).catch(err => {
   *   // errors
   * });
   *
   * @param     {string}    pulseContext                     The name of the application context or case context for which the feed must be fetched.
   * @param     {string}    feedID                           The ID of the feed that must be fetched.
   * @param     {string}    feedClass                        The class associated with the feed to be fetched.
   * @param     {object[]}  feedFilters                      The list of conditions through which the feed to be fetched is filtered.
   * @param     {object[]}  fetchFeedsCancelTokenSource      The list of API requests for fetching the feed.
   * @param     {object}    c11nEnv                          The name of the context where the API is being called.
   * @param     {boolean}   isLoadMore                       The flag that determines if the next set of feeds should be loaded.
   * @returns   {Promise<object>}                            A Promise that resolves to an object.
   */
  getFeeds = (pulseContext, feedID, feedClass, feedFilters, fetchFeedsCancelTokenSource, c11nEnv, isLoadMore) => {
    if (!isLoadMore) this.mentions = [];
    let filterBy = '';
    if (feedFilters && Array.isArray(feedFilters) && feedFilters?.length > 0) {
      feedFilters.forEach((feedFilter) => {
        /* istanbul ignore else */
        if (feedFilter.on) filterBy = `${filterBy}${feedFilter.id},`;
      });
      /* istanbul ignore if */
      if (filterBy === '') filterBy = 'ClearFilters';
    }
    let olderThan = '';
    if (isLoadMore) {
      olderThan = c11nEnv.getValue(`pulse.nextPageFirstEntry`);
    }

    const queryPayload = {
      filterForContext: pulseContext,
      feedID,
      feedClass,
      filterBy,
      pageSize: 9,
      olderThan
    };

    for (let i = 0; i < fetchFeedsCancelTokenSource.length; i += 1) {
      fetchFeedsCancelTokenSource[i].cancel();
    }
    const newCancelTokenSource = getCancelTokenSource();
    fetchFeedsCancelTokenSource.push(newCancelTokenSource);
    return invokeRestApi(
      GET_FEED_MESSAGES,
      { queryPayload, cancelTokenSource: newCancelTokenSource },
      c11nEnv.getContextName()
    )
      .then(async (response) => {
        if (response.status === 200 && response.data) {
          fetchFeedsCancelTokenSource.pop();
          const respMessageIDs = [];
          const respMessages = {};
          let isCustomViewConfigured = false;

          /* istanbul ignore else */
          if (response?.data) {
            /* istanbul ignore else */
            if (response?.data?.data?.FeedList) {
              const feedItemList = response.data.data.FeedList?.pxResults;
              /* istanbul ignore else */
              if (Array.isArray(feedItemList) && feedItemList?.length > 0) {
                feedItemList.forEach((message, index) => {
                  message.ID = message.pzInsKey !== '' ? message.pzInsKey : `message${index}`;
                  respMessageIDs.push(message.ID);
                  respMessages[message.ID] = message;
                  /* istanbul ignore else */
                  if (message?.mentions && Array.isArray(message?.mentions) && message.mentions?.length > 0) {
                    this.mentions.push(message.mentions);
                  }
                  /* istanbul ignore else */
                  if (message?.pxResults && Array.isArray(message?.pxResults) && message.pxResults?.length > 0) {
                    message.pxResults.forEach((item) => {
                      /* istanbul ignore else */
                      if (item?.mentions && Array.isArray(item?.mentions) && item.mentions?.length > 0) {
                        this.mentions.push(item.mentions);
                      }
                    });
                  }
                  /* istanbul ignore else */
                  if (
                    !isCustomViewConfigured &&
                    message.pyMessageViewReference !== 'pzPostDetails' &&
                    message.pyMessageViewReference !== 'pzPrivatePostDetails'
                  )
                    isCustomViewConfigured = true;
                });
              }
            }
            /* istanbul ignore else */
            if (isCustomViewConfigured) {
              await updateViewResources({ uiResources: response.data.uiResources });
            }

            if (!feedFilters) {
              feedFilters = [{ id: 'All', label: 'All', on: true, disabled: false }];
              /* istanbul ignore else */
              if (response?.data?.data?.FeedFilters) {
                const feedFiltersList = response.data.data.FeedFilters?.pxResults;
                let allFilter = true;
                /* istanbul ignore else */
                if (feedFiltersList && Array.isArray(feedFiltersList) && feedFiltersList?.length > 0) {
                  feedFiltersList.forEach((feedFilter) => {
                    const feedFilterItem = {
                      id: feedFilter.pyFeedSourceReference,
                      label: feedFilter.pyLabel,
                      disabled: false,
                      on: feedFilter.pySelected === 'true'
                    };
                    /* istanbul ignore else */
                    if (!feedFilterItem.on) allFilter = false;
                    feedFilters.push(feedFilterItem);
                  });
                  feedFilters.find((feedItem) => feedItem.id === 'All').on = allFilter;
                }
              }
            }
          }

          if (isLoadMore && c11nEnv.getValue(`pulse.messageIDs`)) {
            let messageIDs = c11nEnv.getValue(`pulse.messageIDs`);
            let messages = c11nEnv.getValue(`pulse.messages`);
            messageIDs = [...messageIDs, ...respMessageIDs];
            messages = { ...messages, ...respMessages };
            Object.values(messages).forEach((item) => {
              /* istanbul ignore else */
              if (item?.mentions && Array.isArray(item?.mentions) && item.mentions?.length > 0) {
                this.mentions.push(item.mentions);
              }
              if (item?.pxResults && Array.isArray(item?.pxResults) && item.pxResults?.length > 0) {
                item.pxResults.forEach((ele) => {
                  /* istanbul ignore else */
                  if (ele?.mentions && Array.isArray(ele?.mentions) && ele.mentions?.length > 0) {
                    this.mentions.push(ele.mentions);
                  }
                });
              }
            });
            c11nEnv.updateState({
              pulse: {
                messages,
                messageIDs,
                nextPageFirstEntry: response.data.nextPageFirstEntry ? response.data.nextPageFirstEntry : ''
              }
            });
          } else {
            c11nEnv.updateState({
              pulse: {
                messages: respMessages,
                messageIDs: respMessageIDs,
                nextPageFirstEntry: response.data.nextPageFirstEntry ? response.data.nextPageFirstEntry : ''
              }
            });
          }
          // Removing duplicate mention objects from mentions array.
          const caseMentions =
            [...new Map(this.mentions.flat()?.map((item) => [item.mentionsID, item])).values()]
              ?.filter((item) => Object.prototype.hasOwnProperty.call(item, 'mentionsType'))
              ?.filter((item) => !item?.mentionsType?.toLowerCase().includes('user')) || [];
          return { feedFilters, caseMentions };
        }
        c11nEnv.reportError(`getFeeds call failed ${response.status}`, response.data);
        return '';
      })
      .catch((error) => {
        /* istanbul ignore else */
        if (!isRequestCanceled(error)) {
          c11nEnv.reportError(': Error ocurred during ajax call at getFeeds API : ', error.response);
        }
      });
  };

  /**
   * Posts a message to the given context.
   * @function
   * @static
   * @example <caption>In this example, the API adds the post along with attachment to the given context.</caption>
   * PCore.getFeedUtils().postMessage('DATA-PORTAL','test message', [{"type":"File","category":"File","fileName":"attachment.png","ID":"459c"}], false, 'app/primary_1')
   * .then(() => {
   *   // success
   * }).catch(err => {
   *    // Error handling
   * });
   * @example <caption>In this example, the API adds the reply to the post along with attachment to the provided context.</caption>
   * PCore.getFeedUtils().postMessage('PEGASOCIAL M-214007','test reply message', [{"type":"File","category":"File","fileName":"attachment.png","ID":"459c"}], true, 'app/primary_1')
   * .then(() => {
   *   // success
   * }).catch(err => {
   *    // Error handling
   * });
   * @param   {string}      pulseContext    The name of the application context or case context for which the message must be posted.
   * @param   {string}      message         The message that needs to be posted.
   * @param   {Array}       attachmentIDs   The metadata of the attachments that need to be posted along with the message.
   * @param   {boolean}     isReply         The flag that determines if you want to post a message or a reply to a message.The default value is false. Set isReply to true if you want to post a reply to a message. Set isReply to false if you want to post a message.
   * @param   {object}      c11nEnv         The name of the context where the API is being called.
   * @returns {Promise<object>}             A Promise that resolves to an object.
   */
  postMessage = (pulseContext, message, attachmentIDs = [], isReply = false, c11nEnv) => {
    const reqBody = JSON.stringify({
      context: pulseContext,
      parentMessageId: isReply ? pulseContext : '',
      message,
      attachments: attachmentIDs
    });
    return invokeRestApi(POST_FEED_MESSAGES, { body: reqBody, headers }, c11nEnv.getContextName())
      .then((response) => {
        if ((response.status === 201 || response.status === 200) && response.data) {
          const messageData = response?.data;
          if (isReply) {
            messageData.parentMessageID = pulseContext;
            const { pxResults, caseMentions } = this.buildMessageObject(
              messageData,
              isReply,
              c11nEnv,
              attachmentIDs.length > 0
            );
            c11nEnv.updateState({ pulse: { messages: { [pulseContext]: { pxResults } } } });
            return caseMentions;
          }
          const { messages, messageIDs, caseMentions } = this.buildMessageObject(
            messageData,
            isReply,
            c11nEnv,
            attachmentIDs.length > 0
          );
          c11nEnv.updateState({ pulse: { messages, messageIDs } });
          return caseMentions;
        }
        c11nEnv.reportError(`postMessage call failed with status ${response.status}`, response);
        return '';
      })
      .catch((error) => {
        c11nEnv.reportError(': Error ocurred during ajax call at postMessage API : ', error.response);
      });
  };

  /**
   * Modifies a message associated with the given context.
   * @function
   * @static
   * @example <caption>In this example, the API edits the post whose messageID is W-103</caption>
   * PCore.getFeedUtils().editMessage({'W-103','W-103','test message','app/primary_1', false})
   * .then(() => {
   *   // success
   * }).catch(err => {
   *    // Error handling
   * });
   * @example <caption>In this example, the API edits the reply to the post whose messageID is W-104 and the parentMessageID is W-103</caption>
   * PCore.getFeedUtils().editMessage({'W-104','W-103','test reply message','app/primary_1', true})
   * .then(() => {
   *   // success
   * }).catch(err => {
   *    // Error handling
   * });
   * @param {object}    param                 The object that contains the required data to edit a message.
   * @param {string}    param.messageID       The unique identifier of the message that needs to be edited.
   * @param {string}    param.parentMessageID The unique identifier of the parent message that needs to be edited.
   * @param {string}    param.message         The new message that replaces the existing message.
   * @param {object}    param.c11nEnv         The name of the context where the API is being called.
   * @param {boolean}   param.isReply         The flag that determines if you want to post a message or a reply to a message. The default value is false. Set isReply to true if you want to edit a reply for a message. Set isReply to false if you want to edit a message.
   * @returns {Promise<object>}               A Promise that resolves to an object.
   */
  editMessage = ({ messageID, parentMessageID, message, c11nEnv, isReply = false }) => {
    const body = JSON.stringify({ message });
    const queryPayload = { messageID };
    return invokeRestApi(EDIT_FEED_MESSAGE, { queryPayload, body, headers }, c11nEnv.getContextName())
      .then((response) => {
        if (response?.status === 200 && response?.data) {
          const messageData = response?.data;
          if (isReply) {
            messageData.parentMessageID = parentMessageID;
            const { pxResults, caseMentions } = this.buildMessageObject(messageData, isReply, c11nEnv, false);
            c11nEnv.updateState({ pulse: { messages: { [parentMessageID]: { pxResults } } } });
            return caseMentions;
          }
          const { messages, caseMentions } = this.buildMessageObject(messageData, isReply, c11nEnv, false);
          c11nEnv.updateState({ pulse: { messages } });
          return caseMentions;
        }
        c11nEnv.reportError(`editMessage call failed with status ${response.status}`, response);
        return '';
      })
      .catch((error) => {
        c11nEnv.reportError(': Error ocurred during ajax call at editMessage API : ', error.response);
      });
  };

  /**
   * @private
   * @param {object}  messageData   The object that contains the newly added/edited message details.
   * @param {boolean} isReply       The flag that determines if you want to post/edit a message or a reply to a message.
   * @param {object}  c11nEnv       The name of the context where the API is being called.
   * @param {boolean} hasAttachment The flag that determines if you have an attachment for a message.
   * @returns {object} MessageObject The messageObject
   */
  buildMessageObject = (messageData, isReply, c11nEnv, hasAttachment = false) => {
    const messageObject = {
      ID: messageData?.ID || '',
      tags: messageData?.tags || [],
      pyHasAttachment: hasAttachment,
      pzInsKey: messageData?.ID || '',
      pyMessage: messageData?.message || '',
      mentions: messageData?.mentions || [],
      postedByUser: messageData?.postedByUser,
      pxCreateDateTime: messageData?.postedTime || '',
      pxUpdateDateTime: messageData?.updatedTime || '',
      parentMessageID: messageData?.parentMessageID || '',
      pyIsEdited:
        typeof messageData?.isEdited === 'string' ? messageData?.isEdited === 'true' : messageData?.isEdited || false,
      pyFeed: {
        pyPostedOn: messageData?.postedTime || '',
        pyFeedTitle: messageData?.postedByUser?.name || '',
        pyIconType: 'user',
        pyCommentContext: 'pzInsKey',
        pyIconReference: 'pi pi-case'
      },
      pyContext: '',
      pxIcon: 'globe',
      pyMessageViewReference: 'pzPostDetails'
    };
    const messages = c11nEnv.getValue(`pulse.messages`);
    if (isReply) {
      let { pxResults = [] } =
        Object.values(messages)?.find((item) => item?.ID === messageObject?.parentMessageID) || {};
      const index = pxResults?.findIndex((item) => item?.pzInsKey === messageObject?.ID);
      if (index !== -1) {
        const pxResultsCopy = [...pxResults];
        pxResultsCopy[index] = { ...pxResultsCopy[index], ...messageObject };
        pxResults = pxResultsCopy;
      } else {
        pxResults = [...pxResults, messageObject];
      }
      return {
        pxResults,
        messages: {},
        messageIDs: [],
        caseMentions: this.getCaseMentions(pxResults)
      };
    }
    const messageIDs = c11nEnv.getValue(`pulse.messageIDs`);
    if (messageIDs) {
      const updatedMessage = messageIDs.includes(messageObject.ID)
        ? { ...messages[messageObject.ID], ...messageObject }
        : messageObject;
      const updatedMessages = { ...messages, [messageObject.ID]: updatedMessage };
      const updatedMessageIDs = messageIDs.includes(messageObject.ID) ? messageIDs : [messageObject.ID, ...messageIDs];
      return {
        messages: updatedMessages,
        messageIDs: updatedMessageIDs,
        caseMentions: this.getCaseMentions(Object.values(updatedMessages))
      };
    }
  };

  /**
   * @private
   * @param {Array} array This param carries the list of mention objects
   * @returns   The list of mention objects excluding the user mentions and duplicates
   */
  getCaseMentions = (array) => {
    const mentionsData = array
      ?.filter((item) => item?.mentions && Array.isArray(item?.mentions) && item.mentions?.length > 0)
      ?.map((item) => item.mentions)
      ?.concat(this.mentions);
    return (
      [...new Map(mentionsData.flat()?.map((item) => [item.mentionsID, item])).values()]
        ?.filter((item) => Object.prototype.hasOwnProperty.call(item, 'mentionsType'))
        ?.filter((item) => !item?.mentionsType?.toLowerCase().includes('user')) || []
    );
  };

  /**
   * Likes or unlikes a message.
   * @function
   * @static
   * @example <caption>In this example, the API likes the message whose ID is W-103.</caption>
   * PCore.getFeedUtils().likeMessage({pulseContext:'DATA-PORTAL', likedBy: false, messageID:'W-103', isReply:false, c11nEnv:'app/primary_1'})
   * .then(() => {
   *    // success
   * }).catch(err => {
   *   // errors
   * });
   * @example <caption>In this example, the API dislikes the message whose ID is W-103. </caption>
   * PCore.getFeedUtils().likeMessage({pulseContext:'DATA-PORTAL', likedBy: true, messageID:'W-103', isReply:false, c11nEnv:'app/primary_1'})
   * .then(() => {
   *    // success
   * }).catch(err => {
   *   // errors
   * });
   * @example <caption>In this example, the API likes the reply whose ID is R-103.</caption>
   * PCore.getFeedUtils().likeMessage({pulseContext:'DATA-PORTAL', likedBy: false, messageID:'R-103', isReply:true, c11nEnv:'app/primary_1'})
   * .then(() => {
   *    // success
   * }).catch(err => {
   *   // errors
   * });
   * @example <caption>In this example, the API dislikes the reply whose ID is R-103.</caption>
   * PCore.getFeedUtils().likeMessage({pulseContext:'DATA-PORTAL', likedBy: true, messageID:'R-103', isReply:true, c11nEnv:'app/primary_1'})
   * .then(() => {
   *    // success
   * }).catch(err => {
   *   // errors
   * });
   * @param {object}    param               The object that contains all the required data to like or unlike a specific message.
   * @param {string}    param.pulseContext  The name of the application context or case context for which the feed must be fetched.
   * @param {boolean}   param.likedBy       The value that determines whether to like or unlike the message.
   * @param {string}    param.messageID     The ID of the message that needs to be liked or unliked.
   * @param {boolean}   param.isReply       The flag that determines whether the number of likes must be obtained for a message or a reply to a message.
   * @param {object}    param.c11nEnv       The name of the context where the API is being called.
   */
  likeMessage = ({ pulseContext, likedBy: unLiked, messageID, isReply, c11nEnv }) => {
    const routeKey = unLiked ? UNLIKE_FEED_MESSAGES : LIKE_FEED_MESSAGES;
    const body = JSON.stringify({
      ContextClass: pulseContext
    });
    const queryPayload = {
      pulseContext
    };
    invokeRestApi(routeKey, { body, queryPayload, headers }, c11nEnv.getContextName())
      .then((response) => {
        if (response.status === 200) {
          if (isReply) {
            const msg = c11nEnv.getValue(`pulse.messages.${messageID}`);
            const pxResults =
              msg?.pxResults &&
              Array.isArray(msg?.pxResults) &&
              msg.pxResults?.length > 0 &&
              msg.pxResults?.map((reply) => {
                reply = { ...reply };
                /* istanbul ignore else */
                if (reply.pzInsKey === pulseContext) {
                  let updatedLikeCount = 0;
                  let updatedLikedFlag = false;
                  const likeCount = reply.pyLikes ? reply?.pyLikes?.pxLikeCount : 0;
                  if (unLiked) {
                    updatedLikeCount = likeCount - 1;
                    updatedLikedFlag = false;
                  } else {
                    updatedLikeCount = likeCount + 1;
                    updatedLikedFlag = true;
                  }
                  reply.pyLikes = {
                    pxLikeCount: updatedLikeCount,
                    pxIsLiked: updatedLikedFlag
                  };
                }
                return reply;
              });

            c11nEnv.updateState({
              pulse: {
                messages: {
                  [messageID]: {
                    pxResults
                  }
                }
              }
            });
          } else {
            const msg = { ...c11nEnv.getValue(`pulse.messages.${messageID}`) };
            const likeCount = msg.pyLikes ? msg.pyLikes.pxLikeCount : 0;
            let updatedLikeCount = 0;
            let updatedLikedFlag = false;

            if (unLiked) {
              updatedLikeCount = likeCount - 1;
              updatedLikedFlag = false;
            } else {
              updatedLikeCount = likeCount + 1;
              updatedLikedFlag = true;
            }

            msg.pyLikes = {
              pxLikeCount: updatedLikeCount,
              pxIsLiked: updatedLikedFlag
            };

            c11nEnv.updateState({
              pulse: {
                messages: {
                  [pulseContext]: msg
                }
              }
            });
          }
        } else {
          c11nEnv.reportError(`likeMessage call failed with status ${response.status}`, response);
        }
      })
      .catch((error) => {
        c11nEnv.reportError(': Error ocurred during ajax call at likeMessage API : ', error.response.data);
      });
  };

  /**
   * Deletes a message from a given context.
   * @function
   * @static
   * @example <caption>In this example, the API removes the reply whose ID is R-101 from a message whose ID is W-102.</caption>
   * PCore.getFeedUtils().deleteMessage('W-102', true, 'R-101', 'app/primary_1')
   * .then(() => {
   *    // success
   * }).catch(err => {
   *   // errors
   * });
   * @example <caption>In this example, the API removes the message whose ID is W-103 </caption>
   * PCore.getFeedUtils().deleteMessage('W-103', false, '', 'app/primary_1')
   * .then(() => {
   *    // success
   * }).catch(err => {
   *   // errors
   * });
   * @param {string}  messageID  The ID of the message that needs to be deleted.
   * @param {boolean} isReply    The flag that determines if you want to delete a message or a reply to a message.
   * @param {string}  replyID    The ID of the reply that needs to be deleted.
   * @param {object}  c11nEnv    The name of the context where the API is being called.
   */
  deleteMessage = (messageID, isReply, replyID, c11nEnv) => {
    let messageKey = messageID;
    if (isReply) {
      messageKey = replyID;
    }
    const queryPayload = {
      messageID: messageKey
    };

    invokeRestApi(DELETE_FEED_MESSAGE, { queryPayload }, c11nEnv.getContextName())
      .then((response) => {
        if (response.status === 200) {
          if (isReply) {
            const msg = c11nEnv.getValue(`pulse.messages.${messageID}`);
            const pxResults =
              msg?.pxResults &&
              Array.isArray(msg?.pxResults) &&
              msg.pxResults?.length > 0 &&
              msg.pxResults?.filter((reply) => reply.pzInsKey !== replyID);

            c11nEnv.updateState({
              pulse: {
                messages: {
                  [messageID]: {
                    pxResults
                  }
                }
              }
            });
          } else {
            const msgIDs = c11nEnv.getValue(`pulse.messageIDs`);
            const newMsgIDs =
              msgIDs && Array.isArray(msgIDs) && msgIDs?.length > 0 && msgIDs?.filter((msgID) => msgID !== messageID);

            const msgs = { ...c11nEnv.getValue(`pulse.messages`) };
            delete msgs[messageID];

            c11nEnv.updateState({
              pulse: {
                messageIDs: newMsgIDs,
                messages: msgs
              }
            });
          }
        } else {
          c11nEnv.reportError(`deleteMessage call failed with status ${response.status}`, response);
        }
      })
      .catch((error) => {
        c11nEnv.reportError(': Error ocurred during ajax call at deleteMessage API : ', error.response);
      });
  };

  /**
   * Obtains the list of users who liked the message.
   * @function
   * @static
   * @example <caption>In this example, the API obtains the list of users who liked the message with messageID:W-102</caption>
   * PCore.getFeedUtils().getLikedUsers(W-102, 'app/primary_1')
   * .then(response => {
   *    // response array
   * }).catch(err => {
   *   // errors
   * });
   *
   * @param     {string}      messageID         The ID of the message for which that liked users must be fetched.
   * @param     {object}      c11nEnv           The name of the context where the API is being called.
   * @returns   {Promise<object>}               A Promise that resolves to an object.
   */
  getLikedUsers = (messageID, c11nEnv) => {
    const queryPayload = {
      messageID
    };
    return invokeRestApi(GET_FEED_MESSAGE_LIKES, { queryPayload }, c11nEnv.getContextName())
      .then((response) => response?.data?.users)
      .catch((error) => {
        c11nEnv.reportError(': Error ocurred during ajax call at getLikedUsers API : ', error.response);
      });
  };

  /**
   * Obtains the list of options for the selected object that can be mentioned in a Pulse post.
   * @function
   * @static
   * @example <caption>In this example, the API obtains the list of users that can be mentioned in a Pulse post.</caption>
   * PCore.getFeedUtils().getMentionSuggestions({searchFor:"case",mentionsType = 'Cases',listSize:5}, 'app/primary_1')
   * .then(mentionsResponse => {
   *    // mentionsResponse array
   * }).catch(err => {
   *   // errors
   * });
   * @param     {object}          mentionProps  The object that contains the search parameters for obtaining the list of options for the selected type of object.
   * @param     {object}          c11nEnv       The name of the context where the API is being called.
   * @returns   {Promise<object>}               A Promise that resolves to an object.
   */
  getMentionSuggestions = (mentionProps, c11nEnv) => {
    const { pulseContext, searchFor, mentionsType, listSize = 5 } = mentionProps;
    const queryPayload = {
      pulseContext,
      searchFor,
      mentionsType,
      listSize
    };

    for (let i = 0; i < this.mentionsTagsCancelTokenSource.length; i += 1) {
      this.mentionsTagsCancelTokenSource[i].cancel();
    }
    const newCancelTokenSource = getCancelTokenSource();
    this.mentionsTagsCancelTokenSource.push(newCancelTokenSource);
    return invokeRestApi(
      GET_MENTION_SUGGESTIONS,
      {
        queryPayload,
        cancelTokenSource: newCancelTokenSource
      },
      c11nEnv.getContextName()
    )
      .then((response) => {
        this.mentionsTagsCancelTokenSource = [];
        if (response.status === 200 && response.data) return response?.data;
        c11nEnv.reportError(`Get mention suggestions call failed ${response.status}`, response.data);
        return [];
      })
      .catch((err) => {
        /* istanbul ignore else */
        if (!isRequestCanceled(err)) {
          c11nEnv.reportError(': Error ocurred during ajax call at getMentionSuggestions API : ', err.response.data);
        }
      });
  };

  /**
   * Obtains the list of available types of objects that can be mentioned in a Pulse post.
   * @function
   * @static
   * @example <caption>In this example, the API obtains the list of available types of objects that can be mentioned in a Pulse post.</caption>
   * PCore.getFeedUtils().getMentionTypes()
   * .then(response => {
   *    // response array
   * }).catch(err => {
   *   // errors
   * });
   * @param     {object}          c11nEnv      The name of the context where the API is being called.
   * @returns   {Promise<object>}              A Promise that resolves to an object.
   */
  getMentionTypes = (c11nEnv) => {
    return invokeRestApi(GET_MENTION_TYPES, {}, c11nEnv.getContextName())
      .then((response) => response?.data)
      .catch((err) => {
        c11nEnv.reportError(': Error ocurred during ajax call at getMentionTypes API : ', err.response.data);
      });
  };

  /**
   * Obtains the options suggested for selecting a tag for a given context.
   * @function
   * @static
   * @example <caption>In this example, the API obtains the tags for given context.</caption>
   * PCore.getFeedUtils().getTagSuggestions({searchFor:"test",listSize:5}, 'app/primary_1')
   * .then(tagsResponse => {
   *    // tagsResponse array
   * }).catch(err => {
   *   // errors
   * });
   * @param    {object}         tagProps      The object that contains the search parameters for obtaining the list of tags.
   * @param    {object}         c11nEnv       The name of the context where the API is being called.
   * @returns  {Promise<object>}              A Promise that resolves to an object.
   */
  getTagSuggestions = (tagProps, c11nEnv) => {
    const { searchFor, listSize = 5 } = tagProps;
    const queryPayload = {
      searchFor,
      listSize
    };

    for (let i = 0; i < this.mentionsTagsCancelTokenSource.length; i += 1) {
      this.mentionsTagsCancelTokenSource[i].cancel();
    }
    const newCancelTokenSource = getCancelTokenSource();
    this.mentionsTagsCancelTokenSource.push(newCancelTokenSource);

    return invokeRestApi(
      GET_TAG_SUGGESTIONS,
      {
        queryPayload,
        cancelTokenSource: newCancelTokenSource
      },
      c11nEnv.getContextName()
    )
      .then((response) => {
        this.mentionsTagsCancelTokenSource = [];
        let tagSuggestions = [];
        if (response.status === 200 && response?.data) {
          tagSuggestions =
            response?.data?.tags &&
            Array.isArray(response?.data?.tags) &&
            response.data.tags.length > 0 &&
            response.data.tags?.map((tag) => `#${tag}`);
        } else {
          c11nEnv.reportError(`Get tag suggestions call failed ${response.status}`, response.data);
        }
        return tagSuggestions;
      })
      .catch((err) => {
        /* istanbul ignore else */
        if (!isRequestCanceled(err)) {
          c11nEnv.reportError(': Error ocurred during ajax call at getTagSuggestions API : ', err.response.data);
        }
      });
  };
}

export default new FeedUtils();
