import { from } from 'rxjs';
import { map } from 'rxjs/operators';

import { fetch } from '../../service_broker/fetch';
import { resolveRestApi } from '../../router/app-router';
import { logAndCatchError } from '../../utils/rxjs-utils';
import {
  processHeaders,
  getSubmitData,
  getParentContextChangeSet,
  mergeObjectsAndConcatArrays
} from '../../store/state/epics/util';
import Utils from '../../utils/utils';

export const executeRequest = (actionInPayload) => {
  const { context } = actionInPayload;

  const changeSet = getSubmitData(context);
  let body = {
    ...changeSet.content,
    contextData: false
  };

  const parentContextChangeSet = getParentContextChangeSet(context);
  if (!Utils.isEmpty(parentContextChangeSet)) {
    body = mergeObjectsAndConcatArrays(parentContextChangeSet, body);
  }

  const { restApi, reqType } = resolveRestApi('refreshCaseView', actionInPayload);
  body.interestPage = actionInPayload.interestPage;
  return from(
    fetch(
      restApi,
      {
        method: reqType,
        body
      },
      context
    )
  ).pipe(
    map((response) => {
      return {
        actionInPayload,
        response: response.data,
        headers: processHeaders(response.headers)
      };
    }),
    logAndCatchError(`: Error ocurred during ajax call : `, context)
  );
};

export default {
  executeRequest
};
