import { ofType } from 'redux-observable';
import { catchError, mergeMap, takeUntil, filter } from 'rxjs/operators';
import { of, Observable, empty } from 'rxjs';
import isEqual from 'fast-deep-equal';

import * as actions from '../store/state/actions/action-creators';
// import { ActionManagerInstance } from "../actions/manager"

/**
 * this function is a wrapper around catchError
 * --> helps to log the error and returns error action object
 *
 * @param {string} msg message for the error.
 * @param {object} context context for the error
 * @returns {*} returns the error action object.
 */
export const logAndCatchError = (msg, context, actionMgrID) =>
  catchError((error) => {
    console.error(msg, error);
    /* if (context && context.actionMgrID) {
    ActionManagerInstance.rejectActionByID(context.actionMgrID)
  } */
    return of(actions.error(msg, error, context, null, actionMgrID));
  });

/**
 * this function is a wrapper around takeUntil
 * --> helps to cancel the original request if CANCEL_ACTION was triggered
 *
 * @param {*} action$ observable stream
 * @param {*} reqPayload request Payload
 * @param {*} comparator function to compare properties between cancel request and normal requqest payload
 * @returns {*} returns outer observable
 */
export const continueRequestUntil = (action$, reqPayload, comparator) =>
  takeUntil(
    action$.pipe(
      ofType('CANCEL_ACTION'),
      filter((cancelRequest) => {
        let cancel = false;
        if (comparator && comparator(cancelRequest, reqPayload)) {
          reqPayload.signal.cancel('cancel');
          cancel = true;
        }
        return cancel;
      })
    )
  );

/**
 * --> helps to throttle based on time and distinct payload
 *
 * @param {*} delay number of milliseconds for debounce
 * @param {*} comparator function to compare properties between prev request and next requqest payload
 * @returns {*} returns 1) empty observable within delay time. OR
 *                      2) actual value if delay time crosses.
 */
export const throttleUntilChanged = (delay, comparator) => {
  return (source) => {
    return new Observable((observer) => {
      let lastSeen = {};
      let lastSeenTime = 0;

      return source
        .pipe(
          mergeMap((value) => {
            const now = Date.now();
            let _comparator;
            if (comparator) {
              _comparator = comparator;
            } else {
              _comparator = isEqual;
            }

            if (_comparator && _comparator(value, lastSeen) && now - lastSeenTime < delay) {
              return empty();
            }
            lastSeen = value;
            lastSeenTime = now;
            return of(value);
          })
        )
        .subscribe(observer);
    });
  };
};
