import { from } from 'rxjs';
import { map } from 'rxjs/operators';

import { SCOPE_TYPES } from '../../store/state/epics/util';
import { logAndCatchError } from '../../utils/rxjs-utils';
import CaseAPIs from '../../rest-client/case-apis';
import EnvironmentInfo from '../../environment-info';

const DXAPI_ERROR = 'Error ocurred during ajax call at createWork Epic : ';

export const executeCreateWorkRequest = (actionInPayload, options) => {
  const { class: caseClassName, context, viewType = SCOPE_TYPES.PAGE } = actionInPayload;

  const { removeActionPayload } = options;

  let pageName = actionInPayload?.pageName ? actionInPayload?.pageName : EnvironmentInfo.getDefaultCasePage();
  if (actionInPayload.isTraditionalUICase) {
    pageName = 'pyDetails';
  }
  return from(
    CaseAPIs.createCase(caseClassName, {
      ...actionInPayload,
      pageName,
      viewType,
      context
    })
  ).pipe(
    map(({ response, headers }) => {
      return {
        response,
        actionInPayload,
        headers,
        removeActionPayload
      };
    }),
    logAndCatchError(DXAPI_ERROR, context)
  );
};

export default { executeCreateWorkRequest };
