import { ofType } from 'redux-observable';
import { mergeMap, map } from 'rxjs/operators';
import { from } from 'rxjs';

import { logAndCatchError } from '../utils/rxjs-utils';
import { resolveRestApi } from '../router/app-router';
import { publicConstants } from '../constants';
import * as StateManager from '../store/state/state-manager';
import TargetMap from '../actions/targetmap';
import Utils from '../utils/utils';
import { getTranslatedDXAPIResponse } from '../utils/dxapi-response-translator';
import { getResolvedTargetName } from '../router/routing-manager';
import { ActionManagerInstance } from '../actions/manager';
import * as actionType from '../store/state/actions/index';
import * as actions from '../store/state/actions/action-creators';
import StateMachineManager from '../page-instructions/state-machine-manager';
import {
  processViewResponse,
  getActionPayload,
  processHeaders,
  processDataToDispatchErrorAction,
  getSubmitData,
  getParentContextChangeSet,
  mergeObjectsAndConcatArrays
} from '../store/state/epics/util';
import { fetch, getCancelTokenSource, getActiveRequestCountPerContext } from '../service_broker/fetch';
import * as RuleStore from '../rules/rules-store';

import { appendQueryParamsForRefreshAPI } from './refreshHelpers';

import refreshConstants from './index';

const reqConfigList = {};

const prepareRequestContext = (payload) => {
  const { ID, actionID, context } = payload;
  return `${ID}.${context}.${actionID}`;
};

/**
 * This helper function refresh the case view, case data or assignments
 * Track the pending requests fired for a view and cancel them if any subsequent
 * requests fired again for the same view.
 * @param {object} reqConfig contains info about the http request
 * @param {object} payload contains view information
 * @param {boolean} cancelPendingRequests indicates to bind requests to cancel
 */
const refreshCaseViewDataOrAssignment = (reqConfig, payload, cancelPendingRequests) => {
  const { url, context } = reqConfig;
  if (reqConfig.reqHeaders && reqConfig.reqHeaders.etag) {
    reqConfig.headers = { 'if-match': reqConfig.reqHeaders.etag };
  }
  if (!cancelPendingRequests) {
    return fetch(`${url}`, reqConfig, context);
  }
  const reqContext = prepareRequestContext(payload);
  if (getActiveRequestCountPerContext(reqContext) > 0 && reqConfigList[reqContext]) {
    reqConfigList[reqContext].forEach((request) => request.cancelConfig.cancel());
  }

  const cancelToken = getCancelTokenSource();
  reqConfig.signal = cancelToken.token;
  reqConfig.cancelConfig = cancelToken;
  if (reqConfigList[reqContext]) {
    reqConfigList[reqContext].push(reqConfig);
  } else {
    reqConfigList[reqContext] = [reqConfig];
  }
  return fetch(reqConfig.url, reqConfig, context, { cancelContext: reqContext })
    .then((response) => {
      delete reqConfigList[reqContext];
      return response;
    })
    .catch((error) => {
      if (error && error.code !== 'ERR_CANCELED') {
        delete reqConfigList[reqContext];
      }
      throw error;
    });
};

/**
 * [refreshData]
 * Description : Epic to do side effect - refreshing a view.
 * ACTION_IN -  REFRESH_DATA
 * ACTION_OUT - ERROR or MERGE_DATA
 * @param {object} action$ (Observable<Action>)
 * @param {object} state$ (Returns the object with headers)
 */
const refreshData = (action$, state$) => {
  return action$.pipe(
    ofType(refreshConstants.REFRESH_DATA, actionType.FILL_FORM_WITH_AI), // Check the action is "REFRESH_DATA" or "FILL_FORM_WITH_AI"
    map((action) => {
      return action.payload;
    }), // Pass on the payload
    mergeMap((payload) => {
      const { context, caseWideAction, contextData, caseViewAction, interestPage, options } = payload;
      let restAPI = {};
      const { propertyName = '', classID = '' } = options;
      const changeSet = getSubmitData(context);
      const changedProperty = Utils.getPropertyName(propertyName);
      const propertyNameArray = changedProperty ? changedProperty.split('.') : [];
      const leafPropertyName = propertyNameArray.pop();
      const parentProperty = propertyNameArray.join('.');
      const serverPrecedenceFields = [];
      if (parentProperty) {
        const referencedMetaData = RuleStore.getPropertyMetadata(parentProperty, classID);
        if (referencedMetaData && referencedMetaData.dataRetrievalType === 'copy') {
          const propertyObject = Utils.getIn(changeSet, `content.content.${parentProperty}`) || {};
          if (Object.keys(propertyObject).length > 0) {
            const removedProps = Object.keys(propertyObject).filter((val) => val !== leafPropertyName);
            removedProps.forEach((val) => {
              delete propertyObject[val];
              serverPrecedenceFields.push(`${parentProperty}`);
            });
          }
        }
      }
      payload.serverPrecedenceFields = serverPrecedenceFields;

      let body = {
        ...changeSet.content,
        contextData
      };

      /** merge submitData from parentContext defined on the current context containeritem */
      const parentContextChangeSet = getParentContextChangeSet(context);
      if (!Utils.isEmpty(parentContextChangeSet)) {
        body = mergeObjectsAndConcatArrays(parentContextChangeSet, body);
      }

      if (caseWideAction) {
        restAPI = resolveRestApi('refreshCaseData', payload);
      } else if (caseViewAction) {
        restAPI = resolveRestApi('refreshCaseView', payload);
        body.interestPage = interestPage;
      } else {
        restAPI = resolveRestApi('refreshAssignmentData', payload);
      }
      const { restApi } = restAPI;
      const { reqType } = restAPI;
      const reqHeaders = state$.value.data[context]?.caseInfo?.headers;
      const reqConfig = {
        body,
        reqHeaders,
        context,
        method: reqType,
        url: appendQueryParamsForRefreshAPI(payload, restApi)
      };
      const cancelPendingRequests = !caseViewAction && !caseWideAction;
      return from(refreshCaseViewDataOrAssignment(reqConfig, payload, cancelPendingRequests)).pipe(
        map((response) => {
          const responseJSON = response.data || {};
          return {
            response: { ...responseJSON },
            actionConfig: payload,
            actionMgrID: payload.options.actionMgrID,
            headers: processHeaders(response.headers)
          };
        }),
        logAndCatchError(
          ': Error ocurred during fetch call at refreshData Epic : ',
          payload.context,
          payload?.options?.actionMgrID
        )
      );
    }), // Fetch call to REFRESH_DATA and then pass on the response
    mergeMap(async (data) => {
      if (data && data.type === actionType.ERROR) {
        return processDataToDispatchErrorAction(data); // If there is any error emit the ERROR action
      }
      const { actionConfig, response, headers, actionMgrID } = data;
      const { context, serverPrecedenceFields } = actionConfig;
      const vcTargetName = TargetMap.getTargetViewContainerName(actionType.ACTION_SHOWVIEW);
      const target = getResolvedTargetName(vcTargetName, context);
      const options = { acName: target, context };
      const translatedDXAPIResponse = getTranslatedDXAPIResponse(response, {
        [publicConstants.STOPMERGEACTIONS]: true,
        [publicConstants.ACTIONS.DELETEAVIALACTIONS]: true
      });
      translatedDXAPIResponse.directUrlUpdation = false;
      await processViewResponse(translatedDXAPIResponse, actionConfig, options);
      const commonInfo = { target, context };
      const payload = getActionPayload(translatedDXAPIResponse, commonInfo, headers);
      payload.preserveClientChanges =
        actionConfig.options.preserveClientChanges !== undefined ? actionConfig.options.preserveClientChanges : true;
      const refreshPayload = actions.postProcess(actions.mergeData, payload, { serverPrecedenceFields });
      StateMachineManager.registerForServerChanges();
      StateManager.dispatch(refreshPayload);
      StateMachineManager.unRegisterForServerChanges();
      ActionManagerInstance.resolveActionByID(actionMgrID, payload);
      return [];
    })
  );
};

const refreshEpics = [refreshData];
export default refreshEpics;
