import { publicConstants } from '../constants';
/**
 * @typedef {object} DxAPIResponseObject
 * @property {object} data - contains caseInfo, DataPages data etc.
 * @property {object} uiResources - contains the view resources, contextData, components etc..
 * @property {object} nextAssignmentInfo - contains next assignment information
 */

/**
 * @typedef {object} TranslatedDxAPIResponseObject
 * @property {object} data - contains caseInfo, DataPages data etc.
 * @property {object} uiResources - contains the view resources, contextData, components etc..
 */

/**
 * this function helps to translates the dxAPIResponse
 * @function getTranslatedDXAPIResponse
 * @param {DxAPIResponseObject} dxAPIResponse {@link DxAPIResponseObject}
 * @param {object} options contains flag which decide the treatment on response
 * @returns {DxAPIResponseObject} translatedDXAPIResponse
 */

const setCaseLevelMessages = (confirmationNote, translatedDXAPIResponse) => {
  const caseMessages = [confirmationNote];
  translatedDXAPIResponse.data.caseMessages = caseMessages;
  return translatedDXAPIResponse;
};

export const getTranslatedDXAPIResponse = (dxAPIResponse, options) => {
  if (dxAPIResponse.confirmationLinks) {
    dxAPIResponse.data.caseInfo.confirmationLinks = dxAPIResponse.confirmationLinks;
  }

  if (!dxAPIResponse.uiResources && dxAPIResponse.confirmationNote) {
    let confirmationNoteResponse = dxAPIResponse;
    const { confirmationNote } = confirmationNoteResponse;
    confirmationNoteResponse = setCaseLevelMessages(confirmationNote, confirmationNoteResponse);
    return confirmationNoteResponse;
  }

  if (!dxAPIResponse.uiResources || !dxAPIResponse.data || !dxAPIResponse?.uiResources?.root) return dxAPIResponse;

  const translatedDXAPIResponse = dxAPIResponse;

  const {
    uiResources: { actionButtons, navigation },
    nextAssignmentInfo
  } = translatedDXAPIResponse;

  if (translatedDXAPIResponse.data && translatedDXAPIResponse.data.caseInfo) {
    if (actionButtons) {
      translatedDXAPIResponse.data.caseInfo.actionButtons = actionButtons;
    }
    if (navigation) {
      translatedDXAPIResponse.data.caseInfo.navigation = navigation;
    }
    /* In loadview epic remove the availableActions node to fix mergeDeep issue */
    if (options && options[publicConstants.ACTIONS.DELETEAVIALACTIONS]) {
      delete translatedDXAPIResponse.data.caseInfo.availableActions;
    }
    const { assignments, availableActions = [] } = translatedDXAPIResponse.data.caseInfo;
    /* Use STOPMERGEACTIONS to restrict localactions for Menubutton in Review view */
    if (
      assignments &&
      Array.isArray(assignments) &&
      assignments.length > 0 &&
      assignments[0].actions &&
      Array.isArray(assignments[0].actions) &&
      !(options && options[publicConstants.STOPMERGEACTIONS])
    ) {
      const currentAssignmentActions = assignments[0].actions;
      currentAssignmentActions.forEach((action) => {
        if (action.type === 'Assignment' || action.type === 'LocalAction') {
          availableActions.push(action);
        }
      });
      translatedDXAPIResponse.data.caseInfo.availableActions = availableActions;
    }
    /* In case of ConfirmView both confirmationNaote and Ui resources will present in DX API response */
    if (dxAPIResponse.confirmationNote) {
      const { confirmationNote } = translatedDXAPIResponse;
      const caseMessages = [confirmationNote];
      translatedDXAPIResponse.data.caseMessages = caseMessages;
    }
  }

  if (options && options.isCreateDataObject) {
    translatedDXAPIResponse.data.dataInfo = {};
    translatedDXAPIResponse.data.dataInfo.content = translatedDXAPIResponse.data.pyPortal;
    translatedDXAPIResponse.uiResources.root.config.context = 'dataInfo.content';

    translatedDXAPIResponse.uiResources.context_data.dataInfo =
      translatedDXAPIResponse.uiResources.context_data.dataInfo ?? {};
    translatedDXAPIResponse.uiResources.context_data.dataInfo.content =
      translatedDXAPIResponse.uiResources.context_data.dataInfo.content ?? {};
    translatedDXAPIResponse.uiResources.context_data.dataInfo.content =
      translatedDXAPIResponse.uiResources.context_data?.pyPortal;

    delete translatedDXAPIResponse.data.pyPortal;
    delete translatedDXAPIResponse.key;
  }

  if (nextAssignmentInfo) {
    translatedDXAPIResponse.data.nextAssignmentInfo = nextAssignmentInfo;
  }

  return translatedDXAPIResponse;
};

export default { getTranslatedDXAPIResponse };
