import Utils from '../utils/utils';
import * as StateManager from '../store/state/state-manager';
import * as RulesStore from '../rules/rules-store';
import AnnotationProcessor from '../annotation/annotation-processor';
import Constants from '../constants';
import { mergeData } from '../store/state/actions/action-creators';
import { getValue } from '../store/state/state-manager';

const PAGELISTS = Constants.SUMMARY_OF_LISTS;
const WHENS = Constants.SUMMARY_OF_WHEN_CONDITIONS;

/**
 * This handles all rule resolution or fetching the right rule
 * for rendering.
 * It also contains the logic to maintain the context (stepPage)
 * so that components can render without much effort.
 */
class ContextProcessor {
  /**
   * Merges the data with existing data and also
   * updates the redux store.
   * @param {*} data target data object
   */
  static updateData(data) {
    Object.keys(data).forEach((context) => {
      StateManager.dispatch(
        mergeData({
          context,
          data: data[context]
        })
      );
    });
  }

  /**
   * Updates the context data with incoming contextdata
   * @param {*} ctxData contextdata
   */
  static updateContextData(ctxData) {
    Object.keys(ctxData).forEach((context) => {
      StateManager.dispatch(
        mergeData({
          context,
          data: ctxData[context]
        })
      );
    });
  }

  /**
   * Resolves the when from the contextdata and
   * returns the when result- true or false.
   * @param {*} whenName name of the when-rule
   * @param {object} c11nEnv constellation env
   */
  static resolveWhen(whenName, c11nEnv) {
    return !!c11nEnv.getContextDataValue(`.${WHENS}.${whenName}`);
  }

  /**
   * Returns the localized value for the given field value.
   * @param {*} fieldValue field value
   * @param {object} c11nEnv constellation env
   */
  static resolveFieldValue(fieldValue, c11nEnv) {
    return c11nEnv.getLocalizedValue(fieldValue);
  }

  // /**
  //  * Resolves the section from contextdata and returns the
  //  * UIMetadata of the section.
  //  * @param {*} ref section name or property reference.
  //  * @param {object} c11nEnv constellation env
  //  */
  // static resolveSection(ref, c11nEnv) {
  //   return RulesStore.resolveSection([this.resolveName(ref, c11nEnv)]);
  // }

  /**
   * Resolves the navigations from contextdata and returns the
   * UIMetadata of the navigation.
   * @param {*} ref navigation name or property reference.
   * @param {object} c11nEnv constellation env
   */
  static resolveNavigation(ref, c11nEnv) {
    return RulesStore.resolveNavigation([this.resolveName(ref, c11nEnv)]);
  }

  /**
   * Resolves the navigations from contextdata and returns the
   * UIMetadata of the navigation.
   * @param {*} ref navigation name or property reference.
   * @param {object} c11nEnv constellation env
   * @param {string} classID case type class name
   */
  static resolveView(ref, c11nEnv, classID = '') {
    const views = RulesStore.resolveView([this.resolveName(ref, c11nEnv)]);
    let view = [];
    // If there are multiple views with the same name, resolve them with the classID
    if (Array.isArray(views) && views.length > 0) {
      if (c11nEnv) {
        if (!classID) classID = c11nEnv.getValue(Constants.CLASS_ID);
        view = views.filter((viewObj) => {
          return viewObj[Constants.CLASSID] === classID;
        });
      }
      // If no view with the classID as context class exists, return the first view.(Safe check)
      if (view.length === 0) {
        return views[0];
      }
      return view[0];
    }
    return views;
  }

  // /**
  //  * Resolves the flow action from cntextdata and returns the
  //  * UIMetadata of the flow action.
  //  * @param {*} flowAction flow action name or property reference.
  //  * @param {object} c11nEnv constellation env
  //  */
  // static resolveFlowAction(flowAction, c11nEnv) {
  //   flowAction = this.resolveName(flowAction, c11nEnv);
  //   const sectionObj  = c11nEnv.getContextDataValue(`.$flowactions.${flowAction}`);
  //   const { section } = sectionObj;
  //   return RulesStore.resolveSection(section);
  // }

  // /**
  //  * Resolves the harness from cntextdata and returns the
  //  * UIMetadata of the harness.
  //  * @param {*} ref harness name or property reference.
  //  * @param {object} c11nEnv constellation env
  //  *
  //  */
  // static resolveHarness(ref, c11nEnv) {
  //   return RulesStore.resolveHarness([this.resolveName(ref, c11nEnv)]);
  // }

  /**
   * Special handling to support parameter holding the
   * rule name.
   * @param {*} name name of the param
   * @param {object} c11nEnv constellation env
   */
  static resolveName(name, c11nEnv) {
    if (name.indexOf('param.') === 0) {
      name = name.substring('param.'.length);
      return c11nEnv.getContextDataValue(`.$parameters.${name}`);
    }
    if (name.startsWith('@')) {
      name = AnnotationProcessor.handleAnnotation(name, c11nEnv);
    }
    return name;
  }

  /**
   * Prepares full reference for current context.
   * If currentReference is "pyWorkpage" & newReference is ".Page1", output => pyWorkpage.Page1
   * If currentReference is "pyWorkpage" & newReference is "Page1", output => Page1
   * @param {*} currentReference base reference
   * @param {*} newReference new reference to build
   */
  static getContextPath(currentReference, newReference) {
    if (typeof newReference !== 'number' && !newReference) {
      return currentReference;
    }
    newReference = AnnotationProcessor.trimAnnotation(newReference);

    if (typeof newReference === 'number' || typeof newReference === 'string') {
      if (typeof newReference === 'number' && parseInt(newReference, 10) === newReference) {
        return `${currentReference}[${newReference}]`;
      }
      if (newReference.charAt(0) === '.') {
        return currentReference + newReference;
      }
      return newReference;
    }
    return currentReference;
  }

  /**
   * If component's config has reference (like Section, Harness, flowAction),
   * it would be resolved from that particulat store and returns metadata of that entry.
   * @param {*} component - metadata of component
   * Sample component config {"type":"reference","config":{"type":"section","ref":"MainSec"}}
   * @param {object} c11nEnv Constellation env
   */
  static resolveReference(component, c11nEnv) {
    const { type } = component;
    let referenceRuleMeta;
    if (type === 'reference') {
      const { config } = component;
      const refType = config.type;
      const { ref, name, context } = config;
      let ruleClass;
      if (context) {
        ruleClass = context.startsWith(Constants.CLASS_ANNOTATION)
          ? context.split(Constants.CLASS_ANNOTATION)[1].trim()
          : this.resolveClassID(context, c11nEnv.getPageReference(), c11nEnv.getContextName());
      }
      // if (refType === "section") {
      //   referenceRuleMeta = this.resolveSection(ref, c11nEnv);
      // } else if (refType === "harness") {
      //   referenceRuleMeta = this.resolveHarness(ref, c11nEnv);
      // } else
      // if (refType === "flowaction") {
      //   referenceRuleMeta = this.resolveFlowAction(ref, c11nEnv);
      // } else
      if (refType === 'navigation') {
        referenceRuleMeta = this.resolveNavigation(ref || name, c11nEnv);
      } else if (refType === 'view') {
        referenceRuleMeta = this.resolveView(ref || name, c11nEnv, ruleClass || '');
      } else if (refType === 'widget') {
        // Will be updated further for future release, temporary handling for Widgets
        referenceRuleMeta = { type: 'Widget', config: { name, ruleClass } };
      }
      return referenceRuleMeta;
    }
    return component;
  }

  /**
   * Tokenises and resolves property.
   * @param {string} pageReference context path
   * @param {string} propReference property reference eq., .FirstName or pyWorkPage.FirstName
   * @param {string}  context context name
   */
  static resolvePropertyReference(pageReference, propReference, context) {
    return StateManager.getValue(propReference, pageReference, context);
  }

  /**
   * Returns the property object or string depends on the
   * property.
   * example:
   * PageList: D_Genders.pxResults, will return list object
   * Page: pyWorkPage, will return workpage object.
   * Property: pyWorkPage.FirstName. will return string value.
   * @param {*} propName property reference.
   * @param {string} pageReference pageReference path
   * @param {string} context context name
   */
  static getProperty(propName, pageReference, context) {
    let propertyRef = this.resolvePropertyReference(pageReference, propName, context);
    // It might miss in case of parameterized datapages, where
    // actual datapage name is different then downloaded one.
    if (propertyRef === null || propertyRef === undefined || propertyRef.length === 0) {
      const pageListContextData = StateManager.lookupContextValue(`${pageReference}.${PAGELISTS}`, propName, context);
      if (pageListContextData) {
        // Resolve again
        propertyRef = this.resolvePropertyReference(pageReference, pageListContextData, context);
      }
    }

    return propertyRef;
  }

  /**
   * Returns the string value of the property.
   * @param {*} prop property reference
   * @param {*} pageReference pageReference
   * @param {string} context context name
   */
  static getProperyValue(prop, pageReference, context) {
    prop = AnnotationProcessor.trimAnnotation(prop);
    return this.getProperty(prop, pageReference, context);
  }

  static _handlePropString(config, key, value, c11nEnv) {
    if (value.startsWith('@')) {
      config[key] = AnnotationProcessor.handleAnnotation(value, c11nEnv);
    } else {
      config[key] = value;
    }
  }

  static _resolveProps(config, c11nEnv) {
    if (!config) {
      return;
    }

    Object.keys(config).forEach((key) => {
      const value = config[key];
      if (typeof value === 'string') {
        this._handlePropString(config, key, value, c11nEnv);
      } else if (Array.isArray(value)) {
        value.forEach((p) => {
          this._resolveProps(typeof p === 'object' ? p : config[p], c11nEnv);
        });
      } else if (typeof value === 'object') {
        this._resolveProps(config[key], c11nEnv);
      }
    });
  }

  /**
   * resolveProps to resolve all properties in action config
   * @param {*} config config object to resolve properties
   * @param {*} newConfig target object
   * @param {object} c11nEnv constellation env
   * @returns {object} returns the newConfig
   */
  static resolveProps(config, newConfig, c11nEnv) {
    if (!config || !newConfig) {
      return newConfig;
    }
    Utils.mergeDeep(config, newConfig);
    this._resolveProps(newConfig, c11nEnv);
    return newConfig;
  }

  /**
   *
   * Resolve classID for provided property
   * @param {string} prop - property name like .FirstName or .Student.FirstName
   * @param {string} pageReference - property reference like caseInfo.Content
   * @param {string} context - context info like app/primary/workarea_2
   * @example caseInfo: {
   *  content: {
   *    P1: {
   *        classID: 'Data-P1',
   *        P2: {
   *          classID: 'Data-P2',
   *          t1: 'constellation'
   *        }
   *     }
   *  }
   * }
   * tests: .P1 --> Data-P1
   * test2: .P1.P2 --> Data-P2
   * test3: .P1.P2.t1 --> Data-P2
   * caseInfo.content.EmbedPage
   */
  static resolveClassID(prop, pageReference, context) {
    const propName = AnnotationProcessor.trimAnnotation(prop);
    let fullRef = `${pageReference}${propName}`;
    while (fullRef) {
      const classID = getValue(Constants.CLASS_ID, fullRef, context);
      if (classID) {
        return classID;
      }
      fullRef = fullRef.substring(0, fullRef.lastIndexOf('.'));
    }
    return null;
  }
}

export default ContextProcessor;
