import { resolveRestApi } from '../router/app-router';
import { fetch as axiosFetch } from '../service_broker/fetch';
import constants from '../constants';
import CaseUtils from '../case/utils';
import { getChildContainers } from '../store/state/container-utils';
import AssetLoader from '../utils/asset-loader';

/**
 * The DataApiUtils module contains utility APIs to retrieve information from data views.
 * @module DataApiUtils
 */

const GET_LIST_DATA = 'getListData';
const GET_DATA_VIEW_METADATA = 'getDataViewMetadata';
const GET_LIST_DATA_COUNT = 'getListDataCount';
const POST_DATA_VIEW_METADATA = 'postDataViewMetadata';
const MOVE_LIST_RECORDS = 'moveListRecords';
const ACTION_GET_DATA_OBJECT_VIEW = 'readDataObject';

/**
 * This function helps in fetching the data of a list type data view.
 * @ignore
 * @param {string} dataViewName - name of the data view.
 * @param {object} payload - query object containing information about the list of columns, filters and pagination.
 * @param {string} context - context name eq., primary
 * @param {object} options - - Optional, object containing extra infomation like skipClearErrorMessages to not clear the error messages
 */
const getListDataView = (dataViewName, payload, context, options = {}) => {
  const reqPayload = {
    dataViewName
  };
  const { skipClearErrorMessages, signal, doNotMergeMessagesForStatusCode } = options;
  const { restApi, reqType } = resolveRestApi(GET_LIST_DATA, reqPayload);
  return axiosFetch(
    restApi,
    {
      method: reqType,
      body: payload,
      ...(signal && { signal }),
      ...(skipClearErrorMessages && { skipClearErrorMessages })
    },
    context,
    doNotMergeMessagesForStatusCode
  );
};

/**
 * This function helps in fetching the view metadata of an object initialized through a data page.
 * @param {string} dataViewName - The name of the view that contains the data of the object
 * @param {string} viewName - name of the view.
 * @param {object} dataViewParams - object that will set parameters to the data view
 * @param {string} context - context name eq., primary
 * @param {object} options - - Optional, object containing extra information like skipClearErrorMessages to not clear the error messages
 *
 * *
 * @example  <caption>In the example below, the API retrieves the the Glimpse of an operator.</caption>
 *
 * const dataViewName = "D_pyOperatorGlimpse";
 * const viewName = "pyGlipmse";
 * const dataViewParams = { opname: "joe" };
 * const context = "app/primary_1";
 * PCore.getDataApiUtils().getDataObjectView(dataViewName, viewName, dataViewParams, context)
 * .then(response => {
 *  //The response of this API is as shown below:
 *  {
 *    fetchDateTime: "2020-06-29T11:06:23.896Z"
 *    data: { uiResources: { ... }, data: { ... } }
 *  }
 * })
 * .catch(error => {
 *  console.log(error);
 * });
 *
 * @returns {object} - response object
 *
 * @function
 */
const getDataObjectView = (dataViewName, viewName, dataViewParams, context, options = {}) => {
  const { skipClearErrorMessages, signal, doNotMergeMessagesForStatusCode } = options;
  const queryPayload = {
    data_view_ID: dataViewName,
    view_ID: viewName
  };
  const reqPayload = { dataViewParameters: dataViewParams };
  const { restApi, reqType } = resolveRestApi(ACTION_GET_DATA_OBJECT_VIEW, queryPayload);
  return axiosFetch(
    restApi,
    {
      method: reqType,
      body: reqPayload,
      ...(signal && { signal }),
      ...(skipClearErrorMessages && { skipClearErrorMessages })
    },
    context,
    doNotMergeMessagesForStatusCode
  );
};

/**
 * This API obtains the number of records in a data view as a Promise.
 * @param {string} dataViewName - The name of the data view that contains the records whose count must be obtained.
 * @param {object} payload - Optional, A query object containing the details of list of columns, filter conditions, and pagination to be retrieved.
 * @param {object} payload.dataViewParameters - Optional, An object whose parameters are configured on the data view or data page.
 * @param {object} payload.query - Optional, A command to obtain a set of fields satisfying specific conditions, such as, select, sortBy, filter, etc.
 * @param {object} payload.paging - Optional, An object that obtains a specific number of records from a page.
 * @param {string} context - The name of the context where the API is being called.
 *
 * @example  <caption>In the example below, the API retrieves the number of records of employees whose gender is 'Female' and whose role is 'Finance'.</caption>
 *
 * const dataViewName = "D_EmployeeList";
 * const payLoad = {
 *  "dataViewParameters": {
 *    "dept": "HR"
 *  },
 * "query": {
 *  "distinctResultsOnly": true,
 *   "filter": {
 *     "filterConditions": {
 *       "F1": {
 *         "comparator": "=",
 *         "ignoreCase": true,
 *         "lhs": {
 *           "field": "Role"
 *         },
 *         "rhs": {
 *           "value": "Finance"
 *         }
 *       },
 *      "F2": {
 *         "comparator": "=",
 *         "ignoreCase": true,
 *         "lhs": {
 *           "field": "Gender"
 *         },
 *         "rhs": {
 *           "value": "Female"
 *         }
 *       }
 *     },
 *     "logic": "F1 AND F2"
 *   }
 *   "select": [
 *     {
 *       "field": "pyID"
 *     }
 *   ]
 *  }
 * };
 * const context = "app/primary_1";
 * PCore.getDataApiUtils().getListCount(dataViewName, payload, context)
 * .then(response => {
 *  //The response of this API is as shown below:
 *  {
 *    fetchDateTime: "2020-06-29T11:06:23.896Z"
 *    hasMoreResults: false
 *    resultCount: 4923
 *  }
 * })
 * .catch(error => {
 *  console.log(error);
 * });
 *
 *
 * @returns {object} - response object
 *
 * @function
 */

const getListCount = (dataViewName, payload, context) => {
  const reqPayload = {
    dataViewName
  };
  const { restApi, reqType } = resolveRestApi(GET_LIST_DATA_COUNT, reqPayload);
  return axiosFetch(
    restApi,
    {
      method: reqType,
      body: payload
    },
    context
  );
};

/**
 * This API retrieves the list of data records in a data view as a Promise.
 * @param {string} dataViewName - The name of the data view from which the list of data records must be retrieved.
 * @param {object} payload - Optional, A query object containing the details of list of columns, filter conditions, and pagination to be retrieved.
 * @param {object} payload.dataViewParameters - Optional, An object whose parameters are configured on the data view or data page.
 * @param {object} payload.query - Optional, A command to obtain a set of fields satisfying specific conditions, such as, select, sortBy, filter, etc.
 * @param {object} payload.paging - Optional, An object that obtains a specific number of records from a page.
 * @param {boolean} payload.useExtendedTimeout - Optional, Works only if the data view is sourced by a report definition.
 * When set to true, it increases the timeout to 45 seconds. Otherwise, the timeout is 10 seconds.
 * @param {string} context - The name of the context where the API is being called..
 * @param {object} options - Optional, object containing extra infomation like skipClearErrorMessages
 * @example  <caption>In the example below, the API retrieves the first 10 records of employees whose gender is 'Male' and whose role is 'Software'.</caption>
 *
 * const dataViewName = "D_EmployeeList";
 * const payLoad = {
 * "dataViewParameters": {
    "dept": "Engineering"
  },
  "query": {
    "distinctResultsOnly": true,
    "filter": {
      "filterConditions": {
        "F1": {
          "comparator": "=",
          "ignoreCase": true,
          "lhs": {
            "field": "Role"
          },
          "rhs": {
            "value": "Software"
          }
        },
        "F2": {
          "comparator": "=",
          "ignoreCase": true,
          "lhs": {
            "field": "Gender"
          },
          "rhs": {
            "value": "Male"
          }
        }
      },
      "logic": "F1 AND F2"
    }
    "select": [
      {
        "field": "Name"
      },
      {
        "field": "Role"
      },
      {
        "field": "Gender"
      }
    ]
  },
  "paging":{
    "pageNumber":1,
    "pageSize":10
  }
};
 * const context = "app/primary_1";
 * PCore.getDataApiUtils().getData(dataViewName, payload, context)
 * .then(response => {
 *  // The response of this API is as shown below:
 *  {
 *      data: [
 *        {
 *          "Name" : "Mark wood",
 *          "Role" : "Software",
 *          "Gender" "Male"
 *        },
 *        {
 *          "Name" : "Gabe Edwards",
 *          "Role" : "Software",
 *          "Gender" "Male"
 *        }
 *      ]
 *      fetchDateTime: "2020-06-29T11:06:24.329Z"
 *      pageNumber: 1
 *      pageSize: 10
 *  }
 * })
 * .catch(error => {
 *    console.log(error);
 * });
 *
 *
 * @returns {object} - response object
 *
 * @function
 */
const getData = (dataViewName, payload, context, options = {}) => {
  return getListDataView(dataViewName, payload, context, options);
};

/**
 * This API obtains the metadata of a data view as a Promise.
 * @param {string} dataViewName - The name of the data view whose metadata must be obtained.
 * @param {string} context - context name eq., primary
 *
 * @example  <caption>In the example below, the API returns the metadata of the data view whose name is 'D_BugList'.</caption>
 *
 * const dataViewName = "D_BugList"
 * PCore.getDataApiUtils().getDataViewMetadata(dataViewName);
 *
 * //The response of this api will be like shown below.
 * {
  "classID": "PegaProjMgmt-Work-Bug",
  "className": "Bug",
  "structure": "List",
  "isQueryable": true,
  "fields": [
    {
      "description": "operator who manager assigns work to",
      "fieldID": "pyAssignedOperator",
      "fieldType": "Identifier",
      "isReadOnly": false,
      "name": "Assigned To",
      "dataType": "Identifier"
    },
    {
      "description": "This property is used to identify the work object's parent
      Backlog and should include the pyID of that work object. In the future,
      it will be derived from the user's data input into UserStoryIDEntry or
      based upon the context of the creation of the item.",
      "displayAs": "pxTextInput",
      "fieldID": "BacklogID",
      "fieldType": "Text (single line)",
      "isReadOnly": false,
      "name": "Backlog ID",
      "maxLength": 32,
      "dataType": "Text"
    }
  ]
}
 * @returns {object} - metadata of a data view.
 *
 * @function
 */
const getDataViewMetadata = (dataViewName, context, associationFilter) => {
  const reqPayload = {
    dataViewName
  };
  const hasAssociationFilter = !!associationFilter;
  const routeKey = hasAssociationFilter ? POST_DATA_VIEW_METADATA : GET_DATA_VIEW_METADATA;
  const { restApi, reqType } = resolveRestApi(routeKey, reqPayload);
  if (hasAssociationFilter) {
    return axiosFetch(
      restApi,
      {
        method: reqType,
        body: {
          associationFilter
        }
      },
      context
    );
  }
  return axiosFetch(
    restApi,
    {
      method: reqType
    },
    context
  );
};

