const moveListRecords = (payload, reduxContext) => {
  const { sourceID, destinationID, context, listClass, viewName, viewClassName } = payload;

  const reqPayload = {
    viewName,
    viewClassName
  };
  const { restApi, reqType } = resolveRestApi(MOVE_LIST_RECORDS, reqPayload);

  return axiosFetch(
    restApi,
    {
      method: reqType,
      body: {
        content: { sourceID, destinationID, context, listClass }
      }
    },
    reduxContext
  );
};

const isTimeElapsed = (loadTime, timeoutSecs = '', type = '') => {
  if (timeoutSecs) {
    if (loadTime && type === constants.RELOAD_IF_OLDER_THAN) {
      return new Date(loadTime).setSeconds(timeoutSecs) < Date.now();
    }
    if (loadTime && type === constants.RELOAD_ONCE_PER_INTERACTION) {
      return true;
    }
  }
  return true;
};

// Gets an application scoped key - useful for caching API calls
const getAppCacheKey = (initialKey) => {
  const authoringPreview = getChildContainers('root').find((element) => element.includes('authoring-preview'));
  if (authoringPreview !== undefined) {
    return `${constants.AUTHORING_PREVIEW_CONTAINER}-${initialKey}`;
  }
  return `${AssetLoader.getAppAlias()}-${initialKey}`;
};

export default {
  getData,
  getListCount,
  getDataViewMetadata,
  getDataObjectView,
  getCaseEditMetadata,
  getCaseEditLock,
  updateCaseEditFieldsData,
  moveListRecords,
  isTimeElapsed,
  getAppCacheKey
};
