const negativeAfter = ['binaryOperator', 'unaryOperator', 'openParenthesis'];
const spaceRegex = /^\s*$/;
const identifierRegex = /^[a-zA-Z_.$][(0-9)a-zA-Z0-9_.$]*$/;

const LITERAL = 'literal';
const IDENTIFIER = 'identifier';

// Regular expressions for tokenizing
const escEscapeRegex = /\\\\/;

const preOpRegexElems = [
  // Strings
  "'(?:(?:\\\\')|[^'])*'",
  '"(?:(?:\\\\")|[^"])*"',
  // space
  '\\s+',
  // boolean
  '\\btrue\\b',
  '\\bfalse\\b'
];

const postOpRegexElems = [
  // Identifiers
  '[a-zA-Z_.\\$][(0-9)a-zA-Z0-9_.\\$]*',
  // Date format
  '[0-9]{4}-[0-9]{2}-[0-9]{2}T[0-9]{2}:[0-9]{2}|[0-9]{4}-[0-9]{2}-[0-9]{2}',
  // Numerics (without negative symbol)
  '(?:(?:[0-9]*\\.[0-9]+)|[0-9]+)'
];

const numberRegex = /^-?(?:(?:[0-9]*\.[0-9]+)|[0-9]+)$/;

/**
 * [Lexer]
 * Description : Provides APIs to tokenize and generate the AST for an expression.
 * @class
 */
class Lexer {
  /**
   * Description : Lexer
   * @class
   * @param   {object}        grammar     Supported grammar.
   */
  constructor(grammar) {
    this._grammar = grammar;
  }

  /**
   * [_isNegative]
   * @static
   * Description : Determines if a token has valid a negative sign.
   * @param   {Array}         tokens      List of tokens
   * @returns {boolean}       true if the negative is valid else false.
   */
  static _isNegative(tokens) {
    return negativeAfter.some((operatorType) => operatorType === tokens[tokens.length - 1].tokenType);
  }

  /**
   * [_isWhitespace]
   * @static
   * Description : Checks if the string has all white space characters.
   * @param   {string}        str         Token string
   * @returns {boolean}       true if all characters are whitespace else false
   */
  static _isWhitespace(str) {
    return !!str.match(spaceRegex);
  }

  /**
   * [_createWordboundary]
   * @static
   * Description : Add the word boundaries to the identifiers.
   * @param   {string}        str         Token string
   * @returns {string}        str         Token after escaping the escape characters.
   */
  static _createWordboundary(str) {
    str = str.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
    if (str.match(identifierRegex)) {
      str = `\\b${str}\\b`;
    }
    return str;
  }

  /**
   * [_unquote]
   * @static
   * Description : Unquotes the string.
   * @param   {string}         str        Token string
   * @returns {string}         Unquoted string
   */
  static _unquote(str) {
    const [quote] = str;
    const escQuoteRegex = new RegExp(`\\\\${quote}`, 'g');
    return str
      .substr(1, str.length - 2)
      .replace(escQuoteRegex, quote)
      .replace(escEscapeRegex, '\\');
  }

  /**
   * [_getSplitRegex]
   * Description : Returns the regular expression to split the expression string into elements, which can
   * later be converted to token objects.
   * @private
   * @returns {RegExp}            Regular expression to split the expression string.
   *
   */
  _getSplitRegex() {
    if (!this._splitRegex) {
      // Create the element array from the grammar.
      const elemArray = Object.keys(this._grammar)
        .sort((a, b) => {
          return b.length - a.length;
        })
        .map((elem) => {
          return Lexer._createWordboundary(elem);
        }, this);
      // Generate the split expression.
      this._splitRegex = new RegExp(
        `(${[preOpRegexElems.join('|'), elemArray.join('|'), postOpRegexElems.join('|')].join('|')})`
      );
    }
    return this._splitRegex;
  }

  /**
   * [_splitExpression]
   * Description : Splits the flat expression string into elements.
   * @private
   * @param   {string}            str     Expression string.
   * @returns {Array}             Elements array
   */
  _splitExpression(str) {
    const regex = this._getSplitRegex();
    return str.split(regex).filter((elem) => {
      // Remove empty strings
      return elem;
    });
  }

  /**
   * [_createToken]
   * Description : Creates a token object for an element split from the expression string.
   * @private
   * @param   {string}         element    Token String
   * @returns {object}         token      Token object
   *                               - tokenType
   *                               - value
   *                               - element
   */
  _createToken(element) {
    const token = {
      tokenType: LITERAL,
      value: element,
      element
    };
    if (element[0] === '"' || element[0] === "'") {
      token.value = Lexer._unquote(element);
    } else if (element.match(numberRegex)) {
      token.value = parseFloat(element);
    } else if (element === 'true' || element === 'false') {
      token.value = element === 'true';
    } else if (this._grammar[element]) {
      token.tokenType = this._grammar[element].operatorType;
    } else if (element.match(identifierRegex)) {
      token.tokenType = IDENTIFIER;
    } else {
      // empty else block
    }
    return token;
  }

  /**
   * [_getTokens]
   * Description : For each element split from the expression, creates a token object.
   * @private
   * @param   {Array}             elements        Elements split from the expression string.
   * @returns {Array}             tokens
   */
  _getTokens(elements) {
    const tokens = [];
    let negate = false;
    for (let i = 0; i < elements.length; i += 1) {
      if (Lexer._isWhitespace(elements[i])) {
        if (tokens.length) {
          tokens[tokens.length - 1].element += elements[i];
        }
      } else if (elements[i] === '-' && Lexer._isNegative(tokens)) {
        negate = true;
      } else {
        if (negate) {
          elements[i] = `-${elements[i]}`;
          negate = false;
        }
        tokens.push(this._createToken(elements[i]));
      }
    }
    return tokens;
  }

  /**
   * [tokenize]
   * Description : Public API on the Lexer class to tokenize an expression.
   * @param   {string}            exprStr         Expression string.
   * @returns {Array}             tokens
   */
  tokenize(exprStr) {
    const elements = this._splitExpression(exprStr);
    return this._getTokens(elements);
  }
}

export default Lexer;
