import { ofType } from 'redux-observable';
import { mergeMap } from 'rxjs/operators';

import constants from '../constants';
import { FETCH_DATAPAGE } from '../store/state/actions';
import { mergeData, addSharedData } from '../store/state/actions/action-creators';
import { getSharedDataObject, dispatch } from '../store/state/state-manager';

import DataApiUtils from './DataApiUtils';

export const fetchDataPage = (action$) =>
  action$.pipe(
    ofType(FETCH_DATAPAGE),
    mergeMap(async (action) => {
      const { context } = action.payload;
      const data = {};
      const sharedData = {};
      const promises = Object.keys(action.payload.datapages).map((datapageKey) => {
        const datapage = action.payload.datapages[datapageKey];
        if (datapage.mode === constants.READONLY && datapage?.refreshStrategy) {
          const { timeoutSecs = '', type = '' } = datapage?.refreshStrategy || {};
          const dataPageObjectFromStore = getSharedDataObject()[datapage.name];
          if (dataPageObjectFromStore && datapageKey in dataPageObjectFromStore) {
            const dataPageWithHash = dataPageObjectFromStore[datapageKey];
            const { loadTime } = dataPageWithHash;
            if (!DataApiUtils.isTimeElapsed(loadTime, timeoutSecs, type)) {
              return;
            }
          }
        }
        return DataApiUtils.getData(
          datapage.name,
          {
            dataViewParameters: datapage.dataViewParameters
          },
          context
        )
          .then((response) => {
            const dataOf = datapage.mode === constants.READONLY ? sharedData : data;

            if (dataOf[datapage.name]) {
              dataOf[datapage.name][datapageKey] = {
                pxResults: response.data.data,
                parameters: datapage.dataViewParameters,
                loadTime: response.data?.fetchDateTime
              };
            } else {
              dataOf[datapage.name] = {
                [datapageKey]: {
                  pxResults: response.data.data,
                  parameters: datapage.dataViewParameters,
                  loadTime: response.data?.fetchDateTime
                }
              };
            }
          })
          .catch(() => {
            // TODO: handle failure cases
          });
      });
      await Promise.allSettled(promises);
      const actionsOut = [];
      if (Object.keys(data).length) {
        actionsOut.push(
          mergeData({
            context,
            data
          })
        );
      }
      if (Object.keys(sharedData).length) {
        actionsOut.push(
          addSharedData({
            data: sharedData,
            context
          })
        );
      }
      dispatch(actionsOut);
      return [];
    })
  );
const dataViewEpic = [fetchDataPage];
export default dataViewEpic;
