import Utils from '../utils/utils';

const paragraphStore = {};
const viewStore = [];
const navigationStore = [];
const propertyModelStore = {};
const dataPageModelStore = {};
const personalizationStore = {};
const insightStore = {};
const dataTypeStore = {};
const dataPagesStore = {};

/**
 * Provides APIs to merge source rules to the store, and update viewStore and navigationStore, and to resolves views, fields
 * and dataPages
 * These APIs can be used for different purposes e.g. get propertyModel, update viewStore..
 * @module RulesStore
 * @private
 */

/**
 * [mergeStore]
 * Description : merges the store with the source rules based on the classID. If a rule with the same name and classID
 * exists it will be merged, else created.
 * @function mergeStore
 * @param {*} rules   source rules
 * @param {*} store   Existing store
 * @static
 * @returns {void}
 *
 * @example <caption>Example for mergeStore.</caption>
 * PCore.getMetadataUtils().mergeStore({ apartment: { classID: "c-1" }}, {})
 * example for merging the apartment field rule into the store
 */
export const mergeStore = (rules = {}, store = {}) => {
  // Blind copy to store if store is empty
  if (Object.keys(store).length === 0) {
    Utils.copy(rules, store);
    return;
  }

  Object.keys(rules).forEach((rule) => {
    const ruleModels = rules[rule];
    const storeRuleModels = store[rule];
    if (!storeRuleModels) {
      // store has no entry then set RuleModel
      store[rule] = ruleModels;
    } else {
      // merging new rules with the rules in the store
      const tempStoreModels = Array.isArray(storeRuleModels) ? storeRuleModels : [storeRuleModels];
      const tempRuleModels = Array.isArray(ruleModels) ? ruleModels : [ruleModels];

      tempRuleModels.forEach((newRule) => {
        const index = tempStoreModels.findIndex((storeRule) => storeRule.classID === newRule.classID);
        if (index === -1) {
          tempStoreModels.push(newRule);
        } else {
          const defaultProps = { isDeclarativeTarget: false };
          const newMergedRule = { ...tempStoreModels[index], ...defaultProps, ...newRule };
          tempStoreModels[index] = newMergedRule;
        }
      });

      store[rule] = tempStoreModels.length === 1 ? tempStoreModels[0] : tempStoreModels;
    }
  });
};

/**
 * returns copy of property model from store or null
 * @function getPropertyMetadata
 * @param {string} propertyName property name like 'FirstName'
 * @param {string} currentClassID class id to which property belongs to
 * @static
 * @returns {object | Array | null} returns copy of property model from store or null
 *
 * @example <caption>Example for getPropertyMetadata.</caption>
 * PCore.getMetadataUtils().getPropertyMetadata('apartment', 'c-1')
 * example to get metadata of the field apartment under class c-1
 */
export const getPropertyMetadata = (propertyName, currentClassID) => {
  if (!propertyName) {
    return null;
  }

  const propertyModels = propertyModelStore[propertyName];

  if (!propertyModels) {
    return null;
  }

  if (!currentClassID) {
    if (Array.isArray(propertyModels)) {
      return [...propertyModels]; // properties is array
    }
    return propertyModels; // properties is object
  }

  let metadata;
  if (Array.isArray(propertyModels)) {
    metadata = propertyModels.find((modal) => modal.classID.toLowerCase() === currentClassID.toLowerCase());
  } else {
    metadata =
      propertyModels.classID.toLowerCase() === currentClassID.toLowerCase() ||
      ((propertyModels.type === 'Page' || propertyModels.type === 'Page List') &&
        propertyModels?.pageClass.toLowerCase() === currentClassID.toLowerCase())
        ? propertyModels
        : null;
  }
  return metadata;
};

/**
 * returns copy of embedded property's leafNode model from store or null
 * @function getEmbeddedPropertyMetadata
 * @param {string} propertyName property name like 'Customer.Address.City'
 * @param {string} currentClassID class id to which first level property belongs to
 * @static
 * @returns {object | null} returns copy of embedded property's leafNode model from store or null
 *
 * @example <caption>Example for getEmbeddedPropertyMetadata.</caption>
 * PCore.getMetadataUtils().getEmbeddedPropertyMetadata('Customer.Address.City', 'O02LDN-CosmoReact-Work-Test')
 * example to get metadata of the leafProperty 'City' which needs to be resolved from the top level class 'O02LDN-CosmoReact-Work-Test
 */

export const getEmbeddedPropertyMetadata = (propertyName, currentClassID, embeddedType) => {
  if (!currentClassID || !propertyName) {
    return null;
  }
  if (!propertyName.includes('.')) {
    return { ...getPropertyMetadata(propertyName, currentClassID), embeddedType };
  }
  const [first, ...rest] = propertyName.split('.');
  const metadata = getPropertyMetadata(first, currentClassID);
  return getEmbeddedPropertyMetadata(rest.join('.'), metadata?.pageClass, metadata?.type);
};

/**
 * This API updates the view store
 * @function updateViewStore
 * @param {object} views the object of the views
 * @static
 * @returns {void}
 *
 * @example <caption>Example for updateViewStore.</caption>
 * PCore.getMetadataUtils().updateViewStore({
 *  "view1": { a: 'a' }
 *});
 * example for updating view store with View1
 */
export const updateViewStore = (views) => {
  mergeStore(views, viewStore);
};

/**
 * This API updates the navigation
 * @function updateNavigationStore
 * @param {object} navigations the object of navigation rules
 * @static
 * @returns {void}
 *
 * @example <caption>Example for updateNavigationStore.</caption>
 * PCore.getMetadataUtils().updateNavigationStore({
 *      Navigation1: {
 *          nav: "nav"
 *      }
 *});
 * example for updating navigation store with Navigation1
 */
export const updateNavigationStore = (navigations) => {
  mergeStore(navigations, navigationStore);
};

/**
 * Update store with given resources
 * @function updateStores
 * @param {object} resources uiMetadata - meta data information
 * @static
 * @returns {void}
 *
 * @example <caption>Example for updateStores.</caption>
 * PCore.getMetadataUtils().updateStores({
 *   resources: {
 *     personalizations: {
 *        "test1234": {}
 *  }
 *  }
 *});
 * example for updating store with personalizations
 */
export const updateStores = (resources) => {
  if (resources.views) mergeStore(resources.views, viewStore);
  if (resources.navigations) mergeStore(resources.navigations, navigationStore);
  if (resources.personalizations) mergeStore(resources.personalizations, personalizationStore);
  if (resources.insights) mergeStore(resources.insights, insightStore);
  if (resources.paragraphs) mergeStore(resources.paragraphs, paragraphStore);
  if (resources.dataTypes) mergeStore(resources.dataTypes, dataTypeStore);
  if (resources.datapages) mergeStore(resources.datapages, dataPagesStore);

  if (resources.fields) {
    Object.keys(resources.fields).forEach((key) => {
      const fieldArray = resources.fields[key];
      if (Array.isArray(fieldArray)) {
        if (resources.datapages) {
          fieldArray.forEach((fieldObj) => {
            /* istanbul ignore else */
            if (fieldObj.datasource && fieldObj.datasource.tableType === 'DataPage') {
              fieldObj.datasource.mode = resources.datapages[fieldObj.datasource.name].mode;
            }
          });
        }
        // if only single entry present for field then return object.
        if (fieldArray.length === 1) {
          const [objData] = fieldArray;
          resources.fields[key] = objData;
        }
      } else if (resources.datapages && fieldArray.datasource && fieldArray.datasource.tableType === 'DataPage') {
        // TODO: In future we will always get array once everything is stable remove this code.
        /* istanbul ignore else */
        fieldArray.datasource.mode = resources.datapages[fieldArray.datasource.name].mode;
      }
    });
    const { datapages = {} } = resources;
    Object.keys(datapages).forEach((key) => {
      dataPageModelStore[key] = datapages[key];
    });
  }
  mergeStore(resources.fields, propertyModelStore);
};

/**
 * Will provide view object
 *
 * @function resolveView
 * @param {string} name - view name
 * @static
 * @returns {object} returns view rule
 *
 * @example <caption>Example for resolveView.</caption>
 * PCore.getMetadataUtils().resolveView("View1");
 * example for getting "View1" view rule object
 */
export const resolveView = (name) => {
  return viewStore[name];
};

/**
 * Will provide navigation object
 *
 * @function resolveNavigation
 * @param {string} name - navigation name
 * @static
 * @returns {object} returns navigation rule
 *
 * @example <caption>Example for resolveNavigation.</caption>
 * PCore.getMetadataUtils().resolveNavigation("Navigation1");
 * example for getting "Navigation1" navigation rule object
 */
export const resolveNavigation = (name) => {
  return navigationStore[name];
};

/**
 * This API returns dataPage rule if present in dataPageModelStore
 *
 * @function getDataPageMetadata
 * @param {string} dataPageName name of the dataPage
 * @static
 * @returns {object | null } returns dataPage if present or return null
 *
 * @example <caption>Example for getDataPageMetadata.</caption>
 * PCore.getMetadataUtils().getDataPageMetadata("dataPage");
 * example for getting "dataPage" under "datapages" in resources
 */
export const getDataPageMetadata = (dataPageName) => {
  return dataPageName in dataPageModelStore ? dataPageModelStore[dataPageName] : null;
};

/**
 * This API returns personalizationId rule if present in personalizationStore
 *
 * @function getPersonalizationMetadata
 * @param {string} personalizationId personalization id
 * @static
 * @returns {object | null} returns personalizationId if present or return null
 *
 * @example <caption>Example for getPersonalizationMetadata.</caption>
 * PCore.getMetadataUtils().getPersonalizationMetadata("PersonalizationSample");
 * example for getting "PersonalizationSample" under "personalizations" in resources
 */

export const getPersonalizationMetadata = (personalizationId) => {
  return personalizationId in personalizationStore ? personalizationStore[personalizationId] : null;
};

/**
 * This API returns insightId rule if present in insightStore
 *
 * @function getInsight
 * @param {string} insightId insight id
 * @static
 * @returns {object | null} returns insight object if present or return null
 *
 * @example <caption>Example for getInsight.</caption>
 * PCore.getMetadataUtils().getInsight("InsightId");
 * example for getting "InsightId" under "insights" in resources
 */
export const getInsight = (insightId) => {
  return insightId in insightStore ? insightStore[insightId] : null;
};

/**
 * This API returns fields of given class name
 *
 * @function getFieldsByClassName
 * @param {string} className name of the class
 * @static
 * @returns {object} returns object of the fields for the given class name
 *
 * @example <caption>Example for getFieldsByClassName.</caption>
 * PCore.getMetadataUtils().getFieldsByClassName("O02LDN-CosmoReact-Work-Test ");
 * example for getting fields of the "O02LDN-CosmoReact-Work-Test" class
 */

export const getFieldsByClassName = (className) => {
  const fields = {};
  Object.keys(propertyModelStore).forEach((fieldName) => {
    const props = propertyModelStore[fieldName];
    let matchedField;
    if (Array.isArray(props)) {
      matchedField = props.find((prop) => className === prop.classID);
    } else if (className === props.classID) {
      matchedField = props;
    }
    if (matchedField) {
      fields[fieldName] = Utils.deepClone(matchedField);
    }
  });
  return fields;
};

export const getFieldParameters = (propertyName, classID) => {
  const params = getPropertyMetadata(propertyName, classID)?.datasource?.parameters;
  if (params) {
    return Utils.deepClone(params);
  }

  return null;
};

export const getParagraph = (paragraphName, classID) => {
  let value = null;
  const paragraphObj = paragraphStore[paragraphName];

  if (paragraphObj) {
    if (Array.isArray(paragraphObj)) {
      value = paragraphObj.find((paragraph) => paragraph.classID === classID);
    } else if (paragraphObj.classID === classID) {
      value = paragraphObj;
    }
  }

  return value ? Utils.deepClone(value).content : null;
};

export const getSavableDataPage = (dataClass) => {
  if (dataClass in dataTypeStore) {
    const { savableDataPage } = dataTypeStore[dataClass];
    return savableDataPage;
  }
  return null;
};

export const getDataPageKeys = (dataPageName) => {
  const keys = [];
  let classKeys = [];
  if (dataPageName in dataPagesStore) {
    const dataClass = dataPagesStore[dataPageName].classID;
    const { isAlternateKeyStorage } = dataPagesStore[dataPageName];
    if (isAlternateKeyStorage) {
      dataPagesStore[dataPageName].parameters.forEach((item) =>
        keys.push({ keyName: item.name, isAlternateKeyStorage, linkedField: item.linkedField })
      );
    } else {
      classKeys = dataTypeStore[dataClass].classKeys;
      classKeys.forEach((item) => keys.push({ keyName: item, isAlternateKeyStorage }));
    }
    return keys;
  }
  return null;
};

export const getLookUpDataPage = (dataClass) => {
  if (dataClass in dataTypeStore) {
    const { lookupDataPage } = dataTypeStore[dataClass];
    return lookupDataPage;
  }
  return null;
};

export const getLookUpDataPageInfo = (dataClass) => {
  if (Object.prototype.hasOwnProperty.call(dataTypeStore, dataClass)) {
    const { lookupDataPageInfo } = dataTypeStore[dataClass];
    return lookupDataPageInfo;
  }
  return null;
};
