import { handleChange } from '../../../expression/expression-engine';
import * as actions from '../actions';
import PegaConstants from '../../../constants';
import { getValue, dispatch } from '../state-manager';
import DatapageDependencyMap from '../../../cascade/datapage-dependency-handler';
import { getPropertyMetadata, getDataPageMetadata } from '../../../rules/rules-store';
import Utils from '../../../utils/utils';
import * as RefreshHelpers from '../../../refresh/refreshHelpers';
import { getHiddenFormFields, getReadOnlyFormFields } from '../../../utils/form-handler';
import AnnotationUtils from '../../../annotation/annotation-utils';
import CascadeManager from '../../../cascade/manager';
import { isPropertyDirtyPageInstruction, getChangedInstructions } from '../../../page-instructions';
import PageInstructionUtils from '../../../page-instructions/utils';
import StateMachineManager from '../../../page-instructions/state-machine-manager';

const { APP } = PegaConstants;

let changedProperties = {};

let _isComplexObjectMerged = {};

export const FORM_DIRTY_MESSAGE = 'You will lose unsaved changes. Do you want to continue?';
export const MAX_DOCUMENT_LIMIT_REACHED =
  'You have reached maximum limit of pending changes. Please commit/cancel one or more changes to continue.';

/**
 * Builds form submit object. Object will be
 * in json format.
 * @param {*} context Context whose changes will be returned
 * @param {boolean} includeHiddenFields check to whether add hiddenFields or not to the state
 */
export const getChanges = (context = APP.APP, includeHiddenFields, includeReadOnlyFields) => {
  let hiddenFields = [];
  let readOnlyFields = [];
  if (!includeHiddenFields) {
    hiddenFields = getHiddenFormFields(context);
  }

  if (!includeReadOnlyFields) {
    readOnlyFields = getReadOnlyFormFields(context);
  }
  const resultObj = {};
  const changedPropertiesInContext = changedProperties[context] || {};
  Object.keys(changedPropertiesInContext).forEach((key) => {
    /* istanbul ignore else */
    if (
      Object.prototype.hasOwnProperty.call(changedPropertiesInContext, key) &&
      hiddenFields.indexOf(key) === -1 &&
      readOnlyFields.indexOf(key) === -1
    ) {
      Utils.buildObject(key, resultObj, changedPropertiesInContext[key].value);
    }
  });

  return resultObj;
};

export const mergeListChanges = (context, resultObj = {}) => {
  const pageListChanges = StateMachineManager.buildClientChangeList(context) || {};
  Object.keys(pageListChanges).forEach((key) => {
    Utils.buildObject(key, resultObj, pageListChanges[key]);
  });
};

/**
 * This delete a map of recorded change for a given property in respective context
 * @param {*} payload redux action payload.
 */
const deleteRecordedChange = (payload) => {
  const { context } = payload;
  const reference = payload.reference.startsWith('.') ? payload.reference.substr(1) : payload.reference;
  delete changedProperties[context]?.[reference];
};

/* @private */
export const isComplexObjectMerged = (context) => _isComplexObjectMerged[context];

/**
 * This creates a map for every change with initialValue and the current value
 * to be used for dirty check.
 * @param {*} action redux action object.
 */
const recordChange = (action) => {
  const { payload } = action;
  const { context, initialValue = '', isStrictTracking } = payload;
  const reference = payload.reference.startsWith('.') ? payload.reference.substr(1) : payload.reference;
  changedProperties[context] = changedProperties[context] || {};
  const changeTracker = changedProperties[context][reference];
  if (!changeTracker) {
    changedProperties[context][reference] = {
      initialValue: initialValue || getValue(payload.reference, null, context)
    };
    changedProperties[context][reference].previousValue = changedProperties[context][reference].initialValue;
  } else {
    changedProperties[context][reference].previousValue = getValue(payload.reference, null, context);
  }
  changedProperties[context][reference].value = payload.value;
  if (
    isStrictTracking &&
    changedProperties[context][reference].value === changedProperties[context][reference].initialValue
  ) {
    deleteRecordedChange(payload);
  }
};

export const updateChangeList = ({ value, context, reference }) => {
  if (PageInstructionUtils.isPageInstruction(reference)) {
    return;
  }
  const referencePath = reference?.startsWith('.') ? reference?.substr(1) : reference;
  changedProperties[context] = changedProperties[context] || {};
  const changeTrackerList = changedProperties[context][referencePath];
  if (!changeTrackerList) {
    changedProperties[context][referencePath] = {
      initialValue: getValue(referencePath, null, context)
    };
  }
  changedProperties[context][referencePath].value = value;
};
/**
 * Returns true if the form in the passed context is dirty.
 * @param {*} context Context whose dirtyness will be checked.
 */
export const isContextDirty = (context) => {
  const pageListChanges = getChangedInstructions(context) || {};
  return (
    (changedProperties[context] &&
      Object.keys(changedProperties[context]).some((property) => {
        return changedProperties[context][property].initialValue !== changedProperties[context][property].value;
      })) ||
    Object.keys(pageListChanges).length > 0
  );
};

/**
 * [isPropertyDirty]
 * Description : Returns true if the property has changed after the last refresh call , else false.
 * @param   {string}  context     Store context
 * @param   {string}  property    Property reference
 */
const isPropertyDirty = (context, property) => {
  // If the inputs are incorrect, say property is not dirty.
  if (!(context && property)) {
    return false;
  }
  const propertyObject = changedProperties[context] && changedProperties[context][property];
  if (propertyObject) {
    return propertyObject.previousValue !== propertyObject.value;
  }
  return false;
};

/**
 * returns true if any of the forms across contexts is dirty.
 */
const isDirty = () => {
  return Object.keys(changedProperties).some((context) => {
    return isContextDirty(context);
  });
};

/**
 * Resets the changedProperties map.
 * @param {*} context thread name
 */
export const resetChangedProperties = (context) => {
  if (context && changedProperties[context]) {
    changedProperties[context] = {};
  } else if (context === 'all') {
    changedProperties = {};
    _isComplexObjectMerged = {};
  }
  if (_isComplexObjectMerged[context]) {
    _isComplexObjectMerged[context] = false;
  }
};

/**
 * fetches the datapage for dependent fields
 * @param {*} payload payload including propertyName, classID, context and pagereference
 */
const handleCascadeChange = (payload) => {
  const changedProperty = Utils.getPropertyName(payload.propertyName);
  const { context, pageReference, value } = payload;
  CascadeManager.triggerCascade(context, `${pageReference}.${changedProperty}`, value);
  const dependencies = DatapageDependencyMap.getDependencies(context, `${pageReference}.${changedProperty}`);
  const resettingDependency = CascadeManager.getResetDependencies(context, `${pageReference}.${changedProperty}`);
  const datapagePayload = {
    context,
    datapages: {}
  };

  [...dependencies, ...resettingDependency].forEach((dependency) => {
    const propertyName = Utils.getPropertyNameFromDependency(dependency);
    if (dependency.mode === 'multiRecord') {
      const dependencyList = propertyName.split('.');
      const referencePropName = `.${dependencyList.pop()}`;
      const listPageReference = dependencyList.join('.');
      const stateMachine = StateMachineManager.getStateMachine(context, listPageReference, referencePropName);
      stateMachine.deleteAll(referencePropName);
    } else {
      dispatch({
        type: actions.SET_PROPERTY,
        payload: {
          reference: propertyName,
          value: '',
          context,
          isArrayDeepMerge: true,
          isImplicit: true
        }
      });
    }
  });
  dependencies.forEach((dependency) => {
    let dependencyPageReference = pageReference;
    if (!dependency.startsWith(pageReference)) {
      dependencyPageReference = '';
    }
    const dependentPropertySplitArray = dependency
      .replace(`${dependencyPageReference}`, '')
      .split('.')
      .filter((val) => !!val);
    dependentPropertySplitArray.pop();
    const parentPropertyName = dependentPropertySplitArray.join('.');
    const classID = getValue(`.${parentPropertyName}`, dependencyPageReference, context)?.classID || payload.classID;
    const metadata = getPropertyMetadata(dependency.split('.').pop(), classID);
    const { datasource } = metadata;
    const dataViewParameters = {};
    let hash = [];
    datasource.parameters.forEach((param) => {
      if (param?.value) {
        dataViewParameters[param.name] = AnnotationUtils.isProperty(param.value)
          ? getValue(
              `.${Utils.getFullPropertyReference([parentPropertyName, Utils.getPropertyName(param.value)])}`,
              dependencyPageReference,
              context
            )
          : param.value;

        if (
          dataViewParameters[param.name] === undefined ||
          dataViewParameters[param.name] === null ||
          dataViewParameters[param.name] === ''
        ) {
          dataViewParameters[param.name] = '';
          hash.push(PegaConstants.NULL_STRING);
        } else {
          hash.push(dataViewParameters[param.name]);
        }
      } else {
        dataViewParameters[param.name] = '';
        hash.push(PegaConstants.NULL_STRING);
      }
    });
    hash = datasource.name.concat(
      PegaConstants.UNDERSCORE_STRING,
      Utils.getHashedString(hash.join(PegaConstants.HASH_DELIMITER))
    );
    const { name } = datasource;
    const dataPageMetaData = getDataPageMetadata(name);
    datapagePayload.datapages[hash] = {
      mode: datasource.mode,
      propertyName: dependency,
      name: datasource.name,
      dataViewParameters,
      refreshStrategy: dataPageMetaData?.refreshStrategy
    };
  });
  if (dependencies.length) {
    dispatch({
      type: actions.FETCH_DATAPAGE,
      payload: datapagePayload
    });
  }
};

/**
 * Whenever redux triggers action, this api will
 * be called to handle Field-Change action.
 * @param {*} store the store that will be affected
 */

const propertyChangeMiddleware = (store) => (next) => (action) => {
  const { type, payload = {} } = action || {};

  if (type === actions.FIELD_CHANGE) {
    handleChange(payload);
    handleCascadeChange(payload);
    if (
      isPropertyDirty(payload.context, payload.reference) ||
      isPropertyDirtyPageInstruction(payload.context, payload.reference)
    ) {
      RefreshHelpers.refreshOnPropertyChange(payload);
    }
    PCore.getRefreshManager().triggerRefreshForType('PROP_CHANGE', payload.reference, payload.context);
  }

  if (type === actions.LIST_ACTION) {
    const { context, referenceList } = payload;
    RefreshHelpers.refreshOnPropertyChange({ context, pageReference: referenceList });
  }

  if (type === actions.SET_PROPERTY || type === actions.REPLACE_PROPERTY) {
    const { reference, isImplicit, context, skipDirtyValidation = false } = payload;
    if (!PageInstructionUtils.isPageInstruction(reference) && !skipDirtyValidation) recordChange(action);
    else if (!skipDirtyValidation) {
      _isComplexObjectMerged[`${context}`] = true;
    }
    if (payload.removePropertyFromChangedList || isImplicit) deleteRecordedChange(payload);
  }

  if (type === actions.CLEAR_TRANSIENT_DATA || type === actions.PURGE_TRANSIENT_DATA) {
    const { transientItemID: context, data } = payload;
    Utils.iterateLeafNodes(data, (path) => {
      deleteRecordedChange({
        context,
        value: '',
        reference: path.join('.')
      });
    });
  }

  const resetChangesActionArray = [
    actions.UPDATE_CONTAINER_ITEM,
    actions.REMOVE_CONTAINER_ITEM,
    actions.REPLACE_CONTAINER_ITEM
  ];

  if (type && resetChangesActionArray.includes(type)) {
    resetChangedProperties(payload.context);
    CascadeManager.purgeResetDependencies(payload.context);
    RefreshHelpers.removeViewsFromTargetList(payload.context);
    const childContexts = payload.childContainersContexts || [];
    childContexts.forEach((childContext) => {
      resetChangedProperties(childContext);
    });
    const transientItems = payload.transientItems || [];
    transientItems.forEach((transientItem) => {
      resetChangedProperties(transientItem);
    });
  }

  next(action);
};

export default {
  propertyChangeMiddleware,
  getChanges,
  recordChange,
  isDirty,
  isPropertyDirty,
  isContextDirty,
  resetChangedProperties,
  deleteRecordedChange,
  isComplexObjectMerged
};
