import { from } from 'rxjs';
import { map } from 'rxjs/operators';

import { resolveRestApi } from '../../router/app-router';
import { fetch } from '../../service_broker/fetch';
import { ACTION_SAVE } from '../../store/state/actions';
import { getSubmitData, processHeaders } from '../../store/state/epics/util';
import { logAndCatchError } from '../../utils/rxjs-utils';

export const executeSaveAssignmentRequest = (actionInPayload, state$) => {
  const { assignmentID, actionID, context } = actionInPayload;
  const { restApi, reqType } = resolveRestApi(ACTION_SAVE, { assignmentID, actionID });
  const { content } = getSubmitData(context);
  const reqHeaders = state$.value.data[context]?.caseInfo?.headers;

  return from(
    fetch(
      restApi,
      {
        method: reqType,
        body: content,
        headers: {
          'if-match': reqHeaders && reqHeaders.etag
        }
      },
      context
    )
  ).pipe(
    map((response) => {
      return {
        response: response.data,
        actionInPayload,
        headers: processHeaders(response.headers)
      };
    }),
    logAndCatchError(': Error ocurred during ajax call at saveAndCloseAssignment Epic at save action : ', context)
  );
};

export default { executeSaveAssignmentRequest };
