import constants from '../constants';
import { resolveRestApi } from '../router/app-router';
import { fetch as axiosFetch } from '../service_broker/fetch';
import { isAssignmentInCreateStage, isLocalAction } from '../utils/case-utils';
import { getContainerItemData } from '../store/state/container-utils';
import { GET_CASE_STAGES } from '../store/state/actions/index';
import { getParentContextName } from '../utils/context-utils';
import { getDataObject } from '../store/state/state-manager';

/**
 * Class to provide case information related API's
 */
class CaseInfo {
  constructor(c11nEnv) {
    this.c11nEnv = c11nEnv;
  }

  /**
   * Fetch case stages details for a given case id
   * @example <caption>In this example, the API returns the stages of the case with id "OPB1HW-SPACETRA-WORK RA-43001".</caption>
   * const stages = getPConnect().getCaseInfo().getCaseStages(OPB1HW-SPACETRA-WORK RA-43001);
   * @param {string} caseID The id of the case whose stages are to be fetched<br>
   * @param {string} context The current context where to render
   * @returns {object} - Case stages details of given case id
   */
  getCaseStages(caseID, contextName) {
    const reqPayload = {
      caseID
    };
    const { restApi, reqType } = resolveRestApi(GET_CASE_STAGES, reqPayload);
    return axiosFetch(
      restApi,
      {
        method: reqType
      },
      contextName
    ).then((response) => response.data.stages);
  }

  /**
   * Obtains the name of the case type.
   *
   * @example <caption>In this example, the API returns the name of the case type.</caption>
   * const caseTypeName = getPConnect().getCaseInfo().getCaseTypeName();
   *
   * @returns {string} Case type name
   * <br><br>
   * Eg: Registration
   */
  getCaseTypeName() {
    return this.c11nEnv.getValue(constants.CASE_INFO.CASE_TYPE_NAME);
  }

  /**
   * Returns Case name
   *
   * @example <caption>Example for getName()</caption>
   * const caseName = getPConnect().getCaseInfo().getName();
   *
   * @returns {string} Case name
   * <br><br>
   * Eg: Registration
   */
  getName() {
    return this.c11nEnv.getValue(constants.CASE_INFO.CASE_INFO_NAME);
  }

  /**
   * Returns Case class name
   *
   * @example <caption>Example for getClassName()</caption>
   * const className = getPConnect().getCaseInfo().getClassName();
   *
   * @returns {string} Case class name
   * <br><br>
   * Eg: ON8TTL-C11nGall-Work-CreateStageTest
   */
  getClassName() {
    return this.c11nEnv.getValue(constants.CASE_INFO.CASE_TYPE_ID);
  }

  /**
   * Returns Case key
   *
   * @example <caption>Example for getKey()</caption>
   * const caseKey = getPConnect().getCaseInfo().getKey();
   *
   * @returns {string} Case key
   * <br><br>
   * Eg: ON8TTL-C11NGALL-WORK CST-1
   */
  getKey() {
    return this.c11nEnv.getValue(constants.CASE_INFO.CASE_INFO_ID);
  }

  /**
   * Returns Case ID
   *
   * @example <caption>Example for getID()</caption>
   * const caseKey = getPConnect().getCaseInfo().getID();
   *
   * @returns {string} - Case ID
   * <br><br>
   * Eg: CST-1
   */
  getID() {
    const caseKey = this.getKey();
    if (caseKey) {
      const [, ID] = caseKey.split(' ');
      return ID || caseKey;
    }
    return undefined;
  }

  /**
   * Returns Assignment ID
   *
   * @example <caption>Example for getAssignmentID()</caption>
   * const assignmentID = getPConnect().getCaseInfo().getAssignmentID();
   *
   * @returns {string} Assignment ID
   * <br><br>
   * Eg: ASSIGN-WORKLIST ON8TTL-C11NGALL-WORK CST-51001!CREATEFORM_DEFAULT
   */
  getAssignmentID() {
    return this.c11nEnv.getValue(constants.CASE_INFO.ASSIGNMENT_ID);
  }

  /**
   * Returns Case actions info
   *
   * @example <caption>Example for getActions()</caption>
   * const actions = getPConnect().getCaseInfo().getActions();
   *
   *
   * @returns {Array} Case actions info
   * <br>
   * Eg: {
   *       "secondary": [
   *           {
   *              "jsAction": "cancelAssignment",
   *              "name": "Cancel",
   *               "actionID": "cancel"
   *           }
   *       ],
   *       "main": [
   *           {
   *               "jsAction": "finishAssignment",
   *               "name": "Next   ",
   *               "actionID": "next"
   *           }
   *       ]
   *   }
   */
  getActions() {
    return this.c11nEnv.getValue(constants.CASE_INFO.ACTION_BUTTONS);
  }

  /**
   * Returns current flow action ID that is rendered.
   *
   * @example <caption>Example for getActiveFlowActionID()</caption>
   * const activeFlowActionID = getPConnect().getCaseInfo().getActiveFlowActionID();
   *
   * @returns {string} Current flow action ID
   * <br><br>
   * Eg: MyFlowAction1
   */
  getActiveFlowActionID() {
    const actionID = this.c11nEnv.getValue(constants.CASE_INFO.ACTIVE_ACTION_ID);

    return actionID || this.c11nEnv.getValue(constants.CASE_INFO.ASSIGNMENTACTION_ID);
  }

  /**
   * Returns parent case info when the current assignment is in child case.
   *
   * @example <caption>Example for getParentCaseInfo()</caption>
   * const parentCaseInfo = getPConnect().getCaseInfo().getParentCaseInfo();
   *
   * @returns {object} Parent Case details of current child Case.
   * <br><br>
   * Eg:
   *  {
   *       "hasMoreAncestors":"false",
   *       "name":"Parent case",
   *       "links":{
   *          "open":{
   *             "rel":"other",
   *             "href":"/cases/OTVTGS-ZOMATO-WORK P-13002",
   *             "type":"GET",
   *             "title":"open parent case"
   *          }
   *       },
   *       "ID":"OTVTGS-ZOMATO-WORK P-13002",
   *       "content":{
   *          "pyStatusWork":"New",
   *          "classID":"OTVTGS-Zomato-Work-ParentCase",
   *          "pxUpdatedOperatorAndDateTime":"Gallery less than a minute ago",
   *          "pxUrgencyWork":"10",
   *          "pxCreateOperatorAndDateTime":"Gallery 38 minutes ago"
   *       }
   *    }
   */
  getParentCaseInfo() {
    return this.c11nEnv.getValue(constants.CASE_INFO.PARENTCASEINFO);
  }

  /**
   * Returns true if the view is in perform mode else false
   *
   * @example <caption>Example for isPerform()</caption>
   * const isPerform = getPConnect().getCaseInfo().isPerform();
   *
   * @returns {boolean} Flag to indicate perform mode or not
   *
   */
  isPerform() {
    return !!this.c11nEnv.getValue('nextAssignmentInfo');
  }

  /**
   * Returns true if the view is in review mode else false
   *
   * @example <caption>Example for isReview()</caption>
   * const isReview = getPConnect().getCaseInfo().isReview();
   *
   * @returns {boolean} Flag to indicate review mode or not
   *
   */
  isReview() {
    return !this.isPerform();
  }

  /**
   * Checks and returns true if the current assignment is in create stage.
   * @private
   * @returns {boolean} - Returns true if the current assignment is in create stage else false
   */
  isAssignmentInCreateStage() {
    const caseInfo = this.c11nEnv.getValue(constants.CASE_INFO.CASE_INFO);
    return isAssignmentInCreateStage(caseInfo);
  }

  /**
   * Checks and returns true if the current action is local action.
   * @private
   * @returns {boolean} - Returns true if the current action is local action else false
   */
  isLocalAction() {
    const caseInfo = this.c11nEnv.getValue(constants.CASE_INFO.CASE_INFO);
    return isLocalAction(caseInfo);
  }

  /**
   * Obtains the name of the current assignment's view in a case.
   *
   * @example <caption>Example for getCurrentAssignmentViewName()</caption>
   * const viewName = getPConnect().getCaseInfo().getCurrentAssignmentViewName();
   *
   * @returns {string} View name
   * <br><br>
   * Eg: EmployeeDetails
   */
  getCurrentAssignmentViewName() {
    const caseViewMode = this.c11nEnv.getValue('context_data.caseViewMode');
    let assignmentViewName = null;
    let _isAssignmentInCreateStage = false;

    if (caseViewMode === constants.PERFORM_MODE) {
      if (this.isLocalAction()) {
        return assignmentViewName;
      }

      if (this.isAssignmentInCreateStage()) {
        _isAssignmentInCreateStage = true;
      } else {
        // When cases with no create stage is launched in a modal, below ternary operator else block supports to return the view name.
        const target = this.c11nEnv.getTarget();
        const contextName = this.c11nEnv.getContextName();
        const containerItemData = getContainerItemData(target, contextName);
        const { config } =
          target && contextName ? containerItemData && containerItemData.view : this.c11nEnv.getMetadata();
        assignmentViewName = config?.type === constants.VIEW ? config?.name : null;
      }

      if (
        (assignmentViewName && assignmentViewName.startsWith(constants.PROPERTY_ANNOTATION)) ||
        _isAssignmentInCreateStage
      ) {
        assignmentViewName = this.c11nEnv.getValue(constants.CASE_INFO.VIEW_NAME);
      }
    }
    return assignmentViewName;
  }

  /**
   * Returns the refresh condition of current active case action
   *
   * @example <caption>Example for getActionRefreshConditions()</caption>
   * const refreshConditions = getPConnect().getCaseInfo().getActionRefreshConditions();
   *
   * @returns {object} Refresh Conditions
   * <br><br>
   * Eg:  refreshConditions: [{field: ".Datetime",event: "Changes"}, {field: ".Checkbox",event: "Changes"}]
   * @private
   */
  getActionRefreshConditions() {
    const activeActionID = this.c11nEnv.getValue(constants.CASE_INFO.ACTIVE_ACTION_ID);
    const parentContext = getParentContextName(this.c11nEnv.getContextName());
    let parentContainerData;
    if (parentContext) {
      parentContainerData = getDataObject(parentContext);
    }
    const caseActions = parentContainerData?.caseInfo?.availableActions;
    const currentContainerData = getDataObject(this.c11nEnv.getContextName());
    const refreshConditionsForCurrentContext = currentContainerData?.caseInfo?.refreshConditions;
    if (activeActionID) {
      if (caseActions) {
        const actionObj = caseActions.find((caseAction) => caseAction.ID === activeActionID);
        if (actionObj) {
          return actionObj.refreshConditions;
        }
        return refreshConditionsForCurrentContext;
      }
      return refreshConditionsForCurrentContext;
    }
    return this.c11nEnv.getValue(constants.CASE_INFO.ACTION_REFRESH_CONDITIONS);
  }

  /**
   * Returns Business ID
   * @example <caption>Example for getBusinessID()</caption>
   * const caseKey = getPConnect().getCaseInfo().getBusinessID();
   *
   * @returns {string} Business ID
   * <br><br>
   * Eg: CST-1
   */
  getBusinessID() {
    return this.c11nEnv.getValue(constants.CASE_INFO.BUSINESS_ID);
  }

  getAssignments() {
    return this.c11nEnv.getValue(constants.CASE_INFO.CASE_INFO)?.assignments;
  }

  getChildAssignments() {
    return this.c11nEnv.getValue(constants.CASE_INFO.CASE_INFO)?.childCases?.[0]?.assignments;
  }
}

export default CaseInfo;
