import * as action from './index';

/**
 * This function takes the clientData and updates it based on the changedData. It keeps all the fields present in
 * changedData and removes those fields which are not present in changedData.
 *
 * If there is a pagelist in the clientData which has been changed, then only that particular row will be updated
 * with changedData content. All other rows remain intact.
 *
 * @param {*} changedData -> represents the changed fields in UI(editable fields)
 * @param {*} clientData -> represents all the fields in UI(editable and readonly fields)
 */

const recurseObject = (changedData, clientData) => {
  const changedPagelists = [];
  for (const key in changedData) {
    if (typeof changedData[key] === 'object' && key.indexOf('[') !== -1) {
      const listName = key.substring(0, key.indexOf('['));
      const listIndex = key.substring(key.indexOf('[') + 1, key.indexOf(']'));
      clientData[listName].splice(listIndex, 1, changedData[key]);
      changedPagelists.push(listName);
    } else if (typeof changedData[key] === 'object') {
      if (!clientData[key]) {
        clientData[key] = Array.isArray(changedData[key]) ? [] : {};
      }
      recurseObject(changedData[key], clientData[key]);
    } else {
      clientData[key] = changedData[key];
    }
  }

  for (const key in clientData) {
    if (
      (Array.isArray(clientData[key]) && changedPagelists.indexOf(key) === -1) ||
      (!Array.isArray(clientData[key]) && !Object.hasOwnProperty.call(changedData, key))
    ) {
      delete clientData[key];
    }
  }
};

export const mergeData = (actionPayload, options = {}) => {
  const { serverPrecedenceFields = [] } = options;
  const { context, data: dataInPayload, isArrayDeepMerge, preserveClientChanges = false } = actionPayload;
  const dataChanges = getChanges(context, true);
  mergeListChanges(context, dataChanges);
  const contextData = getDataObject(context);
  let content = {};

  if (contextData && contextData !== undefined) {
    content = JSON.parse(JSON.stringify(contextData));
    recurseObject(dataChanges, content);
  }

  const clientChanges = { caseInfo: { content: { ...content?.caseInfo?.content } } };
  let data = dataInPayload;
  if (preserveClientChanges) {
    serverPrecedenceFields.forEach((val) => {
      Utils.deleteIn(clientChanges, `caseInfo.content.${val}`);
    });
    data = mergeDeep(dataInPayload, clientChanges, {
      isArrayDeepMerge: !isArrayDeepMerge
    });
  }

  return {
    type: action.MERGE_DATA,
    payload: {
      ...actionPayload,
      data
    }
  };
};

