export const removeContainerItem = (payload) => {
  const { context, containerItemID } = payload;
  let _context = context;
  if (!_context) {
    _context = getDataContextName(containerItemID);
  }
  payload.context = _context;
  payload.deleteContext = !isContainerItemContextBeingUsedInOtherContainers(containerItemID);
  payload.childContainers = getChildContainers(containerItemID);
  payload.childContainersContexts = getChildContainersContexts(containerItemID);
  payload.transientItems = getTransientItems([...payload.childContainersContexts, containerItemID]);
  payload.suggestionContexts = getSuggestionContexts(containerItemID);
  if (containerItemID === context) {
    payload.caseID = getValue(constants.CASE_INFO.CASE_INFO_ID, '', context);
    payload.RemoteSystemID = getValue(constants.CASE_INFO.REMOTESYSTEMID, '', context);
  }

  return {
    type: action.REMOVE_CONTAINER_ITEM,
    payload
  };
};

export const removeAllTransientItems = (payload) => {
  const { containerItemID } = payload;
  payload.transientItems = getTransientItems([...getChildContainersContexts(containerItemID), containerItemID]);
  return {
    type: action.REMOVE_ALL_TRANSIENT_ITEMS,
    payload
  };
};

export const removeTransientItem = (payload) => {
  const { transientItemID } = payload;
  payload.transientItems = [transientItemID];
  return {
    type: action.REMOVE_TRANSIENT_ITEM,
    payload
  };
};

