import services from '../services';
import PubSubUtils from '../utils/pubsub-utils';
import { publicConstants } from '../constants';

import { updateAuthInfo, updateHeaders, getAuthInfo } from './fetch';
import PegaAuth from './embed/auth';

/**
 * This method returns true if device is connected to network, else returns false
 * @function isOnline
 * @returns {boolean} true/false
 */

export const isOnline = () => {
  return navigator && navigator.onLine;
};

export const revokeTokens = async () => {
  return new Promise((resolve, reject) => {
    const authInfo = getAuthInfo();
    if (!authInfo?.tokenInfo) {
      reject(new Error('Missing token information.'));
      return;
    }
    const { access_token, refresh_token = null } = authInfo.tokenInfo;
    const { revoke: revokeUri } = authInfo.endPoints;
    const { client_id: clientId, client_secret: clientSecret } = authInfo;

    const hdrs = { 'content-type': 'application/x-www-form-urlencoded' };

    if (clientSecret) {
      
      hdrs.authorization = `Basic ${  window.btoa(`${clientId  }:${  clientSecret}`)}`;
    }
    const tknProps = ['access_token'];

    if (refresh_token) {
      tknProps.push('refresh_token');
    }
    tknProps.forEach(async (prop) => {
      const formData = new URLSearchParams();
      if (!clientSecret) {
        formData.append('client_id', clientId);
      }

      formData.append('token', prop === 'access_token' ? access_token : refresh_token);
      formData.append('token_type_hint', prop);
      await fetch(revokeUri, {
        method: 'POST',
        headers: new Headers(hdrs),
        body: formData.toString()
      });
    });
    updateHeaders({ Authorization: null });
    updateAuthInfo({ tokenInfo: null });
    resolve();
  });
};

export const getReAuthenticationURL = async (authInfo) => {
  const reauthUrlObj = {};
  if (authInfo?.authType === 'OAuth2.0') {
    const pegaAuthObj = new PegaAuth(authInfo, false);
    reauthUrlObj.reauthUrl = await pegaAuthObj.buildAuthorizeUrl();
    reauthUrlObj.redirectOrigin = pegaAuthObj.getRedirectUriOrigin();
    window.addEventListener('message', pegaAuthObj.authMessageHandler, false);
  } else {
    reauthUrlObj.reauthUrl = services.getServerURL() + (services.getReAuthURL() ? `${services.getReAuthURL()}` : '/?reAuth=true');
  }
  return reauthUrlObj;
};

export const redirectToLogin = (url, authInfo) => {
  getReAuthenticationURL(authInfo).then((reAutheticationURLObj) => {
    const props = {
      openReauthMessageModal: true,
      reAutheticationURL: reAutheticationURLObj.reauthUrl,
      url,
      redirectOrigin: reAutheticationURLObj.redirectOrigin
    };
    /* pub sub */
    PubSubUtils.publish(publicConstants.PUB_SUB_EVENTS.EVENT_REAUTH, props);
  });
};

export const getJWTServerDetails = (authInfo) => {
  let url = '';
  let config = {};
  let callback = {};

  if (authInfo?.authType === 'OAuth2.0') {
    url = authInfo.endPoints.token;
    const formData = new URLSearchParams();
    formData.append('client_id', authInfo.client_id);
    formData.append('grant_type', 'refresh_token');
    formData.append('refresh_token', authInfo.tokenInfo.refresh_token);
    config = {
      method: 'POST',
      body: formData.toString(),
      headers: {
        'content-type': 'application/x-www-form-urlencoded'
      }
    };
    callback = {
      success: (response, fetchConfig) => {
        const { data: updatedtokens } = response;
        const tokens = {
          access_token: updatedtokens?.access_token,
          refresh_token: updatedtokens?.refresh_token
        };
        updateAuthInfo({
          tokenInfo: {
            ...authInfo.tokenInfo,
            ...tokens
          }
        });
        updateHeaders({
          Authorization: `${updatedtokens?.token_type} ${updatedtokens?.access_token}`
        });

        fetchConfig.headers = {
          ...fetchConfig.headers,
          Authorization: `${updatedtokens?.token_type} ${updatedtokens?.access_token}`
        };

        const { onTokenRetrieval } = authInfo;
        // call the callback onTokenRetrieval to update pega-embed with new token info
        if (onTokenRetrieval && typeof onTokenRetrieval === 'function') {
          onTokenRetrieval(updatedtokens);
        }
      },

      failure: (portalUrl) => {
        if (!authInfo.popupReauth) {
          PubSubUtils.publish(publicConstants.PUB_SUB_EVENTS.EVENT_FULL_REAUTH, authInfo);
        } else {
          // we need to handle this
          return redirectToLogin(portalUrl, authInfo);
        }
      }
    };
  } else {
    url = `${services.getServerURL()}/updateSession`;
    config = {
      method: 'GET',
      headers: {}
    };
    callback = {
      failure: redirectToLogin
    };
  }
  return { url, config, callback };
};

export const isUpdateSessionRequest = (url, authInfo) => {
  return authInfo?.authType === 'OAuth2.0' ? url?.includes(authInfo.endPoints.token) : url?.includes('updateSession');
};

export const extractConfigFromError = (error) => {
  const { config } = error;
  const context = config.headers && config.headers.context;
  if (config.data) {
    config.body = config.data;
  }
  return { url: config.url, config, context };
};

export default {
  getJWTServerDetails,
  getReAuthenticationURL,
  isUpdateSessionRequest,
  redirectToLogin,
  revokeTokens,
  extractConfigFromError
};
