import Services from '../services';
import * as ActionCreators from '../store/state/actions/action-creators';
import { dispatch, getStore } from '../store/state/state-manager';
import * as ContextUtils from '../utils/context-utils';
import Utils from '../utils/utils';

import * as BrowserHistory from './browser-router';
import RouteParser from './route-parser';
import * as RoutingManager from './routing-manager';
import { getSemanticURLWithRelatedQueryParams } from './helper';
import { capitalizeFirstLetter, GTOKEN, hasNumber, isDynamicPattern, trimURL } from './utils';

/**
 * @file Implements Semantic URL Logic: consists of methods for handling application routing
 * @author WebWiz Team <WebWizDevelopmentTeam@pega.com>
 */

const VIEW = 'VIEW';
const PAGE = 'PAGE';
const ADD_TO_HISTORY = 'add';
const REPLACE_IN_HISTORY = 'replace';
const GLOBAL_QUERYPARAMS = ['portal', 'noPortal', 'preview', 'debug', 'devtool'];

let domain = '';
let defaultViewUrlpath = 'Home';
let applicationLabel = '';
let bRouting = true;

/**
 * this method is a toggle feature to enable or disable application routing.
 * @param {boolean} bEnable boolean to toggle app routing
 */
export const enableAppRouting = (bEnable) => {
  bRouting = bEnable;
  return bRouting;
};

/**
 * this method is a toggle feature to enable or disable application routing
 * @function isAppRoutingEnabled
 * @returns {boolean} isAppRoutingEnabled Flag
 */
export const isAppRoutingEnabled = () => {
  return bRouting;
};

/**
 * Get semantic url for given routeKey (ex: showView)
 * @function getSemanticURL
 * @param {*} routeKey - routeKey
 * @param {*} payload - payload
 * @param {*} queryParameters - queryParameters
 * @returns {*} semantic url or empty string
 */
export const getSemanticURL = (routeKey, payload, queryParameters) => {
  return RouteParser.getSemanticURL(routeKey, payload, queryParameters);
};

/**
 * Get restApi for given routeKey (ex: showView)
 * @function getRestEndPoint
 * @param {*} routeKey - routeKey
 * @param {*} payload - payload
 * @returns {*} restApi and reqType
 */
export const getRestEndPoint = (routeKey, payload) => {
  return RouteParser.getRestEndPoint(routeKey, payload);
};

/**
 * build and return complete rest url including host.
 * @function getRestEndPoint
 * @param {*} routeKey - routeKey
 * @param {*} payload - payload
 * @returns {*} Returns and object containing restApi and reqType properties if the passed routeKey is present in routing info else null.
 */
export const resolveRestApi = (routeKey, payload) => {
  let serverUrl = Services.getServerURL();
  const restObj = getRestEndPoint(routeKey, payload);
  if (!restObj) {
    return null;
  }

  if (!restObj.restApi.startsWith('/')) {
    restObj.restApi = `/${restObj.restApi}`;
  }

  if (serverUrl.endsWith('/')) {
    serverUrl = serverUrl.substring(0, serverUrl.length - 1);
  }
  restObj.restApi = serverUrl + restObj.restApi;
  return restObj;
};

/**
 * this method returns true if View is a Page, otherwise it returns false
 * @function _isViewAPage
 * @param {*} uiResources - uiResources information
 * @returns {boolean} isViewAPage Flag
 */
const _isViewAPage = (uiResources) => {
  if (uiResources && uiResources.root) {
    const { root } = uiResources;
    const { resources } = uiResources;
    const { views } = resources;
    const respRoot = root;

    const rootConfig = respRoot.config;
    const rootConfigType = rootConfig.type;
    const rootConfigName = rootConfig.name;

    if (rootConfigType.toUpperCase() === VIEW) {
      const view = views[rootConfigName];
      let viewObj = view;
      if (Array.isArray(view) && view.length > 0) {
        // take first element if view is an array
        [viewObj] = view;
      }
      const viewObjConfig = viewObj.config;
      if (viewObjConfig.type) {
        // type is optional and will only be there for page
        // and web_shell
        const viewType = viewObjConfig.type;
        return !!(viewObjConfig.urlPath && viewType.toUpperCase() === PAGE);
      }
    }
    return false;
  }
  return false;
};

/**
 * this method is used to read semantic URL/URL pattern from pxURLPath and return semantic URL Information
 * @function _getURLpathInfo
 * @param {*} uiResources - uiResources information
 * @param {*} responseData - response.data
 * @param {*} url - pxURLPath
 * @returns {*} an object with URLpath and addToHistory Flag
 */
const _getURLpathInfo = (uiResources, responseData, url = undefined) => {
  // pass third parameter response.data in _getURLpathInfo
  const { root, resources } = uiResources;
  const { views } = resources;
  const { config: rootConfig } = root;
  const historyAction = ADD_TO_HISTORY; // historyAction is set to addHistory
  let pxURLPath = '';
  if (url) {
    pxURLPath = url;
  } else {
    const view = views[rootConfig.name];
    let viewObj = view;
    if (Array.isArray(view) && view.length > 0) {
      // take first element if view is an array
      [viewObj] = view;
    }
    const viewObjConfig = viewObj.config;
    pxURLPath = viewObjConfig.urlPath;
  }

  const dynamicTokens = pxURLPath.match(GTOKEN);
  if (dynamicTokens) {
    const context = Utils.getIn(responseData, rootConfig.context);
    pxURLPath = pxURLPath.replace(dynamicTokens[0], context.pyID);
  }

  if (pxURLPath[0] === '/') {
    pxURLPath = pxURLPath.substring(1, pxURLPath.length);
  }

  return { pxURLPath, historyAction };
};

/**
 * this method appends global params like portal and noPortal if is present in the url. Don't append non global query params in the current url.
 * Eg: Query params of data objects when navigating from data object page to other pages like Home or MyWork
 * @function _appendGlobalParams
 * @param {*} queryParams - application label
 * @param {*} searchParamsObj - application name
 */
const _appendGlobalParams = (queryParams, searchParamsObj) => {
  for (const [key, value] of searchParamsObj.entries()) {
    if (!queryParams.has(key) && GLOBAL_QUERYPARAMS.includes(key)) {
      queryParams.append(key, value);
    }
  }
  return queryParams;
};

/**
 * this method builds semantic URL
 * @function _buildSemanticURL
 * @param {*} theDomain - domain name
 * @param {*} appName - application name
 * @param {*} urlPathObj - url path object
 */
const _buildSemanticURL = (theDomain, appName, urlPathObj) => {
  let semanticURL;
  let queryParams;

  if (trimURL(appName)) {
    semanticURL = `${theDomain}/${appName}`;
  } else {
    semanticURL = theDomain;
  }
  const urlPath = trimURL(urlPathObj.pxURLPath);
  if (urlPath) {
    semanticURL = `${semanticURL}/${urlPath}`;
  }

  try {
    queryParams = new URL(semanticURL).searchParams;
    semanticURL = semanticURL.includes('?') ? semanticURL.split('?')[0] : semanticURL;
  } catch (e) {
    console.log('Invalid url', e);
    return '';
  }

  const tokens = semanticURL.split('/');
  const lastToken = tokens[tokens.length - 1];
  const searchParamsObj = new URLSearchParams(BrowserHistory.getQueryParams());

  // Removing view name only if noPortal is not present in queryParams
  if (lastToken.toLowerCase() === defaultViewUrlpath.toLowerCase() && !searchParamsObj.has('noPortal')) {
    semanticURL = `${theDomain}/${appName}`;
  }

  if (!RouteParser.isDefaultPortal()) {
    const portalName = RouteParser.getPortalName();
    if (searchParamsObj.has('portal')) {
      searchParamsObj.set('portal', portalName);
    } else {
      searchParamsObj.append('portal', portalName);
    }
  }

  queryParams = _appendGlobalParams(queryParams, searchParamsObj);

  // Append existing query parameters to the semantic url
  if (queryParams.toString()) {
    semanticURL += `?${queryParams.toString()}`;
  }
  return semanticURL;
};

/**
 * this method builds semantic URL
 * @function _buildTitle
 * @param {*} applicationLabel - application label
 * @param {*} appName - application name
 * @param {*} semanticURL - semantic url
 */
const _buildTitle = (applicationLabel, appName, semanticURL) => {
  applicationLabel = applicationLabel && applicationLabel !== '' ? applicationLabel : RouteParser.getApplicationName();
  applicationLabel = capitalizeFirstLetter(applicationLabel);

  if (semanticURL === `${domain}/${appName}/`) {
    return { title: applicationLabel };
  }

  const url = new URL(semanticURL);
  const route = url.pathname.replace(`${appName}/`, '');
  const tokens = route.split('/');

  if (hasNumber(route)) {
    return { title: `${applicationLabel} - ${tokens[tokens.length - 1]}` };
  }
  return { title: `${applicationLabel} - ${tokens[0]}` };
};

/**
 * this method is used to call other methods and finally update/replace semantic url in browser
 * @function _resolveAndUpdateSemanticURL
 * @param {*} translatedData - { uiResources, data, options }
 * @param {*} isURLBookmarked - is URL Bookmarked?
 * @param {*} optionalInformation - {urlPathObjParam, infoParam, optionsParam}
 */
const _resolveAndUpdateSemanticURL = (
  translatedData,
  isURLBookmarked = false,
  { urlPathObjParam, infoParam, optionsParam }
) => {
  let urlPathObj;
  let info;
  let options;

  // If override has turned off use of dynamic Semantic URLs, just return

  // @ts-ignore
  if (window.PCore.getBehaviorOverride('dynamicSemanticUrl') === false) {
    return;
  }

  if (!isURLBookmarked) {
    const { uiResources, data } = translatedData;
    ({ options } = translatedData);
    if (urlPathObjParam && urlPathObjParam.pxURLPath && isDynamicPattern(urlPathObjParam.pxURLPath)) {
      urlPathObj = _getURLpathInfo(uiResources, data, urlPathObjParam.pxURLPath);
    } else {
      urlPathObj = urlPathObjParam || _getURLpathInfo(uiResources, data);
    }
    info = RouteParser.getEndPointInfo(urlPathObj.pxURLPath);
    urlPathObj.pxURLPath = info.urlPath;
  } else {
    urlPathObj = urlPathObjParam;
    info = infoParam;
    options = optionsParam;
  }

  const semanticURL = _buildSemanticURL(domain, RouteParser.getApplicationPath(), urlPathObj);
  /* Building title only for browser though this is not a required parameter and title is completely ignore by browsers.
        Title build here is not in sync with document title as we don't have enough information to build proper title here.
    */
  // Ref: https://github.com/whatwg/html/issues/2174
  const { title } = _buildTitle(applicationLabel, RouteParser.getApplicationPath(), semanticURL);
  const state = BrowserHistory.getStateForSemanticURL(info, { ...options, title });

  if (urlPathObj.historyAction === ADD_TO_HISTORY) {
    BrowserHistory.addToHistory(state, title, semanticURL);
  } else if (urlPathObj.historyAction === REPLACE_IN_HISTORY) {
    BrowserHistory.replaceHistory(state, title, semanticURL);
  }
};

/**
 * this method is wrapper function for _resolveAndUpdateSemanticURL
 * @function _routeProcessor
 * @param {*} translatedData - { uiResources, data, options }
 */
const _routeProcessor = (translatedData) => {
  let urlPathObjParam = null;
  if (translatedData.directUrlUpdation) {
    urlPathObjParam = { historyAction: ADD_TO_HISTORY, pxURLPath: '' };
    urlPathObjParam.pxURLPath = translatedData.semanticURL;
  }
  _resolveAndUpdateSemanticURL(translatedData, false, { urlPathObjParam, infoParam: null, optionsParam: null });
};

/**
 * this method is wrapper function for _buildSemanticURL
 * @param {*} urlPath - object containing pxURLPath
 * @returns {string} - url containing caseId in case of case link along with application domain and app name
 */
export const resolveSemanticURL = (urlPath) => {
  const urlPathObj = { pxURLPath: urlPath };
  return _buildSemanticURL(domain, RouteParser.getApplicationPath(), urlPathObj);
};

/**
 * this method is a public API exposed to other modules
 * @function parseResponse
 * @param {*} translatedData - { uiResources, data, options }
 */
export const parseResponse = (translatedData) => {
  const { uiResources } = translatedData;
  if (isAppRoutingEnabled() && (translatedData.directUrlUpdation || _isViewAPage(uiResources))) {
    _routeProcessor(translatedData);
  }
};

/**
 * this method builds action object from history state object
 * @function _buildActionObject
 * @param {*} obj - history state object
 * @returns {*} action Object
 */
const _buildActionObject = (obj) => {
  const returnObj = {
    routeEndpoint: obj.routeEndpoint,
    requestType: obj.requestType,
    payload: obj.payload,
    routeKey: obj.routeKey,
    options: null
  };
  if (obj.options) {
    returnObj.options = obj.options;
  }

  return returnObj;
};

/**
 * this method listens to browser back/forward event
 * @function publishForBrowserHistory
 * @param {*} event - popstate event
 */
export const publishForBrowserHistory = (event) => {
  const actionName = 'showHistory';
  const stateObj = BrowserHistory.getStateForBackForwardNav(event);

  if (stateObj) {
    dispatch(ActionCreators[actionName](_buildActionObject(stateObj)));
  }
};

/**
 * this method checks if browser url is a semantic url or not.
 * @function isSemanticURL
 * @returns {boolean} isSemanticURL Flag
 */
export const isSemanticURL = () => {
  const appName = RouteParser.getApplicationPath();
  const urlPath = BrowserHistory.getPathNameOfUrl();
  return appName !== urlPath;
};

/**
 * Get the restApi for the given view
 * @param   {string}    viewID - View Name passed in the queryparam
 * @param   {string}    viewClass - View Name passed in the queryparam
 * @returns {object}    restApi - restApi for the view
 */
export const getViewRestEndPoint = (viewID, viewClass) => {
  let restApiObj = { restApi: '' };
  try {
    if (isSemanticURL() && !viewClass) {
      const urlPath = BrowserHistory.getPathNameOfUrl(); // "prweb/app/AppName/caseType/{caseId}"
      const semanticURL = RouteParser.processUrl(urlPath, true); // "caseType/{caseId}"
      const matchedRoute = RouteParser._getMatchedRouteInRoutingTable(semanticURL);
      const routingInfo = RouteParser.getRoutingInfo();
      const tokens = RouteParser.processUrl(urlPath, false).split('/');
      const workID = tokens[tokens.length - 1];
      let { caseID } = routingInfo.routes[matchedRoute].payload;
      caseID = caseID.replace('{workID}', workID);
      restApiObj = RouteParser.getRestEndPoint('loadView', { caseID, viewID });
    } else {
      restApiObj = RouteParser.getRestEndPoint('showView', { page: viewID, pageClass: viewClass });
    }
  } catch (e) {
    // console.error("Semantic URL is not in case context");
  }
  return restApiObj;
};

/**
 * this method checks if browser url is a valid semantic url or not.
 * @function isValidSemanticURL
 * @returns {boolean} isSemanticURL Flag
 */
export const isValidSemanticURL = () => {
  if (isSemanticURL()) {
    const semanticURL = RouteParser.processUrl(BrowserHistory.getPathNameOfUrl(), true);
    const matchedRoute = RouteParser._getMatchedRouteInRoutingTable(semanticURL);

    return !!matchedRoute;
  }
  return false;
};

/**
 * API to return the defaultRoute name
 * @param {string} defaultViewLabel - Default view label configured in portal channel
 */
const buildDefaultRoute = (defaultViewLabel) => {
  const routingInfo = RouteParser.getRoutingInfo();
  // Server Logic: Initially routeName is built by removing spaces and joining. Later removing spaces and joining by -. Checking for both posibilities to find the routeName
  const spaceRemovedRouteName = defaultViewLabel.split(' ').join('');
  const hypenJoinedRouteName = defaultViewLabel.split(' ').join('-').toLowerCase();

  if (routingInfo.routes[spaceRemovedRouteName]) {
    return spaceRemovedRouteName;
  }

  if (routingInfo.routes[hypenJoinedRouteName]) {
    return hypenJoinedRouteName;
  }
  return defaultViewLabel;
};

/**
 * API to set defaultRoute name
 * @param {string} defaultRoute - Default route of portal channel
 */
export const setDefaultRoute = (defaultRoute) => {
  defaultViewUrlpath = defaultRoute;
};

/**
 * API to get defaultRoute name
 * @returns {string} defaultViewUrlpath - Default route of portal channel
 */
export const getDefaultRoute = () => {
  return defaultViewUrlpath;
};

/**
 * this method publish for browser bookmark or refresh in viewcontainer (primary)
 * @function publishForBrowserBookmark
 * @param {*} options - options
 */
export const publishForBrowserBookmark = (options) => {
  const { acName, context, defaultViewLabel } = options;
  const defaultRoute = buildDefaultRoute(defaultViewLabel);
  setDefaultRoute(defaultRoute);
  RouteParser.setDefaultRoute(defaultRoute);
  const newContext = ContextUtils.buildContextName(acName, context);
  const newTarget = RoutingManager.getResolvedTargetName(acName, context);

  const urlPathObjParam = { historyAction: REPLACE_IN_HISTORY, pxURLPath: '' };
  let infoParam;
  const optionsParam = {
    acName: newTarget,
    context: newContext,
    actionConfig: { context: newContext }
  };

  if (isSemanticURL()) {
    const url = getSemanticURLWithRelatedQueryParams(BrowserHistory.getPathNameWithQueryParams());
    infoParam = RouteParser.getEndPointInfo(url);
    urlPathObjParam.pxURLPath = infoParam.urlPath;
    if (typeof infoParam.payload?.pageClass !== 'undefined') {
      infoParam.payload.pageClass = getStore()?.getState()?.data?.app?.pyPortal?.classID;
    }

    const payload = {};
    const actionName = 'showHistory';
    payload.routeEndpoint = infoParam.routeEndpoint;
    payload.requestType = infoParam.requestType;
    payload.payload = infoParam.payload;
    payload.options = optionsParam;
    payload.routeKey = infoParam.routeKey;

    dispatch(ActionCreators[actionName](_buildActionObject(payload)));
  } else {
    infoParam = RouteParser.getEndPointInfo(defaultViewUrlpath);
    urlPathObjParam.pxURLPath = '';
  }
  _resolveAndUpdateSemanticURL(null, true, { urlPathObjParam, infoParam, optionsParam });
};

/**
 * This method initializes instance variables of Class
 * @function init
 * @param {*} routingInfo - Application Routing Table Information
 * @param {boolean} disableBrowserNavigation - Optional local mechanism to disable app navigation through browser history
 */
export const init = (routingInfo, disableBrowserNavigation = false) => {
  domain = routingInfo.domain;
  if (disableBrowserNavigation === false && isAppRoutingEnabled()) {
    BrowserHistory.addEventListenerForHistory(publishForBrowserHistory);
    BrowserHistory.addEventListenerForRefresh();
  }
  RouteParser.init(routingInfo, defaultViewUrlpath);
};

/**
 * This method merges the existing routingInfo with the passed routingInfo
 * @function addRoutes
 * @param {*} routingInfo - Application Routing Table Information
 */
export const addRoutes = routingInfo => {
  RouteParser.addRoutes(routingInfo);
};

/**
 * this method reads value from Application.pyLabel
 * @function initializeApplicationLabel
 * @param {*} applicationData - response.data
 */
export const initializeApplicationLabel = (applicationData) => {
  if (applicationData) {
    applicationLabel = applicationData.pyLabel;
  }
};

/**
 * this method update document title
 * @function updateDocumentTitle
 * @param {string} title - document title
 */
export const updateDocumentTitle = (title) => {
  BrowserHistory.updateDocumentTitle(title);
};
