import * as StateManager from '../../../store/state/state-manager';
import { listAction } from '../../../store/state/actions/action-creators';
import Utils from '../../../utils/utils';

class Move {
  static execute(contextName, target, fromIndex, toIndex, pageReference) {
    const instruction = {
      target: Utils.buildInstructionPageReference(pageReference, target),
      listMoveToIndex: toIndex,
      listIndex: fromIndex,
      instruction: 'MOVE'
    };

    StateManager.dispatch(
      listAction({
        listIndex: fromIndex - 1,
        listMoveToIndex: toIndex - 1,
        context: contextName,
        referenceList: `${pageReference}${target}`,
        instruction: 'MOVE'
      })
    );
    return instruction;
  }
}
export default Move;
