import { ACTION_OPENWORKBYHANDLE, ACTION_SHOWDATA, ACTION_SHOWVIEW } from '../store/state/actions';

import RouteParser from './route-parser';

/**
 * Provides API to work on semantic URLs.
 * These APIs are used for building semantic URLs
 * @module SemanticUrlUtils
 */

/**
 * This API obtains the resolved semantic URL for a route key. (example: openWorkByHandle)
 * @function getResolvedSemanticURL
 * @param {string} routeKey - action type supported by semantic URL utilities
 * @param {object} payload - unique identifiers to match with app routes to get semantic URLs
 * @param {object} params - dynamic URL query parameters and values
 * @returns {string} resolved semantic url or empty string
 * @example <caption>Example for getResolvedSemanticURL()</caption>
 * // In this example, the API takes the supported action from the route key,
 * // identifies the app route from the payload,
 * // assigns the value to the query parameter, and returns the resolved semantic URL.
 * const semanticUrlUtils = PCore.getSemanticUrlUtils();
 * const routeKey = semanticUrlUtils.getActins().ACTION_OPENWORKBYHANDLE;
 * const payload = {caseClassName: "ON8TTL-MyApp-Work-MyCase"};
 * const params = {workID: "C-007"}
 * const resolvedURL = semanticUrlUtils.getResolvedSemanticURL(routeKey, payload, params);
 */
const getResolvedSemanticURL = (routeKey, payload, params) => {
  return RouteParser.getResolvedSemanticURL(routeKey, payload, params);
};

/**
 * This API obtains actions supported by semantic URL utilities.
 * @returns {object} object of all the supported actions
 * @example <caption>Example for getActions()</caption>
 * // this example will return an object containing all the actions supported by semanticURLUtils
 * const semanticUrlUtils = PCore.getSemanticUrlUtils();
 * const { ACTION_OPENWORKBYHANDLE } = semanticUrlUtils.getActions();
 */
const getActions = () => {
  return { ACTION_OPENWORKBYHANDLE, ACTION_SHOWDATA, ACTION_SHOWVIEW };
};

export default {
  getResolvedSemanticURL,
  getActions
};
