import { ofType } from 'redux-observable';
import { map, ignoreElements } from 'rxjs/operators';
import isEqual from 'fast-deep-equal';

import * as actionType from '../actions';
import { ActionManagerInstance } from '../../../actions/manager';
import * as StateManager from '../state-manager';
import { getInitialState } from '../reducers';

// misc-epic is for Redux-Observable Epic's that don't really fit into
//  any of the other existing categories.

const resetStore = (action$) => {
  // Need to define these in this scope so they can be set/used
  //  in various parts of the processing.
  let actionMgrID = 0;

  return action$.pipe(
    ofType(actionType.RESET_STORE),
    map((action) => {
      // extract the Action Manager ID from the payload

      ({ actionMgrID } = action.payload);
      // need to return the Redux-Observable object to keep the chaining working...
      return action.payload;
    }),
    // this is the right place to note that the promise should resolve...

    map((action) => {
      const theCurrentStore = StateManager.getStore().getState();
      const theInitialStore = getInitialState();

      // Make sure the store has been reset
      //  and there was an actionMgrID provided (a real actionMgrID will be > 0 and not undefined)
      if (isEqual(theCurrentStore, theInitialStore) && actionMgrID > 0) {
        ActionManagerInstance.resolveActionByID(actionMgrID, {});
      } else {
        ActionManagerInstance.rejectActionByID(actionMgrID);
      }

      return [];
    }),
    ignoreElements()
  );
};

const miscEpics = [resetStore];

export default miscEpics;
