import Constants from '../constants';
import * as StreamObservable from '../observables/stream-observable';
import ContextProcessor from '../context/context-processor';

import Utils from './utils';
import { getTranslatedDXAPIResponse } from './dxapi-response-translator';
import loadComponents from './component-loader';

/**
 * Provides APIs to manage view metadata in rule store
 * @module ViewResources
 */

/**
 * This is used to update rule store with view metadata and loads respective components
 * @function
 * @example <caption> Example for updateViewResources</caption>
 * PCore.updateViewResources(dxAPIResponse)
 *
 * @param {object} dxAPIResponse - An object containing view metadata.
 */
export const updateViewResources = async (dxAPIResponse) => {
  const translatedDXAPIResponse = getTranslatedDXAPIResponse(dxAPIResponse);
  const components = Utils.getIn(translatedDXAPIResponse, Constants.COMPONENTS);
  StreamObservable.publishResponse(translatedDXAPIResponse, {}, true);
  await loadComponents(components);
};

/**
 * This is used to fetch view metadata from rule store
 * @function
 * @example <caption> Example for fetchViewResources</caption>
 * const viewMetaData = PCore.fetchViewResources("viewname", getPConnect(), "OPB1HW-MyApp-Work-MyCase");
 *
 * @param {string} viewName - Name of the view rule.
 * @param {string} c11nEnv  - Context object. For example: getPConnect()
 * @param {string} classID  - Class name of the case type
 * @returns {object} An object containing view metadata
 */
export const fetchViewResources = (viewName, c11nEnv, classID) => {
  return ContextProcessor.resolveView(viewName, c11nEnv, classID);
};
