import { ofType } from 'redux-observable';
import { of, empty, from } from 'rxjs';
import { ignoreElements, map, mergeMap } from 'rxjs/operators';

import { ActionManagerInstance } from '../../../actions/manager';
import TargetMap from '../../../actions/targetmap';
import RemoteCase from '../../../case/remote-case';
import Constants, { publicConstants } from '../../../constants';
import { getSemanticURL, resolveRestApi } from '../../../router/app-router';
import { getResolvedTargetName } from '../../../router/routing-manager';
import { fetch } from '../../../service_broker/fetch';
import { getTranslatedDXAPIResponse } from '../../../utils/dxapi-response-translator';
import { logAndCatchError } from '../../../utils/rxjs-utils';
import Utils from '../../../utils/utils';
import * as actionType from '../actions';
import * as actions from '../actions/action-creators';
import {
  CONTAINER_NAMES,
  getContainerItemName,
  getDataContextName,
  getTargetFromContainerItemID
} from '../container-utils';
import * as StateManager from '../state-manager';
import { publishCasePreviewEvent } from '../../../case/common-events';
import PubSubUtils from '../../../utils/pubsub-utils';
import HeaderProcessor from '../../../service_broker/header-processor';

import {
  addPreviewResetAction,
  getActionBasedOnContainerMode,
  getActionPayload,
  getActivationDetails,
  getContext,
  getDirtyDetails,
  isAddContainerAction,
  processDataToDispatchErrorAction,
  processHeaders,
  processViewResponse
} from './util';

const { RESOURCE_TYPES } = publicConstants;

const addActionsForLoadView = (containerItemName, containerTarget, payload, dataToNextChain, container) => {
  if (containerItemName) {
    dataToNextChain.actionsOut = [];
    dataToNextChain.actionsOut.push(
      actions.postProcess(actions.activateContainerItem, {
        target: containerTarget,
        containerItemID: containerItemName
      })
    );
  } else {
    dataToNextChain.actionsOut = actions.postProcess(actions.addContainerItem, { ...payload, container });
  }
};

const loadView = (action$) => {
  return action$.pipe(
    ofType(actionType.LOAD_VIEW),
    map((action) => action.payload),
    mergeMap((payload) => {
      const actionPayload = {
        caseID: payload.caseId,
        viewID: payload.viewName,
        caseClassName: payload.caseClassName,
        RemoteSystemID: payload.RemoteSystemID
      };
      const headers = {};

      if (actionPayload.RemoteSystemID) {
        headers.RemoteSystemID = actionPayload.RemoteSystemID;
      } else if (RemoteCase.isRemoteCase(actionPayload.caseClassName)) {
        const { RemoteSystemID } = RemoteCase.getRemoteCase(actionPayload.caseClassName);
        headers.RemoteSystemID = RemoteSystemID;
      }
      let { restApi: restUrl, reqType } = resolveRestApi('loadView', actionPayload);

      // If not in context of a work object then load like a page
      if (payload.viewContext === RESOURCE_TYPES.PAGE) {
        ({ restApi: restUrl, reqType } = resolveRestApi(actionType.ACTION_SHOWVIEW, {
          page: payload.viewName,
          pageClass: payload.pageClass
        }));
      }

      return from(
        fetch(
          restUrl,
          {
            method: reqType,
            headers
          },
          payload.context
        )
      ).pipe(
        map((response) => {
          const key = decodeURI(payload.caseId);
          let respJsonData = '';
          if (typeof response.data === 'string') {
            if (response.data) respJsonData = JSON.parse(response.data);
            else {
              console.error('response data is empty!!');
              respJsonData = {};
            }
          } else {
            respJsonData = response.data;
          }

          return {
            response: { ...respJsonData, key },
            actionConfig: payload,
            headers: processHeaders(response.headers),
            rootView: Utils.getIn(respJsonData, Constants.ROOT)
          };
        }),
        // This is the right way to detect a problem and call the promise's reject
        logAndCatchError(': Error ocurred during ajax call at loadView Epic : ', payload.context)
      );
    }),
    mergeMap(async (data) => {
      const dataToNextChain = {};
      if (data?.type === actionType.ERROR) {
        // processDataToDispatchErrorAction calls ActionManagerInstance.rejectActionByID
        dataToNextChain.actionsOut = processDataToDispatchErrorAction(data);
        return dataToNextChain;
      }
      try {
        dataToNextChain.actionMgrID = data?.actionConfig?.actionMgrID;
        dataToNextChain.rootView = data?.rootView;
        const { actionConfig, response, headers } = data;
        const { containerItemID, container, updateData } = actionConfig;
        const vcTargetName = container || TargetMap.getTargetViewContainerName(actionType.ACTION_SHOWVIEW);
        const target = getResolvedTargetName(vcTargetName, actionConfig.context);
        const context =
          container === CONTAINER_NAMES.PREVIEW && updateData
            ? actionConfig.context
            : getContext(target, vcTargetName, actionConfig.context, {
                buildNew: !!container
              });
        actionConfig.context = context;
        const options = { acName: target, context };
        let translatedDXAPIResponse = {};
        if (container === CONTAINER_NAMES.PREVIEW) {
          translatedDXAPIResponse = getTranslatedDXAPIResponse(response);
        } else {
          translatedDXAPIResponse = getTranslatedDXAPIResponse(response, {
            [publicConstants.STOPMERGEACTIONS]: true,
            [publicConstants.ACTIONS.DELETEAVIALACTIONS]: true
          });
        }

        translatedDXAPIResponse.directUrlUpdation = false;

        await processViewResponse(translatedDXAPIResponse, actionConfig, options);

        const commonInfo = {
          ...actionConfig,
          target,
          context,
          containerItemID: containerItemID || context,
          headers
        };
        const payload = getActionPayload(translatedDXAPIResponse, commonInfo);
        payload.resourceType = RESOURCE_TYPES.CASE;
        if (container && !updateData) {
          payload.caseViewMode = Constants.REVIEW_MODE;
          payload.caseViewMode = Constants.REVIEW_MODE;
          const containerTarget = getTargetFromContainerItemID(payload?.context);
          const containerItemName = getContainerItemName(containerTarget, payload.key);
          addActionsForLoadView(containerItemName, containerTarget, payload, dataToNextChain, container);
        } else {
          // to retain client changes (entered or edited by user), need to set preserveClientChanges flag as true
          payload.preserveClientChanges = true;
          dataToNextChain.actionsOut = actions.postProcess(actions.mergeData, payload);
        }
      } catch (err) {
        console.error(err);
        dataToNextChain.rootView = {
          type: 'error',
          payload: err
        };
      }
      const { caseId, context, viewName } = data.actionConfig;
      if (viewName === 'pyCasePreview') publishCasePreviewEvent({ caseId, context });
      return dataToNextChain;
    }),
    map(({ rootView, actionsOut, actionMgrID = 0 }) => {
      ActionManagerInstance.resolveActionByID(actionMgrID, rootView);
      return actionsOut;
    })
  );
};

const showPage = (action$) => {
  // Need to define these in this scope so they can be set/used
  //  in various parts of the processing.
  let actionMgrID = 0;
  let theReturn = [];

  return action$.pipe(
    ofType(actionType.SHOW_PAGE),
    map((action) => {
      // extract the Action Manager ID from the payload

      ({ actionMgrID } = action.payload);
      return action.payload;
    }),
    mergeMap((payload) => {
      const {
        debugInfo,
        context,
        viewName: page,
        className: pageClass,
        skipSemanticUrl = false,
        containerName = TargetMap.getTargetViewContainerName(actionType.ACTION_SHOWVIEW)
      } = payload;

      const headers = debugInfo && { ...HeaderProcessor.addDebugHeaders(debugInfo) };
      const target = getResolvedTargetName(containerName, context);
      let semanticURL = '';
      let removeActionPayload = {};
      let continueExecution = false;

      if (!skipSemanticUrl) {
        semanticURL = getSemanticURL(actionType.ACTION_SHOWVIEW, { page });
        if (target && semanticURL) {
          const { activate, activateContainerActionPayload } = getActivationDetails(RESOURCE_TYPES.PAGE, target, {
            semanticURL
          });

          if (activate) {
            return of({
              type: actionType.ACTIVATE_CONTAINER_ITEM,
              activateContainerActionPayload,
              actionConfig: payload
            });
          }
        }

        ({ continueExecution, removeActionPayload } = getDirtyDetails(target));

        if (!continueExecution) {
          ActionManagerInstance.rejectActionByID(actionMgrID);
          return empty();
        }
      }

      const { restApi: restUrl, reqType } = resolveRestApi(actionType.ACTION_SHOWVIEW, { page, pageClass });

      return from(
        fetch(
          restUrl,
          {
            method: reqType,
            headers
          },
          context
        )
      ).pipe(
        map((response) => {
          let respJsonData = {};
          if (typeof response.data === 'string') {
            respJsonData = JSON.parse(response.data);
          } else {
            respJsonData = response.data;
          }
          return {
            response: {
              ...respJsonData,
              key: semanticURL,
              semanticURL
            },
            actionConfig: payload,
            headers: processHeaders(response.headers),
            removeActionPayload,
            skipSemanticUrl
          };
        }),
        logAndCatchError(': Error ocurred during ajax call at showPage Epic : ', payload.context)
      );
    }),
    mergeMap(async (data) => {
      if (data && data.type === actionType.ERROR) {
        // processDataToDispatchErrorAction calls ActionManagerInstance.rejectActionByID
        theReturn.push(processDataToDispatchErrorAction(data));
        return theReturn;
      }

      const { type, actionConfig, skipSemanticUrl } = data;

      const containerName = actionConfig.containerName || TargetMap.getTargetViewContainerName(actionType.SHOW_PAGE);
      const target = getResolvedTargetName(containerName, actionConfig.context);

      if (type === actionType.ACTIVATE_CONTAINER_ITEM) {
        const {
          activateContainerActionPayload: { semanticURL, containerItemID }
        } = data;
        const context = getDataContextName(containerItemID);
        const options = { acName: target, context };
        await processViewResponse({ directUrlUpdation: true, semanticURL }, actionConfig, options, true);
        theReturn.push(actions.postProcess(actions.activateContainerItem, { target, containerItemID }));
      } else {
        const context = getContext(target, containerName, actionConfig.context);
        const { response, headers = {}, removeActionPayload } = data;
        const translatedDXAPIResponse = getTranslatedDXAPIResponse(response, {
          isCreateDataObject: actionConfig.isCreateDataObject
        });
        translatedDXAPIResponse.directUrlUpdation = !skipSemanticUrl;
        const options = { acName: target, context };
        await processViewResponse(translatedDXAPIResponse, actionConfig, options);
        const commonInfo = { target, context, containerItemID: context, headers, debugInfo: actionConfig.debugInfo };
        const payload = getActionPayload(translatedDXAPIResponse, commonInfo);
        payload.resourceType = publicConstants.PAGE;
        if (actionConfig.isCreateDataObject) {
          payload.resourceType = publicConstants.RESOURCE_TYPES.DATA;
          payload.resourceStatus = publicConstants.RESOURCE_STATUS.CREATE;
          delete payload.data.caseInfo;
        }
        const action = getActionBasedOnContainerMode(target, payload);
        if (removeActionPayload.containerItemID && isAddContainerAction(action)) {
          theReturn.push(
            actions.postProcess(actions.removeContainerItem, {
              containerItemID: removeActionPayload.containerItemID,
              target
            })
          );
        }
        theReturn.push(action);
        addPreviewResetAction(theReturn);
      }

      // Publish event for when a page is displayed
      PubSubUtils.publish(publicConstants.PUB_SUB_EVENTS.UI_EVENTS.SHOW_PAGE_DONE, {
        target,
        pageName: data.actionConfig.viewName,
        pageClass: data.actionConfig.className
      });

      return theReturn;
    }),
    map(() => {
      // this is the right place to note that the promise should resolve...
      ActionManagerInstance.resolveActionByID(actionMgrID);
      StateManager.dispatch(theReturn);
      theReturn = [];
    }),
    ignoreElements()
  );
};

const uiEpics = [loadView, showPage];

export default uiEpics;
