import Utils from '../utils/utils';

/**
 Theme format:

 "button": {
        "standard": {
            "States": "pega pc action-background-hover 
                      standard-action-background action-border-focus 
                      action-standard general link link-focus ",
            "Body": "pega pc standard-action-background action-standard link "
        },
        "simple": {
            "States": "pega pc action-background-hover borders link-focus ",
            "Body": "pega pc borders link "
        },
        ...
    }
*/
const theme = {};

/**
 * Updates the theme properties to current list.
 * @param {*} themeProps List of formats
 */
export const updateTheme = (themeProps) => {
  Utils.copy(themeProps, theme);
};

/**
 * Process the component specific formats and styles.
 * @param {*} contextObject component context object
 * @param {*} config raw config-uimetadata
 */
export const processTheme = (contextObject, config) => {
  let { type } = config;
  let format = (config.config && config.config.format) || 'standard';
  format = format.toString().toLowerCase().replace(' ', '-');

  if (config.type === 'label') {
    if (format === 'standard') format = 'standard-(label)';
    type = 'labels';
  }

  const themeObj = theme[type];

  if (themeObj) {
    contextObject.className = themeObj[format].Body;
  }
};
