import { from } from 'rxjs';
import { map } from 'rxjs/operators';

import { resolveRestApi } from '../../router/app-router';
import { fetch } from '../../service_broker/fetch';
import { getSubmitData, processHeaders } from '../../store/state/epics/util';
import { logAndCatchError } from '../../utils/rxjs-utils';

import { getActionNameForDxApiCall, getViewTypeForFinishAssignment } from './helper';

const FINISH_ASSIGNMENT_DXAPI_LOG_ERROR_MESSAGE = 'Error ocurred during ajax call at finishAssignment Epic : ';

export const executeFinishAssignmentRequest = (actionInPayload, options) => {
  const {
    assignmentID,
    actionID,
    caseID,
    context,
    additionalHeaders = {},
    isCaseWideAction,
    outcome
  } = actionInPayload;

  const { state$ } = options;

  const viewType = getViewTypeForFinishAssignment(actionInPayload);
  const actionName = getActionNameForDxApiCall(actionInPayload);

  const requestPayload = {
    assignmentID,
    actionID,
    caseID,
    viewType
  };

  let { restApi } = resolveRestApi(actionName, requestPayload);
  let { reqType } = resolveRestApi(actionName, requestPayload);
  if (viewType && restApi.indexOf('viewType=') < 0) {
    restApi = restApi.indexOf('?') < 0 ? `${restApi}?viewType=${viewType}` : `${restApi}&viewType=${viewType}`;
  }
  if (outcome && restApi.indexOf('outcome') < 0) {
    restApi = restApi.indexOf('?') < 0 ? `${restApi}?outcome=${outcome}` : `${restApi}&outcome=${outcome}`;
  }
  if (isCaseWideAction) {
    reqType = 'PATCH';
  }

  const { content, attachmentsExist } = getSubmitData(context);
  actionInPayload.attachmentsExist = attachmentsExist;
  const caseInfoHeaders = state$.value.data[context]?.caseInfo?.headers || {};
  const { etag } = caseInfoHeaders;
  const reqHeaders = {
    'if-match': etag,
    ...additionalHeaders
  };
  return from(
    fetch(
      restApi,
      {
        method: reqType,
        body: content,
        headers: reqHeaders
      },
      context
    )
  ).pipe(
    map((response) => {
      const { data, headers } = response;
      return {
        response: data,
        actionInPayload,
        headers: processHeaders({ ...reqHeaders, ...headers })
      };
    }),
    logAndCatchError(FINISH_ASSIGNMENT_DXAPI_LOG_ERROR_MESSAGE, context)
  );
};

export default executeFinishAssignmentRequest;
