import {
  ADD_MESSAGES,
  CLEAR_MESSAGES,
  REMOVE_CONTAINER_ITEM,
  ADD_CONTAINER_ITEM,
  UPDATE_CONTAINER_ITEM,
  REPLACE_CONTAINER_ITEM,
  LIST_ACTION
} from '../actions';
import { markAsErrorField, removeErrorFieldProperty, removeFormFieldsFromMap } from '../../../utils/form-handler';
import { publicConstants } from '../../../constants';

/**
 * Checks whether message is of type error
 * @param {object} payload payload recieved from action
 */
const isMessageTypeError = (payload) => {
  return (
    payload.type || (payload.messages && payload.messages[0].type) === publicConstants.MESSAGES.MESSAGES_TYPE_ERROR
  );
};

/**
 * Redux middleware to track the redux changes
 * and manipulate the FormFieldsMap object
 * of Form Handler
 * @param {*} store the store that will be affected
 */

const validationMiddleware = (store) => (next) => (action) => {
  const { type = '', payload = {} } = action || {};
  const { category, property, pageReference, context, referenceList } = payload;

  if (category !== 'HTTP' && type === ADD_MESSAGES && isMessageTypeError(payload)) {
    markAsErrorField(context, property, pageReference);
  } else if (category !== 'HTTP' && type === CLEAR_MESSAGES && isMessageTypeError(payload) && property !== undefined) {
    removeErrorFieldProperty(context, property, pageReference);
  } else if (
    category !== 'HTTP' &&
    [ADD_CONTAINER_ITEM, UPDATE_CONTAINER_ITEM, REMOVE_CONTAINER_ITEM, REPLACE_CONTAINER_ITEM].indexOf(type) !== -1
  ) {
    removeFormFieldsFromMap(context);
  } else if (type === LIST_ACTION) {
    removeErrorFieldProperty(context, property, referenceList);
  }
  next(action);
};

export default validationMiddleware;
