import axios from 'axios';
import { from, tap } from 'rxjs';

import { clearMessages, addMessages, getMessages } from '../messages/index';
import PubSubUtils from '../utils/pubsub-utils';
import constants from '../constants';

import fetchErrorCallback from './fetch-errorHandler';
import FetchStatus from './fetchStatus';
import { isUpdateSessionRequest } from './utils';
import HeaderProcessor from './header-processor';

export const UPDATE_SESSION = 'updateSession';
const activeRequestCount = {};
const fetchStatus = new FetchStatus();

let interceptor = {};
let defaultAuthInfo = {};
let defaultAuthConfig = {};
let httpErrorStatusCode = '';

export const setInterceptor = (interceptorObj) => {
  interceptor = interceptorObj;
};

const publishFetchSuccessEvent = (data) => {
  /* publishing event to notify fetch success */
  PubSubUtils.publish(constants.PUB_SUB_EVENTS.FETCH_SUCCESS, data);
};

/**
 * @typedef {object} Config
 * @property {string} method request method(GET, POST, PUT, PATCH, DELETE) to be used when making the request. Default is 'GET'
 * @property {object} headers custom headers to be sent
 * @property {object} body data to be sent as the request body. Only applicable for request methods 'PUT', 'POST', and 'PATCH'
 * @property {number} timeout number of milliseconds before the request times out. Default is '0' (no timeout)
 * @property {boolean} withCredentials indicates whether or not cross-site Access-Control requests. Default is 'false'
 * @property {boolean} withoutDefaultHeaders indicates whether or not to send default headers. Default is 'false'
 * @property {Function} onUploadProgress allows handling of progress events for uploads
 * @property {Function} onDownloadProgress allows handling of progress events for downloads
 * @property {object} signal represents a signal object that allows you to communicate with a fetch request to abort.
 */

/**
 * Seeks url and supporting configuration object for the request to be made.
 * If url is null or undefined, logging a console error.
 * Merge headers passed in config object with the default headers that service broker maintain.
 * Call axios with the prepared config object
 *
 * @param {string} url endpoint url
 * @param {Config} config {@link Config} object which supports additional data for network call
 * @param {*} context context object
 * @param {object} options - Optional, object containing extra parameter like doNotMergeHttpMessagesForStatusCode and cancelContext.
 * @param {string} options.doNotMergeHttpMessagesForStatusCode contains error status code - used to stop merging the error messsages
 * @param {string} options.cancelContext contains unique context string
 * @returns {*} returns the promise object returned by axios
 * @example fetch('http://www.domain.com/employees', {method: 'GET'})
 */
export const fetch = (url, config, context = 'root', options = {}) => {
  const {
    doNotMergeHttpMessagesForStatusCode = null,
    cancelContext = context,
    includeRemoteSystemIdIfPresent = true
  } = options;
  httpErrorStatusCode = doNotMergeHttpMessagesForStatusCode;
  if (url === null) {
    /*  to (url === null || url === undefined) */
    window.console.error('Error: url passed to Service Broker fetch is null (or) undefined');
  }
  const { body, signal, withoutDefaultHeaders = false, ...restConfig } = config;
  const { Authorization: _, ...headers } = config.headers || {}; // pick Authorization from Default headers
  if (!withoutDefaultHeaders) {
    HeaderProcessor.injectCoreAdditionalHeaders(headers, config.body, context, { includeRemoteSystemIdIfPresent });
  }

  if (cancelContext && activeRequestCount[cancelContext] >= 0) {
    activeRequestCount[cancelContext] += activeRequestCount[cancelContext];
  } else if (cancelContext && activeRequestCount[cancelContext] === undefined) {
    activeRequestCount[cancelContext] = 1;
  }
  /* merging headers from config object with default service broker object.
        Any duplicates? --> 'config' headers will override the default ones */
  let fetchHeaders = withoutDefaultHeaders
    ? { ...headers }
    : {
        ...HeaderProcessor.getDefaultHeaders(),
        ...HeaderProcessor.getRegisteredHeaders(),
        ...headers,
        context
      };
  if (
    (url && isUpdateSessionRequest(url, defaultAuthInfo) && fetchStatus.getUpdateSessionStatus()) ||
    !fetchStatus.getUpdateSessionStatus()
  ) {
    if (isUpdateSessionRequest(url, defaultAuthInfo) && defaultAuthInfo?.authType === 'OAuth2.0') {
      fetchHeaders = { ...headers }; // Skip context and defaultHeaders if Oauth2.0 token request
    }
    return from(
      axios({
        context,
        ...restConfig,
        url,
        data: body,
        ...(signal && typeof signal.aborted === 'undefined' && { cancelToken: signal }),
        ...(signal && typeof signal.aborted !== 'undefined' && { signal }),
        headers: { ...fetchHeaders, 'X-Constellation-App': 'Pega'}
      })
    )
      .pipe(
        tap(() => {
          const coreHeaders = HeaderProcessor.getCoreHeadersFromContext(fetchHeaders.context);
          if (coreHeaders) publishFetchSuccessEvent({ ...coreHeaders });
        })
      )
      .toPromise();
  }
  
  return waitForUpdateSession(url, config, context, fetchStatus);
};

export const waitForUpdateSession = (url, config, context, fetchStatus) => {
  return new Promise((resolve, reject) => {
    PubSubUtils.subscribe(
      UPDATE_SESSION,
      (statusObj) => {
        fetchStatus.setUpdateSessionStatus(!statusObj.updateStatus.length > 0);
        if (statusObj.updateStatus === 'success') {
          fetch(url, config, context)
            .then((response) => resolve(response))
            .catch((err) => reject(err));
        } else {
          reject(statusObj.updateStatus.error);
        }
      },
      UPDATE_SESSION
    );
  });
};

const updateActiveRequestCount = (context) => {
  if (activeRequestCount[context] > 0) {
    activeRequestCount[context] -= activeRequestCount[context];
    if (activeRequestCount[context] === 0) {
      delete activeRequestCount[context];
    }
  }
};

axios.interceptors.request.use(
  (config) => {
    if (interceptor.request) {
      config = interceptor.request(config);
    }
    return config;
  },
  (error) => Promise.reject(error)
);

axios.interceptors.response.use(
  (response) => {
    if (response.status === 202 && response.config.headers.context && response.data?.caseInfo?.errorMessage) {
      // handling for 202 response
      const errorMsgArr = [{ type: 'error', message: response.data.caseInfo.errorMessage }];
      let msgContext = response.config.headers.context;
      if (msgContext && msgContext.includes('/workarea')) {
        msgContext = msgContext.substring(0, msgContext.lastIndexOf('/workarea'));
      }
      addMessages({
        messages: errorMsgArr,
        category: 'HTTP',
        context: msgContext
      });
      return Promise.reject(response);
    }

    if (response.config.headers && response.config.headers.context) {
      if (!response.config.skipClearErrorMessages) {
        if (getMessages({ type: 'error', category: 'HTTP', context: response.config.headers.context }).length > 0) {
          clearMessages({
            type: 'error',
            category: 'HTTP',
            context: response.config.headers.context
          });
        }
        if (
          getMessages({
            type: 'error',
            category: response.config.headers.context === constants.APP.APP ? 'PAGE' : '',
            context: response.config.headers.context
          }).length > 0
        ) {
          clearMessages({
            type: 'error',
            category: 'PAGE',
            context: response.config.headers.context
          });
        }
      }
      updateActiveRequestCount(response.config.headers.context);
    }
    if (interceptor.response) {
      response = interceptor.response(response);
    }
    return response;
  },
  (error) => {
    if (error.config && error.config.headers && error.config.headers.context) {
      updateActiveRequestCount(error.config.headers.context);
    }

    if (
      error.response &&
      error.response.status &&
      error.response.status === 401 &&
      (!error.config || !isUpdateSessionRequest(error.config.url, defaultAuthInfo))
    ) {
      PubSubUtils.subscribe(
        UPDATE_SESSION,
        (statusObj) => {
          fetchStatus.setUpdateSessionStatus(!statusObj.updateStatus.length > 0);
        },
        UPDATE_SESSION
      );
    }

    return fetchErrorCallback(
      error,
      fetchStatus,
      defaultAuthInfo,
      error.response && error.response.status && error.response.status === Number(httpErrorStatusCode)
        ? httpErrorStatusCode
        : null
    );
  }
);

/**
 * this function returns number of active request for the given context
 *
 * @param {*} context context object for which the number of active request count is required
 * @returns {number} returns number of active request for the given context
 */
export const getActiveRequestCountPerContext = (context) => {
  return activeRequestCount[context];
};

/**
 * returns the axios cancelToken source object
 *
 * @returns {*} returns the axios cancelToken source object
 *
 */
export const getCancelTokenSource = () => {
  return axios.CancelToken.source();
};

/**
 * when promise is rejected, this function helps to check if it's because the request was cancelled
 *
 * @param {*} error error object when promise rejects
 * @returns {boolean} returns true/false by calling axios.isCancel for given error object
 */
export const isFetchCanceled = (error) => {
  return axios.isCancel(error);
};

export const updateHeaders = (headersObj) => {
  HeaderProcessor.setDefaultHeaders(headersObj);
};

export const updateAuthInfo = (authInfo) => {
  defaultAuthInfo = {
    ...defaultAuthInfo,
    ...authInfo
  };
};

export const updateAuthConfig = (config) => {
  defaultAuthConfig = {
    ...defaultAuthConfig,
    ...config
  };
};

export const getAuthConfig = () => {
  return { ...defaultAuthConfig };
};

export const getAuthInfo = () => {
  return { ...defaultAuthInfo };
};

export const resetFetchStatus = () => {
  fetchStatus.setUpdateSessionStatus(false);
};
