import { updateTheme } from '../theme/theme-processor';
import { updateStores } from '../rules/rules-store';
import { updateDeclareExpressions } from '../expression/expression-engine';
import { parseResponse } from '../router/app-router';
import Utils from '../utils/utils';
import constants from '../constants';

const publishMetadata = (resources) => {
  if (resources) updateStores(resources);
};

export const publishExpressions = (resources, options) => {
  if (resources.DeclareExpressions) {
    updateDeclareExpressions({
      [options.context]: resources.DeclareExpressions
    });
  }
};

const publishTheme = (resources) => {
  if (resources.Theme) {
    updateTheme(resources.Theme);
  }
};

/**
 * Publishes other data
 * @param {*} translatedDXAPIResponse - translatedDXAPIResponse information
 * @param {*} options - other params like context
 * @param {boolean} skipParseResponse - skip Parse Response
 */
export const publishResponse = (translatedDXAPIResponse, options, skipParseResponse = false) => {
  const resources = Utils.getIn(translatedDXAPIResponse, constants.RESOURCERS);
  if (resources) {
    publishMetadata(resources);
    publishExpressions(resources, options);
    publishTheme(resources);
  }
  if (!skipParseResponse) parseResponse({ ...translatedDXAPIResponse, options });
};
