import { combineReducers } from 'redux';

import Constants from '../../../constants';

import { dataReducer } from './data';
import routeReducer from './routing';

let initialState = {};

// TEMP handling contextdata here
export const setInitialState = (appPayload) => {
  const payLoadData = appPayload.Data || appPayload.data;
  const { uiResources = {} } = appPayload;
  const contextData = uiResources[Constants.CONTEXT_DATA] || appPayload[Constants.CONTEXT_DATA];
  initialState = {
    data: {
      [Constants.APP.APP]: {
        ...payLoadData,
        [Constants.CONTEXT_DATA]: contextData
      }
    },
    containers: {}
  };
};

export const getInitialState = () => {
  return initialState;
};

export const getRootReducer = (appPayload) => {
  setInitialState(appPayload);
  return combineReducers({
    data: dataReducer,
    containers: routeReducer
  });
};
