import Validator from '../validator';
import { localeUtils } from '../../locale/locale-utils';

const dateTimeValidator = (type, value) => {
  switch (value) {
    case 'invalid':
      return localeUtils.getLocaleValue(`Incorrect ${type}`, 'Messages');
    case 'incomplete':
      return localeUtils.getLocaleValue(`Incomplete ${type}`, 'Messages');
    default:
      return null;
  }
};

// register all varieties of date & time here
Validator.register('datetime', (value) => dateTimeValidator('DateTime', value));
Validator.register('date', (value) => dateTimeValidator('Date', value));
Validator.register('time', (value) => dateTimeValidator('Time', value));
