import ComponentRegistry from '../components-registry';

import AssetLoader from './asset-loader';

/**
 * Exposes APIs to load the script files and
 * css files from the script mapping from components_map.js file.
 */

const CUSTOM_COMPONENT_PATH = 'component';
const COMPONENT_LOADER = 'component-loader';

/* TODO: Versioning Support */
const loadedComponents = {};

/**
 * Loads the scripts from dependencies list.
 * It reads the dependency from components map and process
 * the scripts mapped in it.
 * Once all scripts loaded sucessfully, it call render.
 * @param {*} components components list eq., section...
 * @param {string} staticServer staticServer url
 * @returns {Promise<object>} promise
 */
const loadComponents = async (components, staticServer) => {
  const loader = AssetLoader.getLoader();
  const staticServerURL = staticServer || AssetLoader.staticServerURL;
  // "dynamicLoadComponents" flag === false means do NOT load components dynamically. Assume
  //  they're all loaded as part of initial bundle.
  // Also, test:ci doesn't resolve import pf PCoreInstance, so access via window.PCore
  if (!components || (window.PCore && window.PCore.getBehaviorOverride('dynamicLoadComponents') === false)) {
    return Promise.resolve();
  }

  if (AssetLoader.getLoader(COMPONENT_LOADER)) {
    return await AssetLoader.getLoader(COMPONENT_LOADER)(components || [], staticServerURL);
  }

  const assetPromises = [];
  components.forEach((name) => {
    /* To avoid loading mutiple times */
    if (loadedComponents[name]) {
      return;
    }
    const component = ComponentRegistry.getComponent(name);
    if (!component) {
      // Not in registry check for custom components
      const srcUrl = `${AssetLoader.getConstellationServiceUrl()}/v860/${AssetLoader.getAppAlias()}/${CUSTOM_COMPONENT_PATH}/${name}.js`;
      assetPromises.push(loader(srcUrl, AssetLoader.getTypes.SCRIPT));
      return;
    }
    const scripts = component.scripts || [];
    scripts.forEach((src) => {
      const srcUrl = src.startsWith('http') ? src : staticServerURL + src;
      assetPromises.push(loader(srcUrl, AssetLoader.getTypes.SCRIPT));
    });

    const styles = component.styles || [];
    styles.forEach((href) => {
      assetPromises.push(loader(staticServerURL + href, AssetLoader.getTypes.STYLE));
    });

    loadedComponents[name] = true;
  });
  return Promise.all(assetPromises);
};

export default loadComponents;
