import { getActiveContainerItemName, getContainerData } from '../store/state/container-utils';
import * as StateManager from '../store/state/state-manager';
import { resolveRestApi } from '../router/app-router';
import { fetch as axiosFetch } from '../service_broker/fetch';
import { ACTION_OPEN_CASE_WIDE_LOCAL_ACTION } from '../store/state/actions';

/**
 * helper functions for case
 */
class CaseUtils {
  /**
   * It Checks whether the Case is Active or Inactive
   * @param {string} key case key
   * @param {string} target target
   * @returns {boolean} returns true if the Case is Active else returns false
   * @public
   * @static
   * @example <caption>Example for isCaseActive.</caption>
   * PCore.getCaseUtils().isCaseActive('PEGACS-WORK-INTERACTION I-383039', app/primary');
   * returns true if the currently opened Case has key PEGACS-WORK-INTERACTION I-383039
   */
  static isCaseActive(key, target) {
    if (!key || !target) return false;
    const containerData = getContainerData(target);
    if (containerData) {
      const activeItemName = getActiveContainerItemName(target);
      const { items = {} } = containerData;
      return items[activeItemName] && items[activeItemName].key === key;
    }
    return false;
  }

  /**
   * This API returns a promise which when resolved returns edit metadata of a case.
   * @param {string} caseID - The caseID on which edit is being performed
   * @param {string} context - context name eq., primary

  * @example  <caption>In the example below, the API returns the edit metadata o a case.</caption>
  *
  * const caseID = "METORG-VEHICLEMANAGER-WORK V-7222";
  * const context = "app/primary_1"
  * PCore.getDataApiUtils().getCaseEditMetadata(caseID, context);
  *
  * //The response of this api will be like shown below.
  * {
  *  uiResources: {},
  *  data: {
  *    caseInfo: {
          "caseTypeID": "MetOrg-VehicleManager-Work-VehiclePurchase",
          "owner": "abc@xyz.com",
          "availableActions": [],
          "lastUpdatedBy": "abc@xyz.com",
          "sla": {},
          "content": {
            "classID": "MetOrg-VehicleManager-Work-VehiclePurchase",
            "VehicleUsage": "",
            "Year": "2013",
            "VehicleType": "e7faf92e-e6b0-4793-b59f-e406a2abdb75",
            "NeededBy": "20200605T181445.999 GMT",
            "Model": "458 Italia",
            "Make": "Ferrari",
            "RequestingDepartment": "52724c8d-54b9-4819-851d-3765098adebb"
          },
          "createdBy": "abc@xyz.com",
          "createTime": "2020-05-28T20:05:41.235Z",
          "urgency": "10",
          "name": "2013 Ferrari 458 ITALIA",
          "stages": [],
          "ID": "METORG-VEHICLEMANAGER-WORK V-7222",
          "lastUpdateTime": "2020-05-28T20:05:41.541Z",
          "stageID": "PRIM3",
          "stageLabel": "Delivery",
          "status": "New"
  *    }
  *  }
  * }
  *
  *
  * @returns {object} - edit metadata of a case.
  *
  * @function
  *
  */
  static getCaseEditMetadata = (caseID, context) => {
    const reqPayload = {
      caseID,
      viewID: 'pyEdit'
    };

    const { restApi, reqType } = resolveRestApi('loadView', reqPayload);

    return axiosFetch(
      restApi,
      {
        method: reqType
      },
      context
    );
  };

  /**
   * This API returns a promise which when resolved indicates that acquiring the lock of the case is successful.
   * @param {string} caseID - The caseID (or pzInsKey) to which the lock has to be acquired.
   * @param {string} context - context name eq., primary
   *
   * @example  <caption>In the example below, the API returns the successful lock to edit the case.</caption>
   *
   * const caseID = "METORG-VEHICLEMANAGER-WORK V-7222";
   * const context = "app/primary_2";
   * PCore.getDataApiUtils().getCaseEditLock(caseID, context);
   *
   * // The response of this api will be like shown below.
   * {
   *  uiResources: {},
   *  data: {}
   * }
   *
   * Having above structure in the response indicates successfull lock acquiring.
   *
   *
   * @returns {object} - complete metadata of the case.
   *
   * @function
   *
   */
  static getCaseEditLock = (caseID, context) => {
    const reqPayload = {
      caseID,
      actionID: 'pyUpdateCaseDetails'
    };

    const { restApi, reqType } = resolveRestApi(ACTION_OPEN_CASE_WIDE_LOCAL_ACTION, reqPayload);

    return axiosFetch(
      `${restApi}?viewType=page`,
      {
        method: reqType
      },
      context
    );
  };

  /**
   * This API returns a promise which when resolved indicates that updation of the case data is successful.
   * @param {string} caseID - The caseID (or pzInsKey) to which the lock has been acquired.
   * @param {object} changeSet - The object which holds the updated details in the case.
   * @param {string} eTag - PCore.getDataApiUtils().getCaseEditLock() response headers contain the "etag" header, pass that value as eTag here.
   * @param {string} context - context name eq., primary

  * @example  <caption>In the example below, the API returns the object indicating successful updation of case data.</caption>
  *
  * const caseID = "METORG-VEHICLEMANAGER-WORK V-7222";
  * const changeSet = { "METORG-VEHICLEMANAGER-WORK V-7222": { Make: "New Value" } };
  * const eTag = "20200831T114802.686 GMT";
  * PCore.getDataApiUtils().updateCaseEditFieldsData(caseID, changeSet, eTag);
  *
  * // The response of this api will be like shown below.
  * {
  *  uiResources: {},
  *  data: {}
  * }
  *
  * Having above structure in the response indicates successfull lock acquiring.
  * {
      "data": {
          "caseInfo": {
            "caseTypeID": "MetOrg-VehicleManager-Work-VehiclePurchase",
            "owner": "reactuser",
            "availableActions": [],
            "lastUpdatedBy": "mohaa5",
            "assignments": [],
            "sla": {},
            "createdBy": "reactuser",
            "createTime": "2020-06-08T12:10:08.813Z",
            "urgency": "10",
            "name": "Vehicle Purchase",
            "stages": [],
            "ID": "METORG-VEHICLEMANAGER-WORK V-10001",
            "lastUpdateTime": "2020-09-01T05:52:54.225Z",
            "stageID": "PRIM5",
            "stageLabel": "Request",
            "status": "New"
          }
        },
        "confirmationNote": "Thank you! The next step in this case has been routed appropriately."
      }
  *
  * @returns {object} - Returns the object with caseInfo and confirmationNote.
  *
  * @function
  *
  */
  static updateCaseEditFieldsData = (caseID, changeSet, eTag, context) => {
    const reqPayload = {
      caseID,
      actionID: 'pyUpdateCaseDetails'
    };

    const { restApi } = resolveRestApi(ACTION_OPEN_CASE_WIDE_LOCAL_ACTION, reqPayload);

    const newData = changeSet[caseID];

    return axiosFetch(
      `${restApi}?viewType=form`,
      {
        method: 'PATCH',
        body: {
          content: {
            ...newData
          }
        },
        headers: {
          'if-match': eTag
        }
      },
      context
    );
  };

  /**
   * returns the caseMessages(confirmationNote)
   * @param {string} containerItemID containerItemID
   * @returns {Array} caseMessages
   * @public
   * @static
   * PCore.getCaseUtils().getCaseMessages('app/primary_2/workarea_2');
   * returns case messages(confirmation note) array if the conatiner has any
   */
  static getCaseMessages = (containerItemID) => {
    return StateManager.getDataObject(containerItemID)?.caseMessages;
  };
}

export default CaseUtils;
