import { applyMiddleware, compose, createStore } from 'redux';
import { createEpicMiddleware } from 'redux-observable';
import { reduxBatch } from '@manaflair/redux-batch';

import { PCoreInstance } from '..';

import { getRootReducer } from './state/reducers';
import rootEpic from './state/epics';
import ChangeObserver from './state/middlewares/property-change-middleware';
import ValidationMiddleware from './state/middlewares/validation-middleware';
import DataPageMiddleware from './state/middlewares/datapage-middleware';
import PubSubMiddleware from './state/middlewares/pubsub-middleware';
import DataReferenceMiddleware from './state/middlewares/datareference-middleware';
import { setStore, getStore } from './state/state-manager';

const epicMiddleware = createEpicMiddleware();

const configureStore = (translatedDXAPIResponse) => {
  const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

  if (getStore() !== null) {
    // Mashup case can call renderApp multiple times for "new" run-through
    // of the flow. renderApp calls configureStore which normally wants to
    // create the Store. However, in mashup case, we want to use the store
    // we already have but reset it to its initial state.
    // console.log(`store already exists! Call PCoreInstance.resetStore()`);
    PCoreInstance.resetStore();
  } else {
    const store = createStore(
      getRootReducer(translatedDXAPIResponse),
      composeEnhancers(
        reduxBatch,
        applyMiddleware(
          ChangeObserver.propertyChangeMiddleware,
          ValidationMiddleware,
          DataPageMiddleware,
          DataReferenceMiddleware,
          epicMiddleware,
          PubSubMiddleware
        ),
        reduxBatch
      )
    );

    epicMiddleware.run(rootEpic);
    setStore(store);
  }
};

export default configureStore;
