import { invokeRestApi } from '../rest-client';

/**
 * Use the API in the TagUtils module to handle the tags of a case.
 * @module TagUtils
 */
const ADD_TAGS = 'addTagsToCase';
const DELETE_TAG = 'deleteCaseTag';
const GET_TAGS = 'getCaseTags';

/**
 * Obtains the view to render the list of cases associated with a specific tag.
 * @function
 * @static
 * @example <caption>In this example, the API obtains the view that renders the list of cases associated with a specific tag in a case whose ID is W-102.</caption>
 * PCore.getTagUtils().getTaggedCases('ORG-MYAPP-WORK W-102', 'app/primary_1')
 * .then(tags => {
 *    // tags array
 * }).catch(err => {
 *   // errors
 * });
 *
 * @param     {string}    caseID    The ID of the case pointing to pzInsKey value from where the view must be downloaded.
 * @param     {object}    c11nEnv   The name of the context where the API is being called.
 * @returns   {Promise<object>}     A Promise that resolves to an object.
 */
const getTaggedCases = (caseID, c11nEnv) => {
  const queryPayload = { caseID, viewID: 'pyTaggedCases' };
  return invokeRestApi('loadView', { queryPayload }, c11nEnv.getContextName())
    .then((response) => response?.data)
    .catch((error) => {
      c11nEnv.reportError(': Error ocurred during ajax call at fetchTaggedcases API : ', error.response);
    });
};

/**
 * Obtains the tags of a specific case.
 * @function
 * @static
 * @example <caption>In this example, the API obtains the tags of a case whose ID is W-102.</caption>
 * PCore.getTagUtils().getTags('ORG-MYAPP-WORK W-102', 'app/primary_1')
 * .then(tags => {
 *    // tags array
 * }).catch(err => {
 *   // errors
 * });
 * @param     {string}    caseID    The ID of the case pointing to pzInsKey value whose tags must be obtained.
 * @param     {object}    c11nEnv   The name of the context where the API is being called.
 * @returns   {Promise<object>}     A Promise that resolves to an object.
 */
const getTags = (caseID, c11nEnv) => {
  const queryPayload = {
    caseID
  };
  return invokeRestApi(GET_TAGS, { queryPayload }, c11nEnv.getContextName())
    .then((response) => response?.data?.tagsList)
    .catch((error) => {
      c11nEnv.reportError(': Error ocurred during ajax call at fetchTags API : ', error.response);
    });
};

/**
 * Adds tags to a specific case.
 * @function
 * @static
 * @example <caption>In this example, the API adds the tags, Tag1 and Tag2 to the case whose ID is W-102.</caption>
 * PCore.getTagUtils().postTags('ORG-MYAPP-WORK W-102', [{ Name : 'Tag1'}, {Name : 'Tag2'}], 'app/primary_1')
 * .then(() => {
 *   // success
 * }).catch(err => {
 *    // Error handling
 * });
 *
 * @param     {string}    caseID    The ID of the case pointing to pzInsKey value to which the tags must be added.
 * @param     {object[]}  tags      The list of tags that must be added to the case.
 * @param     {object}    c11nEnv   The name of the context where the API is being called.
 * @returns   {Promise<object>}     A Promise that resolves to an object.
 */
const postTags = (caseID, tags, c11nEnv) => {
  const queryPayload = {
    caseID
  };
  const reqBody = JSON.stringify({
    tags
  });
  return invokeRestApi(ADD_TAGS, { body: reqBody, queryPayload }, c11nEnv.getContextName())
    .then((response) => response?.data?.results)
    .catch((error) => {
      c11nEnv.reportError(': Error ocurred during ajax call at postTags API : ', error.response);
    });
};

/**
 * Removes a tag from a specific case.
 * @function
 * @static
 * @example <caption>In this example, the API removes the tag whose ID is Tag1 from the case whose ID is W-102.</caption>
 * PCore.getTagUtils().removeTag('ORG-MYAPP-WORK W-102', 'Tag1', 'app/primary_1')
 * .then(() => {
 *    // success
 * }).catch(err => {
 *   // errors
 * });
 *
 * @param     {string}    caseID    The ID of the case pointing to pzInsKey value from which the tag must be removed.
 * @param     {string}    tagID     The ID of the tag that is to be removed from the case.
 * @param     {object}    c11nEnv   The name of the context where the API is being called.
 * @returns   {Promise<object>}     A Promise that resolves to an object.
 */
const removeTag = (caseID, tagID, c11nEnv) => {
  const queryPayload = {
    caseID,
    tagID
  };
  return invokeRestApi(DELETE_TAG, { queryPayload }, c11nEnv.getContextName()).catch((error) => {
    c11nEnv.reportError(': Error ocurred during ajax call at removeTag API : ', error.response);
  });
};

/**
 * Gets URL for opening tags landing page in infinity application
 * @function
 * @static
 * @private
 * @example <caption>In this example, the API get the URL for tags Landing Page.</caption>
 * PCore.getTagUtils().getTagsLandingPageURL('app/primary_1')
 * .then(() => {
 *    // success
 * }).catch(err => {
 *   // errors
 * });
 *
 * @param     {object}    c11nEnv   The name of the context where the API is being called.
 * @returns   {Promise<object>}     A Promise that resolves to an object.
 */
const getTagsLandingPageURL = (c11nEnv) => {
  const dataViewName = 'D_pxGetTagsLandingPageURL';
  return PCore.getDataPageUtils()
    .getPageDataAsync(dataViewName, c11nEnv.getContextName(), {}, { invalidateCache: true })
    .catch((error) => {
      c11nEnv.reportError(': Error ocurred during ajax call getTagsLandingPageURL API : ', error.response);
    });
};

export default { getTaggedCases, getTags, postTags, removeTag, getTagsLandingPageURL };
