import { ofType } from 'redux-observable';
import { map, mergeMap, switchMap } from 'rxjs/operators';
import { of } from 'rxjs';

import * as StateManager from '../../store/state/state-manager';
import * as actions from '../../store/state/actions/action-creators';
import {
  addActionsToParentContext,
  addPreviewResetAction,
  getActionPayload,
  processDataToDispatchErrorAction,
  processViewResponse
} from '../../store/state/epics/util';
import { getTranslatedDXAPIResponse } from '../../utils/dxapi-response-translator';
import {
  getCaseInfoID,
  getContainerNameFromContainerItemID,
  isErrorActionType,
  isParentAndChildContextHoldingSameItem,
  updateKeyAndFlowName,
  setLastRefreshTimeProperty,
  promiseHandler
} from '../common-helper';
import { publishAssignmentSubmissionDoneEvent } from '../common-events';
import { getDataContextName, getTargetFromContainerItemID } from '../../store/state/container-utils';
import { publicConstants } from '../../constants';

import { NAVIGATE_TO_STEP } from './action-creator';
import { executeNavigateToStepRequest } from './async-request';

const { RESOURCE_TYPES } = publicConstants;

export const navigateToStep = (action$, state$) => {
  let actionMgrID = 0;
  let actionsOut = [];

  return action$.pipe(
    ofType(NAVIGATE_TO_STEP),
    switchMap((action) => {
      ({ actionMgrID } = action.payload);

      return of(action.payload).pipe(
        mergeMap((actionInPayload) => {
          return executeNavigateToStepRequest(actionInPayload, { state$ });
        }),
        mergeMap(async (chainedData) => {
          if (chainedData && isErrorActionType(chainedData.type)) {
            actionsOut.push(processDataToDispatchErrorAction(chainedData));
            return actionsOut;
          }

          const { actionInPayload, response, headers } = chainedData;
          const { containerItemID, context = getDataContextName(containerItemID), isModalAction } = actionInPayload;

          const target = getTargetFromContainerItemID(containerItemID);
          const containerName = getContainerNameFromContainerItemID(containerItemID);

          // this is the current container info from where the action got triggered, we shouldn't alter this object
          const currentContainerInfo = Object.freeze({
            target,
            containerItemID,
            context,
            containerName
          });

          const translatedDXAPIResponse = getTranslatedDXAPIResponse(response);
          // [TODO]: we shouldn't use "acName" anywhere, setting this value..since app-router uses this logic
          const options = { acName: target, ...currentContainerInfo };

          await processViewResponse(translatedDXAPIResponse, actionInPayload, options);

          const payload = getActionPayload(translatedDXAPIResponse, {
            ...currentContainerInfo,
            headers
          });
          payload.doDeleteCaseSummary = actionInPayload.doDeleteCaseSummary;
          payload.resourceType = RESOURCE_TYPES.ASSIGNMENT;
          // updating key and flowName
          updateKeyAndFlowName(payload, translatedDXAPIResponse);

          // Attachments widget using it
          setLastRefreshTimeProperty(payload.data, actionInPayload);
          const stages = translatedDXAPIResponse.data.caseInfo.stages;
          publishAssignmentSubmissionDoneEvent({ caseID: getCaseInfoID(translatedDXAPIResponse), isModalAction, stages });

          if (isParentAndChildContextHoldingSameItem(containerItemID)) {
            addActionsToParentContext(payload, actionsOut);
          }

          // updating the container item with previos assignment details
          actionsOut.push(actions.postProcess(actions.updateContainerItem, payload));

          // reset preview container is active
          addPreviewResetAction(actionsOut);
          return actionsOut;
        }),
        map(() => {
          StateManager.dispatch(actionsOut);
          promiseHandler(actionsOut, actionMgrID);
          actionsOut = [];
          return actionsOut;
        })
      );
    })
  );
};

export default navigateToStep;
