import Utils from '../utils/utils';
import * as StateManager from '../store/state/state-manager';
import { listAction } from '../store/state/actions/action-creators';
import Constants, { publicConstants } from '../constants';
import { getNormalizedPropertyName } from '../utils/form-handler';
import FieldUtils from '../fields/index';
import { clearMessages } from '../messages';

import StateMachine from './state-machine/state-machine';
import StateMachineManager from './state-machine-manager';

const instructions = {};
const pageInstructionReferences = {};
const distinctReferenceList = {};


/**
 * Provides APIs to insert/update/delete/reorder for ListActions.
 * @module ListActions
 */

/**
 * This function helps in creating the context entry in instructions if it not exists before.
 * @param {object} c11nEnv - pConnect object for the list
 * @param {string} target - target for which the pageinstruction to be stored
 * @private
 */
const createContextEntry = (c11nEnv, target, pageReference) => {
  const contextName = c11nEnv.getContextName();
  if (!instructions[contextName]) {
    instructions[contextName] = {};
  }
  if (!instructions[contextName][target]) {
    instructions[contextName][target] = { content: [] };
  }

  if (!pageInstructionReferences[contextName]) {
    pageInstructionReferences[contextName] = [];
    distinctReferenceList[contextName] = [];
  }
  if (!StateMachineManager.isStateMachineExist(contextName, pageReference, target)) {
    StateMachineManager.setStateMachine(
      contextName,
      target,
      pageReference,
      new StateMachine(target, pageReference, contextName)
    );
  }
};

/**
 * This function helps in modifying target in nested page list
 * Example referenceList = ".Questionsets[0].Questions" return = ".Questionsets(1).Questions"
 * @param {string} referenceList - reference string showing the path of target property
 * @returns {string} updated referenceList in case of nested page list
 * @private
 */
const handleTargetInNestedPageList = (referenceList) => {
  return FieldUtils.formatPageReference(referenceList);
};

/**
 * This function helps in adding the page-instruction references
 * @param {string} contextName
 * @param {string} reference
 * @private
 */
export const addPageInstructionReferences = (contextName, reference) => {
  const _pageInstructionReferences = pageInstructionReferences[contextName];
  const _distinctReferenceLists = distinctReferenceList[contextName];

  const _ref = getNormalizedPropertyName(reference);
  const _distinctRef = _ref?.substring(0, _ref.indexOf('['));

  if (_distinctReferenceLists?.indexOf(_distinctRef) < 0) {
    distinctReferenceList[contextName].push(_distinctRef);
  }
  if (_pageInstructionReferences?.indexOf(_ref) < 0) {
    pageInstructionReferences[contextName].push(_ref);
  }
};

/**
 * This function helps in retrieving the reference lists for the context passed
 * @param {string} contextName - name of the context- e.g. app/primary_1
 * @returns {object} page instructions
 * @private
 */
export const getPageInstructionReferences = (contextName) => {
  return pageInstructionReferences[contextName] || [];
};

/**
 * This function helps in clearing the reference lists for the context passed
 * @param {string} contextName - name of the context- e.g. app/primary_1
 * @returns {void}
 * @private
 */
export const clearPageInstructionReferences = (contextName) => {
  delete pageInstructionReferences[contextName];
};

/**
 * This function sets isVisible flag for target related  pageInstructions
 * @param {object} c11nEnv - pConnect object for the list
 * @param {boolean} isVisible - isVisible flag
 * @returns {void}
 * @private
 */
export const setVisibility = (c11nEnv, isVisible = true) => {
  const contextName = c11nEnv.getContextName();
  const { selectionMode, selectionList, renderMode, referenceList } = c11nEnv.getComponentConfig();
  let target;
  // usecase for multiselect
  if (selectionMode === Constants.LIST_SELECTION_MODE.MULTI && selectionList) {
    target = selectionList;
  } else if (renderMode === 'Editable' && referenceList) {
    // usecase for Editable list and fieldgroup
    target = c11nEnv.getStateProps().referenceList;
  }
  if (target) {
    target = handleTargetInNestedPageList(target);
    const stateMachine = StateMachineManager.getStateMachine(contextName, '', `${c11nEnv.getPageReference()}${target}`);
    if (stateMachine) stateMachine.visibility = isVisible;
  }
};

/**
 * This function helps in retrieving the instruction for the context passed
 * @param {*} contextName - context name - e.g. app/primary_1
 * @returns {object} page instructions
 * @private
 */
export const getInstructions = (contextName) => {
  return StateMachineManager.buildPageInstructionsByContext(contextName);
};

/**
 * This function helps in retrieving the changed instruction for the context passed
 * @param {string} contextName - context name - e.g. app/primary_1
 * @returns {object} page instructions
 * @private
 */
export const getChangedInstructions = (contextName) => {
  return StateMachineManager.buildPageInstructionsByContext(contextName, true);
};

/**
 * This function helps in clearing the instruction for the context passed
 * @param {string} contextName - context name - e.g. app/primary_1
 * @returns {void}
 */
export const clearInstructions = (contextName) => {
  StateMachineManager.clearStateMachines(contextName);
  delete instructions[contextName];
};

/**
 * This API helps to to add the page context
 * @param {Array} referenceList
 * @param {string} pageReference
 * @param {Object} payload
 * @param {Number} index
 * @private
 */
const addCorrectPageContext = (referenceList, pageReference, payload, index) => {
  let [, nestedProperty] = pageReference.split(`${referenceList}[${index}]`);
  if (nestedProperty !== '' && nestedProperty !== undefined) {
    nestedProperty = nestedProperty.startsWith('.') ? nestedProperty.substring(1) : nestedProperty;
    Object.keys(payload).forEach((key) => {
      payload[`${nestedProperty}.${key}`] = payload[key];
      delete payload[key];
    });
  }
};

/**
 * This function helps in adding INSERT page instruction for referenced list in context
 * @param {object} payload - property values e.g. { prop1: "value", prop2: "value-2"  }
 * @param {number} index - list index of the table row.
 * @param {object} c11nEnv - pConnect object for the list
 * @param {string} pageRef - pageReference
 * @returns {void}
 * @private
 */
const insert = (payload, index, c11nEnv, pageRef, options) => {
  const { skipStateUpdate = false } = options;
  const contextName = c11nEnv.getContextName();
  const referenceList = c11nEnv.getReferenceList();
  const pageReference = pageRef || c11nEnv.getPageReference();
  const target = handleTargetInNestedPageList(referenceList);
  createContextEntry(c11nEnv, target, pageReference);
  addPageInstructionReferences(contextName, `${referenceList}[${index}]`);

  addCorrectPageContext(referenceList, pageReference, payload, index);
  const actionPayload = {
    instruction: Constants.LIST_ACTIONS.INSERT,
    content: {
      ...Utils.unflatten(payload)
    }
  };
  const formSubmitContent = JSON.parse(JSON.stringify(actionPayload)); // cloning the JSON
  delete formSubmitContent.content.classID;

  const stateMachine = StateMachineManager.getStateMachine(contextName, pageReference, target);
  stateMachine.insert(payload, index + 1, skipStateUpdate);
};

/**
 * This returns the reference property name of the list based on the pageReference.
 * @param {string} pageReference - fully qualified name of the table
 * @param {number} index - row index of the table
 * @returns {string} returns the reference property name of the table
 */
const getReferenceListFromPageReference = (pageReference, index) => {
  const startIndex = pageReference.indexOf(publicConstants.CONTENT) + publicConstants.CONTENT.length;
  const endIndex = pageReference.lastIndexOf(`[${index}]`);
  return pageReference.substring(startIndex, endIndex);
};

/**
 * This function helps in adding UPDATE page instruction for referenced list in context
 * @param {object} payload - property values e.g. { prop1: "value", prop2: "value-2"  }
 * @param {object} c11nEnv - pConnect object for the list
 * @param {number} index - list index
 * @param {object} options - options
 * @returns {void}
 * @private
 */
const update = (payload, c11nEnv, index = 0, options = {}) => {
  const { skipStateUpdate = true, pageRef } = options;
  const contextName = c11nEnv.getContextName();
  let referenceList = c11nEnv.getReferenceList();
  const pageReference = pageRef || c11nEnv.getPageReference();

  if (!referenceList) {
    referenceList = getReferenceListFromPageReference(pageReference, index);
  }
  const target = handleTargetInNestedPageList(referenceList);

  createContextEntry(c11nEnv, target, pageReference);
  addPageInstructionReferences(contextName, `${referenceList}[${index}]`);

  addCorrectPageContext(referenceList, pageReference, payload, index);

  const stateMachine = StateMachineManager.getStateMachine(contextName, pageReference, target);
  stateMachine.update(payload, index + 1, skipStateUpdate, options);
};

/**
 * This updates the value of the property present inside the table
 * @param {string} propertyName - name of the property whose value has to be set.
 * @param {string} value - value of the property whose value has to be set.
 * @param {object} c11nEnv - pConnect object for the table.
 * @param {object} options
 * @private
 */
const updateProperty = (propertyName, value, c11nEnv, options) => {
  const pageReference = c11nEnv.getPageReference();

  const { stateMachineTarget } = StateMachineManager.getStateMachineObjByPath(
    `${pageReference}${propertyName}`,
    c11nEnv.getContextName(),
    true
  );
  if (!stateMachineTarget) {
    let parentPageReference = pageReference.substring(0, pageReference.lastIndexOf('['));
    const parenTarget = parentPageReference.substring(parentPageReference.lastIndexOf('.'));
    parentPageReference = parentPageReference.substring(0, parentPageReference.lastIndexOf('.'));

    if (parentPageReference && parenTarget) createContextEntry(c11nEnv, parenTarget, parentPageReference);
  }

  StateMachineManager.executeStateMachine(
    c11nEnv.getContextName(),
    `${pageReference}${propertyName}`,
    value,
    true,
    options
  );
};

/**
 * This function helps in adding DELETE page instruction for referenced list in context
 * @param {number} index - row index of the
 * @param {object} c11nEnv - pConnect object for the list
 * @param {string} pageRef - pageReference
 * @returns {void}
 * @private
 */
const deleteEntry = (index, c11nEnv, pageRef) => {
  const contextName = c11nEnv.getContextName();
  const referenceList = c11nEnv.getReferenceList();
  const pageReference = pageRef || c11nEnv.getPageReference();
  const deletePageRef = `${pageReference + referenceList}[${index}]`;
  const target = handleTargetInNestedPageList(referenceList);

  clearMessages({
    type: publicConstants.MESSAGES.MESSAGES_TYPE_ERROR,
    pageReference: deletePageRef,
    context: contextName,
    isListEntry: true
  });

  createContextEntry(c11nEnv, target, pageReference);
  addPageInstructionReferences(contextName, `${referenceList}[${index}]`);
  const stateMachine = StateMachineManager.getStateMachine(contextName, pageReference, target);
  stateMachine.delete(index + 1);
  const unConnectedStateMachines = StateMachineManager.getUnConnectedStateMachines(contextName) || {};
  Object.keys(unConnectedStateMachines).forEach((key) => {
    if (key.includes(`${pageReference}${target}[${index}]`)) {
      unConnectedStateMachines[key] = null;
    }
  });
};

/**
 * This function helps in adding REORDER page instruction for referenced list in context
 * @param {number} fromIndex - from list index
 * @param {number} toIndex  - to list index
 * @param {object} c11nEnv - pConnect object for the list
 * @returns {void}
 * @private
 */
const reorder = (fromIndex, toIndex, c11nEnv) => {
  const contextName = c11nEnv.getContextName();
  const referenceList = c11nEnv.getReferenceList();
  const pageReference = c11nEnv.getPageReference();
  const target = handleTargetInNestedPageList(referenceList);

  createContextEntry(c11nEnv, target, pageReference);
  addPageInstructionReferences(contextName, `${referenceList}[${toIndex}]`);
  const stateMachine = StateMachineManager.getStateMachine(contextName, pageReference, target);
  stateMachine.move(fromIndex + 1, toIndex + 1);
};

/**
 * This function checks if input and referenceProp are self reference case
 * @param {string} param property to check
 * @param {string} referenceProp reference to check against
 * @returns {boolean} true/false based on if self referencing
 * @private
 */
const isSelfReferencedProperty = (param, referenceProp) => {
  const [, parentPropName] = param.split('.');
  const referencePropParent = referenceProp?.split('.').pop();
  return parentPropName === referencePropParent;
};

/**
 * This function returns the resolved parameter values for content
 * @param {object} parameters datasource parameters
 * @param {object} data data containing the parameter values
 * @param {string} referenceProp reference to check against
 * @returns {object} resolved parameter values
 * @private
 */
const getParameterValues = (parameters, data, referenceProp) => {
  const values = {};
  Object.values(parameters).forEach((param) => {
    if (isSelfReferencedProperty(param, referenceProp)) {
      let propNames = Utils.getPropertyName(param);
      propNames = propNames.substr(propNames.indexOf('.') + 1).split('.');
      if (propNames.length > 1) {
        let resolvedValue = Utils.getIn(data, propNames) || null;
        if (!resolvedValue) {
          const str = `!P!${propNames.join(':')}`;
          resolvedValue = Utils.getIn(data, str) || null;
        }
        Utils.setIn(values, propNames, resolvedValue);
      } else {
        const resolvedValue = Utils.getIn(data, propNames);
        values[propNames[propNames.length - 1]] = resolvedValue;
      }
    }
  });
  return values;
};

/**
 * This function returns the parameters with configured additional properties if any
 * @param {object} additionalProperties configured parameters
 * @param {object} propertyMetadata configured property metadata
 * @param {boolean} isMultiSelectMode multi-select mode or not
 * @returns {object} returns parameters and additional properties if any.
 * @private
 */
const getAdditionalProperties = (additionalProperties, propertyMetadata, isMultiSelectMode) => {
  if (propertyMetadata?.descriptors?.length && !isMultiSelectMode) {
    propertyMetadata.descriptors.forEach((property) => {
      let propNames = Utils.getPropertyName(property);
      propNames = propNames.substr(propNames.indexOf('.') + 1).split('.');
      additionalProperties[propNames.join('.')] = property;
    });
  }
};

/**
 * This function check and clear the required error message(validatemessage prop)
 * @param {object} c11nEnv c11Env object
 * @param {object} fullReference of the field
 * @param {string} required flag to identify if list selection is required
 * @param {string} isMultiSelectMode to check if multiselectmode is enabled
 * @param {string} referencePropName reference prop name
 * @private
 */
const checkAndClearRequiredErrorMessage = (c11nEnv, fullReference, required, isMultiSelectMode, referencePropName) => {
  const rowsSelected = c11nEnv.getValue(fullReference) || {};
  if (required && Object.keys(rowsSelected).length > 0) {
    c11nEnv.clearErrorMessages({
      property: isMultiSelectMode ? referencePropName : c11nEnv.getStateProps().value
    });
  }
};

/**
 * updates the redux store on selection of row(s)
 * @param {object} payload - row(s) context object
 * @param {object} c11nEnv - c11nEnv object
 * @returns {void}
 * @private
 */
const setSelectedRows = (payload, c11nEnv, options) => {
  return new Promise((resolve) => {
    const { skipPageInstructions = false } = options;
    const { selectionMode, selectionList, required } = c11nEnv.getConfigProps();
    const isMultiSelectMode = selectionMode === Constants.LIST_SELECTION_MODE.MULTI;
    const contextName = c11nEnv.getContextName();
    let pageReference = c11nEnv.getPageReference();
    let referencePropName = isMultiSelectMode
      ? selectionList.substring(1)
      : pageReference.substring(pageReference.lastIndexOf('.') + 1);
    const rawViewMetadata = c11nEnv.getRawMetadata();
    let contextPageReference = null;
    const hasNoContextRendering =
      rawViewMetadata.config.dataRelationshipContext &&
      rawViewMetadata.type === 'SimpleTableSelect' &&
      rawViewMetadata.config?.selectionMode === 'single';
    if (hasNoContextRendering) {
      referencePropName = rawViewMetadata.config.dataRelationshipContext;
      contextPageReference = pageReference.concat('.').concat(referencePropName);
      pageReference = contextPageReference;
    }
    const fullReference = isMultiSelectMode ? `${pageReference}${selectionList}` : pageReference;
    const propertyMetadata = isMultiSelectMode
      ? c11nEnv.getFieldMetadata(`${referencePropName}`)
      : c11nEnv.getCurrentPageFieldMetadata(contextPageReference);

    const {
      datasource: { parameters = {} }
    } = propertyMetadata;

    const additionalProperties = { ...parameters };
    getAdditionalProperties(additionalProperties, propertyMetadata, isMultiSelectMode);

    if (isMultiSelectMode) {
      let target = selectionList;
      target = handleTargetInNestedPageList(target);
      const rows = c11nEnv.getValue(fullReference) || [];
      createContextEntry(c11nEnv, target, pageReference);
      const actions = [];
      let startIndex = rows.length;
      payload.forEach((row) => {
        const content = getParameterValues(parameters, row, referencePropName);
        // displayPropertyObject holds displayProperty if it is sent thorugh payload
        // Ex- Multiselect sends display property so that it can be maintained in redux along with unique key
        const displayPropertyObject = {};
        if (Object.prototype.hasOwnProperty.call(row, 'displayProperty')) {
          const displayProperty = row.displayProperty;
          displayPropertyObject[displayProperty] = row[displayProperty];
        }
        const contentEntries = Object.entries(content);
        let index = rows.findIndex((rowContent) =>
          contentEntries.every(([param, value]) => rowContent[param] === value)
        );
        if (index === -1) {
          index = startIndex;
          /* istanbul ignore else */
          if (row.$selected) {
            startIndex += 1;
          }
        } else {
          startIndex = startIndex ? startIndex - 1 : 0;
        }
        // TODO for list to ensure not to add intructions immediately upon selection.
        // Leverage form handler to prepare page instructions before submit
        if (!skipPageInstructions) {
          const stateMachine = StateMachineManager.getStateMachine(contextName, pageReference, target);
          if (row.$selected) stateMachine.insert(content, index + 1, true);
          else stateMachine.delete(index + 1, true);
          /* instructions[contextName][target].content.push({
            instruction: row.$selected ? Constants.LIST_ACTIONS.INSERT : Constants.LIST_ACTIONS.DELETE,
            content,
            target,
            listIndex: index + 1
          }); */
        }

        actions.push({
          instruction: row.$selected ? Constants.LIST_ACTIONS.SELECT : Constants.LIST_ACTIONS.DESELECT,
          listIndex: index,
          content: { ...content, ...displayPropertyObject } // immer will freeze the content obj
        });
      });

      StateManager.dispatch(
        listAction({
          instruction: Constants.LIST_ACTIONS_TYPE.SELECT_DESELECT,
          context: contextName,
          referenceList: `${pageReference}.${referencePropName}`,
          actions
        })
      );
    } else {
      const patchedValues = getParameterValues(additionalProperties, payload[0], referencePropName);
      // sending empty propName as page reference will already have the prop reference
      c11nEnv
        .getActionsApi()
        .updateFieldValue('', patchedValues, hasNoContextRendering ? { contextPageReference: pageReference } : {});
      c11nEnv.getActionsApi().triggerFieldChange('', patchedValues);
    }
    checkAndClearRequiredErrorMessage(c11nEnv, fullReference, required, isMultiSelectMode, referencePropName);
    resolve();
  });
};

/**
 * returns the current selected row(s) details
 * @param {object} withPayload - boolean value, if true returns the entire payload
 * @param {object} c11nEnv - c11nEnv Object
 * @returns {object} returns the selected row(s) details
 * @private
 */
const getSelectedRows = (withPayload, c11nEnv) => {
  const { selectionMode, selectionList } = c11nEnv.getConfigProps();
  const selectedRows = [];
  let pageReference = c11nEnv.getPageReference();
  const isMultiSelectMode = selectionMode === Constants.LIST_SELECTION_MODE.MULTI;
  let referencePropName = isMultiSelectMode
    ? selectionList.substring(1)
    : pageReference.substring(pageReference.lastIndexOf('.') + 1);

  const rawViewMetadata = c11nEnv.getRawMetadata();
  let contextPageReference = null;
  if (
    rawViewMetadata.config.dataRelationshipContext &&
    rawViewMetadata.type === 'SimpleTableSelect' &&
    rawViewMetadata.config?.selectionMode === 'single'
  ) {
    referencePropName = rawViewMetadata.config.dataRelationshipContext;
    contextPageReference = pageReference.concat('.').concat(referencePropName);
    pageReference = contextPageReference;
  }
  const fullReference = isMultiSelectMode ? `${pageReference}${selectionList}` : pageReference;
  let referencePropValue = c11nEnv.getValue(fullReference) || [];
  if (!Array.isArray(referencePropValue)) {
    referencePropValue = [referencePropValue];
  }

  if (withPayload) {
    return Promise.resolve(referencePropValue);
  }
  const { datasource: { parameters = {} } = {} } = isMultiSelectMode
    ? c11nEnv.getFieldMetadata(`${referencePropName}`)
    : c11nEnv.getCurrentPageFieldMetadata(contextPageReference);

  referencePropValue.forEach(row => {
    const keys = Object.values(parameters).reduce((acc, param) => {
      const propertyName = param.substring(param.lastIndexOf('.') + 1);
      return isSelfReferencedProperty(param, referencePropName) && row[propertyName]
        ? `${acc}${row[propertyName]}`
        : acc;
    }, '');
    if (keys.trim()) {
      selectedRows.push(keys.trim());
    }
  });

  return Promise.resolve(selectedRows);
};

/**
 * check whether the page-instrcutions dirtyness in the provided context.
 * @param {string} context
 * @returns boolean return false if no page-instruction is generated else return true
 * @private
 */
export const isPropertyDirtyPageInstruction = context => {
  if (!context) {
    return false;
  }
  const pageInstructions = getChangedInstructions(context);
  return Array.isArray(pageInstructions) && pageInstructions.length > 0;
};

/**
 * clears the selected row(s) in multiselect list and updates the redux store
 * @param {object} c11nEnv - c11nEnv object
 * @returns {void}
 * @private
 */
function clearSelectedRows(c11nEnv) {
  const { selectionList, required } = c11nEnv.getConfigProps();
  const contextName = c11nEnv.getContextName();
  const pageReference = c11nEnv.getPageReference();
  const referencePropName = selectionList;

  const fullReference = `${pageReference}${selectionList}`;
  return new Promise((resolve) => {
    let target = referencePropName;
    target = handleTargetInNestedPageList(target);
    createContextEntry(c11nEnv, target, pageReference);

    const stateMachine = StateMachineManager.getStateMachine(contextName, pageReference, target);
    stateMachine.deleteAll(referencePropName);

    checkAndClearRequiredErrorMessage(c11nEnv, fullReference, required, true, referencePropName);
    resolve();
  });
}

/**
 * @param {object} c11nEnv - pConnect object for the list.
 * @param {*} property - reference property of the table
 * @param {*} propertyNames - list of property names.
 * @private
 */
export const initDefaultPageInstructions = (c11nEnv, property, propertyNames) => {
  const contextName = c11nEnv.getContextName();
  const pageReference = c11nEnv.getPageReference();
  const target = handleTargetInNestedPageList(property);

  createContextEntry(c11nEnv, target, pageReference);
  const stateMachine = StateMachineManager.getStateMachine(contextName, pageReference, target);
  stateMachine.initPageInstructions(c11nEnv, propertyNames);
};

/**
 * This deletes all the changes so far we did to the respected state-machine
 * @param {object} c11nEnv - pConnect object for the table
 * @param {string} property - reference property of the table.
 * @private
 */
const deleteAll = (c11nEnv, property) => {
  const pageReference = c11nEnv.getPageReference();
  const target = handleTargetInNestedPageList(property);
  createContextEntry(c11nEnv, property, pageReference);
  const stateMachine = StateMachineManager.getStateMachine(c11nEnv.getContextName(), pageReference, target);
  stateMachine.deleteAll(property);
};

/**
 * This replaces all the existing properties with the object we sent for reference table.
 * @param {object} c11nEnv - pConnect object for the table
 * @param {string} property - reference property of the table
 * @param {object} payload - payload of the property to which it gets replaced
 * @private
 */
export const replacePage = (c11nEnv, property, payload) => {
  const pageReference = c11nEnv.getPageReference();
  const context = c11nEnv.getContextName();
  createContextEntry(c11nEnv, property, pageReference);
  addPageInstructionReferences(context, Utils.buildInstructionPageReference(pageReference, property));
  const stateMachine = StateMachineManager.getStateMachine(context, pageReference, property);
  stateMachine.replacePage(payload);
};

/**
 * merge page instructions from one context to another context
 *@param {string} fromContext- context name
 *@param {string} toContext- context name
 *@param {string} pageReference- page reference
 *@param {boolean} skipStateUpdate- flag to skip updating redux state
 * @returns {void}
 * @private
 */
export const mergeInstructions = (fromContext, toContext, pageReference, skipStateUpdate) => {
  const pageInstructions = getChangedInstructions(fromContext);
  pageInstructions.forEach(instruction => {
    const target = Utils.convertClipboardPageToPageRefNotation(instruction.target);
    if (!StateMachineManager.isStateMachineExist(toContext, pageReference, target)) {
      StateMachineManager.setStateMachine(
        toContext,
        target,
        pageReference,
        new StateMachine(target, pageReference, toContext)
      );
    }
    const stateMachine = StateMachineManager.getStateMachine(toContext, pageReference, target);
    switch (instruction.instruction.toLowerCase()) {
      case 'insert':
        stateMachine.insert(instruction.content, instruction.listIndex, skipStateUpdate);
        break;
      case 'update':
        stateMachine.update(instruction.content, instruction.listIndex, skipStateUpdate, {});
        break;
      case 'delete':
        stateMachine.delete(instruction.listIndex, skipStateUpdate);
        break;
      case 'replace':
        stateMachine.replacePage(instruction.content);
        break;
      case 'move':
        stateMachine.move(instruction.listIndex, instruction.listMoveToIndex);
        break;
      case 'delete all': {
        const referencePropName = target.substring(target.lastIndexOf('.'), target.length);
        stateMachine.deleteAll(referencePropName);
        break;
      }
      default:
        break;
    }
  });
};

/**
 * This function helps in providing entry point to get list action api's
 * @param {object} c11nEnv - pConnect object for the list
 * @returns {object} returns an object which contains the utility api's for performing list actions.
 */
export const listActions = c11nEnv => {
  return {
    /**
     * This Adds the INSERT page instruction to the referenced list in context.
     * @param {object} payload - The object containing the values to be inserted in the referenced list.
     * @param {number} index -  The position that must be inserted in the referenced list.
     * @param {string} pageRef - The reference to the page within the current context.
     * @param {object} options - The object containing additional information to perform the INSERT operation.
     * @param {boolean} option.skipStateUpdate - The flag that determines if the Redux State must be updated.
     * @returns {void}
     * @example  <caption>Example for insert(). </caption>
     * In this example, the API inserts a row for list page and inserts an INSERT instruction for the submit action.
     *  getPConnect().getListActions().insert({}, 0, 'caseInfo.content', {
          skipStateUpdate: false
        })
     */
    insert: (payload, index, pageRef, options = {}) => insert(payload, index, c11nEnv, pageRef, options),
    /**
     * Adds the UPDATE page instruction to the referenced list in context.
     * @param {object} payload - The object containing the values to be updated in the referenced list.
     * @param {number} index - The position in the referenced list that must be updated.
     * @param {object} options - The object containing additional information to perform the UPDATE operation.
     * @param {boolean} option.skipStateUpdate - The flag that determines if the Redux State must be updated.
     * @param {string} option.pageRef - The reference to the page within the current context.
     * @returns {void}
     * @example  <caption>Example for update() </caption>
     * In this example, the API updates a row for list page with the provided payload and inserts the UPDATE instruction for the submit action.
     *  getPConnect().getListActions().update({
          Name: 'John',
          Experience: 2
        }, 0, {
          skipStateUpdate: false,
          pageRef: "caseInfo.content.Employee"
        });
     */
    update: (payload, index, options) => update(payload, c11nEnv, index, options),
    /**
     * Adds the UPDATE page instruction for a specific field in a row to the referenced list in context.
     * @param {string} propertyName - The property name of the field that must be updated.
     * @param {string} value - The value of the field that must be updated.
     * @param {object} options - The object containing additional information to perform the UPDATE operation.
     * @returns {void}
     * @example  <caption>Example for updateProperty()</caption>
     * In this example, the API updates the Name property in the row for the referenced list page with the provided payload and
     * inserts the UPDATE instruction for the submit action.
     *  getPConnect().getListActions().updateProperty('.Name', 'John Doe');
     */
    updateProperty: (propertyName, value, options) => updateProperty(propertyName, value, c11nEnv, options),
    /**
     * Adds the DELETE page instruction to the referenced list in context.
     * @param {number} index - The position that must be deleted in the referenced list.
     * @param {string} pageRef - The reference to the page within the current context.
     * @returns {void}
     * @example  <caption>Example for deleteEntry()</caption>
     * In this example, the API deletes the specified row in the referenced list page and inserts the DELETE instruction for the submit action.
     * getPConnect().getListActions().deleteEntry(0, 'caseInfo.content')
     */
    deleteEntry: (index, pageRef) => deleteEntry(index, c11nEnv, pageRef),
    /**
     * Adds the MOVE page instruction to the referenced list in context.
     * @param {number} fromindex - The position in the list from which the row must be moved.
     * @param {number} toIndex - The position in the list to which the row must be moved.
     * @returns {void}
     * @example  <caption>Example for reorder()</caption>
     * In this example, the API reorders the row for the referenced list page with the provided
     * indexes and inserts the MOVE instruction for the submit action.
     * getPConnect().getListActions().reorder(1, 0);
     */
    reorder: (fromIndex, toIndex) => reorder(fromIndex, toIndex, c11nEnv),
    /**
     * Updates the Redux Store on selection of rows and adds the SELECT/DESELECT page instruction to the referenced list in context.
     * @param {array} rows - The array containing the details of the selected/deselected rows.
     * @param {object} options - The object containing additional information to perform the SELECT/DESELECT operation.
     * @param {boolean} option.skipPageInstructions - The flag that determines if the page instructions must be added to the referenced list.
     * @returns {void}
     * @example  <caption>Example for setSelectedRows()</caption>
     * In this example, the API selects the rows for the given payload for the referenced list page and inserts the INSERT/DELETE instruction for the submit action.
     * getPConnect().getListActions().setSelectedRows(
        [
          {
            $key: '0759409f-4146-439c-aa25-57d4f495fee5',
            $selected: true,
            Name: 'Alice'
            pyGUID: "0759409f-4146-439c-aa25-57d4f495fee5"
          }
        ]);
     */
    setSelectedRows: (rows, options = {}) => setSelectedRows(rows, c11nEnv, options),
    /**
     * Get the unique keys of each selected row in an array from the referenced list in context.
     * @param {boolean} (optional) withPayload - determines if all the data in the selected rows must be included in the response. default value is false.
     *  if withPayload is set to true all the data in the selected rows are included in the response.
     *  if withPayload is set to false only the unique ids for the selected rows are included in the response.
     * @returns {array} - list of id's along with payload for the selected rows
     * @example  <caption>Example for getSelectedRows()</caption>
     * In this example, the API returns the unique ids of the selected rows as an array.
     * getPConnect().getListActions().setSelectedRows()
     */
    getSelectedRows: (withPayload = false) => getSelectedRows(withPayload, c11nEnv),
    /**
     * Deletes all the previously selected rows by adding the DELETE ALL page instruction to the case or data reference list and updates the
     *  Redux Store.
     * @returns {void}
     * @example  <caption> Example for clearSelectedRows()</caption>
     * In this example, the API deletes all the selected rows for the list page and inserts the DELETE ALL
     * instruction for the submit action.
     * getPConnect().getListActions().clearSelectedRows()
     */
    clearSelectedRows: () => clearSelectedRows(c11nEnv),
    /**
     * Use this API to communicate if the referenced editable list is mounted on the screen to facilitate generation of
     * page-instructions.
     * @param {boolean} isVisible - determines if the referenced list is mounted on the screen.
     * if isVisible is set to true, the referenced editable list is mounted on the screen thereby allowing page-instructions to be generated.
     * if isVisible is set to false, the referenced editable list is not mounted on the screen thereby not allowing page-instructions to be generated.
     * @returns {void}
     * @example  <caption> Example for setVisibility() </caption>
     * In this example, the API communicates that the referenced editable list is mounted on the screen, thus allowing the page-instructions
     * to be generated.
     * getPConnect().getListActions().setVisibility(true)
     */
    setVisibility: isVisible => setVisibility(c11nEnv, isVisible),
    /**
     * Adds the UPDATE page-instructions to initially populated rows of the referenced list in the context.
     * Note: Use this API when the list has existing rows whose data needs to be passed in the payload of the submit action, and
     * after last entry in the list is mounted.
     * @param {string} property - the unique property of list to which the UPDATE page-instructions must be added.
     * @param {array} fields - fields of the referenced list to which the UPDATE page-instructions must be added.
     * @returns {void}
     * @example  <caption>Example for initDefaultPageInstructions()</caption>
     * In this example, the API adds the UPDATE page-instructions to initially populated rows of the list 'Employees'.
     * getPConnect().getListActions().initDefaultPageInstructions('.Employees', ['pyGUID', 'pyName'])
     */
    initDefaultPageInstructions: (property, fields) =>
      initDefaultPageInstructions(c11nEnv, property, fields),
    deleteAll: property => deleteAll(c11nEnv, property),
    /**
     * Adds the REPLACE page instruction to the referenced list in context.
     * Note: This is explicitely used for Address field when we change country.
     * @param {string} property - The unique property of list to which REPLACE page-instrcution will be added.
     * @param {object} payload - The object containing the property & its value to be replaced in the referenced list.
     * @returns {void}
     * @example  <caption>Example for replacePage()</caption>
     * In this example, the API changes the datapresent in property pyCountry and resets the associated fields in the page 'ShippingAddress'.
     * getPConnect().getListActions().replacePage('.ShippingAddress', {pyCountry : IND"})
     */
    replacePage: (property, payload) => replacePage(c11nEnv, property, payload)
  };
};
