import EnvironmentInfo from '../environment-info';
import { getRestEndPoint } from '../router/app-router';

const TEXT_JAVASCRIPT = 'text/javascript';
/**
 * @description
 * Exposes APIs to load the script and css files.
 */
class AssetLoader {
  constructor() {
    this.loaderObj = {};
    this.staticServerURL = '';
    this.appStaticUrl = null;
    this.pegaOnlyUrl = false; // indicates that pega content separated from customer components
    this.loadedAssets = {};
    this.componentsConfig = null; // Pega components aggregate config
    this.appAlias = '';
    this.b2sJWT = null;
  }

  getTypes = {
    SCRIPT: 'script',
    STYLE: 'link'
  };

  /**
   * Returns the static content url
   * @description This exposes the method to fetch static content url
   *
   * @example <caption>Example for getStaticServerUrl()</caption>
   * Example can be seen in C11n getImagePath method
   * Example usage - AssetLoader.getStaticServerUrl()
   * // returns -- Static content url string
   *
   * @see {@link getStaticServerUrl} for further information.
   * @see {@link getImagePath} for example.
   * @returns {object} the Asset Loader Object
   * @function
   */
  getStaticServerUrl() {
    return this.staticServerURL;
  }

  /**
   * Returns the theme schema json
   * @description This exposes the method to fetch theme schema json
   *
   * @example <caption>Example for getThemeSchemaJSON()</caption>
   * Example usage - AssetLoader.getThemeSchemaJSON()
   * // returns -- Theme schema json
   *
   * @see {@link getThemeSchemaJSON} for further information.
   * @returns {object} the Asset Loader Object
   * @function
   * @private
   */
  async getThemeSchemaJSON() {
    const url = `${this.staticServerURL}themeOverrides.schema.json`;
    return fetch(url).then((res) => res.json());
  }

  /**
   * Returns the Constellation service url - no terminating /
   * @description This exposes the method to fetch the Constellation service url
   *
   * @example <caption>Example for getConstellationSErviceUrl()</caption>
   * Example can be seen in C11n getImagePath method
   * Example usage - AssetLoader.getConstellationServiceUrl()
   * // returns -- Constellation service url string
   *
   * @see {@link getConstellationServiceUrl} for further information.
   * @see {@link getImagePath} for example.
   * @returns {object} the Asset Loader Object
   * @function
   */
  getConstellationServiceUrl() {
    return this.staticServerURL.charAt(this.staticServerURL.length - 1) === '/'
      ? this.staticServerURL.substring(0, this.staticServerURL.length - 1)
      : this.staticServerURL;
  }

  /**
   * This method is used to set the appAlias for the current request.
   * @description This exposes the method set the app alias for the current request.
   * @example <caption>Example for setAppAlias()</caption>
   * Example can be seen in ui-bootstrap setAppAlias method
   * @param {string} appAlias name
   * @see {@link setAppAlias} for further information.
   * @function
   */
  setAppAlias(appAlias) {
    this.appAlias = appAlias;
  }

  /**
   * Returns the appAlias for the current application
   * @description This exposes the method to get the app alias for the current request.
   * @example <caption>Example for getAppAlias()</caption>
   * Example can be seen in C11n getImagePath method
   * Example usage - AssetLoader.getAppAlias()
   * // returns -- appAlias string for the current request.
   * @see {@link getAppAlias} for further information.
   * @see {@link getImagePath} for example.
   * @returns {string} the app alias
   * @function
   */
  getAppAlias() {
    return this.appAlias;
  }

  /**
   * This exposes a method to call the Constellation service to fetch the corresponding component config.json that satisfies the specified properties.
   * This call is authenticated by the service, using the token issued for the Constellation service (B2S JWT).
   * @example <caption>Example for getSvcComponentsConfig()</caption>
   * // In this example, the API retrieves an operator image and creates a DOMString containing a URL representing the image Blob object.
   * const templateDefinitionConfigJson = await AssetLoader
   *      .getSvcComponentsConfig([{ field: 'name', value: OneColumn }])
   *      .then((response) => {
   *         if (response?.data?.components?.[0]) {
   *           return response.data.components[0];
   *         }
   *         throw new Error('templateDefinition for OneColumn not found!');
   *      })
   *      .catch(() => {
   *         throw new Error('templateDefinition for OneColumn not found!');
   *      });
   * @param {Array} criteriaToMatch of name-value pairs specifying the search criteria
   * @param {object} options of additional information required such as:
   *  -alternateDesignSystemURL {String} - Contains the URL for an alternative design system, used for fetching additional config-ext.json values
   * @returns {object} - Contains an attribute data that maps to an array of config objects matching the criteria. In the event of an alternateDesignSystemURL specified in the options,
   * and only one config object is obtained via the initial fetch call, the alternative design system's config-ext.json will be embedded in an alternateDesignSystemConfig attribute within
   * the single config of the returned componentData. Errors will be stored inside the alternateDesignSystemConfig attribute as well in the case of an unsuccessful fetch.
   * @function
   */
  async getSvcComponentsConfig(criteriaToMatch, options = {}) {
    let params = '';
    let i = 0;
    criteriaToMatch.forEach((criteria) => {
      params += `${i === 0 ? '?' : '&'}field${i}=${criteria.field}&value${i}=${criteria.value}`;
      i += 1;
    });

    // for CDN architecture, Pega and customer assets are on separate urls's
    return this.pegaOnlyUrl
      ? this.getSvcComponentsConfigSeparatedUrls(criteriaToMatch, params, options)
      : this.getSvcComponentsConfigCombinedUrl(criteriaToMatch, params, options);
  }

  // used for separate pega + app specific service content architecture
  // should always return content: [pega+customer], [pega], []. should not throw an exception - that stops authoring
  async getSvcComponentsConfigSeparatedUrls(criteriaToMatch, params, options) {
    const builtOnApps = EnvironmentInfo.getBuiltOnAppNames();

    const p1 = this.loadAndFilterPegaComponentsConfig(criteriaToMatch, builtOnApps);
    const p2 = this.loadAndCustomerComponentsConfig(params);
    const sdkCompPromise = this.fetchSDKComponent(params);

    // wait for load of pega and customer component configs, and merge Pega and customer results
    const componentsArray = [];
    await Promise.all([p1, p2, sdkCompPromise]).then((cmpntsCfgs) => {
      const list = this.mergeComponentList(cmpntsCfgs);
      componentsArray.push(...list);
    });

    // fetch alternate design system content - Max knows the magic for length of 1 and 0 replacement
    if (options.alternateDesignSystemURL && componentsArray.length === 1) {
      const ads = await this.getSvcComponentsConfigADS(criteriaToMatch, params, options);
      componentsArray[0].alternateDesignSystemConfig = ads;
    }

    return { data: { components: componentsArray } };
  }

  mergeComponentList = (cmptCfgList = []) => {
    const [pegaCmptCfgs = [], customerCmptCfgs = [], sdkCmptCfgs = []] = cmptCfgList;
    const SDK_COMPONENTS_KEY = 'alternateDesignSystemConfig';
    const tempList = [...pegaCmptCfgs, ...customerCmptCfgs];
    if (this.appStaticUrl) {
      return tempList.map((config) => {
        const { name, type, subtype } = config;

        const sdkConfig = sdkCmptCfgs.reduce((carry, cfg) => {
          if (name === cfg.name && type === cfg.type && subtype === cfg.subtype) {
            carry = cfg;
          }
          return carry;
        }, {});

        if (Object.keys(sdkConfig).length > 0) {
          return { ...config, [SDK_COMPONENTS_KEY]: { ...sdkConfig } };
        }
        return config;
      });
    }
    return tempList;
  };

  async fetchSDKComponent(params) {
    if (this.appStaticUrl && this.appStaticUrl.length > 8 && this.appStaticUrl !== this.staticServerURL) {
      const customerComponentsConfigUrl = `${this.appStaticUrl}v102/${this.appAlias}/componentslist-ext${params}`;
      return fetch(customerComponentsConfigUrl, {
        method: 'GET',
        credentials: 'include',
        headers: { Authorization: `Bearer ${this.b2sJWT}` }
      })
        .then((response) => response.json())
        .then((data) => data.components)
        .catch(() => []);
    }
    return Promise.resolve([]);
  }

  // load and filter pega components
  async loadAndFilterPegaComponentsConfig(criteriaToMatch, builtOnApps) {
    return this.loadPegaComponentsConfig().then(() => {
      // here be magic - filter on the potentially multiple name/value clauses
      const componentsArray = [];
      if (this.componentsConfig) {
        this.componentsConfig.forEach((cfg) => {
          let match = true;
          criteriaToMatch.forEach((criteria) => {
            // some configs have an array in some properties, set false if non match on all
            if (Array.isArray(cfg[criteria.field])) {
              if (!cfg[criteria.field].includes(criteria.value)) {
                match = false;
                // break;
              }
            } else if (cfg[criteria.field] !== criteria.value) {
              match = false;
              // break;
            }
          });
          // also check if component allowed apps intersects with builtOn apps
          if (match && this.checkForBuiltOnSupport(builtOnApps, cfg.allowedApplications)) {
            componentsArray.push(cfg);
          }
        });
      }
      return componentsArray;
    });
  }

  // load pega components
  async loadPegaComponentsConfig() {
    let p = null;
    // if not already loaded, retrieve the Pega out-of-the-box component data once - store locally
    if (this.componentsConfig === null) {
      const pegaComponentsConfigUrl = `${
        this.staticServerURL
      }components/componentsconfig.json?v=${new Date().getTime()}`;
      p = fetch(pegaComponentsConfigUrl, { method: 'GET' })
        .then((response) => response.json())
        .then((data) => {
          this.componentsConfig = data;
        })
        .catch(); // kill the bubble
    } else {
      p = Promise.resolve();
    }
    return p;
  }

  // fetch customer component config from app-static svc
  async loadAndCustomerComponentsConfig(params) {
    let p = null;
    if (this.appStaticUrl && this.appStaticUrl.length > 8 && this.appStaticUrl !== this.staticServerURL) {
      const customerComponentsConfigUrl = `${this.appStaticUrl}v103/${this.appAlias}/componentslist${params}`;
      p = fetch(customerComponentsConfigUrl, {
        method: 'GET',
        credentials: 'include',
        headers: { Authorization: `Bearer ${this.b2sJWT}` }
      })
        .then((response) => response.json())
        .then((data) => data.components)
        .catch(() => []);
    } else {
      p = Promise.resolve([]);
    }
    return p;
  }

  checkForBuiltOnSupport(builtOnApps, allowedApplicationsFromConfig) {
    // defult to allow
    if (
      !builtOnApps ||
      !allowedApplicationsFromConfig ||
      builtOnApps.length === 0 ||
      allowedApplicationsFromConfig.length === 0
    ) {
      return true;
    }
    const boa = builtOnApps.split(',');
    if (boa.length === 0) {
      return true;
    }

    return boa.some(function (app) {
      return allowedApplicationsFromConfig.indexOf(app) !== -1;
    });
  }

  // used for pre (CDN+service) architecture
  async getSvcComponentsConfigCombinedUrl(criteriaToMatch, params, options) {
    /*
     * Commented out the following call to serviceBrokerFetch() due to BUG-635925. Also note that ServiceBrokerFetch seems to
     * rely on "context" and "pzctkn" request headers when called from Infinity (see comment in httpmain.js), yet don't appear necessary
     * when invoked from Constellation (i.e., UI Authoring). Because serviceBrokerFetch is Axios-based, return out the data in an object
     * to mimic it (so the corresponding calls to this API do not need to change and serviceBrokerFetch() could be swapped in).
     */
    const componentCallURL = `${this.staticServerURL}v102/${this.appAlias}/componentslist${params}`;

    // Retrieve the out-of-the-box component data
    const componentData = await fetch(componentCallURL, {
      method: 'GET',
      credentials: 'include',
      headers: { Authorization: `Bearer ${this.b2sJWT}` }
    })
      .then((response) => response.json())
      .then((data) => {
        return { data };
      });

    // US-442670: If an alternative design system is provided, and we only have one component in our componentData (i.e., single template / scalar field),
    // fetch the additional config.json settings.
    if (options.alternateDesignSystemURL && componentData?.data?.components?.length === 1) {
      const ads = await this.getSvcComponentsConfigADS(criteriaToMatch, params, options);
      componentData.data.components[0].alternateDesignSystemConfig = ads;
    }

    return componentData;
  }

  getSvcComponentsConfigADS(criteriaToMatch, params, options) {
    const alternateDesignSystemCallURL = `${options.alternateDesignSystemURL}${
      options.alternateDesignSystemURL.endsWith('/') ? '' : '/'
    }components/${criteriaToMatch[0].value}/config-ext.json`;

    // Retrieve the alternate design system component data
    return fetch(alternateDesignSystemCallURL, {
      method: 'GET'
    })
      .then((response) => {
        if (response.ok) {
          return response.json();
        }
        throw new Error(response.statusText);
      })
      .catch((error) => {
        // In the event that the fetch was unsuccessful, place an error inside the returned componentData's alternateDesignSystemConfig attribute
        return { error: error.message };
      });
  }

  /**
   * This exposes a method to call the Constellation service to fetch an image. This call is authenticated
   * by the service, using the token issued for the Constellation service (B2S JWT).
   * @example <caption>Example for getSvcImage()</caption>
   * // In this example, the API retrieves an operator image and creates a DOMString containing a URL representing the image Blob object.
   * const operatorImg = await AssetLoader
   *       .getSvcImage('DATA-CONTENT-IMAGE USER@CONSTELLATION.COM!PNG!/OPERATORIMAGES/')
   *       .then((data) => {
   *          const url = window.URL.createObjectURL(data);
   *          return url;
   *       })
   *       .catch(() => {
   *          console.error('Unable to load the image!');
   *       });
   * // returns -- Promise that resolves to the image (blob) specified as the key
   * @param {string} key Identifier to the image
   * @returns {Promise} Promise that resolves to the image (blob) specified as the key
   * @function
   */
  getSvcImage(key) {
    /*
     * Commented out the following call to serviceBrokerFetch() due to BUG-635925. Also note that ServiceBrokerFetch seems to
     * rely on "context" and "pzctkn" request headers when called from Infinity (see comment in httpmain.js), yet don't appear necessary
     * when invoked from Constellation (i.e., UI Authoring). Return out the blob data.
     */
    const callUrl = `${this.appStaticUrl}v102/${this.appAlias}/image/${key}`;

    return fetch(callUrl, {
      method: 'GET',
      credentials: 'include',
      headers: { Authorization: `Bearer ${this.b2sJWT}` }
    })
      .then((response) => response.blob())
      .catch();

    /*
        // Service broker manages injection of B2S JWT into headers and maintenance of current JWT
        return serviceBrokerFetch(callUrl, {
            method: 'GET',
            credentials: 'include',
            headers: { Authorization: `Bearer ${this.b2sJWT}` }
        });
*/
  }

  /**
   * This API gets the localization instance from the constellation service for a given user locale and an instance key. This call is authenticated
   * by the service, using the token issued for the Constellation service (B2S JWT).
   * @example <caption>Example for getSvcLocale('en_US','WORK-!VIEW!PYCASESUMMARY.json')</caption>
   * // In this example, the API gets the localeJson for the en_US locale and for the view PyCaseSummary in the Work- class.
   * // This API call returns a Promise which when resolved gives the localeJson.
   * const localeJson = await AssetLoader.getSvcLocale('en_US', 'WORK-!VIEW!PYCASESUMMARY.json')
   *       .then((data) => {
   *          // data is the response from the constellation service which is the localeJson
   *       });
   * @param   {string} locale     locale for which we need locale JSON
   * @param   {string} key        Identifier to the JSON files
   * @returns {Promise}           Promise that resolves to a localeJson
   * @function
   */
  getSvcLocale(locale, key) {
    const callUrl = `${this.appStaticUrl}v102/${this.appAlias}/locales/${locale}/${key}`;

    return fetch(callUrl, {
      method: 'GET',
      credentials: 'include',
      headers: { Authorization: `Bearer ${this.b2sJWT}` }
    });
  }

  /**
   * This exposes a method to call the Constellation service to fetch a custom component. This call is authenticated
   * by the service, using the token issued for the Constellation service (B2S JWT).
   * @example <caption>Example for getSvcComponent()</caption>
   * // In this example, the API retrieves the JS implementation for the WeightInput component.
   * const WeightInputDefinition = await AssetLoader
   *      .getSvcComponent('WeightInput.js')
   *      .then((response) => {
   *         return response;
   *       })
   *       .catch(() => {
   *         console.error('Unable to retrieve the component!');
   *       });
   * // returns -- Promise that resolves to the corresponding JS implementation for the component
   * @param {string} name Name of the component whose JS implementation we want to fetch
   * @returns {Promise} Promise that resolves to the corresponding JS implementation for the component
   * @function
   */
  getSvcComponent(name) {
    /*
     * Commented out the following call to serviceBrokerFetch() due to BUG-635925. Also note that ServiceBrokerFetch seems to
     * rely on "context" and "pzctkn" request headers when called from Infinity (see comment in httpmain.js), yet don't appear necessary
     * when invoked from Constellation (i.e., UI Authoring). Return out the JS implementation.
     */
    const callUrl = `${this.appStaticUrl}v102/${this.appAlias}/component/${name}`;

    return fetch(callUrl, {
      method: 'GET',
      credentials: 'include',
      headers: { Authorization: `Bearer ${this.b2sJWT}` }
    })
      .then((response) => {
        if (!response.ok) {
          throw Error(response.statusText);
        }
        return response;
      })
      .then((response) => response.text())
      .then((data) => {
        return data;
      })
      .catch();
  }

  /**
   * This exposes a method to call the Constellation service to fetch a custom component and attach the component script in document HEAD. This call is authenticated
   * by the service, using the token issued for the Constellation service (B2S JWT).
   * @example <caption>Example for loadSvcComponent()</caption>
   * // In this example, the API retrieves the JS implementation for the WeightInput component and attached to document HEAD in a <script /> tag.
   * const WeightInputDefinition = await AssetLoader
   *      .loadSvcComponent('WeightInput')
   *      .then((componentName) => {
   *         return componentName;
   *       })
   *       .catch(() => {
   *         console.error('Unable to retrieve the component!');
   *       });
   * // returns -- Promise that resolves to the corresponding JS implementation for the component
   * @param {string} name Name of the component whose JS implementation we want to fetch
   * @returns {Promise} Promise that resolves to the component name
   * @function
   */

  loadSvcComponent(name) {
    return new Promise((resolve, reject) => {
      this.getSvcComponent(name)
        .then((data) => {
          const element = document.createElement('script');
          element.src = window.URL.createObjectURL(new Blob([data], { type: TEXT_JAVASCRIPT }));
          element.onload = () => resolve(name);
          element.onerror = () => reject(name);
          document.head.appendChild(element);
        })
        .catch(() => {
          reject(name);
        });
    });
  }
  /*
        // Service broker manages injection of B2S JWT into headers and maintenance of current JWT
        return serviceBrokerFetch(callUrl, {
            method: 'GET',
            credentials: 'include',
            headers: { Authorization: `Bearer ${this.b2sJWT}` }
        });
*/

  /**
  * Sets the urls to fetch pega static content and customer static content.
  * @description This sets static server urls as passed in its argument
  *
  * @example <caption>Example for initServer()</caption>
  * Example can be seen in loadLibs,setStaticServerUrl methods in ui_bootstrap
  * Example usage - AssetLoader.initServer('https://mirror.pegacloud.net/prweb')
  * This initiates server with url - https://mirror.pegacloud.net/prweb as static content url
  *
  * @param {string} url url to fetch pega static content
  * @param {string} appUrl Service url to fetch app static content
  * @param {string=} b2sJWT Authorization header token (Optional)
  * This is used to access protected resources

  * @see {@link setStaticServerUrl} for further information.
  * @see {@link loadLibs} for example.
  * @function
  */
  initServer(url, appUrl, b2sJWT) {
    /*
      UC 1: CDN + app-static service
      UC 2: CDN only
      UC 3: service only
                  url      appUrl
      UC1          x          x
      UC2          x       'no-appstatic'
      UC3          x       'no-cdn'
    */

    console.log(`asset-loader.initServer(${url}, ${appUrl})`);

    // init can be called multiple times; must ensure coherent settings
    this.appStaticUrl = null;
    this.staticServerURL = null;
    this.pegaOnlyUrl = false;
    this.componentsConfig = null;

    // uc1, cdn+service
    if (url && appUrl && appUrl !== 'no-appstatic' && appUrl !== 'no-cdn') {
      this.appStaticUrl = appUrl.endsWith('/') ? appUrl : `${appUrl}/`;
      this.staticServerURL = url.endsWith('/') ? url : `${url}/`;
      this.pegaOnlyUrl = true;
    }
    // uc2, cdn only
    else if (url && appUrl && appUrl === 'no-appstatic') {
      this.staticServerURL = url.endsWith('/') ? url : `${url}/`;
      this.appStaticUrl = this.staticServerURL; // added to support below getImage, getComponent, getLoacale calls
      this.pegaOnlyUrl = true;
    }
    // uc3, service only
    else if (url && appUrl && appUrl === 'no-cdn') {
      this.staticServerURL = url.endsWith('/') ? url : `${url}/`;
      this.appStaticUrl = this.staticServerURL; // added to support below getImage, getComponent, getLoacale calls
    }

    console.log(
      `asset-loader.initServer() staticServerURL=${this.staticServerURL}, appStaticUrl=${this.appStaticUrl}, pegaOnlyUrl=${this.pegaOnlyUrl})`
    );

    this.b2sJWT = b2sJWT;
  }

  /**
   * Registers the custom loader to the Asset Loader Class.
   * @description register custom loader to the Asset Loader Class.
   * This can be used to register custom loaders.
   *
   * @example <caption>Example for register() depicting how to register
   * a custom loader viz. font-loader to use loadWebFonts function </caption>
   * Example usage - AssetLoader.register('font-loader', loadWebFonts)
   *
   * @param {string} name name of the loader like Font-Loader etc.,
   * @param {Function} loaderFn loader function, function
   * must return Promise with assets.
   *
   * @function
   */
  register(name, loaderFn) {
    this.loaderObj[name] = loaderFn;
  }

  /**
   * Returns the loader associated/registered with loader name.
   * @description returns custom loader if defined else defaults to default loader.
   *
   * @example <caption>Example for register()</caption>
   * Example usage - AssetLoader.getLoader('font-loader')
   * which returns the loadWebFonts method/API
   *
   * Example can be seen in component-loader loadComponents method
   * @param {string} name name of the loader like Font-Loader etc.,
   *
   * @returns {Function} returns the loader
   * @default defaults to in-build js/css loader
   *
   * @see {@link loadComponents} for example usage.
   *
   * @function
   */
  getLoader(name) {
    let loader = {};
    const defaultLoader = (url, type) => {
      /* TODO: Asynchronously load CSS  */
      return new Promise((resolve, reject) => {
        const element = document.createElement(type);
        // Need to set different attributes depending on tag type
        if (type === 'script') {
          // Important success and error for the promise
          element.onload = () => {
            resolve(url);
          };
          element.onerror = () => {
            reject(url);
          };
          element.type = TEXT_JAVASCRIPT;
          element.charset = 'UTF-8';
          element.async = false;
          element.src = url;
        } else {
          element.type = 'text/css';
          element.rel = 'stylesheet';
          element.href = url;
        }
        // Inject into document to kick off loading
        document.head.appendChild(element);
      }).catch((e) => {
        console.log(e);
      });
    };
    if (name) {
      loader = this.loaderObj[name];
    } else {
      loader = defaultLoader;
    }
    return loader;
  }

  /**
   * Loads the assets from asset dependencies list.
   * @description loads asset dependencies as individual promises
   * @param options
   * It reads the dependency from assets map and process the assets mapped in it.
   * Once all scripts loaded successfully through Promise.all, it will call render.
   *
   * @example <caption>Example for loadAssets()</caption>
   * Example usage - AssetLoader.loadAssets('https://mirror.pegacloud.net/assets/react.prod.js')
   * which loads the react prod file into html
   *
   * Example can be seen in history-epic showHistory method
   * and processViewResponse method in util
   *
   * @param {*} assets assets list eq., section...
   * @returns {Promise<object>} promise Object for the resolved Assets
   *
   * @see {@link processViewResponse} for further information.
   * @see {@link showHistory} for example usage.
   *
   * @function
   */
  loadAssets(assets, options) {
    const { isDevServerMode } = options;
    if (!assets) {
      return Promise.resolve();
    }

    const assetPromises = [];
    assets.forEach((assetItem) => {
      const name = Object.keys(assetItem)[0];
      let url = isDevServerMode ? Object.values(assetItem)[0] : this.getStaticServerUrl();
      url = url.endsWith('/') ? url : `${url}/`;
      /* To avoid loading multiple times */
      if (this.loadedAssets[name]) {
        return;
      }
      if (name.indexOf('.js') !== -1) {
        assetPromises.push(this.getLoader()(url + name, this.getTypes.SCRIPT));
      } else if (name.indexOf('.css') !== -1) {
        this.getLoader()(url + name, this.getTypes.STYLE);
      }
      this.loadedAssets[name] = true;
    });
    return Promise.all(assetPromises);
  }

  /**
   * This exposes a method to call the Constellation app static service to fetch assets from isolations. This call is authenticated
   * by the service, using the token issued for the Constellation service (B2S JWT).
   * @example <caption>Example for getAssetsFromIsolations()</caption>
   * // In this example, the API retrieves logo image and creates a DOMString containing a URL representing the image Blob object.
   * const operatorImg = await AssetLoader
   *       .getAssetsFromIsolations('image/sample.png')
   *       .then((data) => {
   *          const url = window.URL.createObjectURL(data);
   *          return url;
   *       })
   *       .catch(() => {
   *          console.error('Unable to load the image!');
   *       });
   * // returns -- Promise that resolves to the image (blob) specified as the key
   * @param {string} assetPath path to the image
   * @returns {Promise} Promise that resolves to the image (blob) specified as the key
   * @function
   * @private
   */
  getAssetsFromIsolations(assetPath) {
    const base64 = this.b2sJWT.split('.')[1].replace(/-/g, '+').replace(/_/g, '/');
    const jsonPayload = decodeURIComponent(
      window
        .atob(base64)
        .split('')
        .map((char) => `%${`00${char.charCodeAt(0).toString(16)}`.slice(-2)}`)
        .join('')
    );
    const jsonObj = JSON.parse(jsonPayload);
    const isolationID = jsonObj?.tenant_id;
    const { restApi, reqType } =
      getRestEndPoint('getAssetsFromIsolations', {
        isolationID,
        assetPath
      }) || {};
    const callUrl = `${this.appStaticUrl}${restApi}`;
    return fetch(callUrl, {
      method: reqType,
      credentials: 'include',
      headers: { Authorization: `Bearer ${this.b2sJWT}` }
    }).then((response) => response.blob());
  }
}

export default new AssetLoader();
