  /**
   * This API displays a preview of a data item inside the preview panel.
   *
   * @example <caption>Example for showDataPreview()</caption>
   * Example usage - getPConnect().getActionsApi().showDataPreview(ataContext, dataContextParameters, options);
   * // Show the preview of a data item (identified by dataContext and the dataContextParameters) inside preview panel with the extra configuration object provided
   *
   * @param {string} dataContext - Name of the data context<br><br>
   * Examples of <strong>dataContext</strong> include:
   * <br>D_EmployeeDetails
   * @param {string} dataContextParameters - Parameters required for the data context<br><br>
   * Examples of <strong>dataContextParameters</strong> include:
   * <br>{ pyGUID: "0759409f-4146-439c-aa25-57d4f495fee5" }
   * @param {object} options - Optional, object containing extra infomation like containerName which might be required.
   *
   */
  showDataPreview(dataContext, dataContextParameters, options = {}) {
    if (!dataContext) {
      return Promise.reject(new Error('dataContext is required parameter'));
    }

    if (typeof dataContext !== 'string') {
      return Promise.reject(new TypeError('dataContext must be string'));
    }

    if (!dataContextParameters) {
      return Promise.reject(new Error('dataContextParameters is required parameter'));
    }

    if (typeof dataContextParameters !== 'object') {
      return Promise.reject(new TypeError('dataContextParameters must be an object'));
    }

    return this.showData('pyDataPreview', dataContext, dataContextParameters, {
      container: CONTAINER_NAMES.PREVIEW,
      containerName: CONTAINER_NAMES.PREVIEW,
      context: publicConstants.APP.APP,
      ...options,
      skipSemanticUrl: true
    });
  }

  /**
   * This API loads a view with data from server based on the context of a case <br>
   *
   * @example <caption>Example for loadView()</caption>
   * In this example, the API loads the pyReview view with data from a server based on the context of the case whose ID is ON8TTL-C11NGALL-WORK C-7001.
   * Example usage -
   * const loadViewPromise = getPConnect().getActionsApi().loadView(ON8TTL-C11NGALL-WORK C-7001,pyReview); <br>
   * loadViewPromise.then(() => {
   *   // load view success handling
   * }).catch(() => {
   *   // load view failure handling
   * });
   *
   * <br>
   * @param {string} caseId Unique identifier of the case associated with a context
   * @param {string} viewName Name of the view which must be loadedwith data.
   * @param {object} config Object to pass context and container information
   *
   * @returns {Promise<object>} A promise associated with the action (and stored in ActionManager)
   */
  loadView(caseId, viewName, config) {
    let actionConfig = ActionProcessor.buildActionObject(
      {
        caseId,
        viewName,
        caseClassName: this.c11nEnv.getValue(publicConstants.CASE_INFO.CASE_TYPE_ID),
        RemoteSystemID: RemoteCase.getRemoteHeaderByContext(this.c11nEnv.getContextName())
      },
      this.c11nEnv
    );
    if (config) {
      actionConfig = {
        ...actionConfig,
        ...config
      };
    }
    const theType = actionType.LOAD_VIEW;

    // register this action with the ActionManager using
    //  actionMgrID that's been included in the action's payload
    //  and a promise. Note that buildActionObject gets an ActionMgrID
    //  and puts it in the returned object
    const { promise } = this.actionsApiPromise(theType, actionConfig, false);

    StateManager.dispatch({
      type: theType,
      payload: { ...actionConfig }
    });

    // return the Promise associated with this action
    return promise;
  }

  /**
   *
   * Displays the page of a class.
   *
   * @example <caption>Example for showPage()</caption>
   * Example usage -
   * const showPagePromise = getPConnect().getActionsApi().showPage("pyHome", "Data-Portal"); <br>
   * showPagePromise.then(() => {
   *   // show page success handling
   * }).cath(() => {
   *   // show page failure handling
   * });
   *
   * <br>
   * // obtains and displays the view metadata and class name of a page.
   *
   * @param {string} pageName - Name of the page
   * Some examples:<br><br>
   * page = "pyHome"<br>
   * @param {string} className - Class name to which the page belongs to
   * Some examples:<br><br>
   * className: "Data-Portal"
   * <br><br>
   * @param {object} options - Javascript object containing containerName <br>
   * @param {string} options.containerName Name of the container
   * @param {string} options.skipSemanticUrl - Flag to skip semantic url evaluation logic
   * <br>
   * Example:<br>
   * { containerName: "primary" }
   * <br><br>
   * @returns {Promise<object>} A promise associated with the action (and stored in ActionManager)
   */
  showPage(pageName, className, options = {}) {
    if (!pageName) {
      return Promise.reject(new Error(this.MSG_PAGENAME_REQUIRED));
    }

    if (typeof pageName !== 'string') {
      return Promise.reject(new TypeError(this.MSG_PAGENAME_STRING));
    }

    if (!className) {
      return Promise.reject(new Error(this.MSG_CLASSNAME_REQUIRED));
    }

    if (typeof className !== 'string') {
      return Promise.reject(new TypeError(this.MSG_CLASSNAME_STRING));
    }

    const actionConfig = ActionProcessor.buildActionObject(
      {
        viewName: pageName,
        className
      },
      this.c11nEnv
    );
    const theType = actionType.SHOW_PAGE;

    // register this action with the ActionManager using
    //  actionMgrID that's been included in the action's payload
    //  and a promise. Note that buildActionObject gets an ActionMgrID
    //  and puts it in the returned object
    const { promise } = this.actionsApiPromise(theType, actionConfig);

    if (Debugger.getPreviewMode()) {
      actionConfig.debugInfo = { debugId: Utils.generateUniqueID() };
    }

    StateManager.dispatch({
      type: theType,
      payload: {
        ...actionConfig,
        containerName: options.containerName || this.c11nEnv.getContainerName(),
        skipSemanticUrl: options.skipSemanticUrl
      }
    });

    // return the Promise associated with this action
    return promise;
  }

