import { getContainerData } from '../store/state/container-utils';
import { getParentContextName } from '../utils/context-utils';
import constants from '../constants';
import { ACTION_SHOWVIEW } from '../store/state/actions';

import RouteParser from './route-parser';


/**
 * this function helps to build target name
 *
 * @param {string} target -- container target name
 * @param {string} baseContext -- base context
 * @returns {string} returns -- new target name
 */
export const getResolvedTargetName = (target, baseContext) => {
  if (!target || !baseContext) {
    return null;
  }
  let newTargetContainerName = constants.EMPTY_STRING;
  let isTargetContainerPresent;
  if (baseContext) {
    newTargetContainerName = `${baseContext}${constants.FORWARD_SLASH_STRING}${target}`;
    isTargetContainerPresent = getContainerData(newTargetContainerName);

    if (!isTargetContainerPresent && baseContext.indexOf('/') === -1) {
      newTargetContainerName = `${baseContext}${constants.FORWARD_SLASH_STRING}${target}`;
    } else if (!isTargetContainerPresent) {
      newTargetContainerName = getResolvedTargetName(target, getParentContextName(baseContext));
    }
  }
  return newTargetContainerName;
};

/**
 * this function helps to get Routing Info for the passed in Target and Context Name
 *
 * @param {string} target -- container target name
 * @param {string} context -- base context
 * @returns {*} returns -- routing info state object
 */
export const getRoutingInfo = (target, context) => {
  const resolvedTarget = getResolvedTargetName(target, context);
  if (resolvedTarget && getContainerData(resolvedTarget)) {
    return getContainerData(resolvedTarget);
  }
  return {};
};

/**
 * Retrieve semantic URL by view name and view class for showView container action
 * @param {string} viewName - view name
 * @param {string} viewClass - view class
 * @param {*} queryParameters - query params 
 * @returns {*} semantic url or empty string
 * @public
 */
export const getSemanticURLFromRoutingInfo = (viewName, queryParameters) => {
  return RouteParser.getSemanticURL(ACTION_SHOWVIEW, { page: viewName }, queryParameters);
}
