/**
 * helper functions
 */
class Utils {
  /**
   * This API will check whether the reference is page instruction
   * @param {string} reference reference of the property
   * @returns {boolean} returns true if the refrence is a page instruction
   */
  static isPageInstruction(reference) {
    return reference && reference.includes('[') && reference.includes(']');
  }
}

export default Utils;
