import XRayRuntime from './xray-runtime';

class Debugger {
  constructor() {
    this.xray = false;
    this.onXRayCallback = null;
    this.xrayRuntime = new XRayRuntime(this);
    this.isPreview = false;
    this.debugLevels = 'vcv=1;tracer=0;log=0';
  }

  onXRay(callback) {
    this.onXRayCallback = callback;
  }

  toggleXRay(bXRay) {
    this.xray = bXRay;
    this.xrayRuntime.xrayInfo = {};
    if (this.onXRayCallback !== null) {
      this.onXRayCallback(bXRay);
    }
  }

  enableXRay() {
    this.toggleXRay(true);
  }

  disableXRay() {
    this.toggleXRay(false);
  }

  toggle() {
    if (this.xray) {
      this.toggleXRay(false);
    } else {
      this.toggleXRay(true);
    }
  }

  isXRayOn() {
    return this.xray;
  }

  getXRayRuntime() {
    return this.xrayRuntime;
  }

  /**
   * This API set the previewMode to true
   * @example <caption>PCore.getDebugger().setPreviewMode()</caption>
   */
  setPreviewMode() {
    this.isPreview = true;
  }

  /**
   * This API gets the current preview value
   * @example <caption>PCore.getDebugger().getPreviewMode()</caption>
   * @returns {boolean} true/false
   * @private
   */
  getPreviewMode() {
    /* Add logic to identify the previewMode & InfinityX as backend */
    return this.isPreview;
  }

  /**
   * This API set the tracer flag as 1
   * @example <caption>PCore.getDebugger().enableTracer()</caption>
   * @private
   */
  enableTracer = () => {
    this.debugLevels = 'vcv=1;tracer=1;log=0';
  };

  /**
   * This API set the tracer flag as 0
   * @example <caption>PCore.getDebugger().disableTracer()</caption>
   * @private
   */
  disableTracer = () => {
    this.debugLevels = 'vcv=1;tracer=0;log=0';
  };

  getDebugLevels = () => {
    return this.debugLevels;
  };
}

export default new Debugger();
