class ModuleRegistry {
  /**
   * Registers the modules to core
   * @param {string} moduleName name of the module
   * @param {object} module module exposed apis
   * @param {object} pcore pCore instance
   */
  static registerModule(moduleName, module, pcore) {
    pcore[moduleName] = () => module;
  }
}

export default ModuleRegistry;
