import TargetMap from '../../actions/targetmap';
import { ACTION_CREATEWORK } from '../../store/state/actions';
import { isContainerSingleMode } from '../../store/state/container-utils';
import { getDirtyDetails } from '../../store/state/epics/util';
import APP_CONSTANTS from '../../constants';
import Utils from '../../utils/utils';
import EnvironmentInfo from '../../environment-info';
import { didOperatorHasAssignment, isAssignmentInCreateStage } from '../../utils/case-utils';

export const STATE_PATHS = {
  NEXT_ASSIGNMENT_ID: 'data.nextAssignmentInfo.ID',
  FIRST_ASSIGNMENT_ID: 'data.caseInfo.assignments.0.ID',
  NEXT_ASSIGNMENT_INFO: 'nextAssignmentInfo',
  NEXT_ASSIGNMENT_INFO_CONTEXT: 'nextAssignmentInfo.context'
};

export const getContainerName = (createWorkActionPayload) => {
  const { containerName, isInCreateStage } = createWorkActionPayload;
  if (isInCreateStage) return APP_CONSTANTS.MODAL;
  return containerName || TargetMap.getTargetViewContainerName(ACTION_CREATEWORK);
};

export const evaluateDirtyBeforeDxApiCall = (target) => {
  if (isContainerSingleMode(target)) {
    return {
      continueExecution: true,
      removeActionPayload: {}
    };
  }
  return getDirtyDetails(target);
};

export const isInCreateStage = (dxApiResponse) => {
  const caseInfo = Utils.getIn(dxApiResponse, [APP_CONSTANTS.DATA, APP_CONSTANTS.CASE_INFO.CASE_INFO]);
  return (
    isAssignmentInCreateStage(caseInfo) && didOperatorHasAssignment(EnvironmentInfo.getOperatorIdentifier(), caseInfo)
  );
};

export const setDirectUrlUpdation = (dxApiResponse, createWorkActionPayload) => {
  const _isInCreateStage = isInCreateStage(dxApiResponse);
  const { skipBrowserSemanticUrlUpdate = false, isTraditionalUICase } = createWorkActionPayload;
  // URL and title updation should be done only for non create stages.
  Utils.setIn(
    dxApiResponse,
    ['directUrlUpdation'],
    (!_isInCreateStage || isTraditionalUICase) && !skipBrowserSemanticUrlUpdate
  );
};

export const getCaseViewMode = (dxApiResponse) => {
  if (dxApiResponse.confirmationNote) {
    return APP_CONSTANTS.REVIEW_MODE;
  }
  return APP_CONSTANTS.PERFORM_MODE;
};
