import lodashMergeWith from 'lodash.mergewith';

import Utils from '../../utils/utils';
import Constants from '../../constants';
import CascadeManager from '../../cascade/manager';

import * as ActionTypes from './actions';

let store = null;
export const getReferenceTokens = (string) => string.split(/\.(?![^[]+\])|\[|\]/g).filter(Boolean);

/**
 * this function sets storeIn to store object
 *
 * @param {*} storeIn -- store object
 */
export const setStore = (storeIn) => {
  store = storeIn;
};

/**
 * this function returns store object
 *
 * @returns {*} returns store
 */
export const getStore = () => {
  return store;
};

/**
 * this function is wrapper around redux dispatch function and dispatches the action object over store
 *
 * @param {*} action -- action object
 */
export const dispatch = (action) => {
  if (!action) return;
  getStore()?.dispatch(action);
  if (action.type === 'MERGE_DATA') {
    CascadeManager.handleServerChanges(action.payload.data, action.payload.context, action.payload.rootPageReference);
  }
};

/**
 * this function will help to provide context data for the given context
 *
 * @param {string} context -- context name
 * @returns {*} returns -- State Object for the given Context
 */
export const getDataObject = (context) => {
  return getStore().getState().data?.[context];
};

/**
 * This function returns the shared (readonly) data pages from store
 * @public
 * @function getSharedDataObject
 * @returns {object} shared data object
 * @example <caption> Example for getSharedDataObject() </caption>
 * const sharedData = pConnn.getSharedDataObject();
 */
export const getSharedDataObject = () => {
  return getStore().getState().data[Constants.SHARED];
};

/**
 * this function will help to check wether given string is property refrence or not
 *
 * @param {string} str -- Property Name
 * @returns {boolean} returns -- true if str is property otherwise
 *                            -- false
 */
const isProperty = (str) => {
  return str && str.charAt(0) !== '"' && str.charAt(0) !== "'";
};

/**
 * this function will help to trim quotes for given string
 *
 * @param {string} str -- Property Name
 * @returns {string} returns -- trimmed str value
 */
const trimQuotes = (str) => {
  return str && str.trim().substring(1, str.length - 1);
};

/**
 * this function will help to get value of the property from state
 *
 * @param {string} propReference -- property reference e.g. .FirstName or pyWorkPage.FirstName
 * @param {string} pageReference -- page reference e.g. .pyWorkPage, D_Accounts.pxResults(1)
 * @param {*} state -- state object
 * @returns {*} returns -- value of the prop
 */
const _getValue = (propReference, pageReference, state) => {
  if (!isProperty(propReference)) {
    return trimQuotes(propReference);
  }
  const fullReference = propReference.charAt(0) === '.' ? pageReference + propReference : propReference;
  const refTokens = getReferenceTokens(fullReference);
  return Utils.getIn(state, refTokens);
};

/**
 * this function will help to get value of the prop from state
 *
 * @param {string} propReference property reference e.g. .FirstName or pyWorkPage.FirstName
 * @param {string} pageReference page reference e.g. .pyWorkPage, D_Accounts.pxResults(1)
 * @param {string} context context of the property
 * @returns {*} returns -- value of the prop
 */
export const getValue = (propReference, pageReference, context) => {
  return _getValue(propReference, pageReference, getDataObject(context));
};

export const getContextValue = (path, prop, context) => {
  if (path) {
    return _getValue(prop, `${Constants.CONTEXT_DATA}.${path}`, getDataObject(context));
  }
  return _getValue(prop, `${Constants.CONTEXT_DATA}`, getDataObject(context));
};

const lookupValue = (state, pageReference, prop) => {
  const refTokens = getReferenceTokens(`${pageReference}.${prop}`);
  return Utils.getIn(state, refTokens);
};

export const lookupContextValue = (path, prop, context) => {
  return lookupValue(getDataObject(context), `${Constants.CONTEXT_DATA}.${path}`, prop);
};

export const getActionType = (options) => {
  const { context } = options;
  const contextObj = getStore().getState().data[context];

  if (contextObj) return ActionTypes.UPDATE_DOCUMENT;

  const { action } = options;

  if (['createWork', 'showHarness', 'openAssignment', 'openWorkByHandle', 'initRouting'].indexOf(action) !== -1) {
    return ActionTypes.ADD_CONTAINER_ITEM;
  }

  return ActionTypes.UPDATE_DOCUMENT;
};

/**
 * this function helps to remove ref path in state object
 *
 * @param {*} state -- State object
 * @param {*} fullReference -- full reference of nested structure in state object
 * @returns {*} returns -- updated state object
 */
export const removeItem = (state, fullReference) => {
  const refTokens = getReferenceTokens(fullReference);
  return Utils.deleteIn(state, refTokens);
};

export const isMergeable = (obj) => {
  return !Array.isArray(obj) && typeof obj === 'object';
};

export const mergeDeep = (originalData, newObjectData, mergeOptions = {}) => {
  const { isArrayDeepMerge } = mergeOptions;
  if (Array.isArray(originalData) && !newObjectData) {
    // if originalData is of type Array newObject type might be of type Array but because of null
    // it returns null.
    newObjectData = [];
  }
  const mergeDeepClosure = (original, newObject) => {
    if (
      isMergeable(original) &&
      newObject !== null &&
      typeof newObject !== 'string' &&
      typeof newObject !== 'number' &&
      typeof newObject !== 'boolean'
    ) {
      return lodashMergeWith(original, newObject, mergeDeepClosure);
    }

    if (Array.isArray(original) && newObject !== null && Array.isArray(newObject)) {
      if (!isArrayDeepMerge) {
        const newLength = newObject.length;
        if (typeof isArrayDeepMerge === 'undefined' && newLength === original.length) {
          return lodashMergeWith(original, newObject, mergeDeepClosure);
        }
      } else {
        return lodashMergeWith(original, newObject, mergeDeepClosure);
      }
    }
    if (newObject === '' && original && typeof original === 'object') {
      return original;
    }
    return newObject;
  };
  return mergeDeepClosure(originalData, newObjectData);
};

/**
 * this function helps to update value in state object that has sent in corresponding reference
 *
 * @param {*} state -- State object
 * @param {*} fullReference -- full reference of nested structure in state object
 * @param {*} value -- value to update in reference path
 * @param {object} options -- javascript object containing optional parameters like isArrayDeepMerge etc.
 * @param {boolean} options.isArrayDeepMerge - flag to decide whether to deep merge the objects or values inside array or not
 * @returns {*} returns -- updated state object
 */
export const updateValue = (state, fullReference, value, options = {}) => {
  const { isArrayDeepMerge = true } = options;
  const refTokens = getReferenceTokens(fullReference);
  if (typeof value === 'object') {
    const draft = mergeDeep(Utils.getIn(state, refTokens), value, {
      isArrayDeepMerge
    });
    return Utils.setIn(state, refTokens, draft);
  }
  return Utils.setIn(state, refTokens, value);
};

/**
 * this function helps to replace value in state object that has sent in corresponding reference
 *
 * @param {*} state -- State object
 * @param {*} fullReference -- full reference of nested structure in state object
 * @param {*} value -- value to update in reference path
 * @returns {*} returns -- updated state object
 */
export const replaceValue = (state, fullReference, value) => {
  const refTokens = getReferenceTokens(fullReference);
  return Utils.setIn(state, refTokens, value);
};
