import AnnotationsHandler from '../../annotations-handler';

import { createExpression, buildAST, evaluateExpression } from './utils';

AnnotationsHandler.registerHandler('@E', (value, c11nEnv) => {
  try {
    const expression = createExpression(value);
    const exprAst = buildAST(expression);
    return evaluateExpression(exprAst, c11nEnv);
  } catch (e) {
    /* empty catch block */
  }
  return true;
});
