import PegaConstants from '../constants';
import Utils from '../utils/utils';
import DataStore from '../datapage/index';

import DataApiUtils from './DataApiUtils';

class DataApi {
  constructor() {
    this.isInitialised = false;
    this.isQueryable = true;
    this.dataSourceMetaData = {};
    this.nonQueryableData = [];
    this.fetchedNQData = false;
    this.previousSearchText = null;
    this.totalRecordCount = 0;
    this.fetchedRecordCount = 0;
    this.cache = {};
    this.pageNumber = 0;
  }

  static _searchResultsCache = {};

  static init(config, context) {
    return new Promise((resolve, reject) => {
      const columnsList = config.columns.map((column) => column.value);
      const paramList = Object.entries(config.parameters).map(
        ([paramName, paramValue]) => `${paramName}+${paramValue}`
      );
      const uniqueList = [...columnsList, ...paramList].join(PegaConstants.HASH_DELIMITER);
      const hashDigest = Utils.getHashedString(uniqueList);
      const uniqueDataApiKey = `${config.dataSource}_${hashDigest}`;
      const cacheKey = DataApiUtils.getAppCacheKey(config.dataSource);
      if (window.sessionStorage.getItem(cacheKey)) {
        const metaDataCache = JSON.parse(window.sessionStorage.getItem(cacheKey));
        if (context && DataApi._searchResultsCache?.[context]?.[uniqueDataApiKey]) {
          resolve(DataApi._searchResultsCache[context][uniqueDataApiKey]);
        } else if (config.cacheLifeSpan !== 'form' && DataApi._searchResultsCache?.persisted?.[uniqueDataApiKey]) {
          resolve(DataApi._searchResultsCache.persisted[uniqueDataApiKey]);
        } else {
          const metaDataObj = new DataApi();
          metaDataObj.listType = config.listType;
          metaDataObj.dataSource = config.dataSource;
          if (config.enablePagination) {
            metaDataObj.enablePagination = config.enablePagination;
            metaDataObj.pageSize = config.pageSize;
          } else {
            metaDataObj.maxResultsDisplay = config.maxResultsDisplay;
          }
          metaDataObj.parameters = config.parameters;
          metaDataObj.columns = config.columns;
          metaDataObj.matchPosition = config.matchPosition;
          metaDataObj.context = context;
          if (JSON.stringify(metaDataCache.associationFilter) === JSON.stringify(config.associationFilter)) {
            metaDataObj.dataSourceMetaData = metaDataCache.dataSourceMetaData;
            metaDataObj.isQueryable = metaDataCache.isQueryable;
            metaDataObj.isInitialised = metaDataCache.isInitialised;
            metaDataObj.associationFilter = metaDataCache.associationFilter;
            DataApi.initializeCache(context, config, uniqueDataApiKey, metaDataObj);
            resolve(metaDataObj);
          } else {
            metaDataObj.associationFilter = config.associationFilter;
            metaDataObj
              .fetchMetaData(context)
              .then((response) => {
                window.sessionStorage.setItem(
                  cacheKey,
                  JSON.stringify({
                    isQueryable: response.data.isQueryable,
                    dataSourceMetaData: response.data,
                    isInitialised: true,
                    associationFilter: config.associationFilter
                  })
                );
                metaDataObj.isQueryable = response.data.isQueryable;
                metaDataObj.dataSourceMetaData = response.data;
                metaDataObj.isInitialised = true;
                DataApi.initializeCache(context, config, uniqueDataApiKey, metaDataObj);
                resolve(metaDataObj);
              })
              .catch((err) => {
                metaDataObj.isInitialised = false;
                reject(err);
              });
          }
        }
      } else if (config.deferDatasource) {
        const dataApiObj = new DataApi();
        dataApiObj.listType = config.listType;
        dataApiObj.dataSource = config.dataSource;
        if (config.enablePagination) {
          dataApiObj.enablePagination = config.enablePagination;
          dataApiObj.pageSize = config.pageSize;
        } else {
          dataApiObj.maxResultsDisplay = config.maxResultsDisplay;
        }
        dataApiObj.dataSourceMetaData = config.dataSourceMetaData;
        dataApiObj.parameters = config.parameters;
        dataApiObj.columns = config.columns;
        dataApiObj.matchPosition = config.matchPosition;

        const datapageMetadata = PCore.getMetadataUtils().getDataPageMetadata(config.dataSource);
        dataApiObj.isQueryable = datapageMetadata.isQueryable;
        dataApiObj.isInitialised = true;
        dataApiObj.dataSourceMetaData = datapageMetadata;
        window.sessionStorage.setItem(
          cacheKey,
          JSON.stringify({
            isQueryable: datapageMetadata.isQueryable,
            dataSourceMetaData: datapageMetadata,
            isInitialised: true
          })
        );
        dataApiObj.context = context;
        DataApi.initializeCache(context, config, uniqueDataApiKey, dataApiObj);
        resolve(dataApiObj);
      } else {
        const dataApiObj = new DataApi();
        dataApiObj.listType = config.listType;
        dataApiObj.dataSource = config.dataSource;
        if (config.enablePagination) {
          dataApiObj.enablePagination = config.enablePagination;
          dataApiObj.pageSize = config.pageSize;
        } else {
          dataApiObj.maxResultsDisplay = config.maxResultsDisplay;
        }
        dataApiObj.dataSourceMetaData = config.dataSourceMetaData;
        dataApiObj.parameters = config.parameters;
        dataApiObj.columns = config.columns;
        dataApiObj.matchPosition = config.matchPosition;
        dataApiObj.context = context;
        dataApiObj.associationFilter = config.associationFilter;
        dataApiObj
          .fetchMetaData(context)
          .then((response) => {
            window.sessionStorage.setItem(
              cacheKey,
              JSON.stringify({
                isQueryable: response.data.isQueryable,
                dataSourceMetaData: response.data,
                isInitialised: true,
                associationFilter: config.associationFilter
              })
            );
            dataApiObj.isQueryable = response.data.isQueryable;
            dataApiObj.dataSourceMetaData = response.data;
            dataApiObj.isInitialised = true;
            DataApi.initializeCache(context, config, uniqueDataApiKey, dataApiObj);
            resolve(dataApiObj);
          })
          .catch((err) => {
            dataApiObj.isInitialised = false;
            reject(err);
          });
      }
    });
  }

  static clearContextedCache(context) {
    delete DataApi._searchResultsCache[context];
  }

  static initializeCache(context, config, uniqueDataApiKey, dataApiObj) {
    if (context && config.cacheLifeSpan?.toLowerCase() === 'form') {
      if (DataApi._searchResultsCache[context] === undefined) {
        DataApi._searchResultsCache[context] = {};
      }
      DataApi._searchResultsCache[context][uniqueDataApiKey] = dataApiObj;
    } else {
      if (DataApi._searchResultsCache.persisted === undefined) {
        DataApi._searchResultsCache.persisted = {};
      }
      DataApi._searchResultsCache.persisted[uniqueDataApiKey] = dataApiObj;
    }
  }

  fetchMetaData(context) {
    return DataApiUtils.getDataViewMetadata(this.dataSource, context, this.associationFilter);
  }

  getData(searchText) {
    if (this.isQueryable) {
      const queryableDPSchema = this.buildSchemaForQueryableDP(searchText);
      return DataStore.getDataAsync(
        this.dataSource,
        this.context,
        queryableDPSchema.dataViewParameters,
        queryableDPSchema.paging,
        queryableDPSchema.query
      );
    }
    return this.fetchNonQueryableResults(searchText);
  }

  memoizedGetData(searchText) {
    const isResultCached = this.enablePagination
      ? searchText in this.cache && this.pageNumber in this.cache[searchText]
      : searchText in this.cache;
    if (isResultCached) {
      return this.enablePagination ? this.cache[searchText][this.pageNumber] : this.cache[searchText];
    }
    const result = this.getData(searchText);
    if (this.enablePagination) {
      this.cache[searchText] = this.cache[searchText] || {};
      this.cache[searchText][this.pageNumber] = result;
    } else {
      this.cache[searchText] = result;
    }
    return result;
  }
  /* eslint prefer-promise-reject-errors: ["error", {"allowEmptyReject": true}] */

  fetchData(searchText, isPaginated) {
    if (!this.isValidDatasource()) {
      return Promise.reject();
    }
    if (this.isInitialised) {
      this.pageNumber = isPaginated ? this.pageNumber : 1;
      if (searchText !== undefined && searchText !== null) {
        searchText = searchText.toLowerCase();
      }
      return this.memoizedGetData(searchText);
    }
    return Promise.reject();
  }

  fetchPaginatedData(searchText) {
    this.pageNumber += 1;
    return this.fetchData(searchText, true);
  }

  fetchNonQueryableResults(searchText) {
    if (this.fetchedNQData) {
      return new Promise((resolve) => {
        return resolve({
          data: this.filterNonQueryableData(searchText)
        });
      });
    }
    if (this.dataSourceMetaData.structure && this.dataSourceMetaData.structure.toLowerCase() === 'list') {
      return new Promise((resolve, reject) => {
        const nonQueryableDPSchema = this.buildSchemaForNonQueryableDP();
        DataStore.getDataAsync(this.dataSource, this.context, nonQueryableDPSchema.dataViewParameters)
          .then((response) => {
            this.nonQueryableData = response.data ? [...response.data] : [];
            this.fetchedNQData = true;
            return resolve({
              data: this.filterNonQueryableData(searchText)
            });
          })
          .catch((err) => reject(err));
      });
    }
    return null;
  }

  filterNonQueryableData(searchText) {
    const searchColumns = this.columns.filter((column) => column.useForSearch).map((column) => column.value);
    return this.nonQueryableData.reduce((filteredResults, result) => {
      Object.keys(result).every((key) => {
        if (searchColumns.includes(key) && result[key] && this.positionMatches(result[key], searchText)) {
          filteredResults.push({ ...result });
          return false;
        }
        return true;
      });
      return filteredResults;
    }, []);
  }

  positionMatches(text, searchText) {
    switch (this.matchPosition) {
      case 'start':
        return text.toLowerCase().startsWith(searchText.toLowerCase());
      case 'contains':
        return text.toLowerCase().includes(searchText.toLowerCase());
      case 'equals':
        return text.toLowerCase() === searchText.toLowerCase();
      default:
        return false;
    }
  }

  // Build Selecet
  buildSelect() {
    const selectArray = [];
    const colsArr = [];
    this.columns.forEach((column) => {
      if (!colsArr.includes(column.value)) {
        colsArr.push(column.value);
        const selectObj = { field: column.value };
        selectArray.push(selectObj);
      }
    });
    return selectArray;
  }

  // Build filters
  buildFilters(searchText) {
    let filter = {};
    const filterConditions = {};
    this.columns.forEach((column, index) => {
      if (column.useForSearch) {
        const filterCondition = `F${index}`;
        const lhs = {
          field: column.value
        };
        const rhs = {
          value: searchText
        };
        let comparator = 'CONTAINS';

        if (this.matchPosition === 'start') {
          comparator = 'STARTS_WITH';
        } else if (this.matchPosition === 'equals') {
          comparator = 'EQ';
        }
        const filterConditionObj = {
          ...(Number.isNaN(parseFloat(searchText)) && { ignoreCase: 'true' }),
          lhs,
          rhs,
          comparator
        };
        filterConditions[filterCondition] = filterConditionObj;
      }
    });
    const logicString = Object.keys(filterConditions).join(' OR ');
    filter = {
      filterConditions,
      logic: logicString
    };
    return filter;
  }

  buildQuery(searchText) {
    if (!searchText) {
      return {
        select: this.buildSelect(),
        distinctResultsOnly: 'true'
      };
    }
    return {
      select: this.buildSelect(),
      filter: this.buildFilters(searchText),
      distinctResultsOnly: 'true'
    };
  }

  buildPageOptions() {
    return this.enablePagination
      ? {
          pageNumber: this.pageNumber,
          pageSize: this.pageSize
        }
      : {
          maxResultsToFetch: this.maxResultsDisplay
        };
  }

  buildSchemaForQueryableDP(searchText) {
    return {
      dataViewParameters: this.parameters,
      query: this.buildQuery(searchText),
      paging: this.buildPageOptions()
    };
  }

  buildSchemaForNonQueryableDP() {
    return {
      dataViewParameters: this.parameters
    };
  }

  isValidDatasource() {
    return !!this.dataSource;
  }
}

export default DataApi;
