import merge from 'lodash.mergewith';
import { produce } from 'immer';

import Utils from '../../../../utils/utils';
import * as ActionTypes from '../../actions';
import { getInitialState } from '../index';
import { ActionManagerInstance } from '../../../../actions/manager';

const removeChildContainers = (draft, childContainers = []) => {
  childContainers.forEach((element) => {
    Utils.deleteIn(draft, [element]);
  });
};

const mergePayloadInContainerItem = (draft, action) => {
  const { type, payload } = action;
  const isUpdate = type === ActionTypes.UPDATE_CONTAINER_ITEM;
  const {
    target,
    context,
    semanticURL = '',
    root,
    containerItemID,
    childContainers,
    flowName,
    resourceType = '',
    key = '',
    clearView = false
  } = payload;
  if(!root){
    if(clearView){
      Utils.deleteIn(draft, [target, 'items', containerItemID, 'view']);
    } 
  } else {
    Utils.setIn(draft, [target, 'items', containerItemID, 'view'], root);
  }

  if (context) Utils.setIn(draft, [target, 'items', containerItemID, 'context'], context);
  if (flowName) Utils.setIn(draft, [target, 'items', containerItemID, 'flowName'], flowName);

  /* for update container item action, key can be optional (so will have previous value if key is not recieved in payload */
  if (!isUpdate || (isUpdate && key)) {
    Utils.setIn(draft, [target, 'items', containerItemID, 'key'], key.trim());
  }

  /* for update container item action, semanticURL can be optional (so will have previous value if semanticURL is not recieved in payload */
  if (!isUpdate || (isUpdate && semanticURL)) {
    Utils.setIn(draft, [target, 'items', containerItemID, 'semanticURL'], semanticURL);
  }

  /* for update container item action, resourceType can be optional (so will have previous value if resourceType is not recieved in payload */
  if (!isUpdate || (isUpdate && resourceType)) {
    Utils.setIn(draft, [target, 'items', containerItemID, 'resourceType'], resourceType);
  }

  removeChildContainers(draft, childContainers);
  return draft;
};

const replaceContainerItem = (draft, action) => {
  const { actionMgrID, data, target, containerItemID } = action.payload;
  let previousDraft = draft;
  try {
    Utils.setIn(draft, [target, 'items', containerItemID, 'transientItems'], []);
    previousDraft = mergePayloadInContainerItem(draft, action);
    ActionManagerInstance.resolveActionByID(actionMgrID, data, false);
  } catch {
    ActionManagerInstance.rejectActionByID(actionMgrID, false);
  }

  return previousDraft;
};

const routeReducer = (state = getInitialState().containers, action) => {
  // SPECIAL CASE: RESET_STORE action just returns next state as initial state.
  //  No need to run the other reducers so we just return.
  if (action.type === ActionTypes.RESET_STORE) {
    return getInitialState().containers;
  }

  // immer's produce returns the next state so we return what it returns
  return produce(state, (draft) => {
    switch (action.type) {
      case ActionTypes.INIT_CONTAINERS: {
        const { context, name, type, actionMgrID } = action.payload;
        const target = `${context}/${name}`;
        let initObject = null;

        ActionManagerInstance.resolveActionByID(actionMgrID, null, false);
        if (draft[target]) {
          return draft;
        }
        initObject = {
          type,
          accessedOrder: [],
          items: {}
        };

        Utils.setIn(draft, [target], initObject);
        return draft;
      }

      case ActionTypes.REMOVE_CONTAINER: {
        const { containerName, childContainers = [] } = action.payload;
        Utils.deleteIn(draft, [containerName]);
        childContainers.forEach((container) => {
          Utils.deleteIn(draft, [container]);
        });
        return draft;
      }

      case ActionTypes.RESET_CONTAINERS: {
        const { context, name, actionMgrID, childContainers } = action.payload;
        const target = `${context}/${name}`;

        ActionManagerInstance.resolveActionByID(actionMgrID, null, false);

        Utils.setIn(draft, [target, 'accessedOrder'], []);
        Utils.setIn(draft, [target, 'items'], {});
        Array.isArray(childContainers) &&
          childContainers.forEach((container) => {
            Utils.deleteIn(draft, [container]);
          });
        return draft;
      }

      case ActionTypes.ADD_CONTAINER_ITEM: {
        const {
          target,
          context,
          isInCreateStage,
          isBulkAction,
          semanticURL,
          root,
          containerItemID,
          actionMgrID,
          key,
          flowName,
          resourceType,
          actionName,
          isMinimizable,
          showProgress,
          progressMessage,
          modalOptions,
          resourceStatus,
          isMultiRecordData,
          acTertiary,
          parentContext
        } = action.payload;
        const prevContainerState = draft[target];
        const type = prevContainerState && prevContainerState.type;
        const prevAccessedOrder = (prevContainerState && prevContainerState.accessedOrder) || [];
        const prevItems = (prevContainerState && prevContainerState.items) || {};
        let addContainerObject = null;

        if (draft[target] && type === 'single' && prevAccessedOrder.length > 0) {
          return mergePayloadInContainerItem(draft, action);
        }
        // handle the promise that was associated with the action...
        addContainerObject = {
          accessedOrder: [...prevAccessedOrder, containerItemID],
          items: {
            ...prevItems,
            [containerItemID]: {
              view: root,
              semanticURL,
              key,
              flowName,
              context,
              isInCreateStage,
              resourceType,
              actionName,
              isMinimizable,
              ...(isBulkAction && { isBulkAction }),
              showProgress,
              progressMessage,
              modalOptions,
              resourceStatus,
              ...(acTertiary && { acTertiary }),
              ...(isMultiRecordData && { isMultiRecordData }),
              ...(parentContext && { parentContext })
            }
          }
        };
        ActionManagerInstance.resolveActionByID(actionMgrID, null, false);
        draft[target] = merge(draft[target], addContainerObject);
        return draft;
      }

      case ActionTypes.UPDATE_CONTAINER_ITEM: {
        const { actionMgrID, data } = action.payload;
        let previousDraft = draft;
        try {
          previousDraft = mergePayloadInContainerItem(draft, action);
          ActionManagerInstance.resolveActionByID(actionMgrID, data, false);
        } catch {
          ActionManagerInstance.rejectActionByID(actionMgrID, false);
        }

        return previousDraft;
      }

      case ActionTypes.REPLACE_CONTAINER_ITEM: {
        return replaceContainerItem(draft, action);
      }

      case ActionTypes.ACTIVATE_CONTAINER_ITEM: {
        const { target, containerItemID, actionMgrID } = action.payload;
        try {
          const filteredccessedOrder = Utils.getIn(draft, [target, 'accessedOrder']).filter(
            (item) => item !== containerItemID
          );
          filteredccessedOrder.push(containerItemID);
          Utils.setIn(draft, [target, 'accessedOrder'], filteredccessedOrder);
          ActionManagerInstance.resolveActionByID(actionMgrID, null, false);
        } catch {
          ActionManagerInstance.rejectActionByID(actionMgrID, false);
        }
        return draft;
      }

      case ActionTypes.UPMERGE_AND_REMOVE_CONTAINER_ITEM:
      case ActionTypes.REMOVE_CONTAINER_ITEM: {
        const { target, containerItemID, actionMgrID, childContainers } = action.payload;
        try {
          const newAccessedOrder = Utils.getIn(draft, [target, 'accessedOrder']).filter(
            (item) => item !== containerItemID
          );
          Utils.setIn(draft, [target, 'accessedOrder'], newAccessedOrder);
          Utils.deleteIn(draft, [target, 'items', containerItemID]);
          removeChildContainers(draft, childContainers);
          ActionManagerInstance.resolveActionByID(actionMgrID, null, false);
        } catch {
          ActionManagerInstance.rejectActionByID(actionMgrID, false);
        }
        return draft;
      }

      case ActionTypes.ADD_TRANSIENT_ITEM: {
        const { transientItemID, target, contextName } = action.payload;
        const transientItems = Utils.getIn(draft, [target, 'items', contextName, 'transientItems']) ?? [];
        return Utils.setIn(
          draft,
          [target, 'items', contextName, 'transientItems'],
          [...transientItems, transientItemID]
        );
      }

      case ActionTypes.REMOVE_ALL_TRANSIENT_ITEMS: {
        const { target, containerItemID } = action.payload;
        Utils.deleteIn(draft, [target, 'items', containerItemID, 'transientItems']);
        return draft;
      }

      case ActionTypes.REMOVE_TRANSIENT_ITEM: {
        const { target, transientItemID, containerItemID } = action.payload;
        const transientItems = Utils.getIn(draft, [target, 'items', containerItemID, 'transientItems']) ?? [];
        const filteredTransientItems = transientItems.filter((item) => item !== transientItemID);

        return Utils.setIn(draft, [target, 'items', containerItemID, 'transientItems'], filteredTransientItems);
      }

      default:
        return draft;
    }
  });
};

export default routeReducer;
