import Utils from '../../../utils/utils';
import constants from '../../../constants';
import DataPageUtils from '../../../datapage/index';
import { CONTAINER_NAMES } from '../container-utils';

/**
 * This function helps to add the shared data
 * @param {*} draft - immer draft
 * @param {*} sharedData - shared data
 * @private
 */
const addSharedData = (draft, sharedData) => {
  DataPageUtils.updateDataPageStore(sharedData);
  Object.keys(sharedData).forEach((key) => {
    if (Utils.getIn(draft, [constants.SHARED, key])) {
      Object.keys(sharedData[key]).forEach((nestedKey) => {
        Utils.setIn(draft, [constants.SHARED, key, nestedKey], sharedData[key][nestedKey]);
      });
    } else {
      Utils.setIn(draft, [constants.SHARED, key], sharedData[key]);
    }
  });
};

/**
 * This function helps to add the shared data
 * @param {*} draft - immer draft
 * @param {*} payload - payload info e.g. { data: { shared: {} }, context: 'app/primary_1' }
 * @private
 */
export const processSharedData = (draft, payload) => {
  const { data: { shared = {} } = {}, context } = payload;
  addSharedData(draft, shared);

  if (context) {
    Utils.deleteIn(draft, [context, 'shared']);
  }
};

export const setCaseViewMode = (draft, payload, skipDefaultValue = false) => {
  const { context, caseViewMode = !skipDefaultValue ? constants.PERFORM_MODE : undefined, containerName } = payload;
  if (containerName !== CONTAINER_NAMES.WORKAREA && caseViewMode) {
    Utils.setIn(draft, [context, 'context_data', 'caseViewMode'], caseViewMode);
  }
};

export const setActionTypeAndAssignmentId = (draft, payload) => {
  const { context } = payload;
  Utils.setIn(draft, [context, 'context_data', 'actionType'], payload.actionType);
  Utils.setIn(draft, [context, 'context_data', 'assignmentID'], payload.assignmentID);
};
