export const TOKEN = /{(.*?)}/i;
export const GTOKEN = /{(.*?)}/g;

/**
 * this method returns true if pattern is dynamic, else returns false
 * @function isDynamicPattern
 * @param {*} pattern - route/route path token
 * @returns {boolean} isDynamicRoute Flag
 */
export const isDynamicPattern = (pattern) => {
  return pattern.match(TOKEN) !== null;
};

/**
 * this method trims the first and last / from the string
 * @function trimURL
 * @param {string} str - string
 */
export const trimURL = (str) => {
  if (str) {
    if (str[0] === '/') {
      str = str.substring(1, str.length);
    }

    if (str[str.length - 1] === '/') {
      str = str.substring(0, str.length - 1);
    }
  }
  return str;
};

/**
 * this method returns plural form of string
 * @function getPlural
 * @param {string} str - string
 * @returns {string} - pluralized string
 */
export const getPlural = (str) => {
  const lastChar = str[str.length - 1];
  const secondLastChar = str[str.length - 2];

  if (lastChar.toLowerCase() !== 'y') {
    return `${str}s`;
  }

  switch (secondLastChar.toLowerCase()) {
    case 'a':
    case 'e':
    case 'i':
    case 'o':
    case 'u':
      return `${str}s`;

    default:
      str = str.substring(0, str.length - 1);
      return `${str}ies`;
  }
};

/**
 * this method checks if the string has some numeric substring
 * @function hasNumber
 * @param {string} str - string
 * @returns {boolean} - true if numeric substring exists, else false
 */
export const hasNumber = (str) => {
  return /\d/.test(str);
};

/**
 * this method capitalize first letter of each word in the string
 * @function capitalizeFirstLetter
 * @param {string} string - string
 * @returns {string} - string with each word capitalize
 */
export const capitalizeFirstLetter = (string) => {
  let arr = string.split(' ');
  arr = arr.map((element) => element.charAt(0).toUpperCase() + element.slice(1));
  return arr.join(' ');
};

/**
 * this method checks for the portal query param present in the given url
 * @param {URLSearchParams} urlSearchParam - URLSearchParams object
 * @returns {boolean} - true if portal query param exists in the url
 */
export const isPortalParamExists = (urlSearchParam) => {
  const urlParam = urlSearchParam.toString();
  return urlParam.indexOf('portal=') >= 0;
};
