let componentCreator = null;
const resolveStateProps = (config, stateObj, parentstateObjInfo) => {
  if (!config) {
    return;
  }
  Object.keys(config).forEach((key) => {
    if (key !== 'children') {
      // Don't process children
      const valueWithAnnotation = config[key];
      const value = AnnotationProcessor.trimAnnotation(valueWithAnnotation);
      if (
        (typeof value === 'string' || typeof valueWithAnnotation === 'string') &&
        (Utils.isPropertyRef(value) || valueWithAnnotation.startsWith('@W ') || valueWithAnnotation.startsWith('@E '))
      ) {
        if (parentstateObjInfo && parentstateObjInfo.parentKey) parentstateObjInfo.isObjHasValue = true;
        stateObj[key] = value;
      } else if (Array.isArray(value)) {
        value.forEach((p, i) => {
          const parentKeyObjToSend = {
            parentStateObj: stateObj,
            parentKey: key,
            isObjHasValue: !!stateObj[key]
          };
          stateObj[key] = stateObj[key] || [];
          stateObj[key][i] = Object.create({});
          resolveStateProps(p, stateObj[key][i], parentKeyObjToSend);
        });
      } else if (value && typeof value === 'object' && value.type !== 'exp') {
        const parentKeyObjToSend = {
          parentStateObj: stateObj,
          parentKey: key,
          isObjHasValue: !!stateObj[key]
        };
        stateObj[key] = stateObj[key] || {};
        resolveStateProps(value, stateObj[key], parentKeyObjToSend);
      }
    }
  });
  if (parentstateObjInfo && !parentstateObjInfo.isObjHasValue) {
    delete parentstateObjInfo.parentStateObj[parentstateObjInfo.parentKey];
  }
};

const mergeInheritedProps = (_inheritedConfig, rawConfig) => {
  _inheritedConfig._inheritedProps = _inheritedConfig._inheritedProps || [];
  _inheritedConfig.inheritedProps = [..._inheritedConfig._inheritedProps, ...Utils.deepClone(rawConfig.inheritedProps)];
};

class C11nEnv {
  constructor(config) {
    const { meta, isRoot, options = {}, useCustomContext } = config;
    this.meta = meta;
    this._isRoot = isRoot;
    this.options = options;
    this.useCustomContext = useCustomContext;
    this._rawConfig = meta?.config;
    this._isExpressionExist = false;
    this._isWhenExist = false;
    this._isValidatorExist = false;
    this._pageReference = '';
    this._referenceContext = '';
    this._contextName = '';
    this._stateProps = {};
    this._actions = {};
    this._children = '';
    this._additionalRegisteredProps = {};
    this._caseInfo;
    this.viewName = options.viewName;
    this._referenceList = options.referenceList || '';
    this._isInsideList = options.isInsideList || false;
    this._inheritedConfig = {};
    this._localeRuleName = options.localeReference;
    this._containerTarget = options.target;
    this.xrayInfo = {};
    this._readOnly = meta?.config?.readOnly || options.readOnly;
    this._hasSuggestions = false;
    if (options.inheritedConfig) {
      this._inheritedConfig = Utils.deepClone(options.inheritedConfig);
    }
    if (meta?.config?.inheritedProps) {
      mergeInheritedProps(this._inheritedConfig, meta?.config);
    }

    resolveStateProps(this._rawConfig, this._stateProps);
    /**
     * Returns true if there is any state associated with this object
     * @private
     */
    this._isState = !Utils.isObjectEmpty(this._stateProps);
    if (this._rawConfig && this._rawConfig.config) {
      this._isExpressionExist = Utils.isExpressionExist(this._rawConfig.config);
      this._isWhenExist = Utils.isWhenExist(this._rawConfig.config);
      this._isValidatorExist = Validator.isValidatorExist(this._rawConfig.config);
    }

    // [TO-DO] need to remove app/primary_0 hardcode check after viewContainer implementation
    if (this._isRoot) {
      this._contextName = publicConstants.APP.APP;
    } else {
      this._contextName = this.meta?.config?.contextName || this.options.context;
    }

    // Process meta
    this._type = this.meta?.type;

    let optionalPageReference = this.options.pageReference;
    if (optionalPageReference) {
      // resolving pageReference that is passed as options
      optionalPageReference = AnnotationProcessor.handleAnnotation(this.options.pageReference, this);
    }
    this._pageReference = ContextProcessor.getContextPath(
      optionalPageReference || this.options.parentPageReference, // considering parent page reference when current configured reference is invalid
      this._pageReference
    );

    if (!this._pageReference) {
      this._pageReference = '';
    }

    if (this._type === 'reference') {
      this._rawConfig = meta;
    }

    if (this._type === 'reference' && Utils.isWhenExist({ visibility: this.meta?.config.visibility })) {
      recordViewForRefresh(this.viewName, this._contextName, this._pageReference);
    }

    if (this._type !== 'reference' && meta) {
      this._rawConfig = ContextProcessor.resolveReference(meta, this);
    }
    if (this._rawConfig) {
      // Process conditions
      const _metaConfig = this.getRawConfigProps();
      if (_metaConfig) {
        this._localeRuleName =
          AnnotationProcessor.trimAnnotation(_metaConfig.localeReference) || options.localeReference;
        this._isExpressionExist = Utils.isExpressionExist(_metaConfig);
        this._isWhenExist = Utils.isWhenExist(_metaConfig);
        this._isValidatorExist = Validator.isValidatorExist(_metaConfig);
      }

      this._children = this._rawConfig.children;

      if (
        this.options.hasForm &&
        this._rawConfig.config &&
        (Object.prototype.hasOwnProperty.call(this._rawConfig.config, 'value') ||
          this._rawConfig.config.selectionList ||
          (this._rawConfig.config.renderMode === 'Editable' && this._rawConfig.config.referenceList)) &&
        (!this._rawConfig.config.readOnly || this._rawConfig.config.readOnly !== 'true')
      ) {
        setFormFieldAsEditable(this._contextName, this.getFullReference(), this);
      }

      // Populate view Name
      if (this._rawConfig?.type?.toLowerCase() === constants.VIEW && this._rawConfig.name) {
        this.viewName = AnnotationProcessor.handleAnnotation(this._rawConfig.name, this);
      }

      // If component is present in form, then only inspect for refresh targets.
      if (this.options.hasForm) {
        inspectComponentForRefreshTargets(this);
      }
    }
  }

  /**
   * Sets the xray info for xray tool.
   * @function setXRayInfo
   * @param {object} info xray info
   * @private
   */
  setXRayInfo(info) {
    Utils.mergeDeep(info, this.xrayInfo);
  }

  /**
   * Returns the xray info object.
   * @function getXRayInfo
   * @returns {object} xray info object.
   * @private
   */
  getXRayInfo() {
    return this.xrayInfo;
  }

  /**
   * Returns the properties which are bound to state, in sense these properties
   * are reactive.
   * @function getStateProps
   *
   * @example
   * Sample:
   * {
   *   "FirstName": "test value"
   * }
   * @example <caption>getPConnect().getStateProps()</caption>
   * const props = getPConnect().getStateProps();
   *
   * @returns {object} the properties that are bound to the Redux state
   */
  getStateProps() {
    return this._stateProps;
  }

  /**
   * Set the properties which are bound to state, in sense these properties
   * are reactive.
   * @example <caption>getPConnect().setStateProps({'datasourceMetadata':'@FIELD_META .Test'})</caption>
   * @param {object} propsObj object containing each prop name and its value
   * @private
   */
  setStateProps(propsObj) {
    Utils.mergeDeep(propsObj, this._stateProps);
  }

  /**
   * Returns the metadata, this is raw, unresolved metadata
   * @returns {object} metadata
   * @private
   */
  getMetadata() {
    return this._rawConfig;
  }

  /**
   * Returns instance of CaseInfo class bound to redux store current container context
   * @function getCaseInfo
   * @returns {CaseInfo} Returns instance of the CaseInfo class
   */
  getCaseInfo() {
    if (!this._caseInfo) {
      this._caseInfo = new CaseInfo(this);
    }
    return this._caseInfo;
  }

  /**
   * Obtains an entry point to the ListActions class that contains APIs that handle list related actions for
   * components that display page list data and allow modifications on the list.
   * @function getListActions
   * @returns {object} {@link module:ListActions|ListActions} the ListActions object.
   * @example  <caption>In the example </caption>
   * getPConnect().getListActions();
   */
  getListActions() {
    return listActions(this);
  }

  getReferenceList() {
    return this._referenceList;
  }

  isInsideList() {
    return this._isInsideList || /\[\d+\]/g.test(this.getPageReference());
  }

  /**
   * <strong>For Advanced Use Only</strong><br>
   * Returns the component's complete metadata in its raw and unresolved format.
   * This API should only
   * be used in advanced use cases where a component needs the complete,
   * unresolved metadata. <strong>In most situations, the component should
   * only require getConfigProps() to get the resolved configuration metadata of
   * the component.</strong><br><br>
   * <strong>NOTE: the format and content of the metadata returned
   * may change as the DX API changes.</strong>
   *
   * @function getRawMetadata
   * @returns {object} The raw, unresolved metadata for the component
   * @public
   *
   * @example  <caption>In the example below, we see the raw, unresolved metadata for a simple "Table" component. </caption>
   *
   * {
   *   "type": "Table",
   *   "config": {
   *     "referenceList": "D_pyMyWorkList.pxResults",
   *     "rowClickAction": "openAssignment",
   *     "personalizationId": "pyMyWork-D_pyMyWorkList",
   *     "fields": [
   *       {
   *         "type": "Text",
   *         "config": { "label": "@L Priority", "formatType": "number", "value": "@P .pxUrgencyAssign" }
   *       },
   *       {
   *         "type": "Text",
   *         "config": { "label": "@L ID", "value": "@P .pxRefObjectInsName" }
   *       },
   *       {
   *         "type": "Text",
   *         "config": { "label": "@L Label", "value": "@P .pyLabel" }
   *       },
   *       {
   *         "type": "DateTime",
   *         "config": { "sortDirection": "DESC", "sortPriority": "1", "label": "@L Creation date/time", "value": "@P .pxCreateDateTime" }
   *       },
   *       {
   *         "type": "Text",
   *         "config": { "label": "@L Status", "value": "@P .pyAssignmentStatus" }
   *       }
   *     ]
   *   }
   * }
   *
   */
  getRawMetadata() {
    return this.getMetadata();
  }

  setReferenceList(ref) {
    this._referenceList = ref;
  }

  /**
   * Returns the component's configuration information
   * @function getComponentConfig
   * @example
   * example: {
   *    label: "@P .FirstName"
   * }
   *
   * @example <caption>getPConnect().getComponentConfig()</caption>
   * const config = getPConnect().getComponentConfig();
   * the value of config gets the configuration of the component.
   *
   * @returns {object} component configuration information
   * @private
   */
  getComponentConfig() {
    return this.getMetadata()?.config || {};
  }

  /**
   * Checks whether component has expression configured
   * or not.
   *
   * @function isExpressionExist
   * @returns {boolean} true or false
   * @private
   */
  isExpressionExist() {
    return this._isExpressionExist;
  }

  /**
   * Checks whether component has when configured
   * or not.
   * @function isWhenExist
   * @returns {boolean} true or false
   * @private
   */
  isWhenExist() {
    return this._isWhenExist;
  }

  /**
   * This APIchecks if the componentis associated with a condition (such as an expression or when condition) and returns true if anyone of them exists.
   * @function isConditionExist
   * @example <caption> Usage of isConditionExist - getPConnect().isConditionExist()</caption>
   * Usage: const isConditionConfigured = getPConnect().isConditionExist();
   * value of isConditionConfigured will be true or false based on current component configuration.
   *
   * @returns {boolean} true or false
   * @public
   */
  isConditionExist() {
    return this.isExpressionExist() || this.isWhenExist();
  }

  /**
   * Checks whether current component is bound to state or not.
   * If component has any property reference, it will be treated
   * as bound to state.
   *
   * @function isBoundToState
   *
   * @example <caption>getPConnect().isBoundToState()</caption>
   * const isStateBound = getPConnect().isBoundToState();
   * In this example, the API returns a Boolean value which denotes if the current component is bound to a Redux State.
   *
   * @returns {boolean} True if bound to state
   */
  isBoundToState() {
    return this._isState;
  }

  /**
   * Returns the data reference path of the store,
   * where the data value has been stored for current component.
   * @function getPageReference
   *
   * @example
   * For example:
   * @example <caption> While loading a portal(App level)</caption>
   * pageReference value is: "pyPortal"
   * @example <caption> Single component </caption>
   * For components like 'FirstName, LastName', pageReference value is: "caseInfo.content"
   * @example <caption> Embeded components which are under embedded view </caption>
   * For components like 'ZipCode, StreetName' which are under Address view, pageReference value is :"caseInfo.content.Address"
   *
   * @returns {string} pageReference path
   * @public
   */
  getPageReference() {
    return this._pageReference;
  }

  getFullReference() {
    const pageReference = this.getPageReference();
    const componentConfig = this.getComponentConfig();
    let propKey = 'value';
    if (componentConfig.selectionMode === 'multi') {
      propKey = 'selectionList';
    } else if (componentConfig.renderMode === 'Editable') {
      propKey = 'referenceList';
    }
    const prop = this.getComponentConfig()[propKey];
    return ContextProcessor.getContextPath(pageReference, prop);
  }

  /**
   * this function returns the context name under which the component has rendered.
   *
   * @public
   * @function getContextName
   * @returns {string} context name
   *
   * @example <caption>Example for getContextName()</caption>
   * const context = getPConnect().getContextName();
   * //example output : returns => 'app/primary_1'
   *
   */
  getContextName() {
    return this._contextName;
  }

  /**
   * this function returns the Container name with which the component is associated
   *
   * @public
   * @function getContainerName
   * @returns {string} container name
   *
   * @example <caption>Example for getContainerName()</caption>
   * const { containerName = "primary" } = getPConnect().getContainerName();
   * //example output : returns => 'primary'
   *
   */
  getContainerName() {
    const { acTargetName = 'primary', type, name } = this.getComponentConfig();
    if (type?.toLowerCase() !== 'view' && name) {
      return name;
    }

    return this.options.containerName || acTargetName;
  }

  /**
   * this function returns the fully qualified target container name with which the component is associated
   *
   * @public
   * @function getTarget
   * @returns {string} target name
   *
   * @example <caption>Example for getTarget()</caption>
   * const target = getPConnect().getTarget();
   * // example output : returns => 'app/primary'
   */
  getTarget() {
    return this._containerTarget;
  }

  /**
   * This API obtains the name of the view under whichthe current component was rendered.This API returns the name of the viewas a string.
   * @public
   * @function    getCurrentView
   * @returns     {string}        view name
   * @example     <caption>Example for getCurrentView()</caption>
   * In this example, the API returns the name of the view under which the current component was rendered.
   * const viewName = getPConnect().getCurrentView();
   */
  getCurrentView() {
    return this.viewName;
  }

  /**
   * Populates suggestion props if config has value prop and redux store has field in suggestion context.
   *
   * @param {object} configObj configObj to which hasSuggestion props gets added
   * @function populateSuggestionProps
   * @example <caption>Usage: getPConnect().populateSuggestionProps({ label: 'label-name' })  </caption>
   * Example Usage:
   * const configProps = { label: 'label-name' }
   * getPConnect().populateSuggestionProps(configProps);
   *
   * After the above populateAdditionalProps function call, the configProps object value gets updated by suggestionProps
   * all the possible keys added to the configObject are:
   * configObject.hasSuggestions (string) - hasSuggestion value.
   *
   * @private
   */
  populateSuggestionProps(configObj = {}) {
    if (
      this._rawConfig?.config &&
      Object.prototype.hasOwnProperty.call(this._rawConfig.config, 'value') &&
      this.getStateProps()?.value &&
      this.options.hasForm
    ) {
      const isSuggestionContextExist =
        StateManager?.getStore()?.getState()?.data[`${this.getContextName()}/${constants.SUGGESTION}`];
      const suggestionProp = this.getStateProps()?.value;
      const fullSuggestionPropRef = ContextProcessor.getContextPath(this.getPageReference(), suggestionProp);
      const propValue = isSuggestionContextExist
        ? StateUtils.getSuggestionsContext(this.getContextName()).getField(fullSuggestionPropRef)
        : undefined;
      this._hasSuggestions = !!propValue;
      configObj.hasSuggestions = this._hasSuggestions;
      // stamping the hassuggestion value in stateprops so that it will rerender in condition
      this._stateProps.hasSuggestions = this._hasSuggestions;
    }
  }

  /**
   * Returns the object with resolved component
   * configuration of the component.
   * @function getConfigProps
   *
   * @example
   * Example:
   * {
   *   label: "@P .FirstName"
   * }
   * output:
   * {
   *   label: "Hello Name"
   * }
   *
   * @example <caption>getPConnect().getConfigProps()</caption>
   * @param {object} destinationObject the object to which config props gets added.
   * const props = getPConnect().getConfigProps();
   *
   *
   * @returns {object} resolved component props.
   */
  getConfigProps(destinationObject = {}) {
    const allConfig = {
      ...fieldDefaultUtils.getDefaultsforType(this._type),
      readOnly: this._readOnly,
      ...(this._rawConfig && this._rawConfig.config),
      ...this._additionalRegisteredProps,
      ...this._inheritedConfig
    };
    this.populateSuggestionProps(allConfig);
    this.resolveConfigProps(allConfig, destinationObject);

    return Utils.resolveStringBooleans(destinationObject);
  }

  /**
   * Returns the object with raw configuration of the
   * component along with inherited configuration and
   * additional registered props.
   *
   * @example <caption>getPConnect().getRawConfigProps()</caption>
   * const rawProps = getPConnect().getRawConfigProps();
   *
   * @returns {object} Raw component props.
   *
   * @private
   */
  getRawConfigProps() {
    const allConfig = {
      ...fieldDefaultUtils.getDefaultsforType(this._type),
      ...this._rawConfig.config,
      ...this._additionalRegisteredProps,
      ...this._inheritedConfig
    };
    this.populateSuggestionProps(allConfig);
    return allConfig;
  }

  /**
   * Returns the resolved props from the provided input props.
   * @function resolveConfigProps
   * @example
   * Example:
   * {
   *  label: "@P .FirstName"
   * }
   * output:
   * {
   *  label: "Hello Name"
   * }
   * @param {object} configProps Props meant to be resolved.
   * @param {object} destinationObject the object to which config props gets added.
   * @example <caption>getPConnect().resolveConfigProps()</caption>
   * const props = getPConnect().resolveConfigProps({ label: `@P .FirstName` });
   *
   * @returns {object} resolved props.
   * @public
   */
  resolveConfigProps(configProps, destinationObject = {}) {
    if (configProps) {
      ContextProcessor.resolveProps(configProps, destinationObject, this);
      AnnotationProcessor.handleComponentMapping(
        destinationObject,
        {
          type: this.getComponentName(),
          config: configProps
        },
        this
      );
    }
    return destinationObject;
  }

  /**
   * Returns the resolved props from the provided input props and registers them to configuration object which are used for component rendering.
   * When you call the getConfigProps API, the resolved properties are retured.
   *
   * @function registerAdditionalProps
   * @example
   * Example:
   * additionalMeta = {
   *  label: "@P .FirstName"
   * }
   * output:
   * {
   *   label: "Hello Name"
   * }
   * @param {object} additionalMeta Props meant to be resolved.
   * @example <caption>getPConnect().registerAdditionalProps()</caption>
   * const props = getPConnect().registerAdditionalProps({ label: `@P .FirstName` });
   * the props variable will be an object with 'label' value is field value entered for FirstName field.
   * Also this prop ('label') is registered in component configuration for further usage in re-renders.
   * In this example, the API resolves the FirstName property, obtains the first name value, and registers it to label.
   *
   * @returns {object} resolved props.
   * @public
   */
  registerAdditionalProps(additionalMeta = {}) {
    Object.assign(this._additionalRegisteredProps, additionalMeta);
    resolveStateProps(additionalMeta, this._stateProps);
    return this.resolveConfigProps(this._additionalRegisteredProps);
  }

  /**
   * This API deregisters the input properties passed from metaConfig.
   * When you call the getConfigProps API, the deregistered properties are not obtained.
   *
   * @param {Array.<string | number>} deRegisterKeys The props that needs to be deregistered.
   * @function deRegisterAdditionalProps
   * @example <caption>getPConnect().deRegisterAdditionalProps()</caption>
   * getPConnect().deRegisterAdditionalProps(['label']);
   * @returns {boolean} Returns True after unregistering
   * @public
   *
   */
  deRegisterAdditionalProps(deRegisterKeys = []) {
    deRegisterKeys.forEach((key) => {
      delete this._additionalRegisteredProps[key];
    });
    return true;
  }

  /**
   * Populates any additional properties which are specific to component which are not part of component configuration.
   *
   * @function populateAdditionalProps
   * @param {object} configData an object which has props from configuration props data, this objects get populated with new additional properties.
   * @example <caption>Usage: getPConnect().populateAdditionalProps({ label: 'label-name' })  </caption>
   * Example Usage:
   * const configProps = { label: 'label-name' }
   * getPConnect().populateAdditionalProps(configProps);
   *
   * After the above populateAdditionalProps function call, the configProps object value gets updated based on type of the component
   * all the possible keys added to the configObject are:
   * configObject.pageMessages (string) - page messages are added for case_view template.
   * configObject.validatemessage (string) - this key is added for components which are editable and when there is any validation message to be shown.
   * configObject.infoMessage (string) - same as above for editable component which has a warning message to be shown.
   * configObject.status (string) - "error" or "warning" string values.
   * configObject.httpMessages (string) - If there are any top (app) level HTTP messages to be displayed
   * configObject.caseMessages (string) - If there are any cases level messages to be shown.
   * configObject.stages (object) - contains a particular stage info.
   * configObject.fieldMetadata (object) - this object contains the information about the extra configuration which is configured in PRPC.
   *
   * @public
   */
  populateAdditionalProps(configData) {
    const type = this.getComponentName();
    const componentConfig = this.getComponentConfig();
    const isNonPortalRootContainer = type === constants.ROOT_CONTAINER && configData.renderingMode !== 'portal';
    // Preparing pageMessages prop for FlowContainer/ ModalViewContainer
    if (
      type === constants.MODAL_VIEW_CONTAINER ||
      type === constants.CASE_CREATE_STAGE ||
      type === constants.FLOW_CONTAINER
    ) {
      // add validation page messages to flow container
      const context =
        type === constants.FLOW_CONTAINER
          ? this.getContextName()
          : ContainerUtils.getActiveContainerItemName(`${constants.APP.APP}/${this.getContainerName()}`);
      const pageMessages = MessageManager.getMessages({
        context,
        category: publicConstants.PAGE
      });
      configData.pageMessages = pageMessages;
      this._stateProps.pageMessages = configData.pageMessages;
    }

    if (this.isEditable()) {
      // Add field level validation messages to editable components
      let { value: property } = this.getStateProps();
      if (property === undefined && this.getComponentConfig()?.selectionMode === 'multi') {
        property = this.getStateProps().selectionList;
      } else if (
        !property &&
        this.getComponentConfig()?.renderMode === 'Editable' &&
        this.getStateProps().referenceList
      ) {
        property = this.getStateProps().referenceList;
      }

      const errorMessages = MessageManager.getMessages({
        property,
        pageReference: this.getPageReference(),
        context: this.getContextName(),
        type: publicConstants.MESSAGES.MESSAGES_TYPE_ERROR
      });

      const infoMessages = MessageManager.getMessages({
        property,
        pageReference: this.getPageReference(),
        context: this.getContextName(),
        type: publicConstants.MESSAGES.MESSAGES_TYPE_INFO
      });

      if (errorMessages && errorMessages[0]) {
        const localizedErrorMessages = errorMessages.map((errorMsg) => {
          return localeUtils.getLocaleValue(errorMsg.message, 'Messages');
        });
        configData.validatemessage = localizedErrorMessages.join('. ');
        configData.status = constants.FIELD_LEVEL_ERROR;
        this._stateProps.validatemessage = configData.validatemessage;
        this._stateProps.status = configData.status;
      }

      if (infoMessages && infoMessages[0]) {
        configData.infoMessage = infoMessages[0].message;
        configData.status = constants.FIELD_LEVEL_WARNING;
        this._stateProps.infoMessage = configData.infoMessage;
        this._stateProps.status = configData.status;
      }
    }

    if (type === constants.ROOT_CONTAINER) {
      const httpMessages = MessageManager.getRootContainerHttpMessages(
        isNonPortalRootContainer,
        ContainerUtils.getActiveContainerItemName(`${constants.APP.APP}/${this.getContainerName()}`)
      );
      configData.httpMessages = httpMessages;
      this._stateProps.httpMessages = configData.httpMessages;
    }

    if (type === constants.ROOT_CONTAINER) {
      const pageMessages = MessageManager.getMessages({
        context: constants.APP.ROOT,
        category: 'PAGE'
      });
      configData.pageMessages = pageMessages;
      this._stateProps.pageMessages = configData.pageMessages;
    }

    if (type === constants.APP_SHELL && configData.portalDocMode === constants.CONTAINER_TYPE.MULTIPLE) {
      configData.activeCases = CaseAPIs.getOpenedCaseItems(`${publicConstants.APP.APP}/${constants.PRIMARY}`);
      this._stateProps.activeCases = configData.activeCases;
    }

    if (type === constants.APP_SHELL) {
      const pageMessages = MessageManager.getMessages({
        context: constants.APP.APP,
        category: 'PAGE'
      });
      configData.pageMessages = pageMessages ? pageMessages.map((msg) => msg.message) : pageMessages;
      this._stateProps.pageMessages = configData.pageMessages;
    }

    if (configData.value && type === constants.SCALARLIST) {
      this._stateProps.value = configData.value;
    }

    if (
      type === constants.APP_SHELL ||
      type === constants.MODAL_VIEW_CONTAINER ||
      type === constants.CASE_CREATE_STAGE ||
      componentConfig.template === constants.CASE_PREVIEW
    ) {
      let httpMessages = MessageManager.getMessages({
        context: constants.APP.APP,
        category: 'HTTP'
      });

      const activeContainerHttpMessages = MessageManager.getMessages({
        context: ContainerUtils.getActiveContainerItemName(`${constants.APP.APP}/${this.getContainerName()}`),
        category: 'HTTP'
      });

      if (httpMessages) {
        if (activeContainerHttpMessages) {
          httpMessages = httpMessages.concat(
            MessageManager.getMessages({
              context: ContainerUtils.getActiveContainerItemName(`${constants.APP.APP}/${this.getContainerName()}`),
              category: 'HTTP'
            })
          );
        }
      } else {
        httpMessages = MessageManager.getMessages({
          context: ContainerUtils.getActiveContainerItemName(`${constants.APP.APP}/${this.getContainerName()}`),
          category: 'HTTP'
        });
      }

      configData.httpMessages = httpMessages ? httpMessages.map((msg) => msg.message) : httpMessages;
      this._stateProps.httpMessages = configData.httpMessages;
      httpMessages?.map((msg) => {
        if (msg.errorDetails) {
          configData.errorDetails = configData.errorDetails
            ? configData.errorDetails.push(...msg.errorDetails)
            : msg.errorDetails;
          this._stateProps.errorDetails = configData.errorDetails;
        }
        if (msg.correlationID) {
          configData.correlationID = msg.correlationID;
        }
      });
    }

    if (
      type === constants.VIEW_CONTAINER ||
      type === constants.FLOW_CONTAINER ||
      type === constants.MODAL_VIEW_CONTAINER ||
      type === constants.PREVIEW_VIEW_CONTAINER ||
      type === constants.ROOT_CONTAINER
    ) {
      configData.routingInfo = RoutingManager.getRoutingInfo(this.getContainerName(), this._contextName);
      if (type === constants.ROOT_CONTAINER) {
        configData.rootConfig = C11nEnvUtils.getItemView(configData.routingInfo, configData.renderingMode);
        this._stateProps.rootConfig = configData.rootConfig;
      }
      this._stateProps.routingInfo = configData.routingInfo;
      if (type === constants.FLOW_CONTAINER) {
        const caseMessages = MessageManager.getMessages({
          context: this.getContextName(),
          category: constants.CASEMESSAGES
        });
        const contextName = this.getContextName();
        configData.caseMessages = caseMessages;
        this._stateProps.caseMessages = configData.caseMessages;
        configData.caseViewMode = this.getValue('context_data.caseViewMode');
        this._stateProps.caseViewMode = configData.caseViewMode;
        const childContainerId = ContainerUtils.getChildContainers(contextName)[0];
        if (childContainerId) {
          const activeWorkarea = ContainerUtils.getActiveContainerItemName(childContainerId);
          configData.lastUpdateTime =
            StateManager?.getStore()?.getState()?.data?.[activeWorkarea]?.caseInfo?.lastUpdateTime;
          this._stateProps.lastUpdateTime =
            StateManager?.getStore()?.getState()?.data?.[activeWorkarea]?.caseInfo?.lastUpdateTime;
        }
      }
      configData.loadingInfo = this.getLoadingStatus(this.getContextName());
      configData.assignmentNames = this.getCaseInfo()
        .getAssignments()
        ?.map((e) => {
          return e.name;
        });
      configData.childAssignmentNames = this.getCaseInfo()
        .getChildAssignments()
        ?.map((e) => {
          return e.name;
        });
      if (type === constants.MODAL_VIEW_CONTAINER) {
        // get loadingInfo of context inside ModalViewContainer
        const accessedOrder = configData.routingInfo?.accessedOrder || [];
        const key = accessedOrder[accessedOrder.length - 1];
        if (key) configData.loadingInfo = this.getLoadingStatus(key);
      }
      this._stateProps.loadingInfo = configData.loadingInfo;
      this._stateProps.assignmentNames = configData.assignmentNames;
      this._stateProps.childAssignmentNames = configData.childAssignmentNames;
    } else if (
      componentConfig.template === constants.CASE_VIEW ||
      componentConfig.template === constants.CASE_PREVIEW ||
      componentConfig.template === constants.OBJECT_PAGE
    ) {
      configData.caseInfo = this.getCaseSummary();
      this._stateProps.caseInfo = configData.caseInfo;
    } else if (type === constants.CASE_STAGES) {
      configData.stages = this.getCaseStages();
      configData.stageID = this.getStageID();
      this._stateProps.stages = configData.stages;
      this._stateProps.stageID = configData.stageID;
    }
    this.populateFieldMetadata(configData);
  }

  populateFieldMetadata(configData) {
    // Add fieldMetadata prop for non-nullish values.
    const { value, datasource, listType } = this.getComponentConfig(); // {value: "@P .Fullname.firstName"}
    let propertyValue = '';
    let propertyAnnotation = '';
    if (listType === constants.ASSOCIATED) {
      propertyValue = datasource;
      propertyAnnotation = constants.ASSOCIATED_PROPERTY_ANNOTATION;
    } else {
      propertyValue = value;
      propertyAnnotation = constants.PROPERTY_ANNOTATION;
    }
    if (propertyValue && typeof propertyValue === 'string' && propertyValue.includes(propertyAnnotation)) {
      // const propertyName = propertyValue.split(".").slice(1).join("."); //@P .Fullname.firstname to .Fullname.firstname
      const propertyName = AnnotationProcessor.getPropertyName(propertyValue);
      const fieldMetadata = this.getFieldMetadata(propertyName);

      if (fieldMetadata) {
        configData.fieldMetadata = fieldMetadata;
        if (
          configData.fieldMetadata.isDeclarativeTarget ||
          AnnotationUtils.isPropertyInternal(propertyName) ||
          (!configData.datasource &&
            (configData.fieldMetadata.isClassKey || configData.fieldMetadata.isSpecial) &&
            configData.listType !== constants.ASSOCIATED)
        ) {
          configData.readOnly = true;
          this._stateProps.readOnly = configData.readOnly;
          if (configData.fieldMetadata.isDeclarativeTarget) this.registerAdditionalProps({ readOnly: true });
        }
      }
    }
  }

  /**
   * Returns the field model (single model object (or) Array of model objects) (or) null.
   * field model is the information like class it belongs to, editValidate, useValidate, maxLength, etc which are configured on the field.
   * Model becomes an array if the field belongs to more than one class, otherwise we get only one object.
   *
   * @param {string} propertyName property (field) name without dot.
   * @returns {object | Array | null} { classID: 'class-1', type: "text"} | {{ classID: 'class-1', type: "text"}, { classID: 'class-1', type: "text", editValidate: 'funcName'}}
   * @example <caption>Usage of getFieldMetadata</caption>
   * Ex: const firstNameMetaData = getPConnect().getFieldMetadata('FirstName');
   *
   * firstNameMetaData will receive the configured information on the field while configuring it.
   *
   * @private
   */
  getFieldMetadata(propertyName) {
    if (!propertyName) {
      return null;
    }
    const propertyNameSplit = propertyName.split('.');
    if (propertyNameSplit.length < 2) {
      let classID = this.getValue(constants.CLASS_ID);
      if (!classID && this._rawConfig && this._rawConfig.config && this._rawConfig.config.ruleClass) {
        classID = this._rawConfig.config.ruleClass;
      }
      return getPropertyMetadata(propertyName, classID);
    }
    return getPropertyMetadata(
      propertyNameSplit.pop(),
      this.getValue(`.${propertyNameSplit.join('.')}${constants.CLASS_ID}`)
    );
  }

  /**
   * Returns the current page meta data.
   *
   * @param {string} contextPageReference context page reference.
   * @returns {object | Array | null} { classID: 'class-1', type: "text"} | {{ classID: 'class-1', type: "text"}, { classID: 'class-1', type: "text", editValidate: 'funcName'}}
   * @example <caption>Usage of getCurrentPageFieldMetadata</caption>
   * Ex: const firstNameMetaData = getPConnect().getCurrentPageFieldMetadata();
   *
   * firstNameMetaData will receive the configured information on the page while configuring it.
   *
   * @private
   */
  getCurrentPageFieldMetadata(contextPageReference = null) {
    let pageRefs = this.getPageReference().split('.');
    if (contextPageReference !== null) {
      pageRefs = contextPageReference.split('.');
    }
    const fieldName = pageRefs.pop();

    return getPropertyMetadata(fieldName, this.getValue(`${pageRefs.join('.')}${constants.CLASS_ID}`));
  }

  /**
   * Returns the current field classId
   * @returns { string } "class-name"
   * @function getCurrentClassID
   * @example <caption>Usage of getCurrentClassID</caption>
   * Ex: const firstNameClassId = getPConnect().getCurrentClassID();
   *
   * firstNameClassId will receive the class id corresponding to the field.
   *
   * @public
   */
  getCurrentClassID() {
    return this.getValue(constants.CLASS_ID);
  }

  /**
   * Returns case summary and isCaseWideAction function consist of below structure
   * @function getCaseSummary
   * @example
   * {
   * routedTo:"user@constellation.com"
   * urgency:"10"
   * caseID:"OPB1HW-SPACETRA-WORK RA-219"
   * name:"Request approval"
   * caseClass:"OPB1HW-SpaceTra-Work-RequestApproval"
   * ID:"ASSIGN-WORKLIST OPB1HW-SPACETRA-WORK RA-219!REQUEST_FLOW_0"
   * isCaseWideAction
   * }
   *
   * isCaseWideAction() function returns true is the action is a case action else false
   */
  getCaseSummary() {
    const caseInfo = { ...this.getDataObject()[publicConstants.CASE_INFO.CASE_INFO] };
    caseInfo.CaseID = caseInfo.CaseID || this.getValue(publicConstants.CASE_INFO.CASE_INFO_PZINSKEY);

    return {
      ...caseInfo,
      isCaseWideAction: () => {
        return CaseUtils.isCaseWideAction(this.getContextName(), this.getPageReference());
      }
    };
  }

  /**
   * Returns referencedUsers in view with the below structure
   *
   * @function getReferencedUser
   * @param {string} userId is userIdentifier of a user.
   * {
   * "baipc": {
   *   "UserName": "Chandra"
   *  }
   * }
   */
  getReferencedUser(userId) {
    const refUsers = this.getValue('referencedUsers');

    if (Array.isArray(refUsers)) {
      const userObj = refUsers.find((user) => user.UserID === userId);
      if (userObj) {
        return {
          userId: userObj.UserID,
          UserName: userObj.UserName
        };
      }
    }

    return refUsers && refUsers[userId] ? refUsers[userId] : null;
  }

  /**
   * Returns case stages consist of below structure
   * @function getCaseStages
   * @example
   * {
   * ID: "PRIM1"
   * entryTime: "20200520T093921.867 GMT"
   * links: {,}
   * name: "Collect Info"
   * type: "Primary"
   * visited_status: "active"
   * }
   */
  getCaseStages() {
    return this.getDataObject()[publicConstants.CASE_INFO.CASE_INFO].stages;
  }

  getStageID() {
    return this.getDataObject()[publicConstants.CASE_INFO.CASE_INFO].stageID;
  }

  /**
   * Gets the actions object that is used to configure a components event actions.
   * @function getActions
   * @example <caption>Example for getActions()</caption>
   * Example usage - const actions = getPConnect().getActions(); return <Currency value={value} label={label} {...actions} />;
   * // the API provides the currency components action-set.
   *
   * @returns {object} actions configured
   */
  getActions() {
    return this._actions;
  }

  /**
   * Returns true if this C11nEnv is the root
   * @returns {boolean} True is this is the root
   * @private
   */
  isRoot() {
    return this._isRoot;
  }

  /**
   * Checks and resolves any context related info.
   * like, any errors for properties and any validation
   * info for this component.
   * @param {object} target target object to update the context.
   * @private
   */
  resolveContextProps(target) {
    const { value } = this.getStateProps();
    if (value) {
      target.error = this.getContextDataValue(`${this.getPageReference() + value}.error`);
      target.validatemessage = this.getContextDataValue(`${this.getPageReference() + value}.validatemessage`);
    }
  }

  /**
   * Checks whether validators configured for this
   * component or not.
   * @returns {boolean} true or false
   * @private
   */
  isValidatorExist() {
    return !!this._isValidatorExist;
  }

  /**
   * Obtains an entry point to the {@link ValidationApi} object that contains APIs that handle validations on the field.
   * @function getValidationApi
   * @returns {ValidationApi} entry point to the {@link ValidationApi}
   *
   * @example <caption> Example for getValidationApi() </caption>
   * const validationApi = pConn.getValidationApi();
   * @public
   */
  getValidationApi() {
    return new ValidationApi(this);
  }

  /**
   * Checks whether a component is editable, relies on value attribute
   *
   * @function isEditable
   * @example <caption> Example for isEditable() </caption>
   * const isEditable = pConn.isEditable();
   * where isEditable is the boolean that contains true/false
   * In this example, the API checks if a component can be edited and returns the resulting boolean value.
   *
   * @returns {boolean} true or false
   */
  isEditable() {
    return (
      this._rawConfig &&
      this._rawConfig.config &&
      !(this._inheritedConfig && this._inheritedConfig.readOnly) &&
      /* selectionList field OR Editable referenceList */
      (Object.prototype.hasOwnProperty.call(this._rawConfig.config, 'value') ||
        this._rawConfig.config.selectionList ||
        (this._rawConfig.config.renderMode === 'Editable' && this._rawConfig.config.referenceList)) &&
      /* FilteredList field */
      !(typeof this._rawConfig.config?.value === 'string' && this._rawConfig.config.value.includes('@FILTERED_LIST')) &&
      /* readOnly field */
      !(
        Object.prototype.hasOwnProperty.call(this._rawConfig.config, 'readOnly') &&
        (this._rawConfig.config.readOnly === 'true' || this._rawConfig.config.readOnly === true)
      ) &&
      /* disabled field */
      !(
        Object.prototype.hasOwnProperty.call(this._rawConfig.config, 'disabled') &&
        this._rawConfig.config.disabled === true
      )
    );
  }

  /**
   * This API checks if the current component has children.
   *
   * @function hasChildren
   * @example <caption> Example for hasChildren - getPConnect().hasChildren() </caption>
   * Usage: const componentHasChildren = getPConnect().hasChildren();
   * componentHasChildren will be true or false indicating whether current component has children in the component configuration.
   *
   * @returns {boolean} true or false
   * @public
   */
  hasChildren() {
    return !!this._children;
  }

  /**
   * Returns whether the node is visible && has some visible children
   * (if it is of a type which require children).
   * @returns {boolean} whether the node should be visible.
   */
  getComputedVisibility() {
    const isReferenceView = this._type === 'reference' && !!this.getReferencedView();
    const pConnect = isReferenceView ? this.getReferencedViewPConnect().getPConnect() : this;

    if (this.getConfigProps().visibility === false) {
      return false;
    }

    const configProps = pConnect.getConfigProps();

    const requireChildren = {
      includeTypes: ['Region', 'View'],
      excludeTemplates: ['ListView', 'SimpleTable', 'StudioTable']
    };
    let requiresVisibleChildren =
      requireChildren.includeTypes.includes(pConnect._type) &&
      !requireChildren.excludeTemplates.includes(configProps.template);

    const hasVisibleHighlightedData =
      !!configProps.highlightedData && configProps.highlightedData.length && configProps.showHighlightedData;
    if (hasVisibleHighlightedData) {
      requiresVisibleChildren = false;
    }

    if (!requiresVisibleChildren) {
      return true;
    }

    const children = pConnect.getChildren();

    if (!children) {
      return false;
    }

    return children.some((c) => c.getPConnect().getComputedVisibility());
  }

  /**
   * Returns the children iterator
   * @returns {object} iterator
   * @private
   */
  getChildrenIterato() {
    this._children.setContext(this.getPageReference());
    return this._children;
  }

  /**
   * This API assigns a new action(based on the event type and handler)to the list of the actions
   * associated with the current component.
   *
   * @function setAction
   * @example <caption> Example for setAction() </caption>
   * Example usage - setAction("onChange", changeHandler);
   * // this API associates the change handler to the onChange event type, and assigns the new action to the list of the actions associated with the current component.
   *
   * @param {string} eventType The type of the event that determines the new action to be added to the list of actions.
   * @param {Function} handler The function that must be associated with the event type.
   *
   * @public
   */
  setAction(eventType, handler) {
    this._actions[eventType] = handler;
  }

  /**
   * Returns the component name example Button.
   *
   * @function getComponentName
   * @example <caption>getPConnect().getComponentName()</caption>
   * const component = getPConnect().getComponentName();
   *
   * @returns {string} current component name.
   */
  getComponentName() {
    return this._rawConfig && this._rawConfig.type;
  }

  /**
   * Checks whether current component is refreshable
   * or not.
   * Example:
   * If section is configured with refresh this section
   * then section will be treated as refreshable.
   * @returns {boolean} true or false
   * @private
   */
  isComponentRefreshable() {
    return this._rawConfig && this._rawConfig.isRefreshable;
  }

  /**
   * this function helps to obtain the data of the current context from store.
   * Here `data` may contain below information
   *  <ul>
   *    <li>caseInfo</li>
   *    <li>pulse</li>
   *    <li>pxRequestor</li>
   *    <li>Data Pages</li>
   *    <li>...</li>
   *  </ul>
   *
   * @public
   * @function getDataObject
   * @param {string} contextName - qualified context name e.g. app/primary_1
   * @returns {object} data object
   *
   * @example <caption> Example for getDataObject() </caption>
   * const data = pConn.getDataObject();
   * const { caseInfo, pulse } = data;
   *
   */
  getDataObject(contextName) {
    return StateManager.getDataObject(contextName || this.getContextName());
  }

  /**
   * Returns the resolved image path, it resolves the image with current
   * static service.
   *
   * @function getImagePath
   * @example <caption>getPConnect().getImagePath(imageName)</caption>
   * const imagePath = getPConnect().getImagePath("launcher.png");
   *  // output: imagePath -> https://service/image/launcher.png
   * @param {string} imageName image name
   * @returns {string} image path
   */
  /**
   * @param {string} imageName image name
   */
  getImagePath(imageName) {
    return `${AssetLoader.getConstellationServiceUrl()}/v860/${AssetLoader.getAppAlias()}/image/${imageName}?org=${RouteParser.getOrganizationName()}`;
  }

  skipRequiredValidation() {
    return this.options.isBulkAction;
  }

  isBulkAction() {
    return this.options.isBulkAction;
  }

  /**
   * Returns list of children for the current component.
   * And also resets the iterator to start-index.
   *
   * @function getChildren
   * @example <caption>Example usage of getPConnect().getChildren()</caption>
   * const children = getPConnect().getChildren()
   * the children variable will be receiving the list of children components of the current component.
   *
   * @returns {object} returns the list of children
   * @public
   */
  getChildren() {
    if (this._children && this._children.length) {
      const childrenIterator = getChildrenIterator(this._rawConfig, {
        pageReference: this._pageReference,
        context: this._contextName,
        localeReference: this._localeRuleName,
        hasForm: this.options.hasForm,
        isBulkAction: this.options.isBulkAction,
        containerName: this.getContainerName(),
        containerItemName: this.options.containerItemName,
        viewName: this.viewName, // Passing the view name to the iterator. This again comes to c11nEnv in the options object when the object for a child view is created.
        inheritedConfig: this._inheritedConfig,
        target: this._containerTarget,
        readOnly: this._readOnly
      });
      const childrenArr = [];
      while (childrenIterator.hasNext()) {
        const childConfig = childrenIterator.next();
        if (this.isBulkAction()) {
          const type = childConfig.getPConnect().getComponentName();
          const { template } = childConfig.getPConnect().getConfigProps();

          if (
            !(
              C11nEnvUtils.allowedComponetsForBulkProcessing[type] === true ||
              C11nEnvUtils.allowedComponetsForBulkProcessing[type]?.[template] === true
            )
          )
            continue;
        }
        childrenArr.push(childConfig);
      }
      childrenIterator.resetIndex();
      return childrenArr;
    }
    return null;
  }

  /**
   * This API set inherited prop and propagates the prop to all its children.
   *
   * @function setInheritedProp
   * @example <caption>getPConnect().setInheritedProp("displayMode", "DETAILS")</caption>
   *
   * @param {propName} propName name of the prop eq., displayMode
   * @param {*} value value to be set ew., LEFT
   *
   * @returns {void}
   */
  setInheritedProp(propName, value) {
    this._inheritedConfig[propName] = value;
  }

  /**
   * This API returns locale reference rule name this environment object is referencing for the field values.
   *
   * @function getLocaleRuleName
   * @example <caption>getPConnect().getLocaleRuleName()</caption>
   * const localeRuleName = getPConnect().getLocaleRuleName();
   * //Returns "VEHICLE-!VIEW!VEHICLEDETAILS", if the current environment object corresponds to a view named VehicleDetails in Work- class.
   * //The above is the name of localization rule which is 1-1 mapped to a View named VehicleDetails
   *
   * @returns {string} Returns the locale reference rule name.
   */
  getLocaleRuleName() {
    return this._localeRuleName;
  }

  /**
   * Returns the localized value for a string.
   *
   * @function getLocalizedValue
   * @example <caption>getPConnect().getLocalizedValue("First Name", "fields","WORK-!VIEW!CANDIDATEINFO")</caption>
   * If the localization store looks like below.
   *
   * {
   *     "WORK-!VIEW!CANDIDATEINFO" : {
   *          "fields" : {
   *              "First Name" : "Primeiro nome",
   *              "Last Name"  : "ltimo nome"
   *           }
   *      }
   * }
   * The API returns "Primeiro nome" for a rule  "WORK-!VIEW!CANDIDATEINFO".
   *
   * @param   {string} rawString      raw string
   * @param   {string} localePath     locale path
   * @param   {string} localeRuleKey  (optional)Rule Key in the localization store
   * @returns {string} returns the localized value.
   */
  getLocalizedValue(rawString, localePath, localeRuleKey) {
    const ruleKey = localeRuleKey || this.getLocaleRuleName();
    return localeUtils.getLocaleValue(rawString, localePath, ruleKey);
  }

  /**
   * Returns the localized value for a paragraph rule
   * @private
   * @function getLocalizedParagraphValue
   *
   * @param {string} paragraphRuleName name of the paragraph rule
   * @returns {string} content of the paragraph rule
   */
  getLocalizedParagraphValue(paragraphRuleName) {
    const ruleKey = this.getLocaleRuleNameFromKeys(this.getCurrentClassID(), constants.PARAGRAPH, paragraphRuleName);
    return localeUtils.getParagraphLocaleValue(ruleKey, this.getCurrentClassID(), paragraphRuleName);
  }

  /**
   * Returns the localeRuleName for the given Localization rule keys.
   *
   * @example <caption>const ruleKey = getPConnect().getLocaleRuleName("Work-", "View","CandidateInfo")</caption>
   * Here the ruleKey will be  "Work-!View!CandidateInfo"
   *
   * @param   {string} localeClass    Localization rule class
   * @param   {string} localeContext  Localization rule context
   * @param   {string} localeName     Localization rule name
   * @returns {string} localeRuleKey
   * @private
   */
  getLocaleRuleNameFromKeys(localeClass, localeContext, localeName) {
    return `${localeClass}!${localeContext}!${localeName}`;
  }

  /**
   * this function helps to resolve the property value from the state (Redux store).
   * @function getValue
   * @param {string} prop property whose data value is returned
   * @param {string} pageReference pageReference of parent view
   * @returns {string} property value from the state (Redux store).
   *
   * @example <caption>Example for .firstName property </caption>
   * const firstName = getPConnect().getValue('.firstName');
   * // here firstName variable contains the value retrieved from redux store.
   */
  getValue(prop, pageReference) {
    if (this.useCustomContext) {
      prop = prop.indexOf?.('.') === 0 ? prop.substring(1) : prop;
      return prop.split?.('.').reduce((context, split) => context?.[split] ?? undefined, this.useCustomContext);
    }
    if (this._hasSuggestions && this.getStateProps()?.value === prop) {
      return ContextProcessor.getProperyValue(
        prop,
        this.getPageReference(),
        `${this._contextName}/${constants.SUGGESTION}`
      );
    }
    return ContextProcessor.getProperyValue(prop, pageReference || this.getPageReference(), this._contextName);
  }

  setInheritedConfig(rawConfig) {
    if (!rawConfig) return;
    const { readOnly, displayMode, referenceType, hideLabel } = rawConfig;

    if (hideLabel) {
      this._inheritedConfig.hideLabel = hideLabel;
    }

    if (referenceType) {
      this._inheritedConfig.referenceType = referenceType;
    }

    if (displayMode) {
      this._inheritedConfig.displayMode = displayMode;
    }

    if (this._inheritedConfig.readOnly || readOnly) {
      this._inheritedConfig.readOnly = readOnly;
    }

    if (rawConfig.inheritedProps) {
      rawConfig.inheritedProps?.forEach((inheritProp) => {
        if (inheritProp.prop === 'required') {
          this._inheritedConfig[inheritProp.prop] = inheritProp.value;
        }
      });
      // Copy all props for depth calculation
      mergeInheritedProps(this._inheritedConfig, rawConfig);
    }
  }

  /**
   * This function is used to retrieve props that are set on the parent metadata node within
   * the inheritedProps metadata structure. The purpose of inheritedProps is to allow cascading
   * of values in view metadata.
   *
   * In the following example, a label can be given to a view reference node in order to give a
   * unique label each place a view is included, allowing for better reuse.
   *
   * @function getInheritedProps
   * @returns {object} props object that was inherited from the parent
   * @public
   *
   * @example <caption>Getting a label from parent reference</caption>
   * // Parent reference metadata
   * {
   *   "type": "reference",
   *   "config": {
   *     "name": "Address",
   *     "inheritedProps": [
   *       {
   *         "prop": "label",
   *         "value": "@L Shipping address"
   *       }
   *     ]
   *   }
   * }
   *
   * // Child reusable Address view
   * {
   *   "name": "Address",
   *   "type": "View",
   *   "config": {
   *     "template": "OneColumn",
   *     "ruleClass": "Data-Address"
   *     "label": "Generic address"
   *   },
   *   "children": [
   *     {
   *       "name": "A",
   *       "type": "Region",
   *       "children": []
   *     }
   *   ]
   * }
   *
   * // During render of the child view called Address, one would retrieve the inherited label as follows
   *
   * const propsToUse = { label, ...getPConnect().getInheritedProps() };
   *
   * // This would result in propsToUse.label = Shipping address. This inherits the value that was set on the
   * // parent (if the parent had a label property) and would override any label property that was set on the child view.
   */
  getInheritedProps() {
    const props = {};
    const inheritedProps = {};

    this._inheritedConfig.inheritedProps?.forEach((inheritProp) => {
      inheritedProps[inheritProp.prop] = inheritProp.value;
    });

    ContextProcessor.resolveProps(inheritedProps, props, this);
    return props;
  }

  /**
   * this function helps to assign the value to property name (or key) in state (Redux store).
   * @function setValue
   * @param {string} prop property name whose data value need to set
   * @param {string} value property value which need to get assigned to the property name.
   * @param {string} initialValue property initialValue which need to get assigned to the property name.
   * @param {boolean} isImplicit boolean check if it is implicit setValue action
   * @returns {void}
   *
   * @example <caption>Example for .firstName property </caption>
   * getPConnect().setValue('.firstName', 'lorem ipsum');
   * // here examples shows setting the `lorem ipsum` value in firstName property
   */
  setValue(prop, value, initialValue, isImplicit) {
    let isStrictTracking;
    let removePropertyFromChangedList;
    if (this.isInsideList()) {
      this.getListActions().updateProperty(prop, value);
    }
    if (this.isBulkAction()) {
      isStrictTracking = true;
      if (!value) removePropertyFromChangedList = true;
    }
    StateManager.dispatch({
      type: 'SET_PROPERTY',
      payload: {
        reference: `${this.getPageReference()}${prop}`,
        value,
        initialValue,
        isImplicit,
        context: this.getContextName(),
        ...(isStrictTracking && { isStrictTracking }),
        ...(removePropertyFromChangedList && { removePropertyFromChangedList })
      }
    });
  }

  /**
   * This function helps to update the value to property name in change list
   * @function updateDirtyCheckChangeList
   * @param {string} prop property name whose data value need to set
   * @param {string} value property value which need to get assigned to the property name
   * @returns {void}
   *
   * @example <caption>Example for .firstName property </caption>
   * getPConnect().updateDirtyCheckChangeList('.firstName', 'lorem ipsum');
   * // here examples shows setting the `lorem ipsum` value in firstName property
   * @private
   */
  updateDirtyCheckChangeList(prop, value) {
    const changeList = {
      reference: `${this.getPageReference()}${prop}`,
      value,
      context: this.getContextName()
    };
    updateChangeList(changeList);
  }

  /**
   * Return context data value like when results etc.,
   * @example <caption>getPConnect().getContextDataValue(prop)</caption>
   * const value = getPConnect().getContextDataValue('.summary_of_when_conditions__.testWhen');
   *
   * @param {string} prop property whose context data value is returned
   * @param {string} pageReference pageReference of parent view
   * @returns {string} Context data value
   * @private
   */
  getContextDataValue(prop, pageReference) {
    return StateManager.getContextValue(pageReference || this.getPageReference(), prop, this._contextName);
  }

  /**
   * this function helps to merge data object in store deeply.
   * If an element at the same key is present in both store and data object,
   * the value from data object will stay in the store.
   *
   * @function updateState
   * @param {object} dataObject - object to be merged into store.
   *
   * @example <caption>Example usage to merge firstName key to store</caption>
   * getPConnect().updateState({
   *  firstName: "Test"
   * });
   */
  updateState(dataObject) {
    const index = this._pageReference.indexOf(constants.CASE_INFO.CASE_INFO_CONTENT);
    const rootPageIndex = this._pageReference.indexOf('.');
    let rootPageReference = constants.CASE_INFO.CASE_INFO_CONTENT;
    if (index !== 0) {
      rootPageReference = rootPageIndex > 0 ? this._pageReference.substring(rootPageIndex) : this._pageReference;
    }
    StateManager.dispatch(
      mergeData({
        context: this._contextName,
        data: dataObject,
        rootPageReference
      })
    );
  }

  /**
   * this function helps to replace the state of a property or page in the store.
   * @function replaceState
   * @param {string} prop property or page name whose value need to be replaced
   * @param {object} value  value which need to get assigned to the property or page.
   * @returns {void}
   *
   * @example <caption>Example for .address property </caption>
   * getPConnect().replaceState('.address', {
   *  apartment: "Nivee Heights"
   *  streetName: "Park Street",
   * });
   * @param {boolean} options.skipDirtyValidation - flag to decide whether the property has to considered in Dirty Validation. Default is false. If passed as true Dirty Validation will be skipped on the respected property.
   * @example <caption>Example for .address property will not be include in the response of formutils.getChanges() API </caption>
   * getPConnect().replaceState('.address', {
   *  apartment: "Nivee Heights"
   *  streetName: "Park Street",
   * }, {skipDirtyValidation: true });
   */
  replaceState(prop, value, options = {}) {
    const { skipDirtyValidation = false } = options;
    StateManager.dispatch({
      type: 'REPLACE_PROPERTY',
      payload: {
        reference: `${this.getPageReference()}${prop}`,
        value,
        context: this.getContextName(),
        skipDirtyValidation
      }
    });
  }

  /**
   * this function helps to set error at Redux store level
   * so that the errors that need to be handled at infra level can make use of this function
   *
   * @public
   * @function reportError
   * @param {string} errorMsg - Error message to be logged to Redux store.
   * @param {object} errorObj - Error object that contains more error info.
   * @param {string} context - Context data value
   *
   * @example <caption>Example usage for reportError function</caption>
   * getPConnect().reportError(
   *    ': Error ocurred during ajax call at fetchMessages API :',
   *    {
   *      errorClassification: 'Invalid inputs'
   *      errorDetails: {
   *        localizedValue: 'localized error message'
   *      }
   *    }
   * );
   */
  reportError(errorMsg, errorObj, context) {
    StateManager.dispatch(error(errorMsg, errorObj || {}, context));
  }

  /**
   * Returns the env object.
   * @returns {object} env object
   * @private
   */
  getComponent() {
    return componentCreator({
      getPConnect: () => this
    });
  }

  /**
   * This API creates a component whose type is provided as part of the meta object and uses the other parameters to resolve the properties and get the context.
   * The definition of the component type which is provided in meta object is upto consumer.
   * @param {object} componentMeta The metadata of the component to be created.
   * @param {string} dataSource The source reference to the Redux store. This contains properties that will be input to the created component.
   * @param {string} index In case of list datasource mention the index.
   * @param {object} additionalPropsToComp Props will be passed to component apart from the configured values.
   *
   * @function createComponent
   * @example <caption>Usage of createComponent: getPConnect().createComponent(meta, dataSource, index, additionalPropsToComp)</caption>
   * Ex: getPConnect().createComponent(
   *  {
   *    type: "Button",
   *    config: {
   *      label: "@P .FirstName"
   *    }
   *  },
   *  "D_Employees.pxResults",
   *  1,
   *  {
   *    active: true
   *  }
   * );
   *
   * Call the createComponent with meta data and necessary arguments, such that the component receives the resolved props to the created component instance.
   * For React: JSX: <Button label="resolved FirstName property value" /> (or) React.createElement('Button', { label: 'resolved FirstName property value })
   *
   * @returns {object} newly created component instance
   *
   * @public
   */
  createComponent(componentMeta, dataSource, index, additionalPropsToComp) {
    let pageReference = this._pageReference;
    if (dataSource) {
      const pageListContext = StateManager.lookupContextValue(
        `${this.getPageReference()}.${constants.SUMMARY_OF_LISTS}`,
        dataSource,
        this._contextName
      );
      pageReference = ContextProcessor.getContextPath(pageListContext || dataSource, index) || dataSource;
    } else if (additionalPropsToComp && additionalPropsToComp.pageReference) {
      let referencedPage = additionalPropsToComp.pageReference;
      referencedPage = AnnotationProcessor.handleAnnotation(referencedPage, this);
      pageReference = ContextProcessor.getContextPath(this._pageReference, referencedPage) || '';
    }

    return componentCreator(
      createC11nEnv({
        meta: componentMeta,
        options: {
          containerName: this.getContainerName(),
          hasForm: this.options.hasForm,
          ...componentMeta.options,
          pageReference,
          context: this._contextName,
          target: this._containerTarget,
          readOnly: this._readOnly || this._inheritedConfig?.readOnly
        }
      }),
      additionalPropsToComp
    );
  }

  /**
   * This API returns the viewMetadata of the referenced view
   * @function getReferencedView
   * @example <caption>
   * const meta = {
   *  meta: {
   *    type: 'reference',
   *    config: {
   *      type: 'view',
   *      name: 'Address'
   *    }
   *  }
   * }
   * const c11nEnv = createC11nEnv(meta).getPConnect();
   * const viewMetadata = c11nEnv.getReferencedView();
   * 'viewMetadata' is the metadata object of the 'Address' view
   * </caption>
   * @returns {object} referenced view metadata
   */
  getReferencedView() {
    if (!this.meta) return undefined;

    return ContextProcessor.resolveReference(this.meta, this);
  }

  /**
   * This API returns the PConnect object of the referenced view with in a reference
   * @param {boolean} passOptions this allows to pass reference's options to the view if it is true
   * @function getReferencedViewPConnect
   * @example <caption>const getPConnect = getPConnect().getReferencedViewPConnect().getPConnect</caption>
   * @returns {object} An PConnect object with getPConnect of the referenced view with in type reference
   *
   * @public
   */
  getReferencedViewPConnect(passOptions = false) {
    const referenceConfig = { ...this.getComponentConfig() };

    delete referenceConfig?.name;
    delete referenceConfig?.type;

    return createC11nEnv({
      meta: this.getReferencedView(),
      options: {
        ...(passOptions ? this.options : undefined),
        ...referenceConfig,
        context: this.getContextName(),
        pageReference: this.getPageReference()
      }
    });
  }

  /**
   * Returns an entry point to the Container Manager<br>
   * The Container Manager can be used to access any of the APIs found at {@link ContainerManager}
   * @function getContainerManager
   * @returns {ContainerManager} entry point to the {@link ContainerManager}
   * @example <caption>Example for getContainerManager()</caption>
   * const containerManager = getPConnect().getContainerManager();
   * //any of the container manager APIs can be called on the entry point returned
   */
  getContainerManager() {
    return new ContainerManager(this);
  }

  /**
   * Returns an entry point to the Actions API. This entry point
   * can be used to access any of the APIs found at {@link ActionsApi}
   * @function getActionsApi
   * @example
   * Example usage:
   *   const actionsAPI = pConn.getActionsApi();
   * [...]
   *    actionsApi.showPage(actionInfo);
   *    actionsApi.createWork(actionInfo);
   *
   * where actionInfo is the JSON object that contains the information
   * necessary for the action to be properly invoked.
   *
   * @returns {ActionsApi} entry point to the {@link ActionsApi}
   */
  getActionsApi() {
    if (!this._actionsApi) {
      this._actionsApi = new ActionsApi(this);
    }
    return this._actionsApi;
  }

  /**
   * Returns the ActionManager object that can be used
   * to access the Core's ActionManager API
   * @returns {object} the {@link ActionManager} object
   * @private
   */
  getActionManager() {
    return ActionManagerInstance;
  }

  /**
   * used in the Location component of the Nebula layer to obtain the API key
   * for the Google Maps applicationfrom the DSS setting(uiengine/map/googleapikey) and
   * send it to the MapsContext control of the Cosmos layer as a property
   * @function getGoogleMapsAPIKey
   * @returns {string} google maps api key
   * @example <caption>Example for getGoogleMapsAPIKey()</caption>
   * let apiKey = getPConnect().getGoogleMapsAPIKey();
   * // the API key to render Google Maps is obtained
   */
  getGoogleMapsAPIKey() {
    return services.getGoogleMapsAPIKey();
  }

  /**
   * used in the Richtext component of the Nebula layer to obtain the server url.

   * @function  getServerURL
   * @returns {string} server url
   * @example <caption>Example for getServerURL()</caption>
   * let serverurl = getPConnect().getServerURL();
   * // the server url is obtained
   */
  getServerURL() {
    return services.getServerURL();
  }

  /**
   * Returns the loading status of the current container context
   * @function getLoadingStatus
   * @param {string} contextName representing the context for which we need to fetch the loading status
   * @returns {boolean} true/false : Loading Status of the current container
   * @example <caption>Get the Loading status of the current context container</caption>
   * if(getPConnect().getLoadingStatus()==true){
   * //Code to handle the loading status
   * };
   * @private
   */
  getLoadingStatus(contextName) {
    let contextDataObj = null;
    if (!contextName) {
      contextDataObj = this.getDataObject().context_data;
    } else {
      contextDataObj = StateManager.getDataObject(contextName);
      contextDataObj = contextDataObj ? contextDataObj.context_data : null;
    }
    if (contextDataObj) {
      return contextDataObj.isLoading ? contextDataObj.isLoading : false;
    }
    return false;
  }

  /**
   * The errorMsgConfig types defines a JSON configuration object used to specify error messages in the store.
   * It is used as a parameter to {@link clearErrorMessages} to indicate which messages should be cleared.
   *
   * @typedef errorMsgConfig
   * @type {object}
   * @property {string} category type of message that needs to be deleted (category types - HTTP (App or top level), PAGE (use this category for messages above the assignment))
   * @property {string} context on which messages needs to be cleared. Can be empty in which case error messages on app container are cleared.
   * @property {string} property name on which messages needs to be cleared.
   *
   * @example <caption>errorConfig to clear messages on a context</caption>
   * {category: "HTTP", context: "app/primary"}
   * This will clear the error messages at the "app/primary" level which belong to category of type HTTP (like 4XX errors).
   *
   * @example <caption>errorConfig to clear messages on AppShell</caption>
   * {category: "HTTP"}
   * This will clear the error messages at the App level which belongs to category HTTP.
   *
   * @example <caption>errorConfig to clearing messages on FirstName property</caption>
   * {property: ".firstName"}
   * This will clear the error messages related to the property: firstName.
   */

  /**
   * Deletes the error messages associated with a specific context (like an assignment, case flow) or App container.
   * @function clearErrorMessages
   * @param {errorMsgConfig} errorMsgConfig The {@link errorMsgConfig} object contains information that specifies which error messages are cleared
   *
   * @example <caption>Example for clearing messages on a context</caption>
   * getPConnect().clearErrorMessages({category: "HTTP", context: "app/primary"});
   * This will clear the error messages at the "app/primary" level which belong to category of type HTTP (like 4XX errors).
   *
   * @example <caption>Example for clearing messages on AppShell</caption>
   * getPConnect().clearErrorMessages({category: "HTTP"});
   * This will clear the error messages at the App level which belongs to category HTTP.
   *
   * @example <caption>Example for clearing messages on FirstName property</caption>
   * getPConnect().clearErrorMessages({property: ".firstName"});
   * This will clear the error messages on the property firstName.
   *
   * @public
   */
  clearErrorMessages(errorMsgConfig) {
    if (errorMsgConfig?.property) {
      MessageManager.clearMessages({
        type: 'error',
        property: errorMsgConfig.property,
        pageReference: this.getPageReference(),
        context: this.getContextName()
      });
    } else if (errorMsgConfig?.context) {
      MessageManager.clearMessages({
        type: 'error',
        ...errorMsgConfig
      });
    } else {
      MessageManager.clearMessages({
        type: 'error',
        ...errorMsgConfig,
        context: constants.APP.APP
      });
      MessageManager.clearMessages({
        type: 'error',
        ...errorMsgConfig,
        context: ContainerUtils.getActiveContainerItemName(`${constants.APP.APP}/${this.getContainerName()}`)
      });
    }
  }

  resolveDatasourceReference(datasourceRef) {
    const pageListContextData = StateManager.lookupContextValue(
      `${this.getPageReference()}.${constants.SUMMARY_OF_LISTS}`,
      datasourceRef,
      this.getContextName()
    );
    return pageListContextData || datasourceRef;
  }

  /**
   * Removes the field from form map
   * @function removeFormField
   *
   * @example <caption>Example for clearing form on a context</caption>
   * getPConnect().removeFormField();
   * This will clear the form in the current context.
   *
   * @private
   */
  removeFormField() {
    if (
      this._rawConfig &&
      this.options.hasForm &&
      this._rawConfig.config &&
      (Object.prototype.hasOwnProperty.call(this._rawConfig.config, 'value') ||
        Object.prototype.hasOwnProperty.call(this._rawConfig.config, 'readonlyContextList'))
    ) {
      removeFormFieldFromMap(
        this.getContextName(),
        AnnotationProcessor.trimAnnotation(this.getRawConfigProps()?.value) ||
          AnnotationProcessor.trimAnnotation(this.getRawConfigProps()?.readonlyContextList),
        this.getPageReference()
      );
    }
  }

  /**
   * Adds the field from form map
   * @private
   */
  addFormField() {
    if (
      this._rawConfig &&
      this.options.hasForm &&
      this._rawConfig.config &&
      Object.prototype.hasOwnProperty.call(this._rawConfig.config, 'value') &&
      this._type !== constants.SCALARLIST
    ) {
      addFormFieldFromMap(
        this.getContextName(),
        AnnotationProcessor.trimAnnotation(this.getRawConfigProps()?.value),
        this.getPageReference()
      );
    }
  }

  /**
   * This api is used to set the given page value to the given target
   * @param {object} sourcePage source page
   * @param {string} target target property name
   * @example <caption></caption>
   * const dataReferncePConn = getPConnect();
   * dataReferncePConn.setPageValue({street: 'ABC', city: 'DEF'}, '.EmbeddedPage');
   */
  setPageValue(sourcePage, target) {
    delete sourcePage?.classID;
    this.setValue(target, sourcePage);
    addPageFieldToForm(this.getContextName(), `${this.getPageReference()}${target}`, this);
  }

  /**
   * Accepts the value stored in the Suggestions Context for a field.
   * @param {string} target (optional) The field whose value stored in the Suggestions Context is accepted.
   * @function acceptSuggestion
   * @example <caption>Example for acceptSuggestion</caption>
   * In this example, the API accepts the value stored in the Suggestions Context for the Property1 field.
   * getPConnect().acceptSuggestion('.Proerty1');
   * @public
   */
  acceptSuggestion(target) {
    if (this.getStateProps()?.value && this.options.hasForm && this._hasSuggestions) {
      const suggestionContext = StateUtils.getSuggestionsContext(this.getContextName());
      const suggestionProp = this.getStateProps()?.value || target;
      const fullSuggestionPropRef = ContextProcessor.getContextPath(this.getPageReference(), suggestionProp);
      const suggestionValue = suggestionContext.getField(fullSuggestionPropRef);
      this._hasSuggestions = false;
      // it will update the field and trigger validation.
      this.getActionsApi().updateFieldValue(suggestionProp, suggestionValue);
      this.getActionsApi().triggerFieldChange(suggestionProp, suggestionValue);
      suggestionContext.removeField(fullSuggestionPropRef);
    }
  }

  /**
   * Ignores the value stored in the Suggestions Context for a field.
   * @param {string} target (optional) The field whose value stored in the Suggestions Context is ignored.
   * @function ignoreSuggestion
   *
   * @example <caption>Example for ignoreSuggestion</caption>
   * In this example, the API ignores the value stored in the Suggestions Context for the Property1 field.
   * getPConnect().ignoreSuggestion('.Proerty1');
   * @public
   */
  ignoreSuggestion(target) {
    if (this.getStateProps()?.value && this.options.hasForm && this._hasSuggestions) {
      const suggestionProp = this.getStateProps()?.value || target;
      const fullSuggestionPropRef = ContextProcessor.getContextPath(this.getPageReference(), suggestionProp);
      this._hasSuggestions = false;
      StateUtils.getSuggestionsContext(this.getContextName()).removeField(fullSuggestionPropRef);
    }
  }
}

export function createC11nEnv(config) {
  const c11EnvObj = new C11nEnv(config);
  return {
    getPConnect: () => c11EnvObj
  };
}

export function registerComponentCreator(creator) {
  componentCreator = creator;
}

export default {
  createC11nEnv,
  registerComponentCreator
};
