import ServiceConfig from './service-config';

class Configuration {
  constructor(config) {
    this.routingInfo = config.routingInfo;
    this.actionModel = config.actionModel;
    this.additionalHeaders = config.additionalHeaders;
    this.tokens = config.tokens;
    this.semanticUrl = config.semanticUrl;
    this.queryParams = config.queryParams;
    this.noPortal = config.noPortal;
    this.timezone = config.timezone;
    this.noHistory = config.noHistory;
    this.viewName = config.viewName;
    this.theme = config.theme;
    this.fieldDefaults = config.fieldDefaults;
    this.restServerConfig = config.restServerConfig;
    this.dynamicLoadComponents = config.dynamicLoadComponents !== undefined ? config.dynamicLoadComponents : true;
    this.dynamicSemanticUrl = config.dynamicSemanticUrl !== undefined ? config.dynamicSemanticUrl : true;
    this.enableRouting = config.enableRouting !== undefined ? config.enableRouting : true;
    this.locale = config.locale;
    this.environmentInfo = config.environmentInfo;
    this.renderingMode = config.renderingMode || 'FULL_PORTAL';
    this.remoteCaseMapping = config.remoteCaseMapping || {};
    this.envType = config.envType;
    this.serviceConfig = new ServiceConfig(config.serviceConfig);
    this.behaviorOverrides = config.behaviorOverrides;
    this.initBroadcast = !!config.initBroadcast;
    this.cookieComplianceMethod = config.cookieComplianceMethod ?? 'none';
  }
}

export default Configuration;
