import { produce } from 'immer';

import Constants from '../../../../constants';
import Utils from '../../../../utils/utils';
import { LIST_ACTION } from '../../actions';
import { mergeDeep, getReferenceTokens } from '../../state-manager';
import { addPageInstructionReferences } from '../../../../page-instructions';

const insertRow = (draft, context, referenceList, listIndex, content) => {
  Utils.setIn(draft[context], [...referenceList, listIndex], content);
};

const updateRow = (draft, context, referenceList, listIndex, content) => {
  const mergedContent = mergeDeep(Utils.getIn(draft[context], [...referenceList, listIndex], {}), content);
  Utils.setIn(draft[context], [...referenceList, listIndex], mergedContent);
};

const deleteRow = (draft, context, referenceList, listIndex) => {
  const referenceListResults = Utils.getIn(draft[context], referenceList, []);
  referenceListResults.splice(listIndex, 1);
  Utils.setIn(draft[context], referenceList, referenceListResults);
};

const moveRow = (draft, context, referenceList, listIndex, listMoveToIndex) => {
  const exisitingContent = Utils.getIn(draft[context], [...referenceList, listIndex], {});
  const referenceListResults = Utils.getIn(draft[context], referenceList, []);
  referenceListResults.splice(listIndex, 1);
  referenceListResults.splice(listMoveToIndex, 0, exisitingContent);
  Utils.setIn(draft[context], referenceList, referenceListResults);
};

const selectDeselectRow = (draft, context, referenceList, actions) => {
  const rows = Utils.getIn(draft[context], referenceList) || [];
  actions.forEach((action) => {
    const { instruction, content, listIndex } = action;
    if (instruction === Constants.LIST_ACTIONS.SELECT) {
      rows[listIndex] = content;
      referenceList.forEach((ref) => {
        addPageInstructionReferences(context, ref);
      });
    } else {
      delete rows[listIndex];
    }
  });
  Utils.setIn(draft[context], referenceList, rows.filter(Boolean));
};

const deselectAllRows = (draft, context, referenceList) => {
  Utils.setIn(draft[context], referenceList, []);
};

const listReducer = (state, action) =>
  produce(state, (draft) => {
    if (action.type === LIST_ACTION) {
      const { instruction, actions, context, referenceList, listIndex, listMoveToIndex, content = {} } = action.payload;
      const _referenceList = getReferenceTokens(referenceList);

      switch (instruction) {
        case Constants.LIST_ACTIONS.INSERT:
          insertRow(draft, context, _referenceList, listIndex, content);
          break;
        case Constants.LIST_ACTIONS.UPDATE:
          updateRow(draft, context, _referenceList, listIndex, content);
          break;
        case Constants.LIST_ACTIONS.DELETE:
          deleteRow(draft, context, _referenceList, listIndex);
          break;
        case Constants.LIST_ACTIONS.MOVE:
          moveRow(draft, context, _referenceList, listIndex, listMoveToIndex);
          break;
        case Constants.LIST_ACTIONS_TYPE.SELECT_DESELECT:
          selectDeselectRow(draft, context, _referenceList, actions);
          break;
        case Constants.LIST_ACTIONS_TYPE.DESELECT_ALL:
          deselectAllRows(draft, context, _referenceList);
          break;
      }
    }
    return draft;
  });

export default listReducer;
