import { from } from 'rxjs';
import { map } from 'rxjs/operators';

import { resolveRestApi } from '../../router/app-router';
import { fetch } from '../../service_broker/fetch';
import { ACTION_DELETE_CASE_IN_CREATESTAGE } from '../../store/state/actions';
import { processHeaders } from '../../store/state/epics/util';
import { logAndCatchError } from '../../utils/rxjs-utils';

export const executeDeleteCaseInCreateStageRequest = (actionInPayload) => {
  const { caseID, context } = actionInPayload;
  const { restApi, reqType } = resolveRestApi(ACTION_DELETE_CASE_IN_CREATESTAGE, { ID: caseID });

  return from(
    fetch(
      restApi,
      {
        method: reqType
      },
      context
    )
  ).pipe(
    map((response) => {
      return {
        response: response.data,
        actionInPayload,
        headers: processHeaders(response.headers)
      };
    }),
    logAndCatchError(': Error ocurred during ajax call at deleteCaseInCreateStage Epic : ', context)
  );
};

export default {
  executeDeleteCaseInCreateStageRequest
};
