class ServiceConfig {
  constructor(config) {
    this.appAlias = config.appAlias;
    this.appAliasWithoutServlet = config.appAliasWithoutServlet;
    this.googleMapKey = config.googleMapKey;
    this.staticContentServer = config.staticContentServer;
    this.appStaticContentServer = config.appStaticContentServer || null;
    this.contextPath = config.contextPath;
    this.messagingService = config.messagingService || null;
    this.reAuthUrl = config.reAuthUrl || null;
  }
}

export default ServiceConfig;
