import lodashDifference from 'lodash.difference';

import MessagingServiceManager from './manager';

/**
 * Module enables one subscription to websocket and enables
 * many components to subscribe to.
 * Example:
 * Case updaates like adding new file, followers etc.,
 */
class AtomicSubscription {
  /*
   * @description
   * Intialization
   * @param {string} matcher eq., CASE
   * @param {object} criteria eq., {
   *  caseId: "Approval-Request A-101"
   * }
   */
  constructor(matcher, criteria) {
    /**
     * object structure:
     * {
     *  "<subId>": [
     *  {
     *      criteria: {
     *      },
     *      callback: fn
     *  }
     * ]
     * }
     */
    this.atomicSubscribers = {};
    this.connectionsCount = 1;
    this.connected = false;
    this.matcher = matcher;
    this.criteria = criteria;
  }

  /*
   * @description
   * handles messages
   * @param {object} message.message
   * example:
   * {
   *  filter: {
   *      matcher: "CASE",
   *      criteria: {
   *          caseId: "Approval-Request A-101"
   *      }
   *  },
   *  message: {
   *      "attachmentUpdated": true
   *  }
   * }
   *
   * If any message properties matches to criteria then
   * callbacks will be invoked.
   */
  _handleAtomicMessage({ message }) {
    Object.keys(this.atomicSubscribers).forEach((subId) => {
      const subscription = this.atomicSubscribers[subId];
      const subCriteriaKeys = Object.keys(subscription.criteria);
      const messageKeys = Object.keys(message);

      if (lodashDifference(subCriteriaKeys, messageKeys).length === 0) {
        subscription.callback(message);
      }
    });
  }

  _getAutoGenId() {
    this.connectionsCount += 1;
    return `${Date.now()}_${this.connectionsCount}`;
  }

  /*
   * @description
   * Register to events for specific object.
   * @param {object} criteria example {attachmentUpdated: true}
   * @param {function} callback
   * @returns
   */
  subscribe(criteria, callback) {
    if (!this.connected) {
      const subscription = {
        matcher: this.matcher,
        criteria: this.criteria
      };
      MessagingServiceManager.subscribe(subscription, this._handleAtomicMessage.bind(this));
      this.connected = true;
    }

    const subId = this._getAutoGenId();

    this.atomicSubscribers[subId] = {
      criteria,
      callback
    };

    return subId;
  }

  unsubscribe(subscriptionId) {
    delete this.atomicSubscribers[subscriptionId];
  }
}

export default AtomicSubscription;
