import AnnotationsHandler from '../annotations-handler';
import getParagraphStream from '../../dynamic-content/util';

AnnotationsHandler.registerHandler('@PARAGRAPH', (paragraphName, c11nEnv) => {
  const paragraphContent = c11nEnv.getLocalizedParagraphValue(paragraphName);

  return paragraphContent ? getParagraphStream(paragraphContent, c11nEnv) : null;
});
