import PubSubUtils from '../../utils/pubsub-utils';
import APP_CONSTANTS from '../../constants';

export const publishCreateStageDoneEvent = (data) => {
  /* publish create stage info to infinity for case tracking */
  PubSubUtils.publish(APP_CONSTANTS.PUB_SUB_EVENTS.CASE_EVENTS.CREATE_STAGE_DONE, data);
};

export const publishExpressLocalActionSubmitEvent = (data) => {
  /* publishing event to notify, the express local action is submitted */
  PubSubUtils.publish(APP_CONSTANTS.PUB_SUB_EVENTS.EVENT_EXPRESS_LOCALACTION, data);
};
