import { ofType } from 'redux-observable';
import { map, mergeMap } from 'rxjs/operators';

import { ActionManagerInstance } from '../../actions/manager';
import { getResolvedTargetName } from '../../router/routing-manager';
import { getTranslatedDXAPIResponse } from '../../utils/dxapi-response-translator';
import { OPEN_EMBEDDED_DATA_MODAL, ERROR } from '../../store/state/actions';
import { dispatch } from '../../store/state/state-manager';
import {
  processDataToDispatchErrorAction,
  getContext,
  processViewResponse,
  getActionPayload,
  getActionBasedOnContainerMode,
  addPreviewResetAction
} from '../../store/state/epics/util';
import { hasErrorActionType } from '../common-helper';
import { buildModalContext } from '../../utils/context-utils';

import { executeRequest } from './async-request';
import { generateInstructions } from './helper';

export const openEmbeddedDataModal = (action$) => {
  let actionMgrID = 0;
  let actionsOut = [];
  let toContext = '';
  return action$.pipe(
    ofType(OPEN_EMBEDDED_DATA_MODAL),
    map((action) => {
      ({ actionMgrID } = action.payload);
      return action.payload;
    }),
    mergeMap((payload) => {
      return executeRequest(payload);
    }),
    mergeMap(async (data) => {
      if (data && data.type === ERROR) {
        actionsOut.push(processDataToDispatchErrorAction(data));
        return {
          actionsOut,
          actionInPayload: {
            skipPageInstructions: true
          }
        };
      }
      const { actionInPayload, response } = data;
      const containerName = actionInPayload.containerName;
      const target = getResolvedTargetName(containerName, actionInPayload.context);
      let context = getContext(target, containerName, actionInPayload.context); // app/modal_1
      context = buildModalContext(actionInPayload.context, context); /** context = app/primary_1/workarea_1/modal_1 */

      const translatedDXAPIResponse = getTranslatedDXAPIResponse(response);
      translatedDXAPIResponse.key = actionInPayload.ID;
      translatedDXAPIResponse.data.caseInfo.ID = actionInPayload.ID;
      const modalView = {
        isMultiRecordData: true,
        viewName: actionInPayload.viewID,
        /** parentContext = workarea context */
        parentContext: actionInPayload.context
      };
      translatedDXAPIResponse.uiResources.context_data.modalView = modalView;

      const options = { acName: target, context };
      await processViewResponse(translatedDXAPIResponse, actionInPayload, options);
      const commonInfo = { target, context, containerItemID: context };
      const payload = getActionPayload(translatedDXAPIResponse, commonInfo);

      payload.resourceStatus = actionInPayload.actionType;
      payload.isMultiRecordData = true;
      payload.parentContext = actionInPayload.context;

      const action = getActionBasedOnContainerMode(target, payload);

      toContext = context;
      actionsOut.push(action);
      addPreviewResetAction(actionsOut);

      return { actionsOut, actionInPayload };
    }),
    map(({ actionInPayload }) => {
      if (hasErrorActionType(actionsOut)) {
        ActionManagerInstance.rejectActionByID(actionMgrID);
      } else {
        ActionManagerInstance.resolveActionByID(actionMgrID);
      }
      dispatch(actionsOut);
      actionsOut = [];
      if (!actionInPayload.skipPageInstructions) {
        generateInstructions(
          toContext,
          actionInPayload.pageReference,
          actionInPayload.targetProperty,
          actionInPayload.index,
          actionInPayload.actionType
        );
      }
      return actionsOut;
    })
  );
};

export default openEmbeddedDataModal;
