import Constants from '../constants';
import Utils from '../utils/utils';
import * as RuleStore from '../rules/rules-store';
import DataApiUtils from '../data-view/DataApiUtils';
import MessagingService from '../messagingservice/manager';
import CascadeManager from '../cascade/manager';
import { fetch } from '../service_broker/fetch';
import { resolveRestApi } from '../router/app-router';

import Data from './data';

/**
 * @description
 * The Datapage Class contains utility APIs to retrieve data from data pages.
 */

class DataPageUtils {
  isCacheDisabled = false;
  constructor() {
    this.datastore = {};

    /*
     * {
     *  "<datapageName>": {
     *    "callback": {
     *      "<subId>": fn
     *    }
     *  }
     * }
     */
    this.subscribers = {};
    this.isCacheEnabled = true;

    this.subscribedDataPageNames = new Set();
  }

  /**
   * API to get data page results from cache
   * @param {string} dataPageName data page name
   * @param {object} parameters parameters associated to it.
   * parameters: {
   *  "paramName": "paramValue"
   * }
   * @param {object} paging object containing information about the paging
   * paging: {
   *  "pageNumber": 2,
   *  "pageSize": 60
   * }
   * @param {object} query object containing information about the list of columns, filters
   * @private
   */
  getData(dataPageName, parameters = {}, paging, query) {
    const dataPageWithHash = this._generateHash(dataPageName, parameters, query);
    if (dataPageWithHash === null) {
      return [];
    }
    if (this.datastore[dataPageName] && this.datastore[dataPageName][dataPageWithHash]) {
      const dataInstance = this.datastore[dataPageName][dataPageWithHash];

      if (dataInstance.isDirty) {
        return [];
      }
      return dataInstance.getRecords(paging);
    }
    return [];
  }

  _generateHash(dataPageName, parameters, query) {
    const dataPageMetadata = RuleStore.getDataPageMetadata(dataPageName);
    if (!dataPageMetadata) {
      return null;
    }
    const dataPageParameters = dataPageMetadata.parameters || [];

    let hashDigest = '';
    let dataPageWithHash = dataPageName;
    const propValues = [];

    dataPageParameters.forEach((param) => {
      if (parameters[param.name]) {
        propValues.push(parameters[param.name]);
      } else {
        propValues.push(param.defaultValue || Constants.NULL_STRING);
      }
    });

    if (query) {
      propValues.push(JSON.stringify(query));
    }

    /* istanbul ignore else */
    if (propValues.join(Constants.HASH_DELIMITER)) {
      hashDigest = Utils.getHashedString(propValues.join(Constants.HASH_DELIMITER));
      dataPageWithHash = dataPageName + Constants.UNDERSCORE_STRING + hashDigest;
    }

    return dataPageWithHash;
  }

  /**
   * API to get list type of data page results
   * @param {string} dataPageName name of the list type of data page whose results to be fetched
   * @param {string} context context name
   * @param {object} parameters optional, object containing parameters associated to the datapage.
   * @param {object} paging optional, object containing information about the paging
   * @param {object} query optional, object containing information about the list of columns, filters
   * @param {object} options Optional, Javascript object containing optional preferences for fetching data page results
   * @param {boolean} [options.invalidateCache] -  the flag that indicates whether the cache needs to invalidated for the current parameter set passed<br>
   * @param {boolean} [options.purgeDataPageCache] - the flag that indicates whether the cache needs to invalidated for all the parameter sets of the datapage passed<br>
   * {
   *  invalidateCache: true,
   *  purgeDataPageCache: true
   * }
   * @returns {object} response object with list type datapage results
   * @example  <caption>In the example below, the API retrieves the first 10 records of D_EmployeeList</caption>
   * const dataViewName = "D_EmployeeList";
   * const parameters = {
   * "dept": "Engineering"
   * };
   * const paging = {
   *  "pageNumber":1,
   *  "pageSize":10
   * };<br>
   * const query = {
   *  "distinctResultsOnly": true,
   *  "select": [
   *   {
   *     "field": "Name"
   *    },
   *   {
   *    "field": "Role"
   *   },
   *   {
   *    "field": "Gender"
   *   }
   *  ]
   * };
   * const context = "app/primary_1";
   * PCore.getDataPageUtils().getDataAsync(dataViewName, context, paramneters, paging, query);
   * .then(response => {
   *  // The response of this API is as shown below:
   *  {
   *      data: [
   *        {
   *          "Name" : "Mark D",
   *          "Role" : "Software Engineer",
   *          "Gender" "Male"
   *        },
   *        {
   *          "Name" : "Lara",
   *          "Role" : "Electrician",
   *          "Gender" "Female"
   *        }
   *      ]
   *      fetchDateTime: "2020-06-29T11:06:24.329Z"
   *      pageNumber: 1
   *      pageSize: 10
   *  }
   * })
   * .catch(error => {
   *    console.log(error);
   * });
   *
   * @public
   */

  async getDataAsync(dataPageName, context, parameters = {}, paging, query, options = {}) {
    this._subscribeToMessageService(dataPageName);

    if (options.purgeDataPageCache) {
      this.datastore[dataPageName] = {};
    }
    if (!this._isCacheable(dataPageName, query) || options.invalidateCache) {
      const response = await this._fetchData(dataPageName, parameters, paging, query, context, options);
      const data = response?.data?.data ?? [];
      return {
        ...response.data,
        data,
        pageNumber: response?.data?.pageNumber,
        pageSize: response?.data?.pageSize,
        queryStats: response?.data?.queryStats,
        status: response?.status
      };
    }
    const results = this.getData(dataPageName, parameters, paging, query);
    if (results.length > 0) {
      return { data: results };
    }
    const serverResults = await this._fetchData(dataPageName, parameters, paging, query, context, options);
    return {
      ...serverResults.data,
      data: this._mergeDataStore(dataPageName, parameters, paging, query, serverResults),
      pageNumber: serverResults?.data?.pageNumber,
      pageSize: serverResults?.data?.pageSize,
      queryStats: serverResults?.data?.queryStats,
      status: serverResults?.status
    };
  }

  /**
   * API to get page type of data page results
   * @param {string} dataPageName name of the page type of data page whose results to be fetched
   * @param {string} context - optional, name of the context
   * "app/primary_1"
   * @param {object} parameters optional, object containing parameters associated to data page.
   * parameters: {
   *  "paramName": "paramValue"
   * }
   * @param {object} options - optional, Javascript object containing optional preferences for fetching data page results
   * @param {boolean} [options.invalidateCache] -  The flag that indicates whether the cache needs to invalidated for the current parameter set passed<br>
   * {
   *  invalidateCache: true
   * }
   * @returns {object} response object with page type datapage result
   * @example  <caption>In the example below, the API retrieves the D_FollowedBugsCount page type datapage result</caption>
   *  const dataViewName = "D_FollowedBugsCount";
   *  const parameters = {
   *  "ID": "12311"
   *` };
   *  const context = "app/primary_1";
   *  const options = {
   *   invalidateCache: true;
   *  }
   * PCore.getDataPageUtils().getPageDataAsync(dataViewName, context, parameters, options);
   * .then(response => {
   *  // The response of this API is as shown below:
   *  {
   *    "MyFollowedBugsCount":16,
   *    "pzLoadTime":"August 25, 2022 9:50:59 PM UTC",
   *    "pzPageNameHash":"_pa42316787137117pz"}
   * })
   * .catch(error => {
   *    console.log(error);
   * });
   * @public
   */
  async getPageDataAsync(dataPageName, context, parameters = {}, options = {}) {
    const { restApi: restUrl, reqType } = resolveRestApi(Constants.GET_PAGE_DATA, {
      dataViewName: dataPageName,
      dataViewParameters: JSON.stringify(parameters)
    });

    const reqConfig = {
      method: reqType
    };
    if (options && options.invalidateCache) {
      delete this.datastore[dataPageName];
    }
    if (options && !options.invalidateCache && this.datastore[dataPageName]) {
      return this.datastore[dataPageName];
    }
    const getPageDataUrl = restUrl && Object.keys(parameters).length === 0 ? restUrl.split('?')[0] : restUrl;
    return fetch(encodeURI(getPageDataUrl), reqConfig, context).then((response) => {
      if (!this._isPerInteraction(dataPageName)) this.datastore[dataPageName] = response.data || {};
      return response.data;
    });
  }

  updateDataPageStore(sharedDataPages = {}) {
    Object.keys(sharedDataPages).forEach((dataPageName) => {
      if (!this.datastore[dataPageName]) {
        this.datastore[dataPageName] = {};
      }

      const pages = sharedDataPages[dataPageName];

      Object.keys(pages).forEach((hashDpName) => {
        if (this.datastore[dataPageName][hashDpName]) {
          const dataInstance = this.datastore[dataPageName][hashDpName];
          dataInstance.mergeRecords(sharedDataPages[dataPageName][hashDpName].pxResults);
        } else {
          const dataInstance = new Data();
          this.datastore[dataPageName][hashDpName] = dataInstance;
          dataInstance.mergeRecords(sharedDataPages[dataPageName][hashDpName].pxResults);
        }
      });
    });
  }

  _mergeDataStore(dataPageName, parameters, paging, query, results) {
    const dataPageWithHash = this._generateHash(dataPageName, parameters, query);
    const data = results?.data?.data;
    if (!data || data.length === 0) {
      return [];
    }

    if (dataPageWithHash === null) {
      return data;
    }

    if (!this.datastore[dataPageName]) {
      this.datastore[dataPageName] = {};
    }

    if (!this.datastore[dataPageName][dataPageWithHash]) {
      this.datastore[dataPageName][dataPageWithHash] = new Data();
    }

    const dataInstance = this.datastore[dataPageName][dataPageWithHash];
    dataInstance.mergeRecords(data, paging);
    dataInstance.isDirty = false;
    return dataInstance.getRecords(paging);
  }

  async _fetchData(dataPageName, parameters, paging, query, context, options) {
    const { additionalApiParams: { useExtendedTimeout = false, includeTotalCount = false } = {}, ...otherOptions } =
      options;
    const payload = {
      dataViewParameters: parameters
    };

    if (includeTotalCount) {
      payload.includeTotalCount = true;
    }
    if (useExtendedTimeout) payload.useExtendedTimeout = true;
    if (paging) payload.paging = paging;
    if (query) payload.query = query;
    return await DataApiUtils.getData(dataPageName, payload, context, otherOptions);
  }

  isDataPage(propertyRef = '') {
    return propertyRef.startsWith('D_') || propertyRef.startsWith('Declare_');
  }

  _handleCascadeUpdate() {
    const { dataPageName, scope } = this;
    scope._handleMessage({
      message: {
        datapage: dataPageName
      }
    });
  }

  subscribeToUpdate(dataPageName, dependentFields, context, pageReference, callback, subscriptionId) {
    this.subscribers[dataPageName] = this.subscribers[dataPageName] || {};
    this.subscribers[dataPageName].callback = this.subscribers[dataPageName].callback || {};

    if (!this.subscribers[dataPageName].callback[subscriptionId]) {
      this.subscribers[dataPageName].callback[subscriptionId] = callback;
    }
    this._subscribeToCascadeUpdates(dataPageName, dependentFields, context, pageReference, subscriptionId);
  }

  unsubscribe(dataPageName, dependentFields, context, pageReference, subscriptionId) {
    delete this.subscribers[dataPageName]?.callback[subscriptionId];
    if (dependentFields && dependentFields.length > 0) {
      CascadeManager.unRegisterFields(context, pageReference, dependentFields, subscriptionId);
    }
  }

  _subscribeToCascadeUpdates(dataPageName, dependentFields, context, pageReference, subscriptionId) {
    // Subscribe to parameter updates
    if (dependentFields && dependentFields.length > 0) {
      CascadeManager.registerFields(
        context,
        pageReference,
        dependentFields,
        this._handleCascadeUpdate.bind({ dataPageName, scope: this }),
        subscriptionId
      );
    }
  }

  _subscribeToMessageService(dataPageName) {
    if (dataPageName && this.subscribedDataPageNames.has(dataPageName)) {
      return;
    }
    const filter = {
      matcher: 'DATAPAGE_UPDATED',
      criteria: {
        datapage: dataPageName
      }
    };

    MessagingService.subscribe(filter, this._handleMessage.bind(this));
    this.subscribedDataPageNames.add(dataPageName);
  }

  /**
   * Message recieved from web socket service.
   * @param {*} data  = {message, msgId}  format
   * {
   *  matcher: "DATAPAGE_UPDATED",
   *  message: {
   *      "datapage": "D_Test"
   *  }
   * }
   * @private
   */
  _handleMessage(data) {
    const { msgId, message } = data;
    const { datapage } = message;
    const datapageCallback = this.subscribers[datapage];
    this._markDataInstancsDirty(datapage);
    if (datapageCallback && datapageCallback.callback) {
      Object.keys(datapageCallback.callback).forEach((subId) => {
        datapageCallback.callback[subId](data.message, !!msgId);
      });
    }
  }

  _markDataInstancsDirty(dataPageName) {
    if (this.datastore[dataPageName]) {
      const dataInstances = Object.keys(this.datastore[dataPageName]);
      dataInstances.forEach((dataPageHash) => {
        this.datastore[dataPageName][dataPageHash].isDirty = true;
      });
    }
  }

  _isPerInteraction(dataPageName) {
    const dataPageMetadata = RuleStore.getDataPageMetadata(dataPageName);
    return (
      dataPageMetadata &&
      dataPageMetadata.refreshStrategy &&
      dataPageMetadata.refreshStrategy.type === 'reloadOncePerInteraction'
    );
  }

  _isCacheable(dataPageName, query) {
    if (this._isPerInteraction(dataPageName) || !this.isCacheEnabled) {
      return false;
    }
    return !(query && Object.keys(query).length > 1);
  }

  /**
   * disable or enable the cache for
   * @param {boolean} cacheDisable value whether Cache Should be disabled or enabled
   * @example  <caption>In the example below, the API set the value to disable or enable the cache. </caption>
   *  PCore.getDataPageUtils().disableCache(true)
   */
  disableCache(cacheDisable) {
    this.isCacheEnabled = !cacheDisable;
  }
}

export default new DataPageUtils();
