import { getDataObject } from '../store/state/state-manager';
import StateMachineManager from '../page-instructions/state-machine-manager';

import Utils from './utils';

const formFieldsMap = {};
const formPageFieldsMap = {};
const formDuplicateFieldsMap = {};

/**
 * Returns the property name without period if it exists
 * @param {string} propertyName property name to be normalized
 */
export const getNormalizedPropertyName = (propertyName) => {
  return propertyName.charAt(0) === '.' ? propertyName.substring(1) : propertyName;
};

export const getFullPropertyReference = (property = '', pageReference = '') => {
  return `${getNormalizedPropertyName(pageReference)}${property ? '.' : ''}${getNormalizedPropertyName(property)}`;
};

/**
 * Removes the remove FormFields from the formFieldsMap when
 * container item get deleted
 * @param {string} contextName context name to be removed from object
 */
export const removeFormFieldsFromMap = (contextName) => {
  delete formFieldsMap[contextName];
  delete formPageFieldsMap[contextName];
  delete formDuplicateFieldsMap[contextName];
};

export const addPageFieldToForm = (contextName, propertyName, value) => {
  const propName = getNormalizedPropertyName(propertyName);
  if (formPageFieldsMap[contextName]) {
    formPageFieldsMap[contextName].fieldOrder[propName] = value;
  } else {
    formPageFieldsMap[contextName] = {
      fieldOrder: {
        [propName]: value
      }
    };
  }
};

const updateFormDuplicatesFieldMap = (contextName, propName, fieldC11n, value) => {
  if (fieldC11n && fieldC11n.getStateProps().visibility !== value.getStateProps().visibility) {
    if (!formDuplicateFieldsMap[contextName]) {
      formDuplicateFieldsMap[contextName] = {};
    }
    if (!formDuplicateFieldsMap[contextName][propName]) {
      formDuplicateFieldsMap[contextName][propName] = {};
    }
    formDuplicateFieldsMap[contextName][propName][value.getStateProps().visibility] = value;
  }
};

const addFieldToForm = (contextName, propName, value) => {
  if (formFieldsMap[contextName]) {
    const existingC11n = formFieldsMap[contextName].fieldOrder[propName];
    updateFormDuplicatesFieldMap(contextName, propName, existingC11n, value);
    // usecase: view having same field twice. one with read only true and false.
    // if propName already exists with readonly false and new value has readonly true. ignore reinitializing.
    if (!existingC11n || !(!existingC11n.getConfigProps().readOnly && !!value.getConfigProps().readOnly)) {
      formFieldsMap[contextName].fieldOrder[propName] = value;
    }
    formFieldsMap[contextName].fieldOrder[propName].isError = false;
  } else {
    formFieldsMap[contextName] = {
      fieldOrder: {
        [propName]: value
      }
    };
    formFieldsMap[contextName].fieldOrder[propName].isError = false;
  }
};

const updatedFieldC11Env = (context, fieldC11n, key) => {
  let fieldC11nEnv = fieldC11n;

  if (formDuplicateFieldsMap[context]?.[key] && Object.keys(formDuplicateFieldsMap[context][key]).length) {
    Object.keys(formDuplicateFieldsMap[context][key]).forEach((visibility) => {
      if (formDuplicateFieldsMap[context][key][visibility]?.getConfigProps().visibility) {
        const duplicateFieldC11nEnv = formDuplicateFieldsMap[context][key][visibility];
        duplicateFieldC11nEnv.isMounted = fieldC11n.isMounted;
        fieldC11nEnv = duplicateFieldC11nEnv;
      }
    });
  }
  return fieldC11nEnv;
};

/**
 * Adds the fields to the formFieldsMap object
 * of its respective context
 * @param {string} contextName context to add in object
 * @param {string} propertyName property name added under the context
 * @param {*} value value of the property
 */
export const setFormFieldAsEditable = (contextName, propertyName, value) => {
  const propName = getNormalizedPropertyName(propertyName);
  addFieldToForm(contextName, propName, value);
};

/**
 * Sets the error flag to true for the field
 * @param {string} contextName context name containing error property
 * @param {string} propertyName name of the property which has error
 * @param {string} pageReference page reference of the property
 */
export const markAsErrorField = (contextName, propertyName, pageReference) => {
  if (formFieldsMap[contextName]) {
    const propName = getNormalizedPropertyName(getFullPropertyReference(propertyName, pageReference));
    const fieldC11nEnv = updatedFieldC11Env(contextName, formFieldsMap[contextName].fieldOrder[propName], propName);
    /* istanbul ignore else */
    if (fieldC11nEnv) {
      fieldC11nEnv.isError = true;
    }
  }
};

/**
 * Sets the error flag to false for the field
 * @param {string} contextName context name containing error property
 * @param {string} propertyName property to be set valid
 * @param {string} pageReference page reference of the property
 */
export const removeErrorFieldProperty = (contextName, propertyName, pageReference) => {
  if (formFieldsMap[contextName]) {
    const propName = getNormalizedPropertyName(getFullPropertyReference(propertyName, pageReference));
    const fieldC11nEnv = updatedFieldC11Env(contextName, formFieldsMap[contextName].fieldOrder[propName], propName);
    /* istanbul ignore else */
    if (fieldC11nEnv) {
      fieldC11nEnv.isError = false;
      if (formDuplicateFieldsMap[contextName]?.[propName]) {
        Object.keys(formDuplicateFieldsMap[contextName][propName]).forEach((visibility) => {
          formDuplicateFieldsMap[contextName][propName][visibility].isError = false;
        });
      }
    }
  }
};

/**
 * Removes the field from form map
 * @param {string} contextName context name containing the property
 * @param {string} propertyName property to be removed
 * @param {string} pageReference page reference of the property
 */
export const removeFormFieldFromMap = (contextName, propertyName, pageReference) => {
  if (formFieldsMap[contextName]) {
    const propName = getFullPropertyReference(propertyName, pageReference);
    if (formFieldsMap[contextName].fieldOrder[propName]) {
      formFieldsMap[contextName].fieldOrder[propName].isMounted = false;
    }
  }
};

/**
 * Adds the field from form map
 * @param {string} contextName context name containing the property
 * @param {string} propertyName property to be added
 * @param {string} pageReference page reference of the property
 */
export const addFormFieldFromMap = (contextName, propertyName, pageReference) => {
  if (formFieldsMap[contextName]) {
    const propName = getFullPropertyReference(propertyName, pageReference);
    if (formFieldsMap[contextName].fieldOrder[propName]) {
      formFieldsMap[contextName].fieldOrder[propName].isMounted = true;
    }
  }
};

/**
 * Returns the validation object
 */
export const getFormFieldsMap = () => {
  return formFieldsMap;
};

/**
 * this function helps to provide the visibility value of field
 * @param {*} c11nEnv - field c11nEnv object
 * @private
 */
const isFieldVisible = (c11nEnv) => {
  const isExpressionExist = c11nEnv.isExpressionExist();
  return (
    c11nEnv.isMounted !== false &&
    ((isExpressionExist && c11nEnv.getConfigProps().visibility !== false) ||
      (!isExpressionExist &&
        (c11nEnv.getConfigProps().visibility === undefined ||
          c11nEnv.getConfigProps().visibility === 'true' ||
          c11nEnv.getConfigProps().visibility === true)))
  );
};

/**
 * this function helps to return the readonly status of field
 * @param {*} c11nEnv - field c11nEnv object
 * @private
 */
const isFieldReadOnly = (c11nEnv) => {
  const isReadOnly = c11nEnv.getConfigProps().readOnly;
  return isReadOnly === true || isReadOnly === 'true';
};

export const getEditableFormFieldsReferenceList = (context, options = {}) => {
  const editableFormFieldsReferenceList = [];
  let attachmentList = [];
  const formPageFieldsList = [];
  const formFields = getFormFieldsMap();
  const { ignoreFieldsInsideList = true } = options;
  if (formFields[context]) {
    for (const key in formFields[context].fieldOrder) {
      if (Object.prototype.hasOwnProperty.call(formFields[context].fieldOrder, key)) {
        const fieldC11nEnv = formFields[context].fieldOrder[key];
        const isElementVisible = isFieldVisible(fieldC11nEnv);
        const isInsideList = fieldC11nEnv.isInsideList();
        const fieldProperty = fieldC11nEnv.getStateProps()?.value || fieldC11nEnv.getStateProps()?.selectionList;
        if (isElementVisible && fieldC11nEnv.isEditable() && !isFieldReadOnly(fieldC11nEnv)) {
          if (fieldC11nEnv.getMetadata().type === 'Attachment') {
            let attachmentsInfo = (
              PCore.getStoreValue(`.attachmentsList.${fieldProperty.replace('.', '')}`, 'context_data', context) || []
            ).filter((f) => {
              return `${fieldC11nEnv.options.pageReference}.${f.label}` === key;
            });
            attachmentsInfo = Utils.buildAttachmentsFormData(attachmentsInfo);
            if (attachmentsInfo.length > 0) {
              attachmentList = [...attachmentList, ...attachmentsInfo];
            }
          } else if (
            (!isInsideList || (isInsideList && !ignoreFieldsInsideList)) &&
            !Array.isArray(fieldC11nEnv.getValue(key))
          ) {
            const isExecuted = StateMachineManager.executeStateMachine(
              context,
              key,
              fieldC11nEnv.getValue(fieldProperty)
            );
            if (!isExecuted) {
              editableFormFieldsReferenceList.push(key);
            }
          } else if (isInsideList && !isFieldReadOnly(fieldC11nEnv) && fieldProperty) {
            StateMachineManager.executeStateMachine(context, key, fieldC11nEnv.getValue(fieldProperty), false);
            // fieldC11nEnv.getListActions().updateProperty(fieldProperty, fieldC11nEnv.getValue(fieldProperty));
          }
        }
      }
    }
  }
  if (formPageFieldsMap[context]) {
    for (const key in formPageFieldsMap[context].fieldOrder) {
      if (Object.prototype.hasOwnProperty.call(formPageFieldsMap[context].fieldOrder, key)) {
        formPageFieldsList.push(key);
      }
    }
  }
  return {
    formInputs: editableFormFieldsReferenceList,
    attachments: attachmentList,
    formPageFields: formPageFieldsList
  };
};

/**
 * this function provides the values for the formfields present in the given context
 * @param {*} context - context of form
 * @param {*} options - optional parameters like ignoreFieldsInsideList
 * @returns {*} contentObject
 */
export const getFormContent = (context, options = {}) => {
  const resultObj = {};
  const data = getDataObject(context);
  const { ignoreFieldsInsideList = true } = options;
  if (data && data.caseInfo && data.caseInfo.content) {
    const { formInputs, attachments, formPageFields } = getEditableFormFieldsReferenceList(context, {
      ignoreFieldsInsideList
    });
    if (attachments && attachments.length > 0) {
      Utils.buildObject('caseInfo.attachments', resultObj, attachments);
    }
    if (formPageFields && formPageFields.length > 0) {
      formPageFields.forEach((propRef) => {
        let valueObj = Utils.getIn(data, propRef);
        if (typeof valueObj === 'object') {
          // deepClone the object to avoid Readonly object errors when setting properties inside the object.
          valueObj = Utils.deepClone(valueObj);
          delete valueObj?.classID;
        }
        Utils.buildObject(propRef, resultObj, valueObj);
      });
    }
    if (formInputs && formInputs.length > 0) {
      formInputs.forEach((propRef) => {
        Utils.buildObject(propRef, resultObj, Utils.getIn(data, propRef));
      });
    } else {
      Utils.buildObject('caseInfo.content', resultObj, {});
    }
  }
  if (data && data.dataInfo && data.dataInfo.content) {
    const { formInputs } = getEditableFormFieldsReferenceList(context, { ignoreFieldsInsideList });
    if (formInputs && formInputs.length > 0) {
      formInputs.forEach((propRef) => {
        Utils.buildObject(propRef, resultObj, Utils.getIn(data, propRef));
      });
    }
  }
  return resultObj;
};

/**
 * Checks whether form is valid or not
 * based on the validation values for form fields
 * @param {string} context context of form fields to be validated
 * @returns {boolean} boolean
 */

export const isFormValid = (context) => {
  const formFields = getFormFieldsMap();
  let firstErrorFiled;
  let isValid = true;
  if (formFields[context]) {
    for (const key in formFields[context].fieldOrder) {
      if (Object.prototype.hasOwnProperty.call(formFields[context].fieldOrder, key)) {
        const fieldC11nEnv = updatedFieldC11Env(context, formFields[context].fieldOrder[key], key);
        let propKey = 'value';
        let fieldProperty = fieldC11nEnv.getStateProps()[propKey];
        if (!fieldProperty) {
          const componentConfig = fieldC11nEnv.getComponentConfig();
          if (componentConfig.selectionMode === 'multi') {
            propKey = 'selectionList';
          } else if (componentConfig.renderMode === 'Editable') {
            propKey = 'referenceList';
          }
          fieldProperty = fieldC11nEnv.getStateProps()[propKey];
        }
        const isElementVisible = isFieldVisible(fieldC11nEnv);

        if (
          isElementVisible &&
          !fieldC11nEnv.isError &&
          !isFieldReadOnly(fieldC11nEnv) &&
          !fieldC11nEnv.getConfigProps().disabled
        ) {
          fieldC11nEnv.getValidationApi().validate(fieldC11nEnv.getValue(fieldProperty));
        }
        if (!firstErrorFiled && fieldC11nEnv.isError && isElementVisible) {
          firstErrorFiled = key;
        }
        if (isValid && fieldC11nEnv.isError && isElementVisible) {
          isValid = false;
        }
      }
    }
  }
  return isValid;
};

/**
 * Checks whether form has active suggestion
 * @private
 * @param {string} context context of form fields
 * @returns {boolean} boolean
 */
export const isFormHasSuggestions = (context) => {
  const formFields = getFormFieldsMap();
  if (formFields[context]) {
    for (const key in formFields[context].fieldOrder) {
      if (Object.prototype.hasOwnProperty.call(formFields[context].fieldOrder, key)) {
        const fieldC11nEnv = formFields[context].fieldOrder[key];
        if (fieldC11nEnv.getConfigProps().hasSuggestions) {
          return true;
        }
      }
    }
  }
  return false;
};

/**
 * returns all the fields in the form which are visible
 * @param {string} context context
 */
export const getVisibleFormFields = (context) => {
  const formFields = getFormFieldsMap();
  const fields = [];
  if (formFields[context]) {
    for (const key in formFields[context].fieldOrder) {
      if (
        Object.prototype.hasOwnProperty.call(formFields[context].fieldOrder, key) &&
        formFields[context].fieldOrder[key].getConfigProps().visibility !== false
      ) {
        fields.push(key.substring(key.indexOf('content') + 8));
      }
    }
  }
  return fields;
};

/**
 * returns all the fields in the form which are hidden
 * @param {string} context context
 */
export const getHiddenFormFields = (context) => {
  const formFields = getFormFieldsMap();
  const fields = [];
  if (formFields[context]) {
    for (const key in formFields[context].fieldOrder) {
      if (
        Object.prototype.hasOwnProperty.call(formFields[context].fieldOrder, key) &&
        (formFields[context].fieldOrder[key].getConfigProps().visibility === false ||
          formFields[context].fieldOrder[key].isMounted === false)
      ) {
        fields.push(key);
      }
    }
  }
  return fields;
};

/**
 * returns all the fields in the form which are readonly
 * @param {string} context context
 */
export const getReadOnlyFormFields = (context) => {
  const formFields = getFormFieldsMap();
  const fields = [];
  if (formFields[context]) {
    for (const key in formFields[context].fieldOrder) {
      if (
        Object.prototype.hasOwnProperty.call(formFields[context].fieldOrder, key) &&
        formFields[context].fieldOrder[key].getConfigProps().readOnly === true
      ) {
        fields.push(key);
      }
    }
  }
  return fields;
};

/**
 * returns all the fields in the form which are editable
 * @param {string} context context
 * @returns {Array} editable fields information in an array -
[{
    "name": "<field full path>",
    "label": "<field label>",
    "type": "<field type>"
}]
 */
export const getEditableFields = (context) => {
  const editableFormFields = [];
  const formFields = getFormFieldsMap();
  if (formFields[context]) {
    for (const key in formFields[context].fieldOrder) {
      if (Object.prototype.hasOwnProperty.call(formFields[context].fieldOrder, key)) {
        const fieldC11nEnv = formFields[context].fieldOrder[key];
        const isElementVisible = isFieldVisible(fieldC11nEnv);
        if (
          isElementVisible &&
          !isFieldReadOnly(fieldC11nEnv) &&
          !fieldC11nEnv.getConfigProps()?.disabled &&
          fieldC11nEnv.getMetadata()?.type !== 'Attachment' &&
          fieldC11nEnv.getMetadata()?.config?.type !== 'multirecordlist'
        ) {
          const fieldLabel = fieldC11nEnv.getConfigProps()?.label || fieldC11nEnv.getConfigProps()?.caption;
          const fieldType = fieldC11nEnv.getComponentName().toLowerCase();
          editableFormFields.push({ name: key, label: fieldLabel, type: fieldType, fieldC11nEnv });
        }
      }
    }
  }
  return editableFormFields;
};
